import React, { memo } from "react";
import { Box, Divider, Tooltip, Typography } from "@mui/material";
import userStore from "../../../core/stores/userStore";
import ChromeHistories from "./ChromeHistories";
import HistoryList from "./HistoryList";
import { useTranslationContext } from "../../Providers/TranslationProvider";

const HistorySearchresults = () => {
    const { t } = useTranslationContext();
    const { fontColor, searchText, tabSearchShowHistory, historyDataSource, historySearchLimitCount } = userStore((state) => ({
        fontColor: state.settings.fontColor,
        searchText: state.searchText,
        tabSearchShowHistory: state.settings.tabSearchShowHistory,
        historyDataSource: state.settings.historyDataSource,
        historySearchLimitCount: state.settings.historySearchLimitCount
    }));

    if (!tabSearchShowHistory || !searchText) {
        return null;
    }

    return (
        <Box sx={{ color: fontColor || 'text.primary', }}>
            <Divider sx={{ margin: "10px auto" }}>
                <Tooltip title={<Typography>{t('MultipleOpenLinksTips')}</Typography>}>
                    <Typography variant="body1">{t(historyDataSource === 'chrome' ? 'history' : 'RecentlyTabs')}</Typography>
                </Tooltip>
            </Divider>
            {historyDataSource === 'chrome' ? (
                <ChromeHistories limit={historySearchLimitCount} />
            ) : (
                <HistoryList limit={historySearchLimitCount} />
            )}
        </Box>
    );
};

export default memo(HistorySearchresults);