import React from 'react';
import { TextField, MenuItem, ListItemIcon, ListItemText, Typography, ListItem, ListItemButton } from '@mui/material';
import Autocomplete from '@mui/material/Autocomplete';
import useBookmarkFolders from './useBookmarkFolders';
import { useTranslationContext } from '../../Providers/TranslationProvider';
import { Folder } from '@mui/icons-material';
import { Padding } from '@mui/icons-material';

const SelectFolder = ({ selectedFolder, onChange }) => {

    const { t } = useTranslationContext();
    const folders = useBookmarkFolders();

    const renderFolderOptions = (folders) => {
        return folders
            .filter(folder => folder.id && folder.title && folder.level >= 0)
            .map((folder) => ({
                id: folder.id,
                title: folder.title,
                level: folder.level
            }));
    };

    const folderOptions = renderFolderOptions(folders);
    const selectedFolderOption = folderOptions.find(folder => folder.id === selectedFolder) || null;

    return (
        <Autocomplete
            value={selectedFolderOption}
            onChange={(event, newValue) => onChange(newValue ? newValue : null)}
            options={folderOptions}
            getOptionLabel={(option) => option.title}
            isOptionEqualToValue={(option, value) => option.id === value.id}
            renderInput={(params) => <TextField {...params} label={t('selectParentFolder')} fullWidth />}
            renderOption={(props, option) => (
                <ListItem disablePadding   {...props} style={{ paddingLeft: option.level * 12, minHeight: 0, paddingTop: 0, paddingBottom: 0 }}>
                    <ListItemIcon sx={{ minWidth: 0, px: 1 }}>
                        <Folder fontSize="small" />
                    </ListItemIcon>
                    <ListItemText noWrap> {option.title}</ListItemText>

                </ListItem >
            )}
            fullWidth
        />
    );
};

export default SelectFolder;
