import React, { memo } from "react";
import Tooltip from "@mui/material/Tooltip";
import ViewListIcon from "@mui/icons-material/ViewList";
import ViewQuiltIcon from "@mui/icons-material/ViewQuilt";
import { IconButton, Typography } from "@mui/material";
import { useTranslationContext } from "../Providers/TranslationProvider";
import userStore from "../../core/stores/userStore";
import { sendRuntimeMessage } from "../../core/browers/runtime";

const ViewSwitch = () => {
  const { t } = useTranslationContext('translation');
  const { view, fontColor, updateSetting } = userStore((state) => ({
    view: state.settings.view,
    fontColor: state.settings.fontColor,
    updateSetting: state.updateSetting,
  }));

  const handleViewChange = () => {
    const newView = view === "visual" ? "list" : "visual";
    updateSetting("view", newView);
    sendRuntimeMessage("update_setting", { key: "view", value: newView });
  };

  return (
    <Tooltip arrow title={<Typography>{t(view === "visual" ? "view.Compact" : "view.Visual")}</Typography>}>
      <IconButton sx={{ color: fontColor || 'text.primary' }} onClick={handleViewChange}>
        {view === "visual" ? (
          <ViewQuiltIcon />
        ) : (
          <ViewListIcon />
        )}
      </IconButton>
    </Tooltip>
  );
};

export default memo(ViewSwitch);
