import React, { useEffect, useState, useRef } from 'react';
import {
    Box,
    TextField,
    List,
    Typography,
    Divider,
    Stack
} from '@mui/material';
import { useTranslation } from 'react-i18next';
import { OverlayScrollbarsComponent } from 'overlayscrollbars-react';
import { useHotkeys } from "react-hotkeys-hook";
import { selectStore } from "../../../core/stores/selectStore";
import { historyStore } from "../../../core/stores/historyStore";
import PageLoading from '../../component/pageLoading';
import RecentlyTabItem from './RecentlyTabItem';
import userStore from '../../../core/stores/userStore';
import BgOverlay from '../BgOverlay';
import ColorTypography from '../../component/ColorTypography';
import RecentlyTabsHeader from './RecentlyTabsHeader';

const RecentlyTabs = ({ onSelect }) => {

    const { t } = useTranslation();
    const { theme, fontColor, searchQuery, setSearchQuery } = userStore((state) => ({
        theme: state.settings.theme,
        fontColor: state.settings.fontColor,
        searchQuery: state.searchQuery,
        setSearchQuery: state.setSearchQuery,
    }));

    const [localRecentlyTabs, setLocalRecentlyTabs] = useState([]);
    const [loading, setLoading] = useState(true);
    const [selectedIndex, setSelectedIndex] = useState(-1);
    const searchInputRef = useRef(null);
    const listRef = useRef(null);

    const loadHistory = async () => {
        const historyList = historyStore.getState().histories;
        const threeDaysAgo = Date.now() - 3 * 24 * 60 * 60 * 1000;
        const filteredHistoryList = historyList.filter(item => item.closed_time >= threeDaysAgo).slice(0, 100);
        filteredHistoryList.sort((a, b) => b.closed_time - a.closed_time);
        setLocalRecentlyTabs(filteredHistoryList);
        setLoading(false);
    };

    useEffect(() => {
        loadHistory();
    }, []);

    useEffect(() => {
        selectStore.setState({ popupPanelActived: true });
        if (searchInputRef.current) {
            searchInputRef.current.focus();
        }
        return () => {
            selectStore.setState({ popupPanelActived: false });
        };
    }, []);

    const filteredTabs = () => {
        if (!searchQuery) return localRecentlyTabs;
        const queries = searchQuery.trim().toLowerCase().split(/\s+/);
        return localRecentlyTabs.filter(tab =>
            queries.every(query =>
                (tab.title && tab.title.toLowerCase().includes(query)) ||
                (tab.url && tab.url.toLowerCase().includes(query))
            )
        );
    };

    const handleKeyDown = (event) => {
        event.preventDefault();
        setSelectedIndex(prevIndex => (prevIndex + 1) % filteredTabs().length);
    };

    const handleKeyUp = (event) => {
        event.preventDefault();
        setSelectedIndex(prevIndex => (prevIndex - 1 + filteredTabs().length) % filteredTabs().length);
    };

    const handleEnter = (event) => {
        event.preventDefault();
        if (selectedIndex >= 0 && selectedIndex < filteredTabs().length) {
            const selectedItem = filteredTabs()[selectedIndex];
            if (selectedItem) {
                chrome.tabs.create({ url: selectedItem.url });
                if (typeof onSelect === 'function') {
                    onSelect();
                }
            }
        }
    };

    useHotkeys('ArrowDown', handleKeyDown, { enableOnFormTags: ["TEXTAREA", "INPUT"] });
    useHotkeys('ArrowUp', handleKeyUp, { enableOnFormTags: ["TEXTAREA", "INPUT"] });
    useHotkeys('Enter', handleEnter, { enableOnFormTags: ["TEXTAREA", "INPUT"] });

    if (loading && localRecentlyTabs.length === 0) {
        return <PageLoading />;
    }

    return (
        <OverlayScrollbarsComponent defer>
            <BgOverlay />
            <Box sx={{ padding: 0, overflowX: "hidden", color: fontColor || "text.primary" }}>
                <RecentlyTabsHeader />

                {filteredTabs().length === 0 ? (
                    <ColorTypography variant="body1" sx={{ padding: "30px 8px", textAlign: "center" }}>
                        {t('emptyHere')}
                    </ColorTypography>
                ) : (

                    <List sx={{ width: "100%", padding: 0, minHeight: "40px" }} ref={listRef}>
                        {
                            filteredTabs().map((historyItem, index) => (
                                <RecentlyTabItem key={historyItem.id} item={historyItem} onSelect={onSelect} selected={index === selectedIndex} data-index={index} searchText={searchQuery} />
                            ))
                        }
                    </List>

                )}


            </Box>
        </OverlayScrollbarsComponent>
    );
};

export default RecentlyTabs;