import React from "react";
import { Box } from "@mui/material";
import { tabStore } from "../../../core/stores/tabStore";
import { memo } from "react";
import userStore from "../../../core/stores/userStore";
import TabWindow from "./TabWindow";

const TabWindowsList = () => {


  const { PinnedTabsAsIcons, onlyCurrentWindowTabs } = userStore((state) => ({
    PinnedTabsAsIcons: state.settings.PinnedTabsAsIcons,
    onlyCurrentWindowTabs: state.settings.onlyCurrentWindowTabs,
  }));

  const { currentWindow, tabs } = tabStore((state) => ({
    currentWindow: state.currentWindow,
    tabs: state.tabs
      .filter(tab => onlyCurrentWindowTabs ? tab.windowId === state.currentWindow?.id : true)
      .filter(tab => PinnedTabsAsIcons ? tab.pinned === false : true)
      .map(tab => {
        return {
          id: tab.id,
          title: tab.title,
          url: tab.url,
          hostname: tab.hostname,
          created_time: tab.created_time,
          lastAccessed: tab.lastAccessed,
          index: tab.index,
          windowId: tab.windowId,
          groupId: tab.groupId,
          pinned: tab.pinned
        }
      }),
  }));

  const filteredTabs = tabs.filter(tab =>
    (onlyCurrentWindowTabs ? tab.windowId === currentWindow.id : true) &&
    (PinnedTabsAsIcons ? !tab.pinned : true)
  );

  const windowCount = new Set(filteredTabs.map(tab => tab.windowId)).size;

  return (
    <Box>
      <TabWindow key={0} current={true} />
      {windowCount > 1 && <TabWindow key={1} current={false} />}
    </Box>
  );
};

export default memo(TabWindowsList);