import React from "react";
import {
  MenuList,
  MenuItem,
  ListItemText,
  Typography,
  ListItemIcon,
  SvgIcon,
  IconButton,
  Box
} from "@mui/material";
import { Circle, RadioButtonChecked } from "@mui/icons-material";
import { forwardRef } from "react";
import { chromeGroupAvailableColors } from "../../../utils/colors";
import { sendRuntimeMessage } from "../../../core/browers/runtime";
import { useWindowsEvents } from "../../eventHandlers/useWindowsEvents";
import { siteStore } from "../../../core/stores/siteStore";
import { useTranslationContext } from "../../Providers/TranslationProvider";

const TabGroupDomainMenu = forwardRef(({ group, onSelect }, ref) => {
  const { t } = useTranslationContext();
  const { setMenuTarget } = useWindowsEvents();

  const { siteConfig, setSiteConfig } = siteStore((state) => ({
    siteConfig: state.sites.find(d => d.hostname === group.title),
    setSiteConfig: state.setSiteConfig
  }));

  const handleSetGroup = async (color) => {
    setSiteConfig(group.title, { color });
  };

  const handleDeleteGroup = async () => {
    await sendRuntimeMessage('bulk_close_tabs', { tabIds: group.tabIds });
    if (typeof onSelect === 'function') {
      onSelect();
    }
  };

  const handleAddTabsToGroup = async () => {
    await sendRuntimeMessage('add_tabs_to_group', { title: group.title, color: group.color.tabGroupColor, tabIds: group.tabIds });
    if (typeof onSelect === 'function') {
      onSelect();
    }
  };

  const colorButtons = chromeGroupAvailableColors.map(colorInfo => (
    <IconButton
      key={colorInfo.color}
      onClick={() => handleSetGroup(colorInfo.color)}
    >
      {siteConfig?.color === colorInfo.color ? (
        <RadioButtonChecked sx={{ color: colorInfo.hex, fontSize: "18px" }} />
      ) : (
        <Circle sx={{ color: colorInfo.hex, fontSize: "18px" }} />
      )}
    </IconButton>
  ));

  return (
    <Box ref={ref}>
      <MenuList dense sx={{ minWidth: "200px" }}>
        <MenuItem>
          {colorButtons}
        </MenuItem>

        <MenuItem onClick={handleAddTabsToGroup}>
          <ListItemIcon>
            <SvgIcon>
              <svg xmlns="http://www.w3.org/2000/svg" height="20px" viewBox="0 -960 960 960" width="20px" fill="currentColor">
                <path d="M312-240q-29.7 0-50.85-21.15Q240-282.3 240-312v-480q0-29.7 21.15-50.85Q282.3-864 312-864h480q29.7 0 50.85 21.15Q864-821.7 864-792v480q0 29.7-21.15 50.85Q821.7-240 792-240H312Zm0-72h480v-360H528v-120H312v480ZM168-96q-29.7 0-50.85-21.15Q96-138.3 96-168v-552h72v552h552v72H168Zm144-696v480-480Z" />
              </svg>
            </SvgIcon>
          </ListItemIcon>
          <ListItemText>
            <Typography variant="body1">{t("groupMenu.AddTabsToChromeGroups")}</Typography>
          </ListItemText>
        </MenuItem>

        <MenuItem onClick={handleDeleteGroup}>
          <ListItemIcon>
            <SvgIcon>
              <svg xmlns="http://www.w3.org/2000/svg" height="20px" viewBox="0 -960 960 960" width="20px" fill="currentColor">
                <path d="m459-408 93-93 93 93 51-51-93-93 93-93-51-51-93 93-93-93-51 51 93 93-93 93 51 51ZM312-240q-29.7 0-50.85-21.15Q240-282.3 240-312v-480q0-29.7 21.15-50.85Q282.3-864 312-864h480q29.7 0 50.85 21.15Q864-821.7 864-792v480q0 29.7-21.15 50.85Q821.7-240 792-240H312Zm0-72h480v-480H312v480ZM168-96q-29.7 0-50.85-21.15Q96-138.3 96-168v-552h72v552h552v72H168Zm144-696v480-480Z" />
              </svg>
            </SvgIcon>
          </ListItemIcon>
          <ListItemText>
            <Typography variant="body1">{t("groupMenu.RemoveAllTabsFromGroup")}</Typography>
          </ListItemText>
        </MenuItem>
      </MenuList>
    </Box>
  );
});

export default TabGroupDomainMenu;