import React, { useEffect, useRef, useState } from 'react';
import { Box, Typography } from '@mui/material';
import { OverlayScrollbarsComponent } from 'overlayscrollbars-react';
import { useHotkeys } from "react-hotkeys-hook";
import { DndContext, MouseSensor, TouchSensor, closestCenter, useSensor, useSensors } from '@dnd-kit/core';
import { arrayMove, SortableContext, useSortable, verticalListSortingStrategy } from '@dnd-kit/sortable';
import PageLoading from '../../component/pageLoading';
import QuickItem from './QuickItem';
import { selectStore } from "../../../core/stores/selectStore";
import userStore from '../../../core/stores/userStore';
import BgOverlay from '../BgOverlay';
import QuickListHeader from './QuickListHeader';
import { useQuickAccessContext } from '../../Providers/QuickAccessProvider';
import tabStore from '../../../core/stores/tabStore';
import { useTranslationContext } from '../../Providers/TranslationProvider';
import { CSS } from '@dnd-kit/utilities';
import ColorTypography from '../../component/ColorTypography';

const SortableItem = ({ item, onSelect, selected, index }) => {
    const {
        attributes,
        listeners,
        setNodeRef,
        transform,
        transition,
        isDragging,
    } = useSortable({ id: item.uuid });

    const style = {
        transform: CSS.Transform.toString(transform),
        transition,
        zIndex: isDragging ? 999 : 'auto',
        opacity: isDragging ? 0.5 : 1,
    };

    return (
        <div ref={setNodeRef} style={style} {...attributes} {...listeners}>
            <QuickItem item={item} onSelect={onSelect} selected={selected} data-index={index} />
        </div>
    );
};

const QuickList = ({ onSelect }) => {
    const { t } = useTranslationContext();
    const [activeIndex, setActiveIndex] = useState(-1);

    const fontColor = userStore(state => state.settings.fontColor);

    const { initQuickAccess, quickAccess, setQuickAccess } = tabStore((state) => ({
        initQuickAccess: state.initQuickAccess,
        quickAccess: state.quickAccess,
        setQuickAccess: state.setQuickAccess
    }));
    const searchInputRef = useRef(null);
    const listRef = useRef(null);

    const { searchQuery, setSearchQuery } = userStore((state) => ({
        searchQuery: state.searchQuery,
        setSearchQuery: state.setSearchQuery
    }));

    useEffect(() => {
        initQuickAccess();
    }, [initQuickAccess])

    const filteredQuickAccess = quickAccess.filter(item =>
        item.title?.toLowerCase().includes(searchQuery.toLowerCase()) ||
        item.url?.toLowerCase().includes(searchQuery.toLowerCase())
    );

    const handleArrowDown = (event) => {
        event.preventDefault();
        setActiveIndex((prevIndex) => (prevIndex + 1) % filteredQuickAccess.length);
    };

    const handleArrowUp = (event) => {
        event.preventDefault();
        setActiveIndex((prevIndex) => (prevIndex - 1 + filteredQuickAccess.length) % filteredQuickAccess.length);
    };

    const handleEnterKey = (event) => {
        event.preventDefault();
        if (filteredQuickAccess[activeIndex]) {
            const selectedItem = filteredQuickAccess[activeIndex];
            chrome.tabs.create({ url: selectedItem.url });
            if (typeof onSelect === 'function') {
                onSelect(selectedItem);
            }
        }
        setActiveIndex(-1);
    };
    const handleEsc = (event) => {
        event.preventDefault();
        setActiveIndex(-1);
    };

    useHotkeys('ArrowDown', handleArrowDown, { enableOnFormTags: ["TEXTAREA", "INPUT"] });
    useHotkeys('ArrowUp', handleArrowUp, { enableOnFormTags: ["TEXTAREA", "INPUT"] });
    useHotkeys('Enter', handleEnterKey, { enableOnFormTags: ["TEXTAREA", "INPUT"] });
    useHotkeys('esc', handleEsc, { enableOnFormTags: ["TEXTAREA", "INPUT"] });

    const handleDragEnd = ({ active, over }) => {
        if (!active?.id || !over?.id || active.id === over.id) return;
        const oldIndex = quickAccess.findIndex(item => item.uuid === active.id);
        const newIndex = quickAccess.findIndex(item => item.uuid === over.id);
        const newItems = arrayMove(quickAccess, oldIndex, newIndex);
        newItems.forEach((item, index) => {
            setQuickAccess(item.uuid, { ...item, sortIndex: index });
        });
    };

    const sensors = useSensors(
        useSensor(MouseSensor, { activationConstraint: { distance: 10 } }),
        useSensor(TouchSensor, { activationConstraint: { distance: 10 } })
    );

    useEffect(() => {
        selectStore.setState({ popupPanelActived: true });
        if (searchInputRef.current) {
            searchInputRef.current.focus();
        }
        return () => {
            selectStore.setState({ popupPanelActived: false });
        };
    }, []);

    return (
        <OverlayScrollbarsComponent defer>
            <Box sx={{ padding: 0, overflowX: "hidden", color: fontColor || "text.primary" }}>
                {filteredQuickAccess.length === 0 ? (
                    <ColorTypography variant="body1" sx={{ padding: "30px 8px", textAlign: "center", color: "text.secondary" }}>
                        {t('emptyHere')}
                    </ColorTypography>
                ) : (
                    <DndContext
                        collisionDetection={closestCenter}
                        onDragEnd={handleDragEnd}
                        sensors={sensors}
                    >
                        <SortableContext
                            items={filteredQuickAccess.map(item => item.uuid)}
                            strategy={verticalListSortingStrategy}
                        >
                            <div ref={listRef}>
                                {filteredQuickAccess.map((quickAccessItem, index) => (
                                    <SortableItem key={quickAccessItem.uuid} item={quickAccessItem} onSelect={onSelect} selected={index === activeIndex} index={index} />
                                ))}
                            </div>
                        </SortableContext>
                    </DndContext>
                )}
            </Box>
        </OverlayScrollbarsComponent>
    );
};

export default QuickList;
