import React from 'react';
import { ListItemIcon, ListItemText, MenuItem, SvgIcon, Typography } from '@mui/material';
import { OpenInNew } from '@mui/icons-material';
import { useTranslationContext } from '../../../../Providers/TranslationProvider';

const OpenInNewTab = ({ item, handleClose }) => {

    const { t } = useTranslationContext();

    const handleClick = () => {
        chrome.tabs.create({ url: item.url });
        handleClose();
    };

    return (
        <MenuItem dense onClick={handleClick}>
            <ListItemIcon>
                <OpenInNew />
            </ListItemIcon>
            <ListItemText>
                <Typography variant="body1">{t('OpenInNewTab')}</Typography>
            </ListItemText>
        </MenuItem>
    );
};

export default OpenInNewTab;
