import { IconButton, Tooltip, Typography } from "@mui/material";
import React, { memo, useState } from "react";
import { useTranslationContext } from "../Providers/TranslationProvider";
import tabStore from "../../core/stores/tabStore";
import { VolumeOff, VolumeUp } from "@mui/icons-material";
import userStore from "../../core/stores/userStore";

const TabsMute = () => {
  const { t } = useTranslationContext();


  const { fontColor } = userStore((state) => ({
    fontColor: state.settings.fontColor,
  }));

  const soundTabs = tabStore((state) =>
    state.tabs.filter(tab => tab.audible).map(tab => ({
      id: tab.id,
      mutedInfo: tab.mutedInfo,
      audible: tab.audible
    }))
  );

  const [muted, setMuted] = useState(soundTabs.every(tab => tab.mutedInfo.muted));

  const handleClick = async () => {
    for (const tab of soundTabs) {
      await chrome.tabs.update(tab.id, { muted: !muted });
    }
    setMuted(!muted);
  };

  return (
    <Tooltip arrow title={!muted ? <Typography>{t('MuteAllTabsMakingSounds')}</Typography> : ""}>
      <IconButton onClick={handleClick} sx={{ padding: "4px", color: fontColor || "text.primary" }}>
        {muted ? <VolumeUp fontSize="small" /> : <VolumeOff fontSize="small" />}
      </IconButton>
    </Tooltip>
  );
};

export default memo(TabsMute);
