import React, { memo, useMemo } from "react";
import { useSortable } from "@dnd-kit/sortable";
import { CSS } from '@dnd-kit/utilities';
import { Box } from "@mui/material";
import TabGroupChrome from "./TabGroupChrome";
import { useDndEnableContext } from "../../Providers/DndEnableProvider";

const SortableTabGroup = ({ groupId }) => {
    const { enableDnd } = useDndEnableContext();

    const {
        attributes,
        listeners,
        setNodeRef,
        transform,
        transition,
        isDragging,
        isOver,
        index,
        isSorting,
        over,
        activeIndex,
    } = useSortable({ id: groupId, data: { type: "group" }, disabled: !enableDnd });

    const style = useMemo(() => ({
        transform: isSorting ? undefined : CSS.Translate.toString(transform),
        transition,
        zIndex: isDragging ? 999 : 'auto',
        opacity: isDragging ? 0.3 : 1,
    }), [transform, transition, isDragging]);

    return (
        <Box
            ref={setNodeRef}
            active={isDragging}
            style={style}
            sx={over?.id === groupId ? index > activeIndex ? {
                '&:after': {
                    content: '""',
                    width: '100%',
                    height: "2px",
                    backgroundColor: "#4c9ffe",
                    position: 'absolute',
                    left: 0,
                    right: 0,
                    zIndex: 999
                },

            } : {
                '&:before': {
                    content: '""',
                    width: '100%',
                    height: "2px",
                    backgroundColor: "#4c9ffe",
                    position: 'absolute',
                    left: 0,
                    right: 0,
                    zIndex: 999

                },

            } : undefined}

            {...listeners}
            {...attributes}
        >
            <TabGroupChrome groupId={groupId} />
            {isOver && <Box sx={{ border: "2px dashed #999", position: 'absolute', inset: 0 }} />}
        </Box>
    );
};

export default memo(SortableTabGroup);