import React from "react";
import { MenuItem, ListItemText, Typography } from "@mui/material";
import { copyTextToClipboard } from "../../../../../utils/helpers";
import tabStore from "../../../../../core/stores/tabStore";
import * as Sentry from "@sentry/react";
import { useTranslationContext } from "../../../../Providers/TranslationProvider";

export default function CopyLink({ uuid, onSelect, props }) {

    const { t } = useTranslationContext();

    const shortcut = tabStore(state => state.quickAccess.find(shortcut => shortcut.uuid === uuid));

    const handleClick = async () => {
        try {
            await copyTextToClipboard(shortcut.url);
            if (typeof onSelect === 'function') {
                onSelect();
            }
        } catch (error) {
            Sentry.captureException(error);
        }
    };

    return (
        <MenuItem {...props} onClick={handleClick}>
            <ListItemText>
                <Typography variant="body1">{t("tabMenu.CopyLink")}</Typography>
            </ListItemText>
        </MenuItem>
    );
}