import React, { memo } from "react";
import { MenuItem, ListItemText, Typography, ListItemIcon } from "@mui/material";
import { removeTabById } from "../../../../core/browers/tabs";
import { Close } from "@mui/icons-material";
import { useTranslationContext } from "../../../Providers/TranslationProvider";

const CloseTab = ({ tabId, onSelect }) => {
    const { t } = useTranslationContext();

    const handleClick = async () => {
        await removeTabById(tabId);
        if (typeof onSelect === 'function') {
            onSelect();
        }
    };

    return (
        <MenuItem onClick={handleClick}>
            <ListItemIcon>
                <Close />
            </ListItemIcon>
            <ListItemText>
                <Typography variant="body1">{t("tabMenu.closeTab")}</Typography>
            </ListItemText>
        </MenuItem>
    );
};

export default memo(CloseTab);