import React, { useState, useEffect, memo } from "react";
import humanizeDuration from "humanize-duration";
import { Typography } from "@mui/material";
import userStore from "../../../core/stores/userStore";
import { getHostnameFromUrl } from "../../../utils/helpers";
import { useTranslationContext } from "../../Providers/TranslationProvider";

export default memo(function LinkTimeAgo({ url, lastAccessed }) {

  const { t } = useTranslationContext();
  const [now, setNow] = useState(Date.now());

  const { selectedLanguage, theme, fontWeight, fontColor } = userStore((state) => ({
    selectedLanguage: state.settings.selectedLanguage,
    theme: state.settings.theme,
    fontWeight: state.settings.fontWeight,
    fontColor: state.settings.fontColor
  }))

  const hostname = getHostnameFromUrl(url);

  useEffect(() => {
    const timerId = setInterval(() => {
      setNow(Date.now());
    }, 1000);
    return () => clearInterval(timerId);
  }, []);

  const humanizeOptions = {
    delimiter: " ",
    largest: 1,
    round: true,
    language: selectedLanguage.replace("-", "_"),
    fallbacks: ["en"]
  };

  let timeAgo = `${hostname}`;
  if (lastAccessed && !isNaN(lastAccessed)) {
    const humanTimeStr = humanizeDuration(now - lastAccessed, humanizeOptions);
    timeAgo = `${hostname} • ${humanTimeStr} ${t("ago")}`;
  }

  return (
    <Typography
      variant="h5"
      noWrap
      sx={{
        // marginTop: "2px",
        fontWeight: (fontWeight - 200 <= 0 ? 100 : fontWeight - 200),
        color: theme === "dark" ? "text.secondary" : fontColor || "text.secondary",
        opacity: 0.75
      }}
    >
      {timeAgo}
    </Typography >
  );
})
