import React, { memo } from "react";
import userStore from "../../core/stores/userStore";
import OpenedTabs from "./tabs/OpenedTabs";
import { Box } from "@mui/material";
import { forwardRef } from "react";
import HistoryList from "./histories/HistoryList";
import ChromeHistories from "./histories/ChromeHistories";
import ReadingListEntries from "./readingList/ReadingListEntries";
import { ReadingListProvider } from "../Providers/ReadingListProvider";
import BookmarksList from "./bookmarks/BookmarksList";
import QuickList from "./quickAccess/QuickList";


const TabContent = forwardRef(function TabContent(props, ref) {

    const { activeTabIndex, navs } = userStore(state => ({
        activeTabIndex: state.settings.activeTabIndex,
        navs: state.settings.tabNavOptions.navs.filter(nav => nav.enable === true),

    }));

    const contents = {
        0: <OpenedTabs key={"openTabs_default"} />,
        1: <OpenedTabs key={"openTabs"} />,
        2: <BookmarksList key={"bookmark"} showBack={true} />,
        3: <ChromeHistories key={"history"} />,
        4: <HistoryList key={"recentlyTabs"} />,
        5: <QuickList key={"quickAccess"} />,
        6: <ReadingListProvider><ReadingListEntries key={"readingList"} /></ReadingListProvider>,
    };

    return (
        <Box ref={ref}>
            {contents[activeTabIndex || 0]}
        </Box>
    );
});

export default memo(TabContent);