import React, { useState } from 'react';
import { MenuItem, Dialog, DialogTitle, DialogContent, DialogActions, Button, TextField, Box, ListItemIcon, ListItemText, Typography } from '@mui/material';
import { useTranslationContext } from '../../../Providers/TranslationProvider';
import SelectFolder from '../SelectFolder';
import { Edit } from '@mui/icons-material';

const EditFolder = ({ folder, handleClose }) => {
    const { t } = useTranslationContext();
    const [dialogOpen, setDialogOpen] = useState(false);
    const [folderName, setFolderName] = useState(folder.title || '');
    const [parentFolder, setParentFolder] = useState(folder.parentId || '');

    const handleMenuItemClick = () => {
        setDialogOpen(true);
    };

    const handleDialogClose = () => {
        setDialogOpen(false);
        handleClose();
    };

    const handleSave = () => {
        chrome.bookmarks?.update(folder.id, {
            title: folderName
        }, () => {
            if (folder.parentId !== parentFolder) {
                chrome.bookmarks?.move(folder.id, {
                    parentId: parentFolder
                }, () => {
                    setDialogOpen(false);
                    handleClose();
                });
            } else {
                setDialogOpen(false);
                handleClose();
            }
        });
    };

    const handleFolderChange = (newValue) => {
        if (newValue && newValue.id) {
            setParentFolder(newValue.id);
        } else {
            setParentFolder('');
        }
    };

    const isSaveDisabled = !folderName || !parentFolder;

    const handleInputChange = (event) => {
        event.stopPropagation();
        setFolderName(event.target.value);
    };

    const handleKeyDown = (event) => {
        event.stopPropagation();
    };

    return (
        <Box>
            <MenuItem dense onClick={handleMenuItemClick}>
                <ListItemIcon>
                    <Edit />
                </ListItemIcon>
                <ListItemText>
                    <Typography variant="body1">{t('editFolder')}</Typography>
                </ListItemText>
            </MenuItem>
            <Dialog fullWidth open={dialogOpen} onClose={handleDialogClose}>
                <DialogTitle>{t('editFolder')}</DialogTitle>
                <DialogContent>
                    <Box mb={2}>
                        <TextField
                            label={t('folderName')}
                            value={folderName}
                            onChange={handleInputChange}
                            onKeyDown={handleKeyDown}
                            fullWidth
                            margin="normal"
                        />
                    </Box>
                    <Box mb={2}>
                        <SelectFolder
                            selectedFolder={parentFolder}
                            onChange={handleFolderChange}
                        />
                    </Box>
                </DialogContent>
                <DialogActions>
                    <Button onClick={handleDialogClose} color="secondary" variant="contained">
                        {t('Cancel')}
                    </Button>
                    <Button onClick={handleSave} color="primary" variant="contained" style={{ marginRight: 8 }} disabled={isSaveDisabled}>
                        {t('Save')}
                    </Button>
                </DialogActions>
            </Dialog>
        </Box>
    );
};

export default EditFolder;
