import { Box, Divider } from "@mui/material";
import React, { memo, forwardRef, useEffect } from "react";
import { tabStore } from "../../../core/stores/tabStore";
import { userStore } from "../../../core/stores/userStore";
import TabQuickAccess from "./quickAccess";
import { DndContext, closestCenter, MouseSensor, TouchSensor, useSensor, useSensors } from "@dnd-kit/core";
import { arrayMove, SortableContext } from "@dnd-kit/sortable";
import { useSortable } from "@dnd-kit/sortable";
import { CSS } from "@dnd-kit/utilities";
import TabAsIcon from "./TabAsIcon";

const OpenedTabPined = forwardRef((props, ref) => {
    const { onlyCurrentWindowTabs, PinnedTabsAsIcons, pinToQuickAccess, quickAccessHorizontalSpacing, quickAccessVerticalSpacing } = userStore((state) => ({
        onlyCurrentWindowTabs: state.settings.onlyCurrentWindowTabs,
        PinnedTabsAsIcons: state.settings.PinnedTabsAsIcons,
        pinToQuickAccess: state.settings.pinToQuickAccess,
        quickAccessHorizontalSpacing: state.settings.quickAccessHorizontalSpacing,
        quickAccessVerticalSpacing: state.settings.quickAccessVerticalSpacing,
    }));

    const { initQuickAccess, tabIds, quickAccess, uuids, setQuickAccess } = tabStore((state) => ({
        initQuickAccess: state.initQuickAccess,
        tabIds: state.tabs.filter(tab => onlyCurrentWindowTabs ? tab.windowId === state.currentWindow.id : true).filter(tab => tab.pinned).map(tab => tab.id),
        quickAccess: state.quickAccess,
        uuids: state.quickAccess.map(item => item.uuid),
        setQuickAccess: state.setQuickAccess
    }));

    useEffect(() => {
        if (quickAccess.length === 0) {
            initQuickAccess();
        }
    }, [initQuickAccess])

    const handleDragEnd = ({ active, over }) => {
        if (!active?.id || !over?.id || active.id === over.id) return;
        const oldIndex = uuids.indexOf(active.id);
        const newIndex = uuids.indexOf(over.id);
        const newItems = arrayMove(uuids, oldIndex, newIndex);
        newItems.forEach((id, index) => {
            const item = quickAccess.find(item => item.uuid === id);
            setQuickAccess(item.uuid, { ...item, sortIndex: index });
        });
    };

    const sensors = useSensors(
        useSensor(MouseSensor, { activationConstraint: { distance: 10 } }),
        useSensor(TouchSensor, { activationConstraint: { distance: 10 } })
    );

    if ((PinnedTabsAsIcons && tabIds.length > 0) || (pinToQuickAccess && quickAccess.length > 0)) {
        return (
            <Box ref={ref} sx={{ width: 'auto' }}>
                <Box
                    sx={{
                        padding: "5px",
                        display: 'grid',
                        gridTemplateColumns: 'repeat(auto-fill, minmax(26px, 1fr))',
                        gridTemplateRows: "auto",
                        gap: `${quickAccessVerticalSpacing}px ${quickAccessHorizontalSpacing}px`,
                        width: 'auto', // Change this line to make the width auto
                    }}
                >
                    {PinnedTabsAsIcons && tabIds.map(tabId => (
                        <TabAsIcon key={`tab-avatar-${tabId}`} tabId={tabId} />
                    ))}
                    {pinToQuickAccess && (
                        <DndContext
                            collisionDetection={closestCenter}
                            onDragEnd={handleDragEnd}
                            sensors={sensors}
                        >
                            <SortableContext items={uuids}>
                                {uuids.map(id => {
                                    const quick = quickAccess.find(item => item.uuid === id);
                                    return <SortableQuickAccess key={`tab-avatar-${quick.uuid}`} quickAccess={quick} />
                                })}
                            </SortableContext>
                        </DndContext>
                    )}
                </Box>
                <Divider />
            </Box>
        );
    } else {
        return null;
    }
});

const SortableQuickAccess = memo(({ quickAccess }) => {
    const { attributes, listeners, setNodeRef, transform, transition, isDragging } = useSortable({ id: quickAccess.uuid });

    return (
        <div
            ref={setNodeRef}
            style={{
                transform: CSS.Transform.toString(transform),
                transition,
                cursor: isDragging ? 'grabbing' : 'pointer',
                gridColumn: `span ${quickAccess.urls?.length > 2 ? 2 : 1}` // Adjust gridColumn based on quickAccess.urls?.length
            }}
            {...attributes}
            {...listeners}
        >
            <TabQuickAccess quickAccess={quickAccess} />
        </div>
    );
});

export default memo(OpenedTabPined);
