import React, { useRef, useEffect, memo, useState } from "react";
import { ListItem, ListItemButton, Box, Tooltip, Stack, Menu, Popover, List, ListItemAvatar, Avatar, ListItemText } from "@mui/material";
import { getTabItemStyle } from "../../Hooks/useTabItemStyle";
import LinkAvatar from "../../component/links/LinkAvatar";
import LinkTitle from "../../component/links/LinkTitle";
import LinkPreview from "../../component/links/LinkPreview";
import { faviconURL } from "../../../utils/helpers";
import userStore from "../../../core/stores/userStore";
import { useTranslationContext } from "../../Providers/TranslationProvider";
import QuickAccessMenuList from "../tabs/quickAccess/menu/QuickAccessMenuList";

const QuickItem = ({ item, onSelect, selected }) => {
    const { t } = useTranslationContext();
    const { view, buttonPosition, tabPreviewEnable, tabPreviewWaitSeconds, searchQuery, quickAccessGroupView, quickAccessGroupClickAction } = userStore((state) => ({
        view: state.settings.view,
        buttonPosition: state.settings.buttonPosition,
        tabPreviewEnable: state.settings.tabPreviewEnable,
        tabPreviewWaitSeconds: state.settings.tabPreviewWaitSeconds,
        searchQuery: state.searchQuery,
        quickAccessGroupView: state.settings.quickAccessGroupView,
        quickAccessGroupClickAction: state.settings.quickAccessGroupClickAction,
    }));

    const listItemRef = useRef(null);
    const [contextMenu, setContextMenu] = useState(null);
    const [contextMenuAnchorEl, setContextMenuAnchorEl] = useState(null);
    const [popoverOpen, setPopoverOpen] = useState(false);

    useEffect(() => {
        if (selected && listItemRef.current) {
            listItemRef.current.scrollIntoView({ behavior: 'smooth', block: 'center' });
        }
    }, [selected]);

    useEffect(() => {
        const handleContextMenuClose = () => {
            setContextMenu(null);
            setContextMenuAnchorEl(null);
        };

        window.addEventListener("blur", handleContextMenuClose);
        return () => {
            window.removeEventListener("blur", handleContextMenuClose);
        };
    }, []);

    const handleContextMenu = event => {
        event.preventDefault();
        event.stopPropagation();
        setContextMenu({
            mouseX: event.clientX + 2,
            mouseY: event.clientY - 6
        });
        setContextMenuAnchorEl(event.currentTarget);
    };

    const handlePopoverClose = () => {
        setPopoverOpen(false);
    };

    const handleOpenLink = async () => {
        try {
            if (item.urls && item.urls.length > 0) {
                if (quickAccessGroupClickAction === 'openAllUrls') {
                    for (const urlConfig of item.urls) {
                        await chrome.tabs.create({ url: urlConfig.url });
                        await new Promise(resolve => setTimeout(resolve, 500)); // Delay 500ms
                    }
                } else {
                    setPopoverOpen(true);
                }
            } else {
                await chrome.tabs.create({ url: item.url });
            }
        } catch (error) {
            console.error(error);
        } finally {
            if (typeof onSelect === 'function') {
                onSelect();
            }
        }
    };

    return (
        <Box sx={{ position: "relative" }} ref={listItemRef}>
            <ListItem disablePadding onClick={handleOpenLink} onContextMenu={handleContextMenu}>
                <Tooltip
                    followCursor
                    enterDelay={parseFloat(tabPreviewWaitSeconds) * 1000}
                    enterNextDelay={200}
                    componentsProps={{
                        tooltip: {
                            sx: {
                                padding: "15px",
                                background: "transparent",
                                zIndex: 99
                            }
                        }
                    }}
                    title={tabPreviewEnable ? <LinkPreview title={item.title} url={item.url} /> : ""}
                >
                    <ListItemButton
                        selected={selected}
                        component="div"
                        dense={view === "list"}
                        disableGutters
                        sx={getTabItemStyle(item.url)}
                    >
                        <Stack
                            sx={{
                                width: "100%",
                                paddingRight: buttonPosition === "row" ? "8px" : "0",
                                paddingLeft: buttonPosition === "row" ? "0" : "8px"
                            }}
                            direction={buttonPosition}
                            alignItems={"center"}
                            justifyContent={"space-between"}
                            spacing={1}
                        >
                            <LinkAvatar avatarUrl={faviconURL(item.url)} />
                            <LinkTitle title={item.title} url={item.url} lastAccessed={item.lastAccessed} searchText={searchQuery} />
                        </Stack>
                    </ListItemButton>
                </Tooltip>
            </ListItem>
            <Menu
                dense="true"
                open={contextMenu !== null}
                anchorReference="anchorPosition"
                anchorPosition={contextMenu !== null ? { top: contextMenu.mouseY, left: contextMenu.mouseX } : undefined}
                onClose={() => setContextMenu(null)}
                anchorEl={contextMenuAnchorEl}
            >
                <QuickAccessMenuList uuid={item.uuid} handleMenuClose={() => setContextMenu(null)} />
            </Menu>
            <Popover
                open={popoverOpen}
                anchorEl={listItemRef.current}
                onClose={handlePopoverClose}
                anchorOrigin={{
                    vertical: 'top',
                    horizontal: 'center',
                }}
                transformOrigin={{
                    vertical: 'bottom',
                    horizontal: 'center',
                }}
            >
                {quickAccessGroupClickAction === 'expandIcons' && (
                    <Box sx={{ width: "100%" }}>
                        <Box sx={{
                            padding: "5px",
                            display: 'flex',
                            flexWrap: 'wrap',
                            justifyContent: 'flex-start',
                            gap: 1,
                            width: '100%',
                        }}>
                            {item.urls?.map((urlConfig, index) => (
                                <QuickItem key={index} item={urlConfig} onSelect={onSelect} selected={selected} />
                            ))}
                        </Box>
                    </Box>
                )}
                {quickAccessGroupClickAction === 'expandList' && (
                    <List>
                        {item.urls?.map((urlConfig, index) => (
                            <ListItem dense disablePadding key={index}>
                                <ListItemButton dense sx={{ pl: 1, pr: 0, pt: 0, pb: 0 }} onClick={() => handleOpenLink(urlConfig.url)}>
                                    <ListItemAvatar sx={{ minWidth: 0, padding: '5px 10px' }}>
                                        <Avatar sx={{ width: "18px", height: "18px" }} src={urlConfig.favIconUrl} />
                                    </ListItemAvatar>
                                    <ListItemText
                                        sx={{ px: "5px" }}
                                        primary={urlConfig.title}
                                        secondary={urlConfig.url}
                                        secondaryTypographyProps={{
                                            sx: {
                                                whiteSpace: "pre-wrap",
                                                overflow: "hidden",
                                                textOverflow: "ellipsis",
                                                display: "-webkit-box",
                                                WebkitLineClamp: "2",
                                                WebkitBoxOrient: "vertical"
                                            }
                                        }}
                                    />
                                </ListItemButton>
                            </ListItem>
                        ))}
                    </List>
                )}
            </Popover>
        </Box>
    );
};

export default memo(QuickItem);
