import { ListItemIcon, MenuItem, SvgIcon, Typography } from "@mui/material";
import React from "react";
import tabStore from "../../../../core/stores/tabStore";
import { toast } from "react-toastify";
import userStore from "../../../../core/stores/userStore";
import { sendRuntimeMessage } from "../../../../core/browers/runtime";
import { useTranslationContext } from "../../../Providers/TranslationProvider";


export default function UngroupAll({ onSelect }) {

    const { t } = useTranslationContext();

    const { theme, onlyCurrentWindowTabs } = userStore(state => ({
        theme: state.settings.theme,
        onlyCurrentWindowTabs: state.settings.onlyCurrentWindowTabs
    }));

    const { loadTabs, loadGroups, currentWindow } = tabStore((state) => ({
        loadTabs: state.loadTabs,
        loadGroups: state.loadGroups,
        currentWindow: state.currentWindow
    }));

    const handleClick = async (event) => {
        try {
            tabStore.setState({ groups: [], tabs: [] });
            const response = await sendRuntimeMessage("cancel_all_groups", { windowId: onlyCurrentWindowTabs ? currentWindow.id : 0 });
            if (!response.success) {
                if (Array.isArray(response.error)) {
                    for (const err of response.error) {
                        toast.error(t(err || 'ungroupingError', { ns: "error" }), {
                            position: "bottom-center",
                            theme: theme
                        });
                    }
                } else {
                    toast.error(t(response.error || 'ungroupingError', { ns: "error", error: 'Failed to ungroup' }), {
                        position: "bottom-center",
                        theme: theme
                    });
                }
            }

        } catch (error) {
            console.error("Failed to ungroup all tabs", error);
            toast.error(t("ungroupingError", { ns: "error", error: error.message }), {
                position: "bottom-center",
                theme: theme
            });
        } finally {
            if (typeof onSelect === 'function') {
                onSelect();
            }
            await Promise.all([loadGroups(), loadTabs()]);

        }
    }

    if (navigator.userAgent.includes("OPR")) {
        return null;
    }

    return (
        <MenuItem onClick={handleClick}>
            <ListItemIcon>
                <SvgIcon>
                    <svg xmlns="http://www.w3.org/2000/svg" height="24px" viewBox="0 -960 960 960" width="24px" fill="currentColor"><path d="m348-292-56-56 172-172H320v-80h280v280h-80v-144L348-292Zm412-188v-280H480v-80h360v360h-80ZM200-120q-33 0-56.5-23.5T120-200v-640h80v640h640v80H200Z" /></svg>
                </SvgIcon>
            </ListItemIcon>
            <Typography noWrap variant="body1">
                {t("UngroupAll")}
            </Typography>
        </MenuItem>
    )
}