import React, { memo, useState } from "react";
import { Box, Stack, Typography } from "@mui/material";
import { progressBarStore } from "../../core/stores/progressBarStore";
import userStore from "../../core/stores/userStore";

import { siteStore } from "../../core/stores/siteStore";
import tabStore from "../../core/stores/tabStore";
import VolumeControl from "./MediaController/VolumeControl";
import RateControl from "./MediaController/RateControl";
import ProgressBarSlider from "./MediaController/ProgressBarSlider";
import CurrentTime from "./MediaController/CurrentTime";
import TotalDuration from "./MediaController/TotalDuration";
import PlayPauseButton from "./MediaController/PlayPauseButton";
import { useDndEnableContext } from "../Providers/DndEnableProvider";
import { useSettingsContext } from "../Providers/SettingsProvider";

const PlayingProgressBar = ({ tabId }) => {
  const { setEnableDnd } = useDndEnableContext();
  const { fontColor } = useSettingsContext();

  const hostname = tabStore(state => state.tabs.find(tab => tab.id === tabId)?.hostname);
  const globalPlayingProgressBar = userStore(state => ({
    playingProgressBar: state.settings.playingProgressBar,
    enableProgressBar: state.settings.enableProgressBar
  }));
  const playingProgressBarEnabled = siteStore(state => state.sites.find(site => site.hostname === hostname)?.playingProgressBar !== false && globalPlayingProgressBar.playingProgressBar && globalPlayingProgressBar.enableProgressBar);
  const playData = progressBarStore(state => {
    const data = state.playingProgress.find(t => t.tabId === tabId);
    return {
      currentTime: data?.currentTime,
      duration: data?.duration
    };
  });

  if (!playingProgressBarEnabled || playData?.currentTime === undefined || isNaN(playData.duration)) {
    return null;
  }

  const handleMouseEnter = () => {
    setEnableDnd(false)
  };

  const handleMouseLeave = () => {
    setEnableDnd(true)
  };

  return (
    <Box
      sx={{ width: "100%", display: "block", px: 1 }}
      onClick={(event) => { event.stopPropagation(); }}
      onMouseEnter={handleMouseEnter}
      onMouseLeave={handleMouseLeave}
    >
      <Stack useFlexGap direction="row" alignItems="center" spacing={0.2} justifyContent="space-between">
        <Stack direction="row" alignItems="center" spacing={0}>
          <PlayPauseButton tabId={tabId} />
          <VolumeControl tabId={tabId} />
        </Stack>
        <Stack alignItems="center" sx={{ flexGrow: 1, mx: 1 }}>
          <ProgressBarSlider tabId={tabId} />
        </Stack>
        <Stack direction="row" alignItems="center" spacing={0}>
          <RateControl tabId={tabId} />
          <CurrentTime tabId={tabId} /> <Typography variant="body2" sx={{ color: fontColor || 'text.primary' }}>/</Typography> <TotalDuration tabId={tabId} />
        </Stack>
      </Stack>
    </Box>
  );
};

export default memo(PlayingProgressBar);