import React, { useState, useEffect, useRef, memo } from "react";
import { List, Collapse, Divider, Stack, Button, Typography, ListItem } from "@mui/material";
import TabItem from "./TabItem";
import { filterTabs, hexToRgb } from "../../../utils/helpers";
import { tabStore } from "../../../core/stores/tabStore";
import { userStore } from "../../../core/stores/userStore";
import { ArrowRight, ArrowLeft, ArrowDropDown } from "@mui/icons-material";
import { getCustomColor } from "../../../utils/colors";
import { useTranslationContext } from "../../Providers/TranslationProvider";
import SortableTabItem from "./SortableTabItem";
import SiteAvatars from "./SiteAvatars";

const TabWindow = memo(({ current }) => {
  const { t } = useTranslationContext();

  const {
    searchText,
    orderBy,
    sortBy,
    buttonPosition,
    GroupCollapsedShowIcon,
    colorfulBackground,
    PinnedTabsAsIcons,
    onlyCurrentWindowTabs,
    groupExpanded
  } = userStore((state) => ({
    searchText: state.searchText,
    orderBy: state.settings.orderBy,
    sortBy: state.settings.sortBy,
    buttonPosition: state.settings.buttonPosition,
    GroupCollapsedShowIcon: state.settings.GroupCollapsedShowIcon,
    colorfulBackground: state.settings.colorfulBackground,
    PinnedTabsAsIcons: state.settings.PinnedTabsAsIcons,
    onlyCurrentWindowTabs: state.settings.onlyCurrentWindowTabs,
    groupExpanded: state.settings.groupExpanded
  }));

  const { currentWindow, activeTabId, keyboardSelected } = tabStore((state) => ({
    currentWindow: state.currentWindow,
    activeTabId: state.activeTabId,
    keyboardSelected: state.keyboardSelected
  }));

  const tabs = tabStore((state) => state.tabs)
    .filter(tab => onlyCurrentWindowTabs ? tab.windowId === currentWindow.id : true)
    .filter(tab => PinnedTabsAsIcons ? tab.pinned === false : true)
    .filter(tab => current ? tab.windowId === currentWindow.id : tab.windowId !== currentWindow.id);

  const filteredTabs = filterTabs(tabs, searchText, orderBy, sortBy);

  const tabContainerRef = useRef(null);

  useEffect(() => {
    const selectedTabElement = tabContainerRef.current?.querySelector('.tab-item.keybord-selected');
    if (selectedTabElement) {
      selectedTabElement.scrollIntoView({ behavior: "smooth", block: "center", inline: "nearest" });
    } else {
      const activeTabElement = tabContainerRef.current?.querySelector('.tab-item.active');
      if (activeTabElement) {
        activeTabElement.scrollIntoView({ behavior: "smooth", block: "nearest", inline: "nearest" });
      }
    }
  }, [filteredTabs, activeTabId, keyboardSelected]);

  const tabIds = filteredTabs.map(tab => tab.id);

  const [expanded, setExpanded] = useState(groupExpanded);

  useEffect(() => {
    setExpanded(groupExpanded);
  }, [groupExpanded]);

  if (tabIds.length === 0) {
    return null;
  }

  const handleChange = (event) => {
    event.preventDefault();
    event.stopPropagation();
    setExpanded(exp => !exp);
  };

  const colorSetting = getCustomColor('custom', current ? currentWindow.id : 0);

  const groupArrowIcon = () => {
    if (expanded) {
      return <ArrowDropDown sx={{ color: colorSetting.textColor, fontSize: "24px" }} />;
    } else {
      if (buttonPosition === "row") {
        return <ArrowRight sx={{ color: colorSetting.textColor, fontSize: "24px" }} />;
      }
      return <ArrowLeft sx={{ color: colorSetting.textColor, fontSize: "24px" }} />;
    }
  };

  const sideBorder = () => {
    if (buttonPosition === "row") {
      return { margin: "5px 0", borderLeft: `1px dashed ${colorSetting.hex}` };
    }
    return { margin: "5px 0", borderRight: `1px dashed ${colorSetting.hex}` };
  };

  const TabItems = tabIds.map(tabId => <SortableTabItem key={tabId} tabId={tabId} />);

  return (
    <List ref={tabContainerRef} disablePadding sx={sideBorder()}>
      <ListItem
        disableGutters
        sx={{
          padding: 0,
          background: colorSetting ? hexToRgb(colorSetting.hex, 0.1) : "rgba(0,0,0,0.2)",
          "&:hover": {
            backgroundColor: colorfulBackground ? hexToRgb(colorSetting.hex, 0.25) : "rgba(0,0,0,0.05)",
          },
          "&.Mui-selected": {
            backgroundColor: colorfulBackground ? hexToRgb(colorSetting.hex, 0.4) : "rgba(0,0,0,0.15)",
            "&:hover": {
              backgroundColor: colorfulBackground ? hexToRgb(colorSetting.hex, 0.25) : "rgba(0,0,0,0.05)",
            }
          }
        }}
        onClick={handleChange}
      >
        <Stack
          sx={{ width: "100%", ...buttonPosition === "row" ? { paddingRight: "10px" } : { paddingLeft: "10px" } }}
          spacing={0.5}
          direction={buttonPosition}
          alignContent="center"
          justifyContent="space-between"
        >
          <Button
            variant="text"
            sx={{
              flexDirection: buttonPosition,
              borderRadius: "0",
              backgroundColor: colorSetting.hex,
              color: colorSetting.textColor,
              "&:hover": {
                backgroundColor: colorSetting.hex
              },
              padding: "3px 8px 3px 5px",
            }}
            onClick={handleChange}
          >
            {groupArrowIcon()}
            <Typography variant="h4" noWrap>{current ? t("CurrentWindow") : t("OtherWindows")}</Typography>
          </Button>
          <Stack direction={buttonPosition} alignContent="center" justifyContent="flex-end">
            {!expanded && GroupCollapsedShowIcon && <SiteAvatars tabIds={tabIds} />}
          </Stack>
        </Stack>
      </ListItem>
      {expanded && (
        <Collapse in={expanded} timeout="auto" unmountOnExit>
          <Divider />
          <List disablePadding sx={{ width: "100%", padding: 0, position: "relative" }}>
            {TabItems}
          </List>
        </Collapse>
      )}
      <Divider component="li" />
    </List>
  );
});

export default TabWindow;
