import React, { useState } from 'react';
import { MenuItem, Dialog, DialogTitle, DialogContent, DialogActions, Button, TextField, Box, ListItemIcon, ListItemText, Typography } from '@mui/material';
import { useTranslationContext } from '../../../Providers/TranslationProvider';
import SelectFolder from '../SelectFolder';
import { Edit } from '@mui/icons-material';

const EditBookmark = ({ bookmark, handleClose }) => {
    const { t } = useTranslationContext();
    const [open, setOpen] = useState(false);
    const [title, setTitle] = useState(bookmark.title);
    const [url, setUrl] = useState(bookmark.url);
    const [selectedFolder, setSelectedFolder] = useState(bookmark.parentId);

    const handleClick = () => {
        setOpen(true);
    };

    const handleSave = () => {
        chrome.bookmarks?.update(bookmark.id, {
            title: title,
            url: url
        }, () => {
            if (selectedFolder !== bookmark.parentId) {
                chrome.bookmarks?.move(bookmark.id, { parentId: selectedFolder }, () => {
                    setOpen(false);
                    handleClose();
                });
            } else {
                setOpen(false);
                handleClose();
            }
        });
    };

    const handleDialogClose = () => {
        setOpen(false);
    };

    const handleFolderChange = (newValue) => {
        if (newValue && newValue.id) {
            setSelectedFolder(newValue.id);
        } else {
            setSelectedFolder('');
        }
    };

    const isSaveDisabled = !title || !url || !selectedFolder;

    const handleKeyDown = (event) => {
        event.stopPropagation();
    };

    return (
        <Box>
            <MenuItem dense onClick={handleClick}>
                <ListItemIcon>
                    <Edit />
                </ListItemIcon>
                <ListItemText>
                    <Typography variant="body1">{t('editBookmark')}</Typography>
                </ListItemText>
            </MenuItem>
            <Dialog open={open} onClose={handleDialogClose} fullWidth>
                <DialogTitle>{t('editBookmark')}</DialogTitle>
                <DialogContent>
                    <Box mb={2}>
                        <TextField
                            margin="dense"
                            label={t('name')}
                            type="text"
                            fullWidth
                            value={title}
                            onChange={(e) => setTitle(e.target.value)}
                            onKeyDown={handleKeyDown}
                        />
                    </Box>
                    <Box mb={2}>
                        <TextField
                            margin="dense"
                            label="URL"
                            type="text"
                            fullWidth
                            value={url}
                            onChange={(e) => setUrl(e.target.value)}
                            onKeyDown={handleKeyDown}
                        />
                    </Box>
                    <Box mb={2}>
                        <SelectFolder
                            selectedFolder={selectedFolder}
                            onChange={handleFolderChange}
                            onKeyDown={handleKeyDown}
                        />
                    </Box>
                </DialogContent>
                <DialogActions>
                    <Button onClick={handleDialogClose}>{t('Cancel')}</Button>
                    <Button onClick={handleSave} disabled={isSaveDisabled}>{t('Save')}</Button>
                </DialogActions>
            </Dialog>
        </Box>
    );
};

export default EditBookmark;
