import { ListItemIcon, MenuItem, SvgIcon, Typography } from "@mui/material";
import React from "react";
import { sendRuntimeMessage } from "../../../../core/browers/runtime";
import userStore from "../../../../core/stores/userStore";
import tabStore from "../../../../core/stores/tabStore";
import { useTranslationContext } from "../../../Providers/TranslationProvider";

export default function DiscardInactiveTabs({ onSelect }) {
    const { t } = useTranslationContext();

    const onlyCurrentWindowTabs = userStore((state) => state.settings.onlyCurrentWindowTabs);
    const currentWindow = tabStore((state) => state.currentWindow);

    const handleClick = () => {
        sendRuntimeMessage("discard_inactive_tabs", { windowId: onlyCurrentWindowTabs ? currentWindow.id : 0 });
        if (typeof onSelect === 'function') {
            onSelect();
        }
    };

    return (
        <MenuItem onClick={handleClick}>
            <ListItemIcon>
                <SvgIcon>
                    <svg xmlns="http://www.w3.org/2000/svg" height="24px" viewBox="0 -960 960 960" width="24px" fill="currentColor">
                        <path d="M520-200v-560h240v560H520Zm-320 0v-560h240v560H200Zm400-80h80v-400h-80v400Zm-320 0h80v-400h-80v400Zm0-400v400-400Zm320 0v400-400Z" />
                    </svg>
                </SvgIcon>
            </ListItemIcon>
            <Typography noWrap variant="body1">
                {t("DiscardInactiveTabs")}
            </Typography>
        </MenuItem>
    );
}