import { ListItemIcon, MenuItem, SvgIcon, Typography } from "@mui/material";
import React from "react";
import { useTranslationContext } from "../../../Providers/TranslationProvider";


export default function Feedback({ onSelect }) {

    const { t } = useTranslationContext();
    const handleClick = event => {
        const url = "https://github.com/RabbitPair/colorful_sidepanel_tabs_extension/issues/new/choose";
        chrome.tabs.create({ url: url, active: true })
        if (typeof onSelect === 'function') {
            onSelect();
        }
    }

    return (
        <MenuItem onClick={handleClick}>
            <ListItemIcon>
                <SvgIcon>
                    <svg xmlns="http://www.w3.org/2000/svg" height="24px" viewBox="0 -960 960 960" width="24px" fill="currentColor"><path d="M480-360q17 0 28.5-11.5T520-400q0-17-11.5-28.5T480-440q-17 0-28.5 11.5T440-400q0 17 11.5 28.5T480-360Zm-40-160h80v-240h-80v240ZM80-80v-720q0-33 23.5-56.5T160-880h640q33 0 56.5 23.5T880-800v480q0 33-23.5 56.5T800-240H240L80-80Zm126-240h594v-480H160v525l46-45Zm-46 0v-480 480Z" /></svg>
                </SvgIcon>
            </ListItemIcon>
            <Typography noWrap variant="body1">
                {t("SubmitSuggestionsOrIssues")}
            </Typography>
        </MenuItem>
    )
}