import React, { useState } from "react";
import { MenuItem, ListItemText, Typography, ListItemIcon, SvgIcon, Tooltip, Stack } from "@mui/material";
import tabStore from "../../../../core/stores/tabStore";
import { toast } from "react-toastify";
import userStore from "../../../../core/stores/userStore";
import { clearCookiesfromUrl } from "../../../../core/browers/cookies";
import { useTranslationContext } from "../../../Providers/TranslationProvider";
import { Help } from "@mui/icons-material";
import WithPermission from "../../../component/WithPermission";

const ClearCookies = ({ tabId, onSelect }) => {
    const { t } = useTranslationContext();
    const theme = userStore(state => state.settings.theme);
    const [showTooltip, setShowTooltip] = useState(false);

    const url = tabStore(state => state.tabs.find(t => t.id === tabId)?.url);

    const handleClick = async () => {
        await clearCookiesfromUrl(url);
        toast.success(t("tabMenu.CookiesCleared"), {
            position: "bottom-center",
            hideProgressBar: true,
            theme: theme,
            autoClose: 1500
        });
        if (typeof onSelect === 'function') {
            onSelect();
        }
    };

    const isHttpUrl = url?.startsWith("http");

    if (!isHttpUrl) {
        return null;
    }

    return (
        <WithPermission permissionName={"cookies"}>
            <MenuItem
                onClick={handleClick}
                onMouseEnter={() => setShowTooltip(true)}
                onMouseLeave={() => setShowTooltip(false)}
            >
                <ListItemIcon>
                    <SvgIcon>
                        <svg xmlns="http://www.w3.org/2000/svg" height="20px" viewBox="0 -960 960 960" width="20px" fill="currentColor">
                            <path d="m798-264-53-53q19.8-31.91 31.9-68.46Q789-422 792-462q-45-18-75-55.5T679-603q-76-11-129-63t-63-126q-47-3-90.5 9T315-747l-53-53q64-46 141.5-62.5T560-856q-8 44 5 81t41 61.5q28 24.5 68 34t85-1.5q-25 57 7.5 108.5t95.5 53.47q8 70.95-9.5 135.99T798-264Zm-474-96q-25 0-42.5-17.5T264-420q0-25 17.5-42.5T324-480q25 0 42.5 17.5T384-420q0 25-17.5 42.5T324-360ZM762-96l-66-66q-49 32-103.5 49T480-96q-79.38 0-149.19-30T208.5-208.5Q156-261 126-330.81T96-480q0-58 17-112.5T162-696l-66-66 51-51 666 666-51 51Zm-281.89-72Q524-168 565-181t79-34L215-644q-21 38-34 79t-13 84.89q0 129.72 91.19 220.92Q350.39-168 480.11-168ZM420-420Zm134-137Z" />
                        </svg>
                    </SvgIcon>
                </ListItemIcon>
                <ListItemText>
                    <Stack direction="row" alignItems="center" justifyContent="space-between">
                        <Typography variant="body1">{t("tabMenu.ClearCookies")}</Typography>
                        {showTooltip && <Tooltip
                            arrow
                            placement="top"
                            title={<Typography>{t("tabMenu.ClearCookiesTips")}</Typography>}
                        >
                            <Help fontSize="small" color="secondary" sx={{ opacity: '0.5' }} />
                        </Tooltip>
                        }
                    </Stack>
                </ListItemText>
            </MenuItem>
        </WithPermission>
    );
}

export default React.memo(ClearCookies);