import React from 'react';
import { ListItemIcon, ListItemText, MenuItem, Typography } from '@mui/material';
import { countBookmarkUrls } from '../../../../utils/helpers';
import { useTranslationContext } from '../../../Providers/TranslationProvider';
import { OpenInNew } from '@mui/icons-material';

const OpenTabs = ({ folder, handleClose }) => {

    const { t } = useTranslationContext();

    const handleClick = () => {
        const openUrls = (bookmark) => {
            if (bookmark.url) {
                setTimeout(() => {
                    chrome.tabs.create({ url: bookmark.url });
                }, 200); // Delay of 300ms to prevent system slowdown
            } else if (bookmark.children) {
                bookmark.children.forEach(child => openUrls(child));
            }
        };

        openUrls(folder);
        handleClose();
    };

    return (
        <MenuItem dense onClick={handleClick}>

            <ListItemIcon>
                <OpenInNew />
            </ListItemIcon>
            <ListItemText>
                <Typography variant="body1">{t('openAll', { count: countBookmarkUrls(folder) })}</Typography>
            </ListItemText>
        </MenuItem>
    );
};

export default OpenTabs;
