import { immer } from "zustand/middleware/immer";
import { shallow } from 'zustand/shallow';
import { createWithEqualityFn } from 'zustand/traditional';
import { subscribeWithSelector } from 'zustand/middleware';


const countBookmarks = (bookmarks) => {
    let count = 0;
    const countRecursively = (nodes) => {
        nodes.forEach((node) => {
            if (node.children) {
                countRecursively(node.children);
            } else {
                count++;
            }
        });
    };
    countRecursively(bookmarks);
    return count;
};

const flattenBookmarks = (bookmarks) => {
    const flatList = [];
    const flattenRecursively = (nodes, level = 0) => {
        nodes.forEach((node) => {
            flatList.push({ ...node, level });
            if (node.children) {
                flattenRecursively(node.children, level + 1);
            }
        });
    };
    flattenRecursively(bookmarks);
    return flatList;
};

export const bookmarkStore = createWithEqualityFn(subscribeWithSelector(immer((set, get) => ({
    searchQuery: '',
    bookmarksCount: 0,
    bookmarksStatus: [],
    bookmarkTree: [],
    bookmarks: [],
    bookmarkStoreInitialized: false,

    setBookmarkSearchQuery: (query) => {
        set({ searchQuery: query })
    },


    setBookmarksStatus: (id, status) => {
        if (!id || !status) {
            console.warn('setBookmarksStatus: Invalid id or status');
            return;
        }
        status = { ...status, id };
        set((state) => {
            const targetIndex = state.bookmarksStatus.findIndex(item => item.id === id);
            if (targetIndex !== -1) {
                const updatedBookmarksStatus = [...state.bookmarksStatus];
                updatedBookmarksStatus[targetIndex] = { ...updatedBookmarksStatus[targetIndex], ...status };
                return { bookmarksStatus: updatedBookmarksStatus };
            } else {
                return { bookmarksStatus: [...state.bookmarksStatus, status] };
            }
        });

        console.log("bookmarksStatus", get().bookmarksStatus)
    },

    loadBookmarks: async () => {
        console.log("loadBookmarks");
        const bookmarkTree = await chrome.bookmarks?.getTree();
        const firstLevelFolders = bookmarkTree?.[0].children || [];
        set({
            bookmarkTree: firstLevelFolders,
            bookmarks: flattenBookmarks(firstLevelFolders),
            bookmarksCount: countBookmarks(firstLevelFolders),
            bookmarkStoreInitialized: true
        });
    },

}))), shallow);



export const getAllParentFolderNamesByUrl = async (bookmarkUrl) => {
    const parentFolders = [];
    const bookmarks = await chrome.bookmarks?.search(bookmarkUrl.replace(/https?:\/\//, ""));

    if (!bookmarks || bookmarks?.length === 0) {
        return parentFolders;
    }

    const bookmark = bookmarks[0];

    const findAllParents = async (id) => {
        const parent = await chrome.bookmarks.get(id);
        if (parent && parent[0]) {
            parentFolders.push(parent[0].title);
            if (parent[0].parentId) {
                await findAllParents(parent[0].parentId);
            }
        }
    };

    if (bookmark.parentId) {
        await findAllParents(bookmark.parentId);
    }

    return parentFolders.reverse().slice(2);
};

export const setupBookmarkListeners = () => {
    const handleBookmarkChange = () => {
        console.log("handleBookmarkChange");
        bookmarkStore.getState().loadBookmarks();
    };

    chrome.bookmarks?.onChanged.addListener(handleBookmarkChange);
    chrome.bookmarks?.onChildrenReordered.addListener(handleBookmarkChange);
    chrome.bookmarks?.onCreated.addListener(handleBookmarkChange);
    chrome.bookmarks?.onMoved.addListener(handleBookmarkChange);
    chrome.bookmarks?.onRemoved.addListener(handleBookmarkChange);

    return () => {
        chrome.bookmarks?.onChanged.removeListener(handleBookmarkChange);
        chrome.bookmarks?.onChildrenReordered.removeListener(handleBookmarkChange);
        chrome.bookmarks?.onCreated.removeListener(handleBookmarkChange);
        chrome.bookmarks?.onMoved.removeListener(handleBookmarkChange);
        chrome.bookmarks?.onRemoved.removeListener(handleBookmarkChange);
    };
};
