import React, { useState, useEffect, useRef } from "react";
import TextField from "@mui/material/TextField";
import IconButton from "@mui/material/IconButton";
import SearchIcon from "@mui/icons-material/Search";
import ClearIcon from "@mui/icons-material/Clear";
import { Grid, Tooltip } from "@mui/material";
import { userStore } from "../../core/stores/userStore";
import { toast } from "react-toastify";
import { ArrowForward } from "@mui/icons-material";
import { bookmarkStore } from "../../core/stores/bookmarkStore";

const SearchBox = () => {
  const [state, setState] = useState({
    focused: false,
    localSearchText: ""
  });
  const { searchText, setSearchText, fontColor, activeTabIndex, searchQuery, setSearchQuery } = userStore((state) => ({
    searchText: state.searchText,
    setSearchText: state.setSearchText,
    fontColor: state.settings.fontColor,
    activeTabIndex: state.settings.activeTabIndex,
    searchQuery: state.searchQuery,
    setSearchQuery: state.setSearchQuery,
  }));

  const searchBoxRef = useRef(null);

  useEffect(() => {
    if (location.href.includes('popup.html')) {
      searchBoxRef.current.focus();
    }
    setState((prevState) => ({ ...prevState, localSearchText: activeTabIndex === 1 ? searchText : searchQuery }));
  }, [activeTabIndex, searchText, searchQuery]);

  const handleClearClick = () => {
    setState({ focused: false, localSearchText: "" });
    setSearchText("");
    setSearchQuery("");

  };

  const handleSearchInput = (event) => {
    const value = event.target.value;
    setState((prevState) => ({ ...prevState, localSearchText: value }));
    setSearchQuery(value)
    setSearchText(value);

  };

  const handleSearchInNewTab = async () => {
    if (!state.localSearchText) {
      return;
    }
    const querySearch = async () => {
      setState({ focused: false, localSearchText: "" });
      setSearchText("");
      await chrome.search.query({ disposition: "NEW_TAB", text: state.localSearchText });
    };
    chrome.permissions.contains({ permissions: ["search"] }, async (granted) => {
      if (!granted) {
        chrome.permissions.request({ permissions: ["search"] }, async (granted) => {
          if (!granted) {
            toast.error(t("settings.history.dataSource.deniedMessage"));
          } else {
            await querySearch();
          }
        });
      } else {
        await querySearch();
      }
    });
  };

  return (
    <Grid container justifyContent="center" alignItems={"center"} p={2}>
      <TextField
        inputRef={searchBoxRef}
        id="search-text-input"
        size="small"
        fullWidth
        placeholder="Search,Press Enter to search in a new tab"
        variant="outlined"
        autoComplete="off"
        focused={state.focused}
        value={state.localSearchText}

        InputProps={{
          startAdornment: <SearchIcon sx={{ mr: 1, color: fontColor || 'text.primary', fontSize: "1rem" }} />,
          endAdornment: state.localSearchText.length > 0 && (
            <React.Fragment>
              <IconButton onClick={handleClearClick} sx={{ color: fontColor || 'text.primary', }}>
                <ClearIcon size="small" />
              </IconButton>
              <IconButton onClick={handleSearchInNewTab} sx={{ color: fontColor || 'text.primary', }}>
                <Tooltip arrow title="Press Enter to search in a new tab">
                  <ArrowForward size="small" />
                </Tooltip>
              </IconButton>
            </React.Fragment>
          ),
          sx: {
            borderColor: fontColor || 'text.primary',
            color: fontColor || 'text.primary',
            '&::placeholder': {
              color: fontColor || 'text.primary',
              opacity: 1
            },
          },
        }}
        onChange={handleSearchInput}
        onFocus={() => setState((prevState) => ({ ...prevState, focused: true }))}
        onMouseEnter={() => setState((prevState) => ({ ...prevState, focused: true }))}
        onMouseLeave={() => setState((prevState) => ({ ...prevState, focused: false }))}
      />
    </Grid>
  );
};

export default SearchBox;
