import React, { useState, useEffect, memo } from "react";
import { Menu, List, Collapse, Divider, Stack, Button, Typography, ListItem, Box } from "@mui/material";
import SortableTabItem from "./SortableTabItem";
import { filterTabs, hexToRgb } from "../../../utils/helpers";
import { tabStore } from "../../../core/stores/tabStore";
import { userStore } from "../../../core/stores/userStore";
import TabAdd from "../../component/TabAdd";
import { ArrowRight, ArrowLeft, ArrowDropDown } from "@mui/icons-material";
import { chromeGroupAvailableColors, getCustomColor } from "../../../utils/colors";
import { useDndContext, useDndMonitor, useDroppable } from "@dnd-kit/core";
import { useTranslationContext } from "../../Providers/TranslationProvider";
import TabGroupChromeMenu from "./TabGroupChromeMenu";
import GroupHeader from "./groups/GroupHeader";
import { Droppable } from "./Droppable";
import { colord } from "colord";
import SiteAvatars from "./SiteAvatars";
import GroupMenus from "./groups/groupMenus/GroupMenus";
import { useSettingsContext } from "../../Providers/SettingsProvider";
import { useDndEnableContext } from "../../Providers/DndEnableProvider";

const TabGroupChrome = memo(({ groupId, hiddenTabs = false }) => {
  const { t } = useTranslationContext();
  const { setEnableDnd } = useDndEnableContext();

  const { storedGroup, setGroup, loadGroups } = tabStore((state) => ({
    storedGroup: state.groups.find(g => g.id === groupId),
    setGroup: state.setGroup,
    loadGroups: state.loadGroups
  }));

  useEffect(() => {
    if (!storedGroup && groupId > 0) {
      console.warn("group not found", groupId);
      try {
        chrome.tabGroups?.get(groupId).then(newGroup => {
          if (newGroup) {
            setGroup(newGroup.id, newGroup);
          }
        });
      } catch (error) {
        loadGroups()
      }
    }
  }, [storedGroup, groupId, setGroup, loadGroups]);

  const storedGroupLength = tabStore(state => state.groups?.length) || 0;
  const group = groupId === -1
    ? { id: -1, title: t("Ungrouped"), color: chromeGroupAvailableColors[storedGroupLength % chromeGroupAvailableColors.length].color }
    : { ...storedGroup };

  const { searchText, orderBy, sortBy, buttonPosition, GroupCollapsedShowIcon, colorfulBackground, PinnedTabsAsIcons, onlyCurrentWindowTabs, groupCollapsedTitleWidth } = userStore((state) => ({
    searchText: state.searchText,
    orderBy: state.settings.orderBy,
    sortBy: state.settings.sortBy,
    buttonPosition: state.settings.buttonPosition,
    GroupCollapsedShowIcon: state.settings.GroupCollapsedShowIcon,
    colorfulBackground: state.settings.colorfulBackground,
    PinnedTabsAsIcons: state.settings.PinnedTabsAsIcons,
    onlyCurrentWindowTabs: state.settings.onlyCurrentWindowTabs,
    groupCollapsedTitleWidth: state.settings.groupCollapsedTitleWidth
  }));

  const {
    view,
    theme,
    fontColor,
    fontScale,
  } = useSettingsContext();

  useDndMonitor({
    onDragStart(event) {
      console.log(event)
      if (event.active.data.current.type === 'group') {
        setExpanded(false)

      }
    },
    onDragMove(event) { },
    onDragOver(event) { },
    onDragEnd(event) {
      if (event.active.data.current.type === 'group') {
        setExpanded(!group.collapsed)

      }
    },
    onDragCancel(event) { },
  });

  const { setNodeRef } = useDroppable({ id: groupId });
  const currentWindow = tabStore(state => state.currentWindow);

  const tabs = tabStore(state => state.tabs
    .filter(tab => (onlyCurrentWindowTabs ? tab.windowId === currentWindow.id : true))
    .filter(tab => (PinnedTabsAsIcons ? !tab.pinned : true))
    .filter(tab => (groupId > -1 ? tab.groupId === groupId : tab.groupId === -1)));

  const filteredTabs = filterTabs(tabs, searchText, orderBy, sortBy);
  const tabIds = filteredTabs.map(tab => tab.id);

  const [expanded, setExpanded] = useState(!group.collapsed);
  const [contextMenu, setContextMenu] = useState(null);
  const [contextMenuAnchorEl, setContextMenuAnchorEl] = useState(null);

  useEffect(() => {
    setExpanded(!group.collapsed);
    window.addEventListener("blur", handleContextMenuClose);
    return () => {
      window.removeEventListener("blur", handleContextMenuClose);
    };
  }, [group.collapsed]);

  const handleContextMenu = (event) => {
    setEnableDnd(false);
    if (groupId > 0) {
      event.preventDefault();
      event.stopPropagation();
      setContextMenu({
        mouseX: event.clientX + 2,
        mouseY: event.clientY - 6
      });
      setContextMenuAnchorEl(event.currentTarget);
    }
  };

  const handleContextMenuClose = () => {
    setContextMenu(null);
    setEnableDnd(true);
  };

  const handleChange = async (event) => {
    event.preventDefault();
    event.stopPropagation();
    const newExpandedState = !expanded;
    setExpanded(newExpandedState);
    try {
      await chrome.tabGroups?.update(groupId, { collapsed: !newExpandedState });
    } catch (error) {
      console.error("Failed to update Chrome group state", error);
    }
  };

  const colorSetting = getCustomColor(group?.color);

  const TabItems = tabIds.map(tabId => <SortableTabItem key={tabId} tabId={tabId} />);

  if (tabIds.length === 0 || !group) {
    return null;
  }

  return (
    <List key={group.id} disablePadding sx={{ my: "3px" }}>
      <ListItem
        disableGutters
        sx={{ padding: 0 }}
        onClick={handleChange}
        onContextMenu={handleContextMenu}
      >
        <Stack
          sx={{
            width: "100%",
            // paddingRight: buttonPosition === "row" ? "10px" : undefined,
            // paddingLeft: buttonPosition !== "row" ? "10px" : undefined
          }}
          spacing={0}
          direction={buttonPosition}
          alignItems="center"
          justifyContent="space-between"
        >
          <Box flexShrink={1} sx={{ width: expanded && groupCollapsedTitleWidth ? `${groupCollapsedTitleWidth * 100}%` : "auto" }}>
            <GroupHeader group={group} />
          </Box>
          <Stack
            flexShrink={0}
            direction={buttonPosition}
            alignItems="center"
            justifyContent="flex-end"
            sx={{ flexGrow: 1, width: expanded && groupCollapsedTitleWidth ? `${(1 - groupCollapsedTitleWidth) * 100}%` : "auto" }}
          >
            <TabAdd showText={false} groupId={group.id} windowId={group.windowId} />
            {!expanded && GroupCollapsedShowIcon && !hiddenTabs && <SiteAvatars tabIds={tabIds} max={6} />}
          </Stack>
        </Stack>
        {groupId > 0 && (
          <Menu
            key={"menu" + group.id}
            dense="true"
            open={contextMenu !== null}
            anchorEl={contextMenuAnchorEl}
            onClose={handleContextMenuClose}
            onClick={(e) => e.stopPropagation()}
          >
            {/* <TabGroupChromeMenu key={"menu" + group.id} groupId={groupId} onSelect={handleContextMenuClose} /> */}
            <GroupMenus group={group} onSelect={handleContextMenuClose} anchorEl={contextMenuAnchorEl} setAnchorEl={setContextMenuAnchorEl} /> {/* Add this line */}
          </Menu>
        )}
      </ListItem>
      {expanded && !hiddenTabs && (
        <Collapse in={expanded} timeout="auto" sx={{ pl: '10px' }} >
          <Divider />
          {expanded && <List disablePadding ref={setNodeRef} sx={{ justifyContent: "center", borderLeft: `3px solid ${colorSetting.hex}` }}>
            {TabItems}
          </List>
          }
        </Collapse>
      )}
      {/* {!expanded && GroupCollapsedShowIcon && !hiddenTabs && <SiteAvatars tabIds={tabIds} max={6} />} */}
      {/* <Divider component="li" /> */}
    </List>
  );
});

export default TabGroupChrome;