import React, { useState, useRef, useEffect } from "react";
import { ListItem, ListItemButton, Box, Tooltip, IconButton, Menu, MenuItem, ListItemIcon, ListItemText, Typography, Stack } from "@mui/material";
import { Delete, Markunread, Done, CheckCircle, RadioButtonUnchecked } from "@mui/icons-material";
import { faviconURL } from "../../../utils/helpers";
import { deleteReadingListItem, updateReadingListItem } from "../../../core/browers/readingList";
import userStore from "../../../core/stores/userStore";

import { getTabItemStyle } from "../../Hooks/useTabItemStyle";
import LinkAvatar from "../../component/links/LinkAvatar";
import LinkTitle from "../../component/links/LinkTitle";
import LinkPreview from "../../component/links/LinkPreview";
import { useTranslationContext } from "../../Providers/TranslationProvider";

const ReadingListEntry = ({ item, onDelete, onUpdate, onSelect, selected }) => {

    const { t } = useTranslationContext();
    const { view, buttonPosition, tabPreviewEnable, tabPreviewWaitSeconds, searchQuery } = userStore((state) => ({
        updateSetting: state.updateSetting,
        view: state.settings.view,
        buttonPosition: state.settings.buttonPosition,
        tabPreviewEnable: state.settings.tabPreviewEnable,
        tabPreviewWaitSeconds: state.settings.tabPreviewWaitSeconds,
        setSearchText: state.setSearchText,
        activeTabIndex: state.settings.activeTabIndex,
        showFullUrl: state.settings.showFullUrl,
        AlwaysShowCloseButton: state.settings.AlwaysShowCloseButton,
        searchQuery: state.searchQuery,

    }));

    const [anchorEl, setAnchorEl] = useState(null);
    const open = Boolean(anchorEl);
    const listItemRef = useRef(null);
    const [isRead, setIsRead] = useState(item.hasBeenRead);

    useEffect(() => {
        if (selected && listItemRef.current) {
            listItemRef.current.scrollIntoView({ behavior: 'smooth', block: 'center' });
        }
    }, [selected]);

    const handleOpenLink = () => {
        chrome.tabs.create({ url: item.url });
        if (typeof onSelect === 'function') {
            onSelect();
        }
    }

    const handleContextMenu = (event) => {
        event.preventDefault();
        setAnchorEl(event.currentTarget);
    };

    const handleCloseMenu = () => {
        setAnchorEl(null);
    };

    const handleToggleReadStatus = async (event) => {
        event.stopPropagation();
        const newReadStatus = !isRead;
        await updateReadingListItem(item.url, { hasBeenRead: newReadStatus });
        setIsRead(newReadStatus);
        onUpdate(item.url, { read: newReadStatus });
        handleCloseMenu();
    };

    const handleDeleteItem = async (event) => {
        event.stopPropagation();
        await deleteReadingListItem(item.url);
        onDelete(item.url);
        handleCloseMenu();
    };

    return (
        <Box sx={{ position: "relative" }} onContextMenu={handleContextMenu} ref={listItemRef}>
            <ListItem disablePadding onClick={handleOpenLink} >
                <Tooltip
                    followCursor
                    enterDelay={parseFloat(tabPreviewWaitSeconds) * 1000}
                    enterNextDelay={200}
                    componentsProps={{
                        tooltip: {
                            sx: {
                                padding: "15px",
                                background: "transparent",
                                zIndex: 99
                            }
                        }
                    }}
                    title={tabPreviewEnable ? <LinkPreview title={item.title} url={item.url} /> : ""}
                >
                    <ListItemButton
                        selected={selected}
                        component="div"
                        dense={view === "list"}
                        disableGutters
                        sx={getTabItemStyle(item.url)}
                    >
                        <Stack
                            sx={{
                                width: "100%",
                                paddingX: "10px",
                            }}
                            direction={buttonPosition}
                            alignItems={"center"}
                            justifyContent={"space-between"}
                            spacing={1}
                        >
                            <LinkAvatar avatarUrl={faviconURL(item.url)} />
                            <LinkTitle title={item.title} url={item.url} lastAccessed={item.creationTime} searchText={searchQuery} />
                            <Tooltip title={isRead ? t('Read') : t('Unread')}>
                                <IconButton onClick={handleToggleReadStatus}>
                                    {isRead ? <CheckCircle fontSize="small" color="primary" /> : <RadioButtonUnchecked fontSize="small" color="action" />}
                                </IconButton>
                            </Tooltip>
                        </Stack>
                    </ListItemButton>
                </Tooltip>
            </ListItem>
            <Menu
                anchorEl={anchorEl}
                open={open}
                onClose={handleCloseMenu}
                anchorOrigin={{
                    vertical: 'top',
                    horizontal: 'right',
                }}
                transformOrigin={{
                    vertical: 'top',
                    horizontal: 'right',
                }}
            >
                <MenuItem onClick={handleToggleReadStatus}>
                    <ListItemIcon>
                        {isRead ? <Markunread fontSize="small" /> : <Done fontSize="small" />}
                    </ListItemIcon>
                    <ListItemText>
                        <Typography variant="body1"> {isRead ? t('markAsUnread') : t('markAsRead')}</Typography>
                    </ListItemText>
                </MenuItem>
                <MenuItem onClick={handleDeleteItem}>
                    <ListItemIcon>
                        <Delete fontSize="small" />
                    </ListItemIcon>
                    <ListItemText>
                        <Typography variant="body1"> {t('Delete')}</Typography>
                    </ListItemText>
                </MenuItem>

            </Menu>
        </Box>
    );
};

export default ReadingListEntry;
