import { tabJoinDomainGroup } from "../../core/browers/tabGroups";
import {
  addTabRemoveList,
  formatTabData,
  getCurrentTab,
  handleThumbnailCreation,
  isTabInRemoveList,
  playingVideoSwitchPip,
  removeTabById
} from "../../core/browers/tabs";
import tabStore, { initTabMetadataCache, setTabMetaCache } from "../../core/stores/tabStore";
import userStore from "../../core/stores/userStore";
import historyStore from "../../core/stores/historyStore";
import { debounce, delay } from "lodash-es";;
import { faviconURL, isValidUrl, scrollToActiveTab } from "../../utils/helpers";
import { sendTabMessage } from "../../core/browers/runtime";
import { progressBarStore } from "../../core/stores/progressBarStore";

const { autoCreateOrJoinSameDomainGroup } = userStore.getState().settings;
const { getTabConfig, setTabConfig, activeTab, closeTab, resortTabs, setGroup, loadTabs, closeGroup, hotTabIndexes } = tabStore.getState();
const { addHistory } = historyStore.getState();
const { setPlayingProgress, deletePlayingProgress } = progressBarStore.getState();

const handleTabOnCreated = async tab => {
  const tabId = tab.id;
  const tabData = await formatTabData(tab);
  const tabConfigs = { created_time: Date.now(), lastAccessed: Date.now(), ...tabData };
  await setTabConfig(tabId, tabConfigs);
  hotTabIndexes();
  await resortTabs();
  await initTabMetadataCache(tabId);

  if (autoCreateOrJoinSameDomainGroup) {
    await tabJoinDomainGroup(tabId);
  }
};

const handleTabOnUpdated = async (tabId, changeInfo, tab) => {
  console.log("handleTabOnUpdated", { tabId, changeInfo, tab });

  if (await isTabInRemoveList(tabId)) return;

  const tabConfig = await getTabConfig(tabId);
  const tabData = await formatTabData(tab);
  const tabConfigs = { ...tabConfig, ...tabData };
  await setTabConfig(tabId, tabConfigs);

  sendTabMessage(tabId, "tabUpdated", { tabId, changeInfo, tab });

  if (changeInfo.status === "complete" && isValidUrl(tab.url) && tab.active) {
    delay(thumbnailCaptured, 500, tabId);
  }

  if (tab.url) {
    // Record URL history
    let urlHistories = tabConfig?.urlHistories ? [...tabConfig.urlHistories] : [];
    const newUrlEntry = {
      tabId: tab.id,
      title: tab.title || tabConfig.title,
      url: tab.url,
      favIconUrl: tab?.favIconUrl || tabConfig?.favIconUrl || faviconURL(tab.url),
      active: true
    };

    const isDuplicate = urlHistories.some(entry => entry.url === tab.url);
    if (!isDuplicate) {
      urlHistories = urlHistories.map(entry => ({ ...entry, active: false }));
      urlHistories.push({ ...newUrlEntry, createdAt: Date.now(), lastAccessed: Date.now() });
    } else {
      // Update the existing entry to be active
      urlHistories = urlHistories.map(entry =>
        entry.url === tab.url ? {
          ...entry,
          active: true,
          lastAccessed: Date.now(),
          ...(tab.favIconUrl && { favIconUrl: tab.favIconUrl }),
          ...(tab.title && { title: tab.title })
        } : { ...entry, active: false }
      );
    }

    urlHistories.sort((a, b) => a.lastAccessed - b.lastAccessed);
    await setTabConfig(tabId, { urlHistories });
    await setTabMetaCache(tabId, 'urlHistories', urlHistories);
  }



  if (changeInfo.status === "loading" && changeInfo.url) {

    if (isValidUrl(changeInfo.url)) {
      setPlayingProgress(tabId, {});
      sendTabMessage(tabId, "tab_url_updated");
    }
  }
};

const thumbnailCaptured = debounce(async tabId => {
  const thumbnail = await handleThumbnailCreation(tabId);
  if (thumbnail) {
    await setTabMetaCache(tabId, "thumbnail", thumbnail);
    await setTabConfig(tabId, { thumbnail });
  }
}, 500);

const handleTabOnActivated = async activeInfo => {
  if (await isTabInRemoveList(activeInfo.tabId)) return;

  await activeTab(activeInfo.tabId);
  await playingVideoSwitchPip(activeInfo.tabId, "tab", "exitPictureInPicture");
  scrollToActiveTab();
  delay(thumbnailCaptured, 500, activeInfo.tabId);
};

const handleTabOnRemoved = async (tabId, removeInfo) => {
  console.log("handleTabOnRemoved", { tabId, removeInfo });
  await addTabRemoveList(tabId);
  const tabConfig = await getTabConfig(tabId);
  await addHistory(tabConfig);
  await closeTab(tabId);
  await resortTabs();
  deletePlayingProgress(tabId);
  hotTabIndexes();
};

const handleTabOnReplaced = async (addedTabId, removedTabId) => {
  console.log("handleTabOnReplaced", { addedTabId, removedTabId });

  if (await isTabInRemoveList(addedTabId)) return;
  if (await isTabInRemoveList(removedTabId)) {
    return await removeTabById(addedTabId);
  }

  const tabConfig = await getTabConfig(removedTabId);
  await closeTab(removedTabId);
  deletePlayingProgress(removedTabId);
  if (autoCreateOrJoinSameDomainGroup) {
    await tabJoinDomainGroup(addedTabId);
  }
  const tab = await chrome.tabs.get(addedTabId);
  const tabData = await formatTabData(tab);
  const tabConfigs = { created_time: Date.now(), lastAccessed: Date.now(), ...tabConfig, ...tabData };
  await setTabConfig(addedTabId, tabConfigs);
  hotTabIndexes();
};

const handleTabOnMoved = async (tabId, moveInfo) => {
  console.log("handleTabOnMoved", { tabId, moveInfo });
  await loadTabs();
  hotTabIndexes();
};

const handleTabOnDetached = async (tabId, detachInfo) => {
  console.log("handleTabOnDetached", { tabId, detachInfo });
  await resortTabs();
  hotTabIndexes();
};

const handleTabOnAttached = async (tabId, attachInfo) => {
  console.log("handleTabOnAttached", { tabId, attachInfo });
  await resortTabs();
  hotTabIndexes();
};

const handleOnZoomChange = async (ZoomChangeInfo) => {
  console.log("handleOnZoomChange", { ZoomChangeInfo });
};


const handleTabGroupOnMoved = async group => {
  await setGroup(group.id, group);
  console.log("handleTabGroupOnUpdated", { group });
};

const handleTabGroupOnCreated = async group => {
  await setGroup(group.id, group);
  console.log("handleTabGroupOnUpdated", { group });
  hotTabIndexes();
};
const handleTabGroupOnUpdated = async group => {
  await setGroup(group.id, group);
  console.log("handleTabGroupOnUpdated", { group });
};

const handleTabGroupOnRemoved = async group => {
  console.log("tabGroupsOnRemoved", { group });
  closeGroup(group.id);
};

const handleWindowsOnFocusChanged = async () => {
  const currentTab = await getCurrentTab();
  if (currentTab) {
    tabStore.setState({ activeWindowId: currentTab.windowId, activeTabId: currentTab.id });
  }
};

export const tabChangedEvents = () => {
  chrome.tabs.onCreated.addListener(handleTabOnCreated);
  chrome.tabs.onUpdated.addListener(handleTabOnUpdated);
  chrome.tabs.onActivated.addListener(handleTabOnActivated);
  chrome.tabs.onRemoved.addListener(handleTabOnRemoved);
  chrome.tabs.onReplaced.addListener(handleTabOnReplaced);
  chrome.tabs.onMoved.addListener(handleTabOnMoved);
  chrome.tabs.onAttached.addListener(handleTabOnAttached);
  chrome.tabs.onDetached.addListener(handleTabOnDetached);
  chrome.tabs.onZoomChange.addListener(handleOnZoomChange);

  chrome.tabGroups?.onMoved.addListener(handleTabGroupOnMoved);
  chrome.tabGroups?.onCreated.addListener(handleTabGroupOnCreated);
  chrome.tabGroups?.onUpdated.addListener(handleTabGroupOnUpdated);
  chrome.tabGroups?.onRemoved.addListener(handleTabGroupOnRemoved);

  chrome.windows.onFocusChanged.addListener(handleWindowsOnFocusChanged);
};
