import React from "react";
import { MenuItem, ListItemText, Typography, ListItemIcon, SvgIcon } from "@mui/material";
import { useTranslationContext } from "../../../Providers/TranslationProvider";
import tabStore from "../../../../core/stores/tabStore";
import { sendTabMessage } from "../../../../core/browers/runtime";

export default function PipTab({ tabId, onSelect }) {
    const { t } = useTranslationContext();

    const { pipSupport, pipOn } = tabStore((state) => {
        const tab = state.tabs.find(tab => tab.id === tabId);
        return {
            pipSupport: tab?.pipSupport,
            pipOn: tab?.pipOn
        };
    });

    const handleClick = async () => {
        await sendTabMessage(tabId, pipOn ? 'exitPictureInPicture' : "enterPictureInPicture");
        if (typeof onSelect === 'function') {
            onSelect();
        }
    };

    const pipIcon = (
        <SvgIcon>
            {pipOn ? (
                <svg xmlns="http://www.w3.org/2000/svg" height="20px" viewBox="0 -960 960 960" width="20px" fill="currentColor">
                    <path d="M168-192q-29.7 0-50.85-21.15Q96-234.3 96-264v-240h72v240h624v-432H456v-72h336q29.7 0 50.85 21.16Q864-725.68 864-695.96v432.24Q864-234 842.85-213T792-192H168Zm525-120 51-51-93-93h69v-72H528v192h72v-69l93 93ZM96-576v-192h288v192H96Zm384 96Z" />
                </svg>
            ) : (
                <svg xmlns="http://www.w3.org/2000/svg" height="20px" viewBox="0 -960 960 960" width="20px" fill="currentColor">
                    <path d="M96-480v-72h165L71-743l50-50 191 190v-165h72v288H96Zm72 288q-29.7 0-50.85-21.15Q96-234.3 96-264v-144h72v144h336v72H168Zm624-264v-240H456v-72h336q29.7 0 50.85 21.15Q864-725.7 864-696v240h-72ZM576-192v-192h288v192H576Z" />
                </svg>
            )}
        </SvgIcon>
    );

    if (!pipSupport) {
        return null;
    }

    return (
        <MenuItem onClick={handleClick}>
            <ListItemIcon>
                {pipIcon}
            </ListItemIcon>
            <ListItemText>
                <Typography variant="body1">{t(pipOn ? "tabMenu.ExitPip" : "tabMenu.EnterPip")}</Typography>
            </ListItemText>
        </MenuItem>
    );
}