import React, { useState, useRef, useEffect } from 'react';
import { Box, Divider, Stack, Typography, IconButton, TextField, Tooltip, Menu, MenuItem, Checkbox, ListItemIcon, ListItemText, SvgIcon } from '@mui/material';
import SearchIcon from '@mui/icons-material/Search';
import CloseIcon from '@mui/icons-material/Close';
import { useTranslationContext } from '../../Providers/TranslationProvider';
import { Search } from '@mui/icons-material';
import userStore from '../../../core/stores/userStore';
import { UnfoldMore, UnfoldLess } from '@mui/icons-material';
import { Folder, List, Sort } from '@mui/icons-material'; // Import Sort icon
import MoreVertIcon from '@mui/icons-material/MoreVert';
import { Bookmarks } from '@mui/icons-material';
import { SortByAlpha, Link, AccessTime, Add } from '@mui/icons-material'; // Import icons for sort options
import { Bookmark } from '@mui/icons-material';
import { bookmarkStore } from '../../../core/stores/bookmarkStore';
import ColorTypography from '../../component/ColorTypography';
import { ArrowBack } from '@mui/icons-material';
import { memo } from 'react';

const BookmarksHeader = ({ showBack }) => {

    console.log("BookmarksHeader render")


    const { t } = useTranslationContext();
    const [isSearching, setIsSearching] = useState(false);
    const searchInputRef = useRef(null);
    const { updateSetting, theme, fontColor, bookmarkViewMode, bookmarkHideEmptyFolders, bookmarkSortOrder, bookmarkExpandAll, bookmarkTreeConnectorLines, bookmarkShowUrl, searchQuery, setSearchQuery } = userStore((state) => ({
        updateSetting: state.updateSetting,
        theme: state.settings.theme,
        fontColor: state.settings.fontColor,
        bookmarkViewMode: state.settings.bookmarkViewMode,
        bookmarkHideEmptyFolders: state.settings.bookmarkHideEmptyFolders,
        bookmarkSortOrder: state.settings.bookmarkSortOrder,
        bookmarkExpandAll: state.settings.bookmarkExpandAll,
        bookmarkTreeConnectorLines: state.settings.bookmarkTreeConnectorLines,
        bookmarkShowUrl: state.settings.bookmarkShowUrl,
        searchQuery: state.searchQuery,
        setSearchQuery: state.setSearchQuery,
    }));

    const bookmarksCount = bookmarkStore((state) => state.bookmarksCount);


    const [anchorEl, setAnchorEl] = useState(null);
    const [sortAnchorEl, setSortAnchorEl] = useState(null);

    useEffect(() => {
        if (isSearching) {
            searchInputRef.current?.focus();
        }
    }, [isSearching]);

    const handleSearchClick = () => setIsSearching(true);
    const handleCloseSearch = () => {
        setIsSearching(false);
        setSearchQuery('');
    };

    const handleExpandToggle = () => {
        updateSetting('bookmarkExpandAll', !bookmarkExpandAll);
    };

    const handleViewModeToggle = () => {
        const newMode = bookmarkViewMode === 'tree' ? 'list' : 'tree';
        updateSetting('bookmarkViewMode', newMode);
    };

    const handleMenuClick = (event) => {
        setAnchorEl(event.currentTarget);
    };

    const handleMenuClose = () => {
        setAnchorEl(null);
    };

    const handleSortMenuClick = (event) => {
        setSortAnchorEl(event.currentTarget);
    };

    const handleSortMenuClose = () => {
        setSortAnchorEl(null);
    };

    const handleSortChange = (order) => {
        updateSetting('bookmarkSortOrder', order);
        handleSortMenuClose();
    };

    const handleToggleHideEmptyFolders = () => {
        const newStatus = !bookmarkHideEmptyFolders
        updateSetting('bookmarkHideEmptyFolders', newStatus);

    };
    const handleToggleTreeConnectorLines = () => {
        const newStatus = !bookmarkTreeConnectorLines
        updateSetting('bookmarkTreeConnectorLines', newStatus);

    };

    const handleToggleShowUrl = () => {
        const newStatus = !bookmarkShowUrl
        updateSetting('bookmarkShowUrl', newStatus);

    };

    const handleBack = async () => {
        await updateSetting('showTabNavs', true);
        await updateSetting('activeTabIndex', 1);
    };


    const sortOptions = [
        { value: 'default', label: t("default"), icon: <Bookmark /> },
        { value: 'title', label: t("title"), icon: <SortByAlpha /> },
        { value: 'url', label: t("url"), icon: <Link /> },
        { value: 'recentlyUsed', label: t("recentlyUsed"), icon: <AccessTime /> },
        { value: 'recentlyAdded', label: t("recentlyAdded"), icon: <Add /> }
    ];

    const handleSearchChange = (e) => {
        setSearchQuery(e.target.value);
    };

    return (
        <Stack direction="row" justifyContent="space-between" alignItems="center" spacing={1}>

            {showBack && (isSearching || searchQuery) ? (
                <Stack direction="row" alignItems="center" spacing={1} sx={{ width: '100%' }}>
                    <TextField
                        size='small'
                        fullWidth
                        variant="outlined"
                        placeholder={t('searchByKeyword')}
                        value={searchQuery}
                        onChange={handleSearchChange}
                        inputRef={searchInputRef}
                        InputProps={{
                            startAdornment: <Search sx={{ mr: 1, color: theme === "dark" ? "text.primary" : fontColor || "text.primary" }} />,
                            endAdornment: <IconButton sx={{ color: theme === "dark" ? "text.primary" : fontColor || "text.primary" }} onClick={handleCloseSearch}><CloseIcon /></IconButton>,
                            sx: {
                                borderColor: fontColor || 'text.primary',
                                color: theme === "dark" ? "text.primary" : fontColor || "text.primary",
                                '&::placeholder': {
                                    color: theme === "dark" ? "text.primary" : fontColor || "text.primary",
                                    opacity: 1
                                },
                            },
                        }}
                    />
                </Stack>
            ) : (
                <>
                    {showBack && <>
                        <IconButton sx={{ color: fontColor || 'text.primary' }} onClick={handleBack}>
                            <ArrowBack fontSize="small" />
                        </IconButton>
                        <ColorTypography variant='h4'>{t("bookmarks")} 〈{bookmarksCount}〉</ColorTypography>
                    </>

                    }
                    <Stack direction="row" justifyContent="center" alignItems="center" spacing={0}>
                        {showBack && <IconButton sx={{ color: fontColor || 'text.primary' }} onClick={handleSearchClick}>
                            <SearchIcon fontSize="small" />
                        </IconButton>
                        }
                        {bookmarkViewMode === 'tree' && (
                            <Tooltip title={bookmarkExpandAll ? t("CollapseAll") : t("ExpandAll")}>
                                <IconButton sx={{ color: fontColor || 'text.primary' }} onClick={handleExpandToggle}>
                                    {bookmarkExpandAll ? <UnfoldLess fontSize="small" /> : <UnfoldMore fontSize="small" />}
                                </IconButton>
                            </Tooltip>
                        )}
                        <Tooltip title={bookmarkViewMode === 'tree' ? t("switchToListView") : t("switchToTreeView")}>
                            <IconButton sx={{ color: fontColor || 'text.primary' }} onClick={handleViewModeToggle}>
                                {bookmarkViewMode === 'tree' ? <List fontSize="small" /> : <Folder fontSize="small" />}
                            </IconButton>
                        </Tooltip>
                        <Tooltip title={t("sortBy")}>
                            <IconButton sx={{ color: fontColor || 'text.primary' }} onClick={handleSortMenuClick}>
                                <Sort fontSize="small" />
                            </IconButton>
                        </Tooltip>
                        <IconButton sx={{ color: fontColor || 'text.primary' }} onClick={handleMenuClick}>
                            <MoreVertIcon fontSize="small" />
                        </IconButton>
                        <Menu
                            anchorEl={anchorEl}
                            open={Boolean(anchorEl)}
                            onClose={handleMenuClose}
                        >
                            <MenuItem onClick={handleToggleHideEmptyFolders}>
                                <ListItemIcon>
                                    <Checkbox
                                        checked={bookmarkHideEmptyFolders}
                                        onChange={handleToggleHideEmptyFolders}
                                        color="primary"
                                    />
                                </ListItemIcon>
                                <ListItemText>
                                    <Typography variant='body1'>  {t("hideEmptyFolders")}</Typography>
                                </ListItemText>
                            </MenuItem>
                            <MenuItem onClick={handleToggleTreeConnectorLines}>
                                <ListItemIcon>
                                    <Checkbox
                                        checked={bookmarkTreeConnectorLines}
                                        onChange={handleToggleTreeConnectorLines}
                                        color="primary"
                                    />
                                </ListItemIcon>
                                <ListItemText>
                                    <Typography variant='body1'>  {t("displayTreeConnectorLines")}</Typography>
                                </ListItemText>
                            </MenuItem>
                            <MenuItem onClick={handleToggleShowUrl}>
                                <ListItemIcon>
                                    <Checkbox
                                        checked={bookmarkShowUrl}
                                        onChange={handleToggleShowUrl}
                                        color="primary"
                                    />
                                </ListItemIcon>
                                <ListItemText>
                                    <Typography variant='body1'>  {t("showBookmarkUrl")}</Typography>
                                </ListItemText>
                            </MenuItem>
                            <MenuItem onClick={() => chrome.tabs.create({ url: 'chrome://bookmarks/' })}>
                                <ListItemIcon sx={{ display: "flex", justifyContent: "center" }}>
                                    <Bookmarks />
                                </ListItemIcon>
                                <ListItemText>
                                    <Typography variant='body1'>
                                        {t("bookmarkManager")}
                                    </Typography>
                                </ListItemText>

                            </MenuItem>
                        </Menu>
                        <Menu
                            anchorEl={sortAnchorEl}
                            open={Boolean(sortAnchorEl)}
                            onClose={handleSortMenuClose}
                        >
                            {sortOptions.map(option => (
                                <MenuItem selected={bookmarkSortOrder === option.value} key={option.value} onClick={() => handleSortChange(option.value)}>
                                    <ListItemIcon>
                                        {option.icon}
                                    </ListItemIcon>
                                    {option.label}
                                </MenuItem>
                            ))}
                        </Menu>
                    </Stack>
                </>
            )}
        </Stack>
    );
};

export default memo(BookmarksHeader);
