import React, { useEffect, useState } from "react";
import { Box, List } from "@mui/material";
import { filterTabs } from "../../../utils/helpers";
import { historyStore } from "../../../core/stores/historyStore";
import HistoryItem from "./HistoryItem";
import PageLoading from "../../component/pageLoading";
import { userStore } from "../../../core/stores/userStore";

const HistoryList = ({ limit, onSelect }) => {
  const { loadHistories, historyStoreInitialized, histories } = historyStore((state) => ({
    loadHistories: state.loadHistories,
    historyStoreInitialized: state.historyStoreInitialized,
    histories: state.histories,
  }));

  const { searchText } = userStore((state) => ({
    searchText: state.searchText,
  }));

  const [filteredHistories, setFilteredHistories] = useState([]);

  useEffect(() => {
    loadHistories();
  }, [loadHistories]);

  useEffect(() => {
    let filtered = filterTabs(histories, searchText, 'desc', 'closed_time');
    if (limit) {
      filtered = filtered.slice(0, limit);
    }
    setFilteredHistories(filtered);
  }, [histories, searchText, limit]);

  return (
    <Box sx={{ padding: 0 }}>
      {historyStoreInitialized ? (
        <List sx={{ width: "100%", padding: 0 }}>
          {filteredHistories.map((history) => (
            <HistoryItem key={history.urlMD5} urlMD5={history.urlMD5} onSelect={onSelect} />
          ))}
        </List>
      ) : (
        <PageLoading />
      )}
    </Box>
  );
};

export default React.memo(HistoryList);
