import React, { useState } from 'react';
import { MenuItem, ListItemIcon, ListItemText, Typography, Dialog, DialogActions, DialogContent, DialogContentText, DialogTitle, Button, Box } from '@mui/material';
import { useTranslationContext } from '../../../Providers/TranslationProvider';
import { FolderDelete } from '@mui/icons-material';

const DeleteFolder = ({ folder, handleClose }) => {
    const { t } = useTranslationContext();
    const [open, setOpen] = useState(false);

    const handleClickOpen = () => {
        setOpen(true);
    };

    const handleCloseDialog = () => {
        setOpen(false);
        handleClose();
    };

    const handleDelete = async () => {
        try {
            await chrome.bookmarks?.removeTree(folder.id);
        } catch (error) {
            console.error("Failed to delete folder:", error);
        }
        handleCloseDialog();
    };

    return (
        <Box>
            <MenuItem dense onClick={handleClickOpen}>
                <ListItemIcon>
                    <FolderDelete />
                </ListItemIcon>
                <ListItemText>
                    <Typography variant="body1">{t('deleteFolder')}</Typography>
                </ListItemText>
            </MenuItem>
            <Dialog
                open={open}
                onClose={handleCloseDialog}
                aria-labelledby="alert-dialog-title"
                aria-describedby="alert-dialog-description"
            >
                <DialogTitle id="alert-dialog-title">{t('deleteFolder')}</DialogTitle>
                <DialogContent>
                    <DialogContentText id="alert-dialog-description">
                        {t('deleteFolderConfirmation')}
                    </DialogContentText>
                </DialogContent>
                <DialogActions>
                    <Button onClick={handleCloseDialog} color="primary">
                        {t('cancel')}
                    </Button>
                    <Button onClick={handleDelete} color="primary">
                        {t('delete')}
                    </Button>
                </DialogActions>
            </Dialog>
        </Box>
    );
};

export default DeleteFolder;
