import React, { memo } from "react";
import { AvatarGroup, Button, Box } from "@mui/material";
import { SortableContext, horizontalListSortingStrategy } from "@dnd-kit/sortable";
import SortableTabIcon from "./SortableTabIcon";

const SiteAvatars = ({ tabIds, max = 5 }) => {
  return (
    <AvatarGroup
      max={max}
      sx={{
        alignItems: "center",
        "& .MuiAvatar-root": { border: "none" }
      }}
      slotProps={{
        additionalAvatar: {
          sx: {
            width: "auto",
            height: "24px",
            borderRadius: "3px",
            alignSelf: "center",
            display: "inline-flex",
            justifyContent: "center",
            padding: "0px",
            backgroundColor: "transparent"
          }
        }
      }}
      renderSurplus={surplus => (
        <Button variant="text" size="small" sx={{ minWidth: "auto", color: "#eb0028", backgroundColor: "transparent", fontSize: "11px", paddingX: "5px" }}>+{surplus}</Button>
      )}
      spacing={0}
    >
      <SortableContext items={tabIds} strategy={horizontalListSortingStrategy}>
        <Box sx={{ display: 'flex', alignItems: 'center', flexWrap: 'wrap' }}>
          {tabIds.map(tabId => (
            <SortableTabIcon key={`tab-avatar-${tabId}`} tabId={tabId} />
          ))}
        </Box>
      </SortableContext>
    </AvatarGroup>
  );
};

export default memo(SiteAvatars);
