import { ListItemIcon, MenuItem, SvgIcon, Typography } from "@mui/material";
import React from "react";
import { sendRuntimeMessage } from "../../../../core/browers/runtime";
import { useTranslationContext } from "../../../Providers/TranslationProvider";


export default function SortTabsByDomain({ onSelect }) {

    const { t } = useTranslationContext();
    const handleClick = async (event) => {
        sendRuntimeMessage("sort_tabs_by_domain");
        if (typeof onSelect === 'function') {
            onSelect();
        }
    }

    return (
        <MenuItem onClick={handleClick}>
            <ListItemIcon>
                <SvgIcon>
                    <svg xmlns="http://www.w3.org/2000/svg" height="24px" viewBox="0 -960 960 960" width="24px" fill="currentColor"><path d="M280-120 80-320l200-200 57 56-104 104h607v80H233l104 104-57 56Zm400-320-57-56 104-104H120v-80h607L623-784l57-56 200 200-200 200Z" /></svg>
                </SvgIcon>
            </ListItemIcon>
            <Typography noWrap variant="body1">
                {t("SortTabsByDomain")}
            </Typography>
        </MenuItem>
    )
}