import { Add } from "@mui/icons-material";
import { Button, IconButton, ListItemIcon, ListItemText, MenuItem, Tooltip, Typography } from "@mui/material";
import React, { memo } from "react";
import { useTranslationContext } from "../Providers/TranslationProvider";
import { userStore } from "../../core/stores/userStore";

const TabAdd = ({ groupId = 0, hostname = '', showText = true, windowId = 0, shortcut = false, onSelect }) => {
    const { updateSetting, activeTabIndex, theme, fontColor } = userStore((state) => ({
        updateSetting: state.updateSetting,
        activeTabIndex: state.settings.activeTabIndex,
        theme: state.settings.theme,
        fontColor: state.settings.fontColor,
    }));

    const { t } = useTranslationContext();

    const handleTabAddNew = async (event) => {
        event.preventDefault();
        event.stopPropagation();
        if (typeof onSelect === 'function') {
            onSelect();
        }
        const params = {
            active: true,
        };
        if (hostname) {
            params.url = hostname.indexOf("://") > -1 ? hostname : "https://" + hostname;
        }
        if (windowId) {
            params.windowId = windowId;
        }
        const newTab = await chrome.tabs.create({ active: true, ...params });
        if (groupId && groupId > 0) {
            await chrome.tabs.group({ tabIds: [newTab.id], groupId: groupId });
        }
        if (activeTabIndex > 1) {
            await updateSetting('activeTabIndex', 1);
        }
    };

    if (shortcut) {
        return (
            <MenuItem onClick={handleTabAddNew}>
                <ListItemIcon>
                    <Add />
                </ListItemIcon>
                <ListItemText>
                    <Typography noWrap>{t("NewTab")}</Typography>
                </ListItemText>
                <Typography variant="body2" color="text.secondary">
                    Ctrl +T
                </Typography>
            </MenuItem>
        );
    }

    if (!showText) {
        return (
            <Tooltip arrow title={<Typography>{t("NewTab")}</Typography>}>
                <IconButton sx={{ padding: "5px", color: fontColor || 'text.primary' }} onClick={handleTabAddNew}>
                    <Add />
                </IconButton>
            </Tooltip>
        );
    }

    return (
        <Button sx={{ padding: "5px", color: fontColor || 'text.primary' }} startIcon={<Add />} variant="text" onClick={handleTabAddNew}>
            <Typography noWrap variant="body1" sx={{ color: theme === "dark" ? "text.primary" : fontColor || "text.primary" }}>
                {t("NewTab")}
            </Typography>
        </Button>
    );
};

export default memo(TabAdd);
