import { Box, Card, CardContent, CardMedia, Typography } from "@mui/material";
import React from "react";
import userStore from "../../../../core/stores/userStore";
import { decodedUrl } from "../../../../utils/helpers";


export default function Preview({ quickAccess }) {

    const { fontWeight, } = userStore((state) => ({
        fontWeight: state.settings.fontWeight,
    }))


    // const color = tab.color || noColor;

    return (
        <Card sx={{
            padding: 0, minWidth: "200px",
        }}>
            <CardContent sx={{ padding: "8px" }}>
                <Typography gutterBottom variant="h3" color="text.primary" component="div" sx={{
                    overflow: "hidden",
                    textOverflow: "ellipsis",
                    display: "-webkit-box",
                    WebkitLineClamp: "2",
                    fontWeight: fontWeight,
                    WebkitBoxOrient: "vertical", margin: " 8px 0"
                }}>
                    {quickAccess.title}
                </Typography>
                {quickAccess.urls ? (
                    quickAccess.urls.map((urlObj, index) => (
                        <Box key={index} sx={{ my: 1 }}>
                            <Typography variant="h4" color="text.primary" sx={{ whiteSpace: "pre-wrap", wordWrap: "break-word", wordBreak: "break-all", textWrap: "break-word", overflow: "hidden", textOverflow: "ellipsis", display: "-webkit-box", WebkitLineClamp: "1", WebkitBoxOrient: "vertical" }}>
                                {urlObj.title}
                            </Typography>
                            <Typography variant="h5" color="text.secondary" sx={{ whiteSpace: "pre-wrap", wordWrap: "break-word", wordBreak: "break-all", textWrap: "break-word", overflow: "hidden", textOverflow: "ellipsis", display: "-webkit-box", WebkitLineClamp: "3", WebkitBoxOrient: "vertical" }}>
                                {decodedUrl(urlObj.url)}
                            </Typography>
                        </Box>
                    ))
                ) : (
                    <Typography variant="h4" color="text.secondary" sx={{ whiteSpace: "pre-wrap", wordWrap: "break-word", wordBreak: "break-all", textWrap: "break-word", overflow: "hidden", textOverflow: "ellipsis", display: "-webkit-box", WebkitLineClamp: "3", WebkitBoxOrient: "vertical" }}>
                        {decodedUrl(quickAccess.url)}
                    </Typography>
                )}
            </CardContent>
        </Card>
    )
}