import tabStore, { setTabMetaCache } from "../../core/stores/tabStore";
import userStore from "../../core/stores/userStore";
import { siteStore } from "../../core/stores/siteStore";
import { playingVideoSwitchPip, saveTabPictureInPictureInfo } from "../../core/browers/tabs";
import { progressBarStore } from "../../core/stores/progressBarStore";
import { memoryStore } from "../../utils/memoryStore";
import { getCurrentWindow } from "../../core/browers/windows";

let currentWindow = null;
let quickAccessSets = new Set();

export const initRuntimeMessageEvents = async () => {
    currentWindow = await getCurrentWindow();
    runtimeMessageEvents();
};

const runtimeMessageEvents = () => {

    const { setTabConfig, setQuickAccess, loadTabs, loadGroups } = tabStore.getState();
    const { setSiteConfig } = siteStore.getState();
    const { setReadingProgress, setPlayingProgress } = progressBarStore.getState();
    const { settings, updateSetting, } = userStore.getState();

    let port = null;
    let reconnectAttempts = 0;
    const maxReconnectAttempts = 5;

    const portKeepAlive = async (port) => {
        if (port) {
            port.onDisconnect.addListener(async () => {
                console.log("port.onDisconnect", currentWindow.id);
                if (reconnectAttempts < maxReconnectAttempts) {
                    reconnectAttempts++;
                    await new Promise(resolve => setTimeout(resolve, 1000)); // Wait for 1 second before retrying
                    port = chrome.runtime.connect({ name: `sidepanel_${currentWindow.id}` });
                    portKeepAlive(port);
                } else {
                    console.error("Max reconnect attempts reached. Stopping reconnection attempts.");
                }
            });
        }
    };

    const handleRuntimeOnMessage = (message, sender, sendResponse) => {
        console.log("handleRuntimeOnMessage", { message, sender });
        processRuntimeMessage(message, sender, sendResponse);
        return true;
    };

    async function processRuntimeMessage(message, sender, sendResponse) {
        try {
            const { data } = message;
            const tabId = data?.tabId || sender.tab?.id;
            switch (message.action) {
                case "is_sidepanel_opened": {
                    sendResponse({ success: true, data: { is_sidepanel_opened: true } });
                    break;
                }
                case 'page_enter_fullscreen': {
                    if (settings.fullScreenHideSidePanel) {
                        window.close();
                    }
                    sendResponse({ success: true });
                    break;
                }
                case 'page_exit_fullscreen': {
                    sendResponse({ success: true });
                    break;
                }

                case 'close_sidepanel': {
                    window.close();
                    sendResponse({ success: true });
                    break;
                }
                case 'update_setting': {
                    await updateSetting(data.key, data.value);
                    sendResponse({ success: true });
                    break;
                }
                case 'update_site_config': {
                    await setSiteConfig(data.hostname, data.config);
                    sendResponse({ success: true });
                    break;
                }
                case 'update_tab_config': {
                    await setTabConfig(tabId, data.tabConfigs);
                    sendResponse({ success: true });
                    break;
                }
                case 'move_tabs_to_window':
                case 'move_tabs_to_new_window':
                case 'move_group_to_new_window': {
                    await Promise.all([loadTabs(), loadGroups()]);
                    sendResponse({ success: true });
                    break;
                }
                case 'page_scrolled': {
                    setReadingProgress(tabId, data.scrollProgress);
                    sendResponse({ success: true });
                    break;
                }
                case 'playing_timeupdate': {
                    const checkLock = memoryStore.get(`playing_progress_bar_locked_${tabId}`);
                    if (!checkLock) {
                        if (!data.isUsableVideo) {
                            // console.log("handleRuntimeOnMessage", { message, sender })

                            // setPlayingProgress(tabId, {});
                        } else {
                            setPlayingProgress(tabId, data);

                        }
                    }

                    sendResponse({ success: true, checkLock: checkLock });
                    break;
                }
                case 'page_picture_in_picture': {
                    // await saveTabPipInfo(tabId, sender.frameId, data);
                    const pipData = await saveTabPictureInPictureInfo(tabId, sender.frameId, data);
                    setTabConfig(tabId, pipData);
                    sendResponse({ success: true });
                    break;
                }
                case 'page_video_intersection_changed': {
                    if (message.data.isIntersecting && message.data.duration !== 'Infinity') {
                        // await scrollSwitchPictureInPicture(sender.tab.id, 'exitPictureInPicture')
                        await playingVideoSwitchPip(sender.tab.id, 'scroll', 'exitPictureInPicture')
                    } else {
                        await playingVideoSwitchPip(sender.tab.id, 'scroll', 'playing_video_enterPictureInPicture')
                    }
                    await setTabConfig(sender.tab.id, { video_visiable: message.data.video_visiable });
                    sendResponse({ success: true })
                    break;
                }

                case 'addQuickAccess': {
                    const items = message.data.items;
                    const storedQuickAccess = tabStore.getState().quickAccess;
                    for (const item of storedQuickAccess) {
                        quickAccessSets.add(item.url);
                    }
                    for (let i = 0; i < items.length; i++) {
                        const item = items[i];
                        if (storedQuickAccess.findIndex(q => q.url === item.url) === -1 && !quickAccessSets.has(item.url)) {
                            await setQuickAccess(crypto.randomUUID(), item)
                            quickAccessSets.add(item.url);
                        }
                    }
                    sendResponse({ success: true });
                    break;
                }
                default: {
                    // sendResponse({ success: true, error: "" });
                    break;
                }
            }
        } catch (error) {
            console.error("Async operation failed:", error);
            try {
                sendResponse({ error: error.message });
            } catch {
                console.error("send error Response failed:", error);
            }
        }
    }

    port = chrome.runtime.connect({ name: `sidepanel_${currentWindow.id}` });
    portKeepAlive(port);
    chrome.runtime.onMessage.addListener(handleRuntimeOnMessage);
}