import React, { useState, useEffect, memo, useRef } from "react";
import { Box, IconButton, Menu, Tooltip } from "@mui/material";
import { PushPin } from "@mui/icons-material";
import tabStore, { tabIntersectionObserver } from "../../../core/stores/tabStore";
import { setTabActive } from "../../../core/browers/tabs";
import userStore from "../../../core/stores/userStore";
import { hexToRgb } from "../../../utils/helpers";
import { useTranslationContext } from "../../Providers/TranslationProvider";
import TabAvatar from "./tab/TabAvatar";
import TabPreview from "./tab/TabPreview";
import TabMenu from "./tabMenu";

const TabAsIcon = ({ tabId }) => {
    const { t } = useTranslationContext();

    const { tab, loadTabs, keyboardSelected } = tabStore((state) => ({
        tab: state.tabs.find(tab => tab.id === tabId),
        loadTabs: state.loadTabs,
        keyboardSelected: state.keyboardSelected?.key === tabId,
    }));

    const isActiveTab = tab?.active;

    const { tabPreviewEnable, tabPreviewWaitSeconds } = userStore((state) => ({
        tabPreviewEnable: state.settings.tabPreviewEnable,
        tabPreviewWaitSeconds: state.settings.tabPreviewWaitSeconds
    }));

    const [contextMenu, setContextMenu] = useState(null);
    const tabRef = useRef(null);

    useEffect(() => {
        const handleContextMenuClose = () => {
            setContextMenu(null);
        };

        window.addEventListener("blur", handleContextMenuClose);

        if (tabRef.current) {
            if (isActiveTab) {
                tabIntersectionObserver().observe(tabRef.current);
            } else {
                tabIntersectionObserver().unobserve(tabRef.current);
            }
        }

        return () => {
            window.removeEventListener("blur", handleContextMenuClose);
        };
    }, [isActiveTab]);

    const handleContextMenu = (event) => {
        event.preventDefault();
        event.stopPropagation();
        setContextMenu({
            mouseX: event.clientX + 2,
            mouseY: event.clientY - 6
        });
    };

    const handleHighlightTab = async (event) => {
        event.preventDefault();
        event.stopPropagation();
        try {
            await setTabActive(tab.id);
        } catch (error) {
            const queryTabs = await chrome.tabs.query({ url: tab.url });
            for (const queryTab of queryTabs) {
                await chrome.tabs.reload(queryTab.id);
            }
            loadTabs();
        }
    };

    const handleMenuClose = (event) => {
        setContextMenu(null);
    };

    return (
        <Box
            sx={{ flex: 1, minWidth: "1.75rem" }}
            ref={tabRef}
            className={(isActiveTab ? 'tab-item active' : 'tab-item') + (keyboardSelected ? ' keybord-selected' : '')}>
            <Tooltip
                arrow
                followCursor
                enterDelay={(parseFloat(tabPreviewWaitSeconds) || 1.2) * 1000}
                enterNextDelay={200}
                componentsProps={{
                    tooltip: {
                        sx: {
                            padding: "15px",
                            background: "transparent",
                        }
                    }
                }}
                title={tabPreviewEnable ? <TabPreview title={tab.title} thumbnail={tab.thumbnail} url={tab.url} lastAccessed={tab.lastAccessed} /> : ""}
            >
                <span> {/* Add a span to ensure Tooltip always has children */}
                    <IconButton
                        variant="square"
                        color="inherit"
                        size="small"
                        sx={{
                            border: tab?.active || keyboardSelected ? `1px groove ${tab?.color ? hexToRgb(tab.color.hex, 1) : "rgba(0,0,0,0.25)"}` : "1px groove transparent",
                            padding: "5px",
                        }}
                        onContextMenu={handleContextMenu}
                        onClick={handleHighlightTab} // Ensure the event is passed correctly
                    >
                        <TabAvatar status={tab.status} favIconUrl={tab.favIconUrl} url={tab.url} pinned={tab.pinned} />
                        {tab?.pinned &&
                            <PushPin
                                sx={{
                                    transform: "rotate(45deg)",
                                    color: tab?.color?.hex,
                                    position: "absolute",
                                    top: '0px', right: '0px',
                                    width: '10px',
                                    height: '10px'
                                }} />
                        }
                    </IconButton>
                </span>
            </Tooltip>

            {keyboardSelected && <Tooltip open={true} title={tab?.title}><span></span></Tooltip>}

            <Menu
                dense="true"
                open={contextMenu !== null}
                anchorReference="anchorPosition"
                anchorPosition={contextMenu !== null ? { top: contextMenu.mouseY, left: contextMenu.mouseX } : undefined}
                onClose={handleMenuClose}
            >
                <TabMenu tabId={tab?.id} handleMenuClose={handleMenuClose} />
            </Menu>
        </Box >
    );
};

export default memo(TabAsIcon);