import React, { useState } from 'react';
import { MenuItem, Dialog, DialogTitle, DialogContent, DialogActions, Button, TextField, Box, ListItemIcon, ListItemText, Typography } from '@mui/material';
import { useTranslationContext } from '../../../Providers/TranslationProvider';
import SelectFolder from '../SelectFolder';
import { BookmarkAdd } from '@mui/icons-material'; // Import the BookmarkAdd icon

const AddBookmark = ({ folder, handleClose }) => {
    const { t } = useTranslationContext();
    const [open, setOpen] = useState(false);
    const [title, setTitle] = useState('');
    const [url, setUrl] = useState('');
    const [selectedFolder, setSelectedFolder] = useState(folder.id);

    const handleClick = () => {
        chrome.tabs.query({ active: true, currentWindow: true }, (tabs) => {
            const currentTab = tabs[0];
            setTitle(currentTab.title);
            setUrl(currentTab.url);
            setOpen(true);
        });
    };

    const handleSave = () => {
        chrome.bookmarks?.create({
            parentId: selectedFolder,
            title: title,
            url: url
        }, () => {
            setOpen(false);
            handleClose();
        });
    };

    const handleDialogClose = () => {
        setOpen(false);
    };

    const handleFolderChange = (newValue) => {
        if (newValue && newValue.id) {
            setSelectedFolder(newValue.id);
        } else {
            setSelectedFolder('');
        }
    };

    const isSaveDisabled = !title || !url || !selectedFolder;

    const handleKeyDown = (event) => {
        event.stopPropagation();
    };

    return (
        <Box>
            <MenuItem dense onClick={handleClick}>
                <ListItemIcon>
                    <BookmarkAdd />
                </ListItemIcon>
                <ListItemText>
                    <Typography variant="body1"> {t('addBookmark')}</Typography>
                </ListItemText>
            </MenuItem>
            <Dialog open={open} onClose={handleDialogClose} fullWidth>
                <DialogTitle>{t('addBookmark')}</DialogTitle>
                <DialogContent>
                    <Box mb={2}>
                        <TextField
                            margin="dense"
                            label={t('name')}
                            type="text"
                            fullWidth
                            value={title}
                            onChange={(e) => setTitle(e.target.value)}
                            onKeyDown={handleKeyDown}
                        />
                    </Box>
                    <Box mb={2}>
                        <TextField
                            margin="dense"
                            label="URL"
                            type="text"
                            fullWidth
                            value={url}
                            onChange={(e) => setUrl(e.target.value)}
                            onKeyDown={handleKeyDown}
                        />
                    </Box>
                    <Box mb={2}>
                        <SelectFolder
                            selectedFolder={selectedFolder}
                            onChange={handleFolderChange}
                            onKeyDown={handleKeyDown}
                        />
                    </Box>
                </DialogContent>
                <DialogActions>
                    <Button onClick={handleDialogClose}>{t('Cancel')}</Button>
                    <Button onClick={handleSave} disabled={isSaveDisabled}>{t('Save')}</Button>
                </DialogActions>
            </Dialog>
        </Box>
    );
};

export default AddBookmark;
