import React, { memo } from 'react';
import { History } from '@mui/icons-material';
import userStore from '../../../../core/stores/userStore';
import BrowserHistory from '../../browserHistory';
import DrawerAction from './DrawerAction';

const ActionHistory = () => {
    const showLabel = userStore(state => state.settings.bottomNavOptions.showLabel && state.settings.bottomNavOptions.navs.find(nav => nav.name === 'history')?.showLabel);

    return (
        <DrawerAction
            icon={<History />}
            labelKey='history'
            permissionName="history"
            ChildComponent={BrowserHistory}
            showLabel={showLabel}
        />
    );
};

export default memo(ActionHistory);