import React, { memo } from "react";
import { useSortable } from "@dnd-kit/sortable";
import { CSS } from '@dnd-kit/utilities';
import TabItem from "./TabItem";
import { Box } from "@mui/material";
import { useDndEnableContext } from "../../Providers/DndEnableProvider";

const SortableTabItem = ({ tabId }) => {
  const { enableDnd } = useDndEnableContext();

  const {
    attributes,
    listeners,
    setNodeRef,
    transform,
    transition,
    isDragging,
    index,
    isSorting,
    over,
    activeIndex,
  } = useSortable({ id: tabId, data: { type: "tab" }, disabled: !enableDnd });

  const style = {
    transform: isSorting ? undefined : CSS.Translate.toString(transform),
    transition,
    zIndex: isDragging ? 999 : 'auto',
    opacity: isDragging ? 0.5 : 1,

  };

  return (
    <Box
      ref={setNodeRef}
      id={tabId}
      active={isDragging}
      style={style}
      sx={over?.id === tabId ? index > activeIndex ? {
        '&:after': {
          content: '""',
          width: '100%',
          height: "2px",
          backgroundColor: "#4c9ffe",
          position: 'absolute',
          left: 0,
          right: 0,
        },

      } : {
        '&:before': {
          content: '""',
          width: '100%',
          height: "2px",
          backgroundColor: "#4c9ffe",
          position: 'absolute',
          left: 0,
          right: 0,

        },

      } : undefined}

      {...listeners}
      {...attributes}
    >
      <TabItem tabId={tabId} />
    </Box >
  );
};

export default memo(SortableTabItem);