import React from "react";
import { UnfoldMore, UnfoldLess } from "@mui/icons-material";
import { tabStore } from "../../core/stores/tabStore";
import userStore from "../../core/stores/userStore";
import { IconButton, Tooltip, Typography } from "@mui/material";
import { useTranslationContext } from "../Providers/TranslationProvider";

export default function ExpandedSwitch() {
  const { t } = useTranslationContext();

  const { groupBy, fontColor, groupExpanded, updateSetting } = userStore((state) => ({
    groupBy: state.settings.groupBy,
    fontColor: state.settings.fontColor,
    groupExpanded: state.settings.groupExpanded,
    updateSetting: state.updateSetting
  }));

  const { groupCount } = tabStore((state) => ({
    groupCount: state.tabs.filter(tab => tab.groupId > -1)?.length
  }))


  const handleClick = (event, val) => {
    event.preventDefault();
    event.stopPropagation();
    updateSetting('groupExpanded', val);
    chrome.tabGroups?.query({}, groups => {
      for (const group of groups) {
        if (group.collapsed === val) {
          chrome.tabGroups?.update(group.id, { collapsed: !val });
        }
      }

    })
  };

  switch (groupBy) {
    case 'mixed':
      if (groupCount === 0) return null;
      break;
    case 'domain':
    case 'chromeGroup':
    case 'tabWindow':
      break;
    default:
    case 'list':
      return null;
  }

  return (
    groupExpanded ? (
      <Tooltip arrow title={<Typography>{t("CollapseAll")}</Typography>}>
        <IconButton sx={{ color: fontColor || 'text.primary' }} onClick={e => handleClick(e, false)}>
          <UnfoldMore size="small" />
        </IconButton>
      </Tooltip>
    ) : (
      <Tooltip arrow title={<Typography>{t("ExpandAll")}</Typography>}>
        <IconButton sx={{ color: fontColor || 'text.primary' }} onClick={e => handleClick(e, true)}>
          <UnfoldLess size="small" />
        </IconButton>
      </Tooltip>
    )
  );
}
