import ArrowDropDownIcon from "@mui/icons-material/ArrowDropDown";
import CheckIcon from "@mui/icons-material/Check";
import {
  MenuList,
  MenuItem,
  ListItemText,
  Menu,
  Button,
  ListItemIcon,
  Typography,
  Tooltip,
  IconButton,
  Box,
} from "@mui/material";
import React, { useEffect, useState } from "react";
import { useTranslationContext } from "../Providers/TranslationProvider";
import { userStore } from "../../core/stores/userStore";
import { sendRuntimeMessage } from "../../core/browers/runtime";
import { Sort } from "@mui/icons-material";

export default function TabOrderBy({ showText = true }) {
  const { t } = useTranslationContext('translation');

  const [anchorEl, setAnchorEl] = useState(null);
  const open = Boolean(anchorEl);

  const { groupBy, sortBy, orderBy, theme, updateSetting, fontColor } = userStore((state) => ({
    groupBy: state.settings.groupBy,
    sortBy: state.settings.sortBy,
    orderBy: state.settings.orderBy,
    theme: state.settings.theme,
    updateSetting: state.updateSetting,
    fontColor: state.settings.fontColor
  }));

  useEffect(() => {
    const handleMenuClose = () => {
      setAnchorEl(null);
    };

    window.addEventListener("blur", handleMenuClose);
    return () => {
      window.removeEventListener("blur", handleMenuClose);
    };
  }, []);

  const items = [
    { type: "newest", name: t("SortBy.Latest"), order: "desc", sort: "created_time" },
    { type: "oldest", name: t("SortBy.Earliest"), order: "asc", sort: "created_time" },
    { type: "lastAccessed", name: t("SortBy.LastAccessed"), order: "desc", sort: "lastAccessed" },
    { type: "domain", name: t("SortBy.Website"), order: "asc", sort: "hostname" },
    { type: "name", name: t("SortBy.Title"), order: "asc", sort: "title" },
    { type: "index_asc", name: t("SortBy.TabIndex"), order: "asc", sort: "index" },
    { type: "index_desc", name: t("SortBy.TabReverse"), order: "desc", sort: "index" }
  ];

  const orderName = items.find(item => item.sort === sortBy && item.order === orderBy)?.name || "";

  const handleClick = (event) => {
    event.preventDefault();
    event.stopPropagation();
    setAnchorEl(event.currentTarget);
  };

  const handleChanged = async (ordering) => {
    setAnchorEl(null);
    await updateSetting("orderBy", ordering.order);
    await updateSetting("sortBy", ordering.sort);
    await sendRuntimeMessage("update_setting", { key: "orderBy", value: ordering.order });
    await sendRuntimeMessage("update_setting", { key: "sortBy", value: ordering.sort });
  };

  if (!['list', 'mixed'].includes(groupBy)) {
    return null;
  }

  return (
    <Box>
      <Tooltip arrow title={<Typography>{t("SortBy.SortBy")}</Typography>}>
        {showText ? (
          <Button
            id="session-order-button"
            size="small"
            sx={{
              color: fontColor || 'text.primary',
            }}
            aria-label="settings"
            aria-controls={open ? "session-order-menu" : undefined}
            aria-haspopup="true"
            aria-expanded={open ? "true" : undefined}
            onClick={handleClick}>
            <Typography noWrap variant="body1" sx={{ color: theme === 'dark' ? 'text.primary' : fontColor || 'text.primary' }} >{orderName}</Typography>
            <ArrowDropDownIcon size="small" />
          </Button>
        ) : (
          <IconButton onClick={handleClick} sx={{ color: fontColor || "text.primary" }}><Sort size="small" /></IconButton>
        )}
      </Tooltip>
      <Menu
        dense={"true"}
        open={open}
        onClose={() => setAnchorEl(null)}
        anchorEl={anchorEl}
        id={"session-order-menu"}
        MenuListProps={{
          "aria-labelledby": "session-order-button"
        }}>
        <MenuList dense sx={{ minWidth: "200px" }}>
          {items.map(item => (
            <MenuItem onClick={() => handleChanged(item)} key={item.type}>
              {item.order === orderBy && item.sort === sortBy ? (
                <>
                  <ListItemIcon>
                    <CheckIcon />
                  </ListItemIcon>
                  <ListItemText>
                    <Typography variant="body1">
                      {t("SortBy.SortBy")} : {item.name}
                    </Typography>
                  </ListItemText>
                </>
              ) : (
                <ListItemText inset>
                  <Typography variant="body1">
                    {t("SortBy.SortBy")} {item.name}
                  </Typography>
                </ListItemText>
              )}
            </MenuItem>
          ))}
        </MenuList>
      </Menu>
    </Box>
  );
}
