import { Menu } from "@mui/material";
import React, { useState } from "react";
import MoveChromeGroupToNewWindow from "./MoveChromeGroupToNewWindow";
import UnGroup from "./UnGroup";
import DeleteGroup from "./DeleteGroup";
import UpdateGroup from "./UpdateGroup";
import AddGroupToQuickAccess from "./AddGroupToQuickAccess";

export default function GroupMenus({ group, onSelect, anchorEl, setAnchorEl }) {
    const handleCloseMenu = () => {
        setAnchorEl(null);
        if (onSelect) {
            onSelect();
        }
    };

    return (
        <Menu
            anchorEl={anchorEl}
            open={Boolean(anchorEl)}
            onClose={handleCloseMenu}
        >
            <UpdateGroup group={group} onClose={handleCloseMenu} />
            <MoveChromeGroupToNewWindow group={group} onClose={handleCloseMenu} />
            <UnGroup group={group} onClose={handleCloseMenu} />
            <DeleteGroup group={group} onClose={handleCloseMenu} />
            <AddGroupToQuickAccess group={group} onClose={handleCloseMenu} />
        </Menu>
    );
}