import React, { memo } from 'react';
import { Delete } from '@mui/icons-material';
import userStore from '../../../../core/stores/userStore';
import RecentlyTabs from '../../recentlyTabs';
import DrawerAction from './DrawerAction';

const ActionRecentlyTabs = () => {
    const showLabel = userStore(state => state.settings.bottomNavOptions.showLabel && state.settings.bottomNavOptions.navs.find(nav => nav.name === 'recentlyTabs')?.showLabel);

    return (
        <React.Fragment>
            <DrawerAction
                icon={<Delete />}
                labelKey='RecentlyTabs'
                ChildComponent={RecentlyTabs}
                showLabel={showLabel}
            />
        </React.Fragment>
    );
};

export default memo(ActionRecentlyTabs);