import React, { useState } from "react";
import { MenuItem, ListItemText, Typography, ListItemIcon, SvgIcon, Drawer, Dialog, Button, FormControl, InputLabel, InputAdornment, IconButton, Box, Stack, Popover, Autocomplete, TextField } from "@mui/material";
import { useTranslationContext } from "../../../Providers/TranslationProvider";
import tabStore from "../../../../core/stores/tabStore";
import userStore from "../../../../core/stores/userStore";
import { chromeGroupAvailableColors } from "../../../../utils/colors";
import { camelCase, upperFirst } from "lodash-es";
import { RadioButtonChecked, Circle } from "@mui/icons-material";
import { ArrowRight } from "@mui/icons-material";
import { Add } from "@mui/icons-material";
import { ArrowDropDown } from "@mui/icons-material";
import { siteStore } from "../../../../core/stores/siteStore";
import { getHostnameFromUrl } from "../../../../utils/helpers";
import { sendRuntimeMessage } from "../../../../core/browers/runtime";

export default function AddTabToGroup({ tabId, onSelect }) {
    const { t } = useTranslationContext();
    const [anchorEl, setAnchorEl] = useState(null);
    const [openDialog, setOpenDialog] = useState(false);
    const [newGroupName, setNewGroupName] = useState("");
    const [newGroupColor, setNewGroupColor] = useState("");
    const [showList, setShowList] = useState(false);
    const [drawerOpen, setDrawerOpen] = useState(false);

    const { groupId, url, title } = tabStore(state => ({
        groupId: state.tabs.find(t => t.id === tabId)?.groupId,
        url: state.tabs.find(t => t.id === tabId)?.url,
        title: state.tabs.find(t => t.id === tabId)?.title,
    }));
    const { setSiteConfig } = siteStore((state) => ({
        setSiteConfig: state.setSiteConfig
    }))

    const { customGroupNames, customGroupRemember } = userStore(state => ({
        customGroupNames: [{ name: title }, { name: getHostnameFromUrl(url) }, ...state.settings.customGroupNames],
        customGroupRemember: state.settings.customGroupRemember
    }));

    if (navigator.userAgent.includes("OPR")) {
        return null;
    }

    const handleMenuOpen = (event) => {
        setAnchorEl(event.currentTarget);
        setDrawerOpen(true);
    };

    const handleMenuClose = () => {
        setAnchorEl(null);
        setDrawerOpen(false);
    };

    const handleNewGroup = () => {
        setOpenDialog(true);
        setDrawerOpen(false);
    };

    const handleDialogClose = () => {
        setOpenDialog(false);
        setNewGroupName("");
        setNewGroupColor("");
    };

    const handleCreateNewGroup = async () => {
        try {
            const groupId = await chrome.tabs.group({ tabIds: [tabId] });
            await chrome.tabGroups?.update(groupId, { title: newGroupName, color: newGroupColor });

            const hostname = getHostnameFromUrl(url);
            if (customGroupNames.some(group => group.name === newGroupName) && customGroupRemember) {
                await setSiteConfig(hostname, { groupName: newGroupName, groupColor: newGroupColor });
                await sendRuntimeMessage('update_site_config', { hostname, config: { groupName: newGroupName, groupColor: newGroupColor } });
            }

            handleDialogClose();
            if (typeof onSelect === 'function') onSelect();
        } catch (error) {
            console.error('Error creating new group:', error);
            // Handle error (e.g., show error message to user)
        }
    };

    const handleAddToExistingGroup = (groupId) => {
        chrome.tabs.group({ tabIds: [tabId], groupId });
        handleMenuClose();
        if (typeof onSelect === 'function') onSelect();
    };

    // Get existing groups
    const existingGroups = tabStore(state => state.groups)?.filter(g => g.id != groupId);

    console.log(existingGroups)

    return (
        <React.Fragment>
            <MenuItem onClick={handleMenuOpen}>
                <ListItemIcon>
                    <SvgIcon>
                        {/* ... existing SVG ... */}
                    </SvgIcon>
                </ListItemIcon>
                <ListItemText>
                    <Typography variant="body1">{t("addTabToGroup")}</Typography>
                </ListItemText>
                <IconButton>{showList ? <ArrowDropDown /> : <ArrowRight />}</IconButton>
            </MenuItem>

            <Popover
                open={drawerOpen}
                anchorEl={anchorEl}
                onClose={handleMenuClose}
                anchorOrigin={{
                    vertical: 'bottom',
                    horizontal: 'left',
                }}
                transformOrigin={{
                    vertical: 'top',
                    horizontal: 'left',
                }}
            >
                <Box sx={{ width: 250, padding: 1 }}>
                    <MenuItem onClick={handleNewGroup}>
                        <ListItemIcon>
                            <Add />
                        </ListItemIcon>
                        <Typography variant="body1">{t("newGroup")}</Typography>
                    </MenuItem>
                    {existingGroups.map((group) => {
                        const groupTitle = group.title || `"${tabStore.getState().tabs.find(tab => tab.groupId === group.id)?.title?.substring(0, 20)}" and other tabs` || t('tabMenu.UntitledGroup');
                        return (
                            <MenuItem key={group.id} onClick={() => handleAddToExistingGroup(group.id)}>
                                <ListItemIcon>
                                    <Circle sx={{ color: chromeGroupAvailableColors.find(c => c.tabGroupColor === group.color)?.hex, fontSize: "12px" }} />
                                </ListItemIcon>
                                <ListItemText>
                                    <Typography variant="body1">{groupTitle}</Typography>
                                </ListItemText>
                            </MenuItem>
                        );
                    })}
                </Box>
            </Popover>

            <Dialog open={openDialog} onClose={handleDialogClose}>
                <Box sx={{ p: 2 }}>
                    <FormControl fullWidth variant="outlined">
                        <Autocomplete
                            size="small"
                            id="rename-group-name-input"
                            options={customGroupNames}
                            getOptionLabel={(option) => option.name}
                            value={customGroupNames.find(option => option.name === newGroupName) || null}
                            onChange={(event, newValue) => setNewGroupName(newValue?.name || "")}
                            onInputChange={(event, newInputValue) => setNewGroupName(newInputValue)}
                            freeSolo
                            renderInput={(params) => <TextField {...params} placeholder={t("Name")} />}
                        />
                    </FormControl>
                    <Stack direction="row" >
                        {chromeGroupAvailableColors.map(colorInfo => (
                            <IconButton
                                key={colorInfo.color}
                                onClick={() => setNewGroupColor(colorInfo.color)}
                            >
                                {newGroupColor === colorInfo.color ? (
                                    <RadioButtonChecked sx={{ color: colorInfo.hex, fontSize: "14px" }} />
                                ) : (
                                    <Circle sx={{ color: colorInfo.hex, fontSize: "14px" }} />
                                )}
                            </IconButton>
                        ))}
                    </Stack>
                    <FormControl fullWidth variant="outlined" sx={{ pt: 2 }}>
                        <Button variant="contained" sx={{ backgroundColor: chromeGroupAvailableColors.find(c => c.color === newGroupColor)?.hex || '#eb0022' }} onClick={handleCreateNewGroup} disabled={!newGroupColor}>{t("Save")}</Button>
                    </FormControl>
                </Box>

            </Dialog>
        </React.Fragment>
    );
}