import { immer } from "zustand/middleware/immer";
import { getUserConfig, userPublicLocalStorage } from "../user";
import { shallow } from 'zustand/shallow';
import { createWithEqualityFn } from 'zustand/traditional';
import { pick, uniqBy } from "lodash-es";;
import { md5 } from "../../utils/crypto";
import { availableColors } from "../../utils/colors";
/*eslint no-return-await: "error"*/
export const localCacheHistoriesInstance = async () => {
    return userPublicLocalStorage('history');
}
let historyMap = new Map();

export const historyStore = createWithEqualityFn(immer((set, get) => ({
    histories: [],
    historyStoreInitialized: false,
    keyboardSelected: 0,
    loadHistories: async () => {
        const { historyKeepDays = 3 } = (await getUserConfig('user_settings')) || {};
        const cacheInstance = await localCacheHistoriesInstance();
        const histories = await cacheInstance.all();
        const finalHistories = histories.filter(history => history.closed_time >= Date.now() - historyKeepDays * 1000 * 60 * 60 * 24);
        set({ histories: finalHistories, historyStoreInitialized: true });
    },
    addHistory: async (history) => {
        if (!history || !history.url || history.url.includes("about:blank") || history.url.includes("chrome://newtab")) {
            console.log('Invalid history entry');
            return;
        }

        const { tabIntoHistoryNeedAliveSeconds } = (await getUserConfig('user_settings')) || {};
        if (Date.now() < history.created_time + tabIntoHistoryNeedAliveSeconds * 1000) return;

        const urlMD5 = md5(history.url);
        const data = pick(history, ["id", 'created_time', 'domain', 'favIconUrl', 'hostname', 'title', 'url', 'groupId', 'lastAccessed']);
        const color = availableColors[data.id % availableColors.length];
        const newHistory = { ...data, closed_time: Date.now(), urlMD5, color };

        set((state) => {
            const targetIndex = state.histories.findIndex(h => h.urlMD5 === urlMD5);
            if (targetIndex !== -1) {
                const updatedHistories = [...state.histories];
                updatedHistories[targetIndex] = { ...updatedHistories[targetIndex], ...newHistory };
                historyMap.set('recentTabs', updatedHistories);
                return { histories: updatedHistories };
            } else {
                const finalHistories = [newHistory, ...state.histories];
                historyMap.set('recentTabs', finalHistories);
                return { histories: finalHistories };
            }
        });

        const cacheInstance = await localCacheHistoriesInstance();
        await cacheInstance.set(urlMD5, newHistory);
    },
    deleteHistory: async (urlMD5) => {
        const cacheInstance = await localCacheHistoriesInstance();
        await cacheInstance.remove(urlMD5);
        const finalHistories = get().histories.filter(h => h.urlMD5 !== urlMD5);
        set({ histories: finalHistories });
        historyMap.set('recentTabs', finalHistories);
    }
})), shallow);

export const chromeVisitHistories = async () => {
    const { historyKeepCount = 500, historyKeepDays = 7 } = (await getUserConfig('user_settings')) || {};

    if (!("history" in chrome)) {
        return [];
    }

    let results = await chrome.history?.search({
        text: "",
        maxResults: historyKeepCount,
        endTime: Date.now(),
        startTime: Date.now() - historyKeepDays * 1000 * 60 * 60 * 24
    });

    results = uniqBy(results, 'url');
    const formatResults = results.map(history => ({
        ...history,
        lastAccessed: history.lastVisitTime,
        closed_time: history.lastVisitTime,
        color: availableColors[history.id % availableColors.length],
        hostname: new URL(history.url).hostname,
        urlMD5: md5(history.url)
    }));

    historyMap.set('chrome', formatResults);
    return formatResults;
};

export const getHistories = async (historyDataSource) => {
    if (historyMap.has(historyDataSource)) {
        return historyMap.get(historyDataSource);
    }

    if (historyDataSource === 'chrome') {
        await chromeVisitHistories();
    } else {
        await historyStore.getState().loadHistories();
        const histories = historyStore.getState().histories;
        historyMap.set(historyDataSource, histories);
    }

    return historyMap.get(historyDataSource);
};

export const cleanHistoriesByHostname = async (hostname) => {
    if (!("history" in chrome)) {
        return;
    }

    try {
        // Get all visits for the given hostname
        const visits = await chrome.history.search({
            text: hostname,
            startTime: 0,
            maxResults: 1000000
        });

        // Filter visits to only include those from the exact hostname
        const hostnameVisits = visits.filter(visit => {
            try {
                return new URL(visit.url).hostname === hostname;
            } catch (e) {
                return false;
            }
        });

        // Delete each visit
        for (const visit of hostnameVisits) {
            await chrome.history.deleteUrl({ url: visit.url });
        }

        // Update the chrome history in our store
        await chromeVisitHistories();

        console.log(`Cleaned ${hostnameVisits.length} history items for ${hostname}`);
    } catch (error) {
        console.error(`Error cleaning history for ${hostname}:`, error);
    }
}


if ("history" in chrome) {
    chrome.history?.onVisited.addListener(async () => {
        await chromeVisitHistories();
    });
    chrome.history?.onVisitRemoved.addListener(async () => {
        await chromeVisitHistories();
    });
}

export default historyStore;