import React from "react";
import { ListItemIcon, ListItemText, MenuItem, SvgIcon, Typography } from "@mui/material";
import { sendRuntimeMessage } from "../../../../../core/browers/runtime";
import { useTranslationContext } from "../../../../Providers/TranslationProvider";

export default function UnGroup({ group, onClose }) {
    const { t } = useTranslationContext();

    const handleUnGroup = async (event) => {
        event.stopPropagation();
        if (group && group.id) {
            const response = await sendRuntimeMessage("chrome_ungroup", { groupId: group.id });
            console.log("handleUnGroup response", response);
            if (response.success === false) {
                toast.error(t(response.error, { ns: "error" }) || "Failed to ungroup");
            }
        }
        if (typeof onClose === 'function') {
            onClose();
        }
    };

    return (
        <MenuItem dense onClick={handleUnGroup}>
            <ListItemIcon>
                <SvgIcon>
                    <svg xmlns="http://www.w3.org/2000/svg" height="20px" viewBox="0 -960 960 960" width="20px" fill="currentColor">
                        <path d="m339-288-51-51 191-190H313v-72h288v288h-72v-166L339-288Zm405-192v-264H480v-72h336v336h-72ZM216-144q-29.7 0-50.85-21.15Q144-186.3 144-216v-600h72v600h600v72H216Z" />
                    </svg>
                </SvgIcon>
            </ListItemIcon>
            <ListItemText>
                <Typography variant="body1">{t("groupMenu.Ungroup")}</Typography>
            </ListItemText>
        </MenuItem>
    );
}
