
import { getDomainFromUrl } from "../../utils/helpers";
import { getActiveTab } from "./tabs";

export const getActivedTabCookies = async () => {
    const currentTab = await getActiveTab();
    if (currentTab) {
        const cookies = await getTabCookies(currentTab.id);
        return cookies
    }
    return []
}


export async function getTabCookies(tabId) {
    if (tabId <= 0) {
        return []
    }
    const tab = await chrome.tabs.get(tabId);
    const domain = getDomainFromUrl(tab.url || tab.pendingUrl)
    const cookies = await chrome.cookies.getAll({ domain: domain });
    //    const cookiesList = cookies.filter(cookie => cookie.domain.endsWith(domain));
    // return cookiesList;
    return cookies;
}

export const getTabCookieStrings = async (tabId) => {
    const cookies = await getTabCookies(tabId)
    const cookieStr = cookies.filter(cookie => cookie.session || cookie.expirationDate >= Math.floor(Date.now() / 1000)).map(cookie => {
        return cookie.name + '=' + cookie.value + ";"
    }).join('')
    return cookieStr
}

export const formatCookieString = (setCookieString, url) => {
    const cookieParts = setCookieString.split(';').map(part => part.trim());
    const cookieNameValue = cookieParts[0]; // The first part is the cookie name and value
    const [cookieName, ...valueParts] = cookieNameValue.split('=');
    const cookieValue = valueParts.join('=') || ''; // If there is no value, default to an empty string

    // The rest of the cookieParts array contains additional cookie attributes
    const cookieAttributes = cookieParts.slice(1).reduce((attrs, part) => {
        const [attrKey, ...attrValueParts] = part.split('=');
        const attrValue = attrValueParts.join('=') || true; // Default to true if no value is provided for flag attributes
        attrs[attrKey.toLowerCase()] = attrValue;
        return attrs;
    }, {});

    let formattedCookie = {
        name: cookieName,
        value: cookieValue,
        domain: cookieAttributes.domain || '.' + getDomainFromUrl(url), // Default to 'localhost' if domain is not specified
        path: cookieAttributes.path || '/', // Default to '/' if path is not specified
        secure: 'secure' in cookieAttributes, // Presence of 'secure' means true
        httpOnly: 'httponly' in cookieAttributes, // Presence of 'httponly' means true
        sameSite: cookieAttributes.samesite || 'no_restriction', // Default 'SameSite' attribute value
        expirationDate: cookieAttributes.expires ? new Date(cookieAttributes.expires).getTime() / 1000 : null // Convert 'expires' to a timestamp in seconds
    };
    if (cookieAttributes.expires) {
        formattedCookie.expirationDate = new Date(cookieAttributes.expires).getTime() / 1000
        formattedCookie.session = false
    } else {
        formattedCookie.session = true
    }



    return formattedCookie;
}


export const clearCookiesfromUrl = async (url, skipAccountCookies = false) => {
    return chrome.cookies.getAll({ url: url }, function (cookies) {
        if (chrome.runtime.lastError) {
            console.error(chrome.runtime.lastError);
            return false;
        }
        cookies.forEach(function (cookie) {
            if (!skipAccountCookies || !/account|auth|login/i.test(url)) {
                chrome.cookies.remove({ url: url, name: cookie.name }, function () {
                    if (chrome.runtime.lastError) {
                        console.error(chrome.runtime.lastError);
                    }
                });
            }
        });
        return true;
    });
}
