import React from "react";
import { ListItemIcon, ListItemText, MenuItem, SvgIcon, Typography } from "@mui/material";
import { sendRuntimeMessage } from "../../../../../core/browers/runtime";
import { useTranslationContext } from "../../../../Providers/TranslationProvider";

export default function MoveChromeGroupToNewWindow({ group, onClose }) {
    const { t } = useTranslationContext();


    const handleMoveChromeGroupToNewWindow = async (event) => {
        event.stopPropagation();
        if (group && group.id > -1) {
            sendRuntimeMessage("move_group_to_new_window", { groupId: group.id, moveProperties: { index: -1 } });
        }
        if (typeof onClose === 'function') {
            onClose();
        }
    };

    return (
        <MenuItem dense onClick={handleMoveChromeGroupToNewWindow}>
            <ListItemIcon>
                <SvgIcon>
                    <svg xmlns="http://www.w3.org/2000/svg" height="20px" viewBox="0 -960 960 960" width="20px" fill="currentColor">
                        <path d="M312-240q-29.7 0-50.85-21.15Q240-282.3 240-312v-96h72v96h480v-408H312v96h-72v-168q0-29.7 21.15-50.85Q282.3-864 312-864h480q29.7 0 50.85 21.15Q864-821.7 864-792v480q0 29.7-21.15 50.85Q821.7-240 792-240H312ZM168-96q-29.7 0-50.85-21.15Q96-138.3 96-168v-552h72v552h552v72H168Zm348-264-51-51 69-69H240v-72h294l-69-69 51-51 156 156-156 156Z" />
                    </svg>
                </SvgIcon>
            </ListItemIcon>
            <ListItemText>
                <Typography variant="body1">{t("groupMenu.MoveToNewWindow")}</Typography>
            </ListItemText>
        </MenuItem>
    );
}
