import React, { useEffect, useState } from "react";
import userStore from "../../../../core/stores/userStore";
import ColorTypography from "../../../component/ColorTypography";


const ReadingList = () => {
    const { navSetting } = userStore((state) => ({
        navSetting: state.settings.tabNavOptions.navs.find(nav => nav.name === 'readingList')
    }));


    const [readingListCount, setReadingListCount] = useState(0);

    const fetchReadingListCount = async () => {
        try {
            const results = await chrome.readingList?.query({});
            setReadingListCount(results?.length || 0);
        } catch (e) {
            console.error("Failed to fetch reading list count", e);
        }
    };
    useEffect(() => {
        fetchReadingListCount();
        chrome.permissions?.onAdded.addListener(fetchReadingListCount);

    }, []);


    return (
        navSetting?.showCount ? <ColorTypography>〈{readingListCount}〉</ColorTypography> : null
    );
};

export default ReadingList;
