import React from 'react';
import { Typography } from '@mui/material';
import { userStore } from '../../core/stores/userStore';

const ColorTypography = ({ children, sx, ...otherProps }) => {
    const { theme, fontColor } = userStore((state) => ({
        theme: state.settings.theme,
        fontColor: state.settings.fontColor,
    }));

    return (
        <Typography
            noWrap
            variant="body1"
            sx={{ color: theme === "dark" ? "text.primary" : fontColor || "text.primary", ...sx }}
            {...otherProps}
        >
            {children}
        </Typography>
    );
};

export default React.memo(ColorTypography);