import { ListItemIcon, MenuItem, SvgIcon, Typography } from "@mui/material";
import React from "react";
import tabStore from "../../../../core/stores/tabStore";
import { toast } from "react-toastify";
import userStore from "../../../../core/stores/userStore";
import { sendRuntimeMessage } from "../../../../core/browers/runtime";
import { getActiveTab } from "../../../../core/browers/tabs";
import { useTranslationContext } from "../../../Providers/TranslationProvider";

export default function GroupTabsByDomain({ onSelect }) {
    const { t } = useTranslationContext();

    const { theme, onlyCurrentWindowTabs } = userStore(state => ({
        theme: state.settings.theme,
        onlyCurrentWindowTabs: state.settings.onlyCurrentWindowTabs
    }));

    const { loadTabs, loadGroups, currentWindow } = tabStore(state => ({
        loadTabs: state.loadTabs,
        loadGroups: state.loadGroups,
        currentWindow: state.currentWindow
    }));

    const handleClick = async () => {
        try {
            tabStore.setState({ groups: [], tabs: [] });

            const response = await sendRuntimeMessage("group_tabs_by_domain", { windowId: onlyCurrentWindowTabs ? currentWindow.id : 0 });

            const activeTab = await getActiveTab();
            if (activeTab && activeTab.groupId > -1) {
                await chrome.tabGroups?.update(activeTab.groupId, { collapsed: false });
            }
            if (response.error) {
                toast.error(response.error, {
                    position: "bottom-center",
                    theme: theme
                });
            }
        } catch (error) {
            console.error("Failed to group tabs by domain", error);
            toast.error(t("An error occurred while grouping tabs"), {
                position: "bottom-center",
                theme: theme
            });
        } finally {
            if (typeof onSelect === 'function') {
                onSelect();
            }
            await loadGroups();
            await loadTabs();
        }
    };

    if (navigator.userAgent.includes("OPR")) {
        return null;
    }


    return (
        <MenuItem onClick={handleClick}>
            <ListItemIcon>
                <SvgIcon>
                    <svg xmlns="http://www.w3.org/2000/svg" height="24px" viewBox="0 -960 960 960" width="24px" fill="currentColor">
                        <path d="M320-240q-33 0-56.5-23.5T240-320v-480q0-33 23.5-56.5T320-880h480q33 0 56.5 23.5T880-800v480q0 33-23.5 56.5T800-240H320Zm0-80h480v-320H520v-160H320v480ZM160-80q-33 0-56.5-23.5T80-160v-560h80v560h560v80H160Zm160-720v480-480Z" />
                    </svg>
                </SvgIcon>
            </ListItemIcon>
            <Typography noWrap variant="body1">
                {t("GroupBySite")}
            </Typography>
        </MenuItem>
    );
}