import React, { memo, useRef } from "react";
import TabGroupDomain from "./TabGroupDomain";
import { Box } from "@mui/material";
import { tabStore } from "../../../core/stores/tabStore";
import { uniq } from "lodash-es";;
import { userStore } from "../../../core/stores/userStore";
import { filterTabs, getHostnameFromUrl } from "../../../utils/helpers";

const TabGroupDomainList = () => {
  const { searchText, orderBy, sortBy, onlyCurrentWindowTabs, PinnedTabsAsIcons } = userStore((state) => ({
    searchText: state.searchText,
    orderBy: state.settings.orderBy,
    sortBy: state.settings.sortBy,
    onlyCurrentWindowTabs: state.settings.onlyCurrentWindowTabs,
    PinnedTabsAsIcons: state.settings.PinnedTabsAsIcons,
  }));

  const currentWindow = tabStore((state) => state.currentWindow);
  const tabs = tabStore((state) => state.tabs
    .filter(tab => onlyCurrentWindowTabs ? tab.windowId === currentWindow.id : true)
    .filter(tab => PinnedTabsAsIcons ? !tab.pinned : true)
    .map(tab => {
      return {
        id: tab.id,
        title: tab.title,
        url: tab.url,
        hostname: tab.hostname,
        created_time: tab.created_time,
        lastAccessed: tab.lastAccessed,
        index: tab.index,
        windowId: tab.windowId,
        groupId: tab.groupId,
        pinned: tab.pinned
      }
    })
  );


  const filteredTabs = filterTabs(tabs, searchText, orderBy, sortBy, currentWindow.id);

  const uniqueHostnames = uniq(filteredTabs.map(tab => getHostnameFromUrl(tab.url)));

  console.log("filteredTabs", filteredTabs, currentWindow);
  console.log("uniqueHostnames", uniqueHostnames);

  const tabContainerRef = useRef(null);

  return (
    <Box sx={{ paddingTop: "5px" }} ref={tabContainerRef}>
      {uniqueHostnames.map((uniqueHostname, index) => (
        <TabGroupDomain
          key={uniqueHostname}
          hostname={uniqueHostname}
          index={index}
        />
      ))}
    </Box>
  );
};

export default memo(TabGroupDomainList);
