import React, { useState } from "react";
import { MenuItem, ListItemText, Typography, ListItemIcon, SvgIcon, Tooltip, Stack } from "@mui/material";
import tabStore from "../../../../core/stores/tabStore";
import { toast } from "react-toastify";
import userStore from "../../../../core/stores/userStore";
import { sendTabMessage } from "../../../../core/browers/runtime";
import { clearCookiesfromUrl } from "../../../../core/browers/cookies";
import { useTranslationContext } from "../../../Providers/TranslationProvider";
import { Help } from "@mui/icons-material";
import WithPermission from "../../../component/WithPermission";
import { cleanHistoriesByHostname } from "../../../../core/stores/historyStore";

export default function CleanBrowsingData({ tabId, onSelect }) {
    const { t } = useTranslationContext();
    const theme = userStore(state => state.settings.theme);

    const tab = tabStore(state => {
        const { id, url, hostname } = state.tabs.find(t => t.id === tabId) || {};
        return { id, url, hostname };
    });

    const handleClick = async () => {
        await clearCookiesfromUrl(tab.url);
        await cleanHistoriesByHostname(tab.hostname)
        const response = await sendTabMessage(tab.id, 'clear_browsing_data', {
            options: {
                cache: true,
                cacheStorage: true,
                indexedDB: true,
                localStorage: true,
                serviceWorkers: true,
                webSQL: true
            }
        });
        if (response?.success) {
            toast.success(t("tabMenu.CacheCleared"), {
                position: "bottom-center",
                hideProgressBar: true,
                theme: theme,
                autoClose: 1500
            });
        } else {
            toast.error(t("tabMenu.RefreshTips"), {
                position: "bottom-center",
                hideProgressBar: true,
                theme: theme,
                autoClose: 1500
            });
        }
        if (typeof onSelect === 'function') {
            onSelect();
        }
    };

    const isHttpUrl = tab.url?.startsWith("http");

    const [showTooltip, setShowTooltip] = useState(false);

    if (!isHttpUrl) {
        return null;
    }

    return (
        <WithPermission permissionName={"cookies"}>
            <MenuItem
                onClick={handleClick}
                onMouseEnter={() => setShowTooltip(true)}
                onMouseLeave={() => setShowTooltip(false)}

            >
                <ListItemIcon>
                    <SvgIcon>
                        <svg xmlns="http://www.w3.org/2000/svg" height="20px" viewBox="0 -960 960 960" width="20px" fill="currentColor">
                            <path d="M432-552h96v-240q0-20.4-13.8-34.2Q500.4-840 480-840q-20.4 0-34.2 13.8Q432-812.4 432-792v240ZM216-408h528v-72H216v72Zm-48 240h96v-60q0-15.3 10.29-25.65Q284.58-264 299.79-264t25.71 10.35Q336-243.3 336-228v60h108v-60q0-15.3 10.29-25.65Q464.58-264 479.79-264t25.71 10.35Q516-243.3 516-228v60h108v-60q0-15.3 10.29-25.65Q644.58-264 659.79-264t25.71 10.35Q696-243.3 696-228v60h96l-38-168H207l-39 168Zm624 72H168q-34.97 0-56.48-27Q90-150 98-184l46-200v-96q0-29.7 21.15-50.85Q186.3-552 216-552h144v-240q0-50 35-85t85-35q50 0 85 35t35 85v240h144.25Q774-552 795-530.85T816-480v96l46 200q8 34-13.5 61T792-96ZM216-408v-72 72Zm312-144h-96 96Z" />
                        </svg>
                    </SvgIcon>
                </ListItemIcon>
                <ListItemText>
                    <Stack direction="row" alignItems="center" justifyContent="space-between">
                        <Typography variant="body1">{t("tabMenu.ClearCookiesAndSiteData")}</Typography>
                        {showTooltip && <Tooltip arrow placement="top" title={<Typography>{t("tabMenu.ClearCacheTips")}</Typography>}>
                            <Help
                                fontSize="small"
                                color="secondary"
                                sx={{ opacity: '0.5' }}

                            />
                        </Tooltip>
                        }
                    </Stack>
                </ListItemText>
            </MenuItem>
        </WithPermission>
    );
}