import { Badge, ListItemIcon, MenuItem, SvgIcon, Typography } from "@mui/material";
import React from "react";
import { useState } from "react";
import { useEffect } from "react";
import { getUserConfig, setUserConfig } from "../../../../core/user";
import { useTranslationContext } from "../../../Providers/TranslationProvider";


export default function Changelog({ onSelect }) {

    const { t } = useTranslationContext();


    const [newVersionBadge, setNewVersionBadge] = useState(false)
    const checkBadges = async () => {
        const newVersionBadge = await getUserConfig("new_changelog_badge");
        setNewVersionBadge(newVersionBadge)
    }

    const clearBadges = async () => {
        setNewVersionBadge(false);
        await setUserConfig("new_changelog_badge", false);
    }


    useEffect(() => {
        checkBadges();
    }, [])

    const handleClick = event => {
        const url = "https://www.rabbitpair.com/changelog";
        chrome.tabs.create({ url: url, active: true })
        if (typeof onSelect === 'function') {
            onSelect();
        }
        if (newVersionBadge) {
            clearBadges();
        }
    }

    return (
        <MenuItem onClick={handleClick}>
            <ListItemIcon>
                <SvgIcon>
                    <svg xmlns="http://www.w3.org/2000/svg" height="24px" viewBox="0 -960 960 960" width="24px" fill="currentColor"><path d="M200-120q-33 0-56.5-23.5T120-200v-560q0-33 23.5-56.5T200-840h440l200 200v440q0 33-23.5 56.5T760-120H200Zm0-80h560v-400H600v-160H200v560Zm80-80h400v-80H280v80Zm0-320h200v-80H280v80Zm0 160h400v-80H280v80Zm-80-320v160-160 560-560Z" /></svg>
                </SvgIcon>
            </ListItemIcon>
            {newVersionBadge ? (<Badge color="error" variant="dot"><Typography noWrap variant="body1">
                {t("Changelog")}
            </Typography></Badge>) : (<Typography noWrap variant="body1">
                {t("Changelog")}
            </Typography>)}

        </MenuItem>
    )
}