import React from "react";
import { MenuItem, ListItemText, Typography, ListItemIcon } from "@mui/material";
import tabStore from "../../../../../core/stores/tabStore";
import * as Sentry from "@sentry/react";
import { useTranslationContext } from "../../../../Providers/TranslationProvider";

export default function OpenInIncognitoWindow({ uuid, onSelect }) {

    const { t } = useTranslationContext();

    const shortcut = tabStore(state => state.quickAccess.find(shortcut => shortcut.uuid === uuid));

    const handleClick = async () => {
        try {
            if (shortcut.urls && shortcut.urls.length > 0) {
                const openUrls = shortcut.urls.map(urlConfig => (urlConfig.url));
                const window = await chrome.windows.create({ url: openUrls, type: 'normal', incognito: true });
            } else {
                chrome.windows.create({ url: shortcut.url, type: 'normal', incognito: true });
            }
            if (typeof onSelect === 'function') {
                onSelect();
            }
        } catch (error) {
            console.error(error);
            Sentry.captureException(error);
        }
    };

    return (
        <MenuItem onClick={handleClick}>
            <ListItemText>
                <Typography variant="body1">
                    {shortcut.urls && shortcut.urls.length > 0 ? t("openAllInIncognitoWindow", { count: shortcut.urls.length }) : t("OpenInIncognitoWindow")}
                </Typography>
            </ListItemText>
        </MenuItem>
    );
}