import React from "react";
import { MenuItem, ListItemText, Typography, ListItemIcon, SvgIcon } from "@mui/material";
import { useTranslationContext } from "../../../Providers/TranslationProvider";
import { tabStore } from "../../../../core/stores/tabStore";
export default function ReplicateTab({ tabId, onSelect }) {

    const { t } = useTranslationContext();

    const { url, windowId } = tabStore((state) => ({
        url: state.tabs.find(t => t.id === tabId)?.url,
        windowId: state.tabs.find(t => t.id === tabId)?.windowId
    }));

    const handleClick = async () => {
        chrome.tabs.create({ url: url, windowId: windowId });
        if (typeof onSelect === 'function') {
            onSelect();
        }
    };


    return (

        <MenuItem onClick={handleClick}>
            <ListItemIcon>
                <SvgIcon>
                    <svg xmlns="http://www.w3.org/2000/svg" height="20px" viewBox="0 -960 960 960" width="20px" fill="currentColor">
                        <path d="M312-240q-29.7 0-50.85-21.15Q240-282.3 240-312v-480q0-29.7 21.15-50.85Q282.3-864 312-864h480q29.7 0 50.85 21.15Q864-821.7 864-792v480q0 29.7-21.15 50.85Q821.7-240 792-240H312Zm0-72h480v-360H528v-120H312v480Zm0-480v480-480ZM246-96v-72h72v72h-72Zm-78-600H96q0-29.7 21.15-50.85Q138.3-768 168-768v72ZM396-96v-72h72v72h-72Zm150 0v-72h72v72h-72Zm-378-72v72q-29.7 0-50.85-21.15Q96-138.3 96-168h72Zm-72-78v-72h72v72H96Zm0-150v-72h72v72H96Zm0-150v-72h72v72H96Zm600 378h72q0 29.7-21.15 50.85Q725.7-96 696-96v-72Z" />
                    </svg>
                </SvgIcon>
            </ListItemIcon>
            <ListItemText>
                <Typography variant="body1">{t("tabMenu.ReplicateTab")}</Typography>
            </ListItemText>
        </MenuItem>

    );
}