import { immer } from "zustand/middleware/immer";
import { shallow } from 'zustand/shallow';
import { createWithEqualityFn } from 'zustand/traditional';
import { subscribeWithSelector } from 'zustand/middleware';

export const progressBarStore = createWithEqualityFn(subscribeWithSelector(immer((set, get) => ({
    readingProgress: [],
    playingProgress: [],
    getReadingProgress: (tabId) => {
        return get().readingProgress.find(t => t.tabId === tabId)?.progress || 0;
    },
    setReadingProgress: (tabId, progress) => {
        set((state) => {
            const targetIndex = state.readingProgress.findIndex(t => t.tabId === tabId);
            if (targetIndex !== -1) {
                state.readingProgress[targetIndex].progress = progress;
            } else {
                state.readingProgress.push({ tabId, progress });
            }
        });
    },
    deleteReadingProgress: (tabId) => {
        set((state) => {
            state.readingProgress = state.readingProgress.filter(t => t.tabId !== tabId);
        });
    },
    setPlayingProgress: (tabId, data) => {
        set((state) => {
            const targetIndex = state.playingProgress.findIndex(t => t.tabId === tabId);
            if (targetIndex !== -1) {
                state.playingProgress[targetIndex] = { tabId, ...data };
            } else {
                state.playingProgress.push({ tabId, ...data });
            }
        });
    },
    deletePlayingProgress: (tabId) => {
        set((state) => {
            state.playingProgress = state.playingProgress.filter(t => t.tabId !== tabId);
        });
    }
})), shallow));
