// src/utils/sentry-config.js

import * as Sentry from "@sentry/react";

const IGNORED_ERRORS = [
  "ResizeObserver loop limit exceeded",
  "ResizeObserver loop completed with undelivered notifications.",
  "Non-Error exception captured",
  "Script error.",
  "Cannot access contents",
  "Cannot access a chrome://",
  "Frame with ID 0",
  "(intermediate value)(intermediate value)(intermediate value).querySelector is not a function",
  "Request failed with status code 404",
  "Failed to fetch",
  "network error",
  /NetworkError: A network error occurred/,
  /Failed response from server: 500/,
  "Failed to load resource: net::ERR_CONNECTION_REFUSED",
  "Failed to load resource: net::ERR_CONNECTION_RESET",
  "Failed to load resource: net::ERR_NETWORK_CHANGED",
  "Failed to load resource: net::ERR_TIMED_OUT",
  "Warning: componentWillReceiveProps has been renamed, and is not recommended for use.",
  "Warning: componentWillMount has been renamed, and is not recommended for use.",
  /SecurityError: .+ blocked a frame with origin/,
  /Could not establish connection. Receiving end does not exist/,
  /The message port closed before a response was received/,
  "Error: Cannot inject content-scripts into page",
  "Unable to download all specified images.",
  /A listener indicated an asynchronous response by returning true/,
  /message channel is closed/,
  "Extension context invalidated",
  /No tab with id/,
  /No group with id/,
  /will be removed from/,
  /is not a recognized permission/,
  'reading \'sendMessage\'',
  /^Invalid Tab ID/,
  /Transaction timed out due to inactivity/,
  /(No window with id: )(\d+).?/,
  /(disconnected from wss)[(]?:\/\/[\w./:-]+: \d+:: Normal Closure[)]?/,
  /^disconnected from .+: [0-9]+:: .+$/,
  /^unsubscribed from .+: [0-9]+:: .+$/,
  "No matching message handler",
  "Manifest: property 'background' is deprecated and will be ignored in manifest version 3.",
  "Manifest: property 'persistent' is deprecated and will be ignored in manifest version 3.",
  "Refused to execute inline script because it violates the following Content Security Policy directive",
  "Blocked script execution in 'about:srcdoc' because the document's frame is sandboxed and the 'allow-scripts' permission is not set.",
  "ServiceWorker registration failed: ",
  "ServiceWorker script evaluation failed: ",
  "Cannot access 'window.document' after 'window.close'.",
  "QuotaExceededError: The quota has been exceeded.",
  "Cross-Origin Request Blocked: The Same Origin Policy disallows reading the remote resource at ...",
  "TypeError: Failed to fetch",
];

const shouldIgnoreError = (error) => {
  return IGNORED_ERRORS.some(pattern =>
    typeof pattern === 'string'
      ? error.includes(pattern)
      : pattern.test(error)
  );
};

export const initSentry = () => {
  Sentry.init({
    dsn: process.env.SENTRY_DSN || "",
    integrations: [
      Sentry.browserTracingIntegration()
    ],
    environment: process.env.NODE_ENV,
    release: `rabbitpair@${chrome.runtime.getManifest().version}`,

    // Adjust sampling rate based on environment
    tracesSampleRate: process.env.NODE_ENV === 'production' ? 0.5 : 1.0,

    // Set a lower value for production to conserve quota
    maxBreadcrumbs: process.env.NODE_ENV === 'production' ? 20 : 50,

    // Only enable in production
    enabled: process.env.NODE_ENV === 'production',

    beforeSend(event, hint) {
      // Don't send events for ignored errors
      if (event.exception && shouldIgnoreError(hint.originalException.message)) {
        return null;
      }

      // Trim long stack traces to save on data
      if (event.exception && event.exception.values) {
        event.exception.values.forEach(exception => {
          if (exception.stacktrace && exception.stacktrace.frames) {
            exception.stacktrace.frames = exception.stacktrace.frames.slice(-10);
          }
        });
      }

      return event;
    },

    // Set a maximum number of events per session
    maxSessionEvents: 100,

    // Configure custom error fingerprinting
    beforeErrorClass: (integration, defaultFn, errorData) => {
      const { message } = errorData;
      if (message && message.includes('Network error')) {
        return 'NetworkErrorGroup';
      }
      return defaultFn(errorData);
    },
  });
};

export const captureError = (error, context = {}) => {
  if (process.env.NODE_ENV === 'production' && !shouldIgnoreError(error.message)) {
    Sentry.captureException(error, { extra: context });
  } else {
    console.error('Error:', error, 'Context:', context);
  }
};

export const SentryErrorBoundary = Sentry.ErrorBoundary;

