import React, { useEffect, useState } from "react";
import { Box, Stack, Typography } from "@mui/material";
import { useTranslationContext } from "../../../Providers/TranslationProvider";
import userStore from "../../../../core/stores/userStore";
import WithPermission from "../../../component/WithPermission";
import ColorTypography from "../../../component/ColorTypography";


const BrowserHistory = () => {

    const { t } = useTranslationContext();
    const [historyCount, setHistoryCount] = useState(0);
    const { navSetting } = userStore((state) => ({
        navSetting: state.settings.tabNavOptions.navs.find(nav => nav.name === 'history')
    }));

    const fetchHistoryCount = async () => {
        const count = await new Promise((resolve) => {
            const microsecondsPerTwoDays = 1000 * 86400 * (navSetting?.days || 2);
            const twoDaysAgo = (new Date()).getTime() - microsecondsPerTwoDays;
            chrome.history?.search({ text: '', startTime: twoDaysAgo, maxResults: 2000 }, (results) => {
                resolve(results.length);
            });
        });
        setHistoryCount(count);
    };
    useEffect(() => {
        fetchHistoryCount();
        chrome.permissions?.onAdded.addListener(fetchHistoryCount);
    }, []);

    return (
        <>
            {navSetting?.showCount ? <ColorTypography>〈{historyCount}〉</ColorTypography> : null}
        </>
    );
};

export default BrowserHistory;
