import React, { memo } from "react";
import { Public } from "@mui/icons-material";
import { Avatar, ListItemAvatar } from "@mui/material";
import userStore from "../../../core/stores/userStore";

export default memo(function LinkAvatar({ avatarUrl }) {
    const { avatorSize, buttonPosition } = userStore((state) => ({
        avatorSize: Math.floor((state.settings.view === "visual" ? 28 : 20) * state.settings.fontScale),
        buttonPosition: state.settings.buttonPosition
    }))
    return (
        <ListItemAvatar sx={{
            display: "flex",
            justifyContent: "center",
            minWidth: avatorSize,
            padding: buttonPosition ? "0 5px 0 10px" : "0 10px 0 5px"
        }} >
            <Avatar
                variant="square"
                slotProps={{ img: { referrerPolicy: "no-referrer" } }}
                sx={{

                    background: "transparent",
                    width: avatorSize,
                    height: avatorSize,
                    alignContent: "center"
                }}

                aria-label="recipe"
                src={avatarUrl}>
                <Public sx={{ fontSize: avatorSize }} color="primary" />
            </Avatar>

        </ListItemAvatar>
    )
})