import React, { createContext, useContext, useState } from 'react';

const ReadinListContext = createContext();

export const ReadingListProvider = ({ children }) => {
    const [searchQuery, setSearchQuery] = useState('');
    const [ReadinListCount, setReadinListCount] = useState(0);

    return (
        <ReadinListContext.Provider value={{
            searchQuery,
            setSearchQuery,
            ReadinListCount,
            setReadinListCount
        }}>
            {children}
        </ReadinListContext.Provider>
    );
};

export const useReadinListContext = () => useContext(ReadinListContext);
