import React from "react";
import { MenuItem, ListItemText, Typography, ListItemIcon } from "@mui/material";
import tabStore from "../../../../../core/stores/tabStore";
import * as Sentry from "@sentry/react";
import { useTranslationContext } from "../../../../Providers/TranslationProvider";

export default function OpenInNewWindow({ uuid, onSelect }) {

    const { t } = useTranslationContext();

    const shortcut = tabStore(state => state.quickAccess.find(shortcut => shortcut.uuid === uuid));

    const handleClick = async () => {
        try {
            if (shortcut.urls && shortcut.urls.length > 0) {
                const openUrls = shortcut.urls.map(urlConfig => (urlConfig.url));
                const window = await chrome.windows.create({ url: openUrls, type: 'normal' });
                if (shortcut.type === 'group') {
                    const tabIds = window.tabs.map(tab => tab.id);
                    const groupId = await chrome.tabs.group({ tabIds, createProperties: { windowId: window.id } });
                    await chrome.tabGroups.update(groupId, {
                        title: shortcut.title,
                        color: shortcut.color || 'grey'
                    });
                }
            } else {
                chrome.windows.create({ url: shortcut.url, type: 'normal' });
            }
            if (typeof onSelect === 'function') {
                onSelect();
            }
        } catch (error) {
            console.error(error);
            Sentry.captureException(error);
        }
    };

    return (
        <MenuItem onClick={handleClick}>
            <ListItemText>
                <Typography variant="body1">
                    {shortcut.urls && shortcut.urls.length > 0 ? t("openAllInNewWindow", { count: shortcut.urls.length }) : t("OpenInNewWindow")}
                </Typography>
            </ListItemText>
        </MenuItem>
    );
}