import React, { memo } from "react";
import { Close } from "@mui/icons-material";
import { IconButton, Tooltip, Typography } from "@mui/material";
import { removeTabById } from "../../../../core/browers/tabs";
import { useTranslationContext } from "../../../Providers/TranslationProvider";
import { useSettingsContext } from "../../../Providers/SettingsProvider";



const TabClose = ({ tabId }) => {
  const { t } = useTranslationContext();

  const settings = useSettingsContext();

  const handleCloseTab = async (event) => {
    event.stopPropagation();
    await removeTabById(tabId);
  };

  return (
    <Tooltip arrow placement="top" title={<Typography>{t('tabMenu.closeTab')}</Typography>} aria-label={t('tabMenu.closeTab')}>
      <IconButton className="close-button" sx={{ padding: "4px", color: "inherit", display: settings.AlwaysShowCloseButton ? "flex" : "none" }} onClick={handleCloseTab}>
        <Close size="small" />
      </IconButton>
    </Tooltip>
  );
};

export default memo(TabClose);
