import React from "react";
import { MenuItem, ListItemText, Typography, ListItemIcon } from "@mui/material";
import { AddtoReadinglist, isSupportReadingList } from "../../../../core/browers/readingList";
import tabStore from "../../../../core/stores/tabStore";
import { toast } from "react-toastify";
import { PostAdd } from "@mui/icons-material";
import userStore from "../../../../core/stores/userStore";
import { useTranslationContext } from "../../../Providers/TranslationProvider";
import WithPermission from "../../../component/WithPermission";

export default function AddToReadingList({ tabId, onSelect }) {
    const { t } = useTranslationContext();
    const theme = userStore(state => state.settings.theme);

    const tab = tabStore(state => {
        const { title, url } = state.tabs.find(t => t.id === tabId) || {};
        return { title, url };
    });

    const handleClick = async () => {
        await AddtoReadinglist(tab);
        toast.success(t("AddedToReadingListSuccessfully"), {
            position: "bottom-center",
            hideProgressBar: true,
            theme: theme,
            autoClose: 1500
        });
        if (typeof onSelect === 'function') {
            onSelect();
        }
    };

    if (navigator.userAgent.includes("OPR")) {
        return null;
    }

    return (
        <WithPermission permissionName={"readingList"}>
            <MenuItem onClick={handleClick}>
                <ListItemIcon>
                    <PostAdd />
                </ListItemIcon>
                <ListItemText>
                    <Typography variant="body1">{t("tabMenu.AddToReadingList")}</Typography>
                </ListItemText>
            </MenuItem>
        </WithPermission>
    );
}