import React from 'react';
import { ArrowRight, ArrowLeft, ArrowDropDown } from "@mui/icons-material";
import { useSettingsContext } from '../../../Providers/SettingsProvider';

const GroupArrowIcon = ({ collapsed, textColor }) => {

    const { buttonPosition } = useSettingsContext();

    if (!collapsed) {
        return <ArrowDropDown fontSize='small' sx={{ color: textColor }} />;
    } else {
        return buttonPosition === "row"
            ? <ArrowRight fontSize='small' sx={{ color: textColor }} />
            : <ArrowLeft fontSize='small' sx={{ color: textColor }} />;
    }
};

export default GroupArrowIcon;