import React, { useEffect, useState, useRef } from "react";
import { Box, List, Typography } from "@mui/material";
import { getReadingList, deleteReadingListItem } from "../../../core/browers/readingList";
import ReadingListEntry from "./ReadingListEntry";
import { OverlayScrollbarsComponent } from "overlayscrollbars-react";
import { useHotkeys } from "react-hotkeys-hook";
import { useTranslationContext } from "../../Providers/TranslationProvider";
import userStore from "../../../core/stores/userStore";
import BgOverlay from "../BgOverlay";
import PermissionRequired from "../bottomNav/actions/PermissionRequired";

const ReadingListEntries = ({ onSelect }) => {
    const { t } = useTranslationContext();
    const { fontColor, searchQuery } = userStore((state) => ({
        fontColor: state.settings.fontColor,
        searchQuery: state.searchQuery,
    }));
    const [readingList, setReadingList] = useState([]);
    const [selectedIndex, setSelectedIndex] = useState(-1);
    const listRef = useRef(null);

    const fetchReadingList = async () => {
        const list = await getReadingList();
        setReadingList(list);
    };

    useEffect(() => {
        fetchReadingList();
    }, []);

    const handleDelete = async (url) => {
        await deleteReadingListItem(url);
        setReadingList(prevList => prevList.filter(item => item.url !== url));
    };

    const handleUpdate = (url, updates) => {
        setReadingList(prevList => prevList.map(item => item.url === url ? { ...item, ...updates } : item));
    };

    const filteredReadingList = () => {
        if (!searchQuery) return readingList;
        const queries = searchQuery.trim().toLowerCase().split(/\s+/);
        return readingList.filter(item =>
            queries.every(query =>
                (item.title && item.title.toLowerCase().includes(query)) ||
                (item.url && item.url.toLowerCase().includes(query))
            )
        );
    };

    useHotkeys('ArrowDown', (event) => {
        event.preventDefault();
        setSelectedIndex(prevIndex => (prevIndex + 1) % filteredReadingList().length);
    }, { enableOnFormTags: ["TEXTAREA", "INPUT"] });

    useHotkeys('ArrowUp', (event) => {
        event.preventDefault();
        setSelectedIndex(prevIndex => (prevIndex - 1 + filteredReadingList().length) % filteredReadingList().length);
    }, { enableOnFormTags: ["TEXTAREA", "INPUT"] });

    useHotkeys('Enter', (event) => {
        event.preventDefault();
        if (selectedIndex >= 0 && selectedIndex < filteredReadingList().length) {
            const selectedItem = filteredReadingList()[selectedIndex];
            if (selectedItem) {
                chrome.tabs.create({ url: selectedItem.url });
                if (typeof onSelect === 'function') {
                    onSelect();
                }
            }
        }
    }, { enableOnFormTags: ["TEXTAREA", "INPUT"] });


    return (
        <OverlayScrollbarsComponent defer>
            <BgOverlay />
            <Box sx={{ padding: 0, overflowX: "hidden", color: fontColor || "text.primary" }}>
                <PermissionRequired permission="readingList" updatePermission={fetchReadingList} />
                {filteredReadingList().length === 0 ? (
                    <Typography variant="body1" sx={{ padding: "30px 8px", textAlign: "center", color: "text.secondary" }}>
                        {t('emptyHere')}
                    </Typography>
                ) : (
                    <List sx={{ width: "100%", padding: 0 }} ref={listRef}>
                        {filteredReadingList().map((item, index) => {
                            return <ReadingListEntry
                                key={item.url}
                                item={item}
                                onDelete={handleDelete}
                                onUpdate={handleUpdate}
                                onSelect={onSelect}
                                selected={index === selectedIndex}
                                data-index={index}
                            />
                        })}
                    </List>
                )}
            </Box>
        </OverlayScrollbarsComponent>
    );
};

export default ReadingListEntries;
