import React, { memo, useState, useRef, useCallback } from "react";
import { ListItemButton, Box, Stack, Typography, Avatar, SvgIcon, IconButton, Tooltip, Menu } from "@mui/material";
import { useSettingsContext } from "../../../Providers/SettingsProvider";
import { Public } from "@mui/icons-material";
import tabStore, { setTabMetaCache } from "../../../../core/stores/tabStore";
import { useTranslationContext } from "../../../Providers/TranslationProvider";
import CopyMarkdown from "./menuItems/CopyMarkdown";
import CopyTitle from "./menuItems/CopyTitle";
import CopyUrl from "./menuItems/CopyUrl";
import OpenInNewTab from "./menuItems/OpenInNewTab";
import OpenInNewWindow from "./menuItems/OpenInNewWindow";
import OpenInIncognitoWindow from "./menuItems/OpenInIncognitoWindow";
import DeleteTabHistory from "./menuItems/DeleteTabHistory";
import DeleteTabHistories from "./menuItems/DeleteTabHistories";
import TabHistoryPreview from "./TabHistoryPreview";
import { toast } from "react-toastify";
import { sendTabMessage } from "../../../../core/browers/runtime";
import TabHistoryExpand from "./menuItems/TabHistoryExpand";
import TabUrl from "../tab/TabUrl";
import { decodedUrl } from "../../../../utils/helpers";

export default memo(function TabHistoryItem({ urlHistory }) {
  console.log("urlHistory render", urlHistory);

  const { t } = useTranslationContext();
  const { view, theme, fontColor, buttonPosition, tabHistoryShowUrl } = useSettingsContext();
  const listItemRef = useRef(null);
  const [contextMenu, setContextMenu] = useState(null);

  const handleContextMenu = useCallback((event) => {
    event.preventDefault();
    setContextMenu(contextMenu === null ? listItemRef.current : null);
  }, [contextMenu]);

  const handleClose = useCallback(() => {
    setContextMenu(null);
  }, []);

  const handleClick = async (event) => {
    event.stopPropagation();
    console.log("handleClick TabHistoryItem", event);
    const { button, ctrlKey, metaKey } = event;
    const { url, tabId } = urlHistory;

    try {
      if (button === 1 || (ctrlKey && button === 0) || (metaKey && button === 0)) {
        await chrome.tabs.create({ url });
      } else if (button === 0) {
        await chrome.tabs.update(tabId, { url });
      }
    } catch (error) {
      if (error.message.includes("Saved groups are not editable.")) {
        const response = await sendTabMessage(urlHistory.tabId, 'redirect_url', { url: urlHistory.url })
        console.log("TabHistoryItem handleClick response", response)
        if (!response?.success) {
          toast.error(t("Saved groups are not editable.", { ns: "error" }), {
            position: "bottom-center",
            autoClose: 1500,
            theme: "colored"
          });
        }
      } else {
        console.error("Error in handleClick:", error);
      }
    }
  };

  const handleDelete = async (event) => {
    event.stopPropagation();
    const { tabId } = urlHistory;
    const tabConfig = await tabStore.getState().getTabConfig(tabId);
    const updatedUrlHistories = tabConfig.urlHistories.filter(history => history.url !== urlHistory.url);
    await tabStore.getState().setTabConfig(tabId, { urlHistories: updatedUrlHistories });
    await setTabMetaCache(tabId, 'urlHistories', updatedUrlHistories);
  };

  return (
    <Box
      sx={{
        position: "relative",
        paddingInlineStart: '18px',
        '&:before': {
          borderLeft: `1px solid ${theme === "dark" ? "#fff" : (fontColor === 'inherit' || !fontColor) ? "#ccc" : fontColor}`,
          content: '""',
          height: '100%',
          left: buttonPosition === 'row' ? '18px' : 'auto',
          right: buttonPosition !== 'row' ? '18px' : 'auto',
          position: 'absolute',
          top: tabHistoryShowUrl ? "-18px" : '-10px',
        },
        '&:after': {
          borderTop: `1px solid ${theme === "dark" ? "#fff" : (fontColor === 'inherit' || !fontColor) ? "#ccc" : fontColor}`,
          content: '""',
          position: 'absolute',
          top: tabHistoryShowUrl ? `16px` : `9px`,
          left: buttonPosition === 'row' ? '18px' : 'auto',
          right: buttonPosition !== 'row' ? '18px' : 'auto',
          width: '12px',
          zIndex: 999
        },
      }}
    >
      <ListItemButton
        selected={urlHistory.active}
        component="div"
        ref={listItemRef}
        onContextMenu={handleContextMenu}
        onClick={handleClick}
        // onMouseDown={handleClick}
        dense={view === "list"}
        disableGutters
        sx={{
          width: "100%",
          ...(buttonPosition === "row" ? { paddingRight: "0px", paddingLeft: "12px" } : { paddingLeft: "0px", paddingRight: "25px" }),
          paddingY: "2px",
          "&:hover": {
            background: theme === "dark" ? "rgba(255, 255, 255, 0.1)" : "rgba(0, 0, 0, 0.1)",
            backdropFilter: "blur(5px)",
            '& .close-button': {
              display: "flex",
            },
          },
          "&.Mui-selected": {
            background: theme === "dark" ? "rgba(255, 255, 255, 0.2)" : "rgba(0, 0, 0, 0.2)",
            backdropFilter: "blur(5px)",
            zIndex: 1,
            color: theme === "dark" ? 'text.primary' : fontColor || 'text.primary',
          }
        }}
      >
        <Stack direction={buttonPosition} alignItems="center" spacing={1} sx={{ width: "100%" }}>
          {/* <Tooltip title={t('deleteTabHistory')}>
            <IconButton
              onClick={handleDelete}
              onMouseDown={e => e.stopPropagation()}
              component='div' sx={{ padding: '0px' }}>
              <SvgIcon>
                <svg xmlns="http://www.w3.org/2000/svg" height="24px" viewBox="0 -960 960 960" width="24px" fill="#5f6368"><path d="m336-280 144-144 144 144 56-56-144-144 144-144-56-56-144 144-144-144-56 56 144 144-144 144 56 56ZM200-120q-33 0-56.5-23.5T120-200v-560q0-33 23.5-56.5T200-840h560q33 0 56.5 23.5T840-760v560q0 33-23.5 56.5T760-120H200Zm0-80h560v-560H200v560Zm0-560v560-560Z" /></svg>
              </SvgIcon>
            </IconButton>
          </Tooltip> */}

          <Avatar
            variant="square"
            slotProps={{ img: { referrerPolicy: "no-referrer" } }}
            sx={{
              background: "transparent",
              width: '12px',
              height: '12px',
              alignContent: "center",
            }}
            src={urlHistory.favIconUrl}>
            <Public sx={{ fontSize: '9px' }} color="primary" />
          </Avatar>
          <Tooltip
            followCursor
            enterDelay={1500}
            enterNextDelay={200}
            componentsProps={{ tooltip: { sx: { padding: "15px", background: "transparent", zIndex: 99 } } }}
            title={<TabHistoryPreview
              title={urlHistory.title}
              url={urlHistory.url}
              lastAccessed={urlHistory.lastAccessed}
            />}
          >
            <Box sx={{ width: "100%" }}>
              <Typography variant="body2" sx={{ whiteSpace: "nowrap", overflow: "hidden", textOverflow: "ellipsis" }}>{urlHistory.title}</Typography>
              {tabHistoryShowUrl && <Typography
                variant="h5"
                color="text.secondary"
                sx={{

                  whiteSpace: "pre-wrap",
                  wordWrap: "break-word",
                  wordBreak: "break-all",
                  textWrap: "break-word",
                  overflow: "hidden",
                  textOverflow: "ellipsis",
                  display: "-webkit-box",
                  WebkitLineClamp: 1,
                  WebkitBoxOrient: "vertical",
                  fontWeight: 200,
                  color: theme === "dark" ? "text.secondary" : fontColor || "text.secondary",
                  opacity: 0.3,
                }}
              >
                {decodedUrl(urlHistory.url, false)}
              </Typography>}
            </Box>
          </Tooltip>
        </Stack>
      </ListItemButton>
      <Menu anchorEl={contextMenu} open={Boolean(contextMenu)} onClose={handleClose}>
        {Boolean(contextMenu) && (
          <Box>
            <OpenInNewTab item={urlHistory} handleClose={handleClose} />
            <OpenInNewWindow item={urlHistory} handleClose={handleClose} />
            <OpenInIncognitoWindow item={urlHistory} handleClose={handleClose} />
            <DeleteTabHistory item={urlHistory} handleClose={handleClose} />
            <DeleteTabHistories item={urlHistory} handleClose={handleClose} />
            <TabHistoryExpand tabId={urlHistory.tabId} handleClose={handleClose} />
            <CopyUrl item={urlHistory} handleClose={handleClose} />
            <CopyTitle item={urlHistory} handleClose={handleClose} />
            <CopyMarkdown item={urlHistory} handleClose={handleClose} />
          </Box>
        )}
      </Menu>
    </Box>
  );
});