import React, { memo } from 'react';
import userStore from '../../../../core/stores/userStore';
import { Settings } from '@mui/icons-material';
import Action from './Action';

const ActionSettings = () => {
    const showLabel = userStore(state => state.settings.bottomNavOptions.showLabel && state.settings.bottomNavOptions.navs.find(nav => nav.name === 'settings')?.showLabel);

    const handleClick = () => {
        chrome.runtime.openOptionsPage();
    };

    return (
        <React.Fragment>
            <Action icon={<Settings />} labelKey="Settings" showLabel={showLabel} handleClick={handleClick} />
        </React.Fragment>
    );
};

export default memo(ActionSettings);