import React, { useState, useEffect, useRef, useCallback } from 'react';
import { ListItem, ListItemButton, Collapse, List, Tooltip, Stack, Box, Menu, SvgIcon, Typography, Divider } from '@mui/material';
import { ExpandLess, ExpandMore, Folder, Delete } from '@mui/icons-material';
import { ArrowRight, ArrowDropDown } from '@mui/icons-material';
import BookmarkFolderContextMenu from "./BookmarkFolderContextMenu";
import { countBookmarkUrls } from '../../../utils/helpers';
import { useTranslationContext } from '../../Providers/TranslationProvider';
import userStore from '../../../core/stores/userStore';
import AddBookmark from './menuItems/AddBookmark';
import CheckBookmarks from './menuItems/CheckBookmarks';
import OpenTabs from './menuItems/OpenTabs';
import OpenAllInNewWindow from './menuItems/OpenAllInNewWindow';
import OpenAllInIncognitoWindow from './menuItems/OpenAllInIncognitoWindow';
import EditFolder from './menuItems/EditFolder';
import DeleteFolder from './menuItems/DeleteFolder';
import AddFolder from './menuItems/AddFolder';
import { memo } from 'react';
import { getTabItemStyle } from '../../Hooks/useTabItemStyle';
import BookmarkTreeNode from './BookmarkTreeNode';
import { bookmarkStore } from '../../../core/stores/bookmarkStore';

const BookmarkFolder = ({ bookmarkId, level, selected }) => {
    const { bookmark } = bookmarkStore((state) => ({
        bookmark: state.bookmarks.find(b => b.id === bookmarkId),
    }));
    console.log("BookmarkFolder render", bookmark.title)

    const { t } = useTranslationContext();

    const { theme, fontScale, fontColor, fontWeight, bookmarkHideEmptyFolders, bookmarkExpandAll, bookmarkTreeConnectorLines, bookmarkViewMode } = userStore((state) => ({
        theme: state.settings.theme,
        fontScale: state.settings.fontScale,
        fontColor: state.settings.fontColor,
        fontWeight: state.settings.fontWeight,
        bookmarkHideEmptyFolders: state.settings.bookmarkHideEmptyFolders,
        bookmarkExpandAll: state.settings.bookmarkExpandAll,
        bookmarkTreeConnectorLines: state.settings.bookmarkTreeConnectorLines,
        bookmarkViewMode: state.settings.bookmarkViewMode,
    }));
    const [isOpen, setIsOpen] = useState(level === 0 ? true : false);
    const [contextMenu, setContextMenu] = useState(null);
    const listItemRef = useRef(null);

    useEffect(() => {
        if (level === 0) {
            setIsOpen(true);
        } else {
            setIsOpen(bookmarkExpandAll);
        }
    }, [bookmarkExpandAll]);

    useEffect(() => {
        window.addEventListener("blur", handleClose);
        return () => {
            window.removeEventListener("blur", handleClose);
        };
    }, []);

    const handleToggleFolder = useCallback(() => {
        setIsOpen(!isOpen);
    }, [isOpen]);

    const handleContextMenu = useCallback((event) => {
        event.preventDefault();
        setContextMenu(
            contextMenu === null
                ? listItemRef.current
                : null,
        );
    }, [contextMenu]);

    const handleClose = useCallback(() => {
        setContextMenu(null);
    }, []);

    if (bookmarkHideEmptyFolders && countBookmarkUrls(bookmark) === 0) {
        return null;
    }

    return (
        <React.Fragment>
            <Box
                sx={bookmarkTreeConnectorLines && bookmarkViewMode === 'tree' && bookmark.level > 0 ? {
                    position: 'relative',
                    paddingInlineStart: '20px',
                    '&:before': {
                        borderLeft: `1px solid ${theme === "dark" ? "#fff" : (fontColor === 'inherit' || !fontColor) ? "#ccc" : fontColor}`,
                        content: '""',
                        height: '100%',
                        // left: `${bookmark.level * 1}rem`,
                        position: 'absolute',
                        top: 0,
                    },
                    '&:after': {
                        borderTop: `1px solid ${theme === "dark" ? "#fff" : (fontColor === 'inherit' || !fontColor) ? "#ccc" : fontColor}`,
                        content: '""',
                        position: 'absolute',
                        top: `${20 * fontScale}px`,
                        width: '12px',
                    },
                } : {}}
            >
                <ListItem
                    disablePadding
                    sx={{
                        pl: level * 1.5,
                    }}
                    onContextMenu={handleContextMenu}
                >
                    <Tooltip enterDelay={1500} title={t('containsCountURLs', { count: countBookmarkUrls(bookmark) })} arrow>
                        <ListItemButton

                            disableGutters
                            selected={selected}
                            onClick={handleToggleFolder}
                            ref={listItemRef}
                            sx={{
                                width: "100%",
                                ...getTabItemStyle(bookmark.id),
                                ...(selected && {
                                    backgroundColor: 'action.selected',
                                    '&:hover': {
                                        backgroundColor: 'action.selected',
                                    },
                                }),
                            }}
                        >
                            <Stack direction="row" alignItems="center" justifyContent="flex-start" spacing={1} sx={{
                                width: "100%",
                                pl: bookmarkTreeConnectorLines ? 0 : 1,
                                pr: 1
                            }}>
                                <Stack direction="row" alignItems="center">
                                    <Box>
                                        {isOpen ? <SvgIcon sx={{ fontSize: 22 * fontScale }}>
                                            <svg xmlns="http://www.w3.org/2000/svg" height="24px" viewBox="0 -960 960 960" width="24px" fill="currentColor"><path d="M160-160q-33 0-56.5-23.5T80-240v-480q0-33 23.5-56.5T160-800h240l80 80h320q33 0 56.5 23.5T880-640H447l-80-80H160Zm84-80h516l72-240H316l-72 240Zm0 0 72-240-72 240Zm-84-400v-80 80Z" /></svg>
                                        </SvgIcon> : <SvgIcon sx={{ fontSize: 22 * fontScale }}>
                                            <svg xmlns="http://www.w3.org/2000/svg" height="24px" viewBox="0 -960 960 960" width="24px" fill="currentColor"><path d="M160-160q-33 0-56.5-23.5T80-240v-480q0-33 23.5-56.5T160-800h240l80 80h320q33 0 56.5 23.5T880-640v400q0 33-23.5 56.5T800-160H160Zm0-80h640v-400H447l-80-80H160v480Zm0 0v-480 480Z" /></svg>
                                        </SvgIcon>
                                        }
                                    </Box>
                                </Stack>
                                <Typography variant='h4'
                                    sx={{
                                        paddingTop: "4px",
                                        paddingBottom: "4px",
                                        color: theme === "dark" ? "text.primary" : fontColor || "text.primary",
                                        fontWeight: fontWeight,
                                    }}
                                >{bookmark.title}</Typography>
                            </Stack>
                        </ListItemButton>
                    </Tooltip>
                </ListItem>
                <Collapse in={isOpen} timeout="auto">
                    {isOpen && <List component="div" disablePadding>
                        {bookmark.children.map(child => (
                            child.url ? (
                                <BookmarkTreeNode key={child.id} bookmarkId={child.id} />
                            ) : (
                                <BookmarkFolder key={child.id} bookmarkId={child.id} level={level + 1} />
                            )
                        ))}
                    </List>
                    }

                </Collapse>
            </Box>


            <Menu
                anchorEl={contextMenu}
                open={Boolean(contextMenu)}
                onClose={handleClose}
            >
                {Boolean(contextMenu) && <Box>
                    <AddBookmark folder={bookmark} handleClose={handleClose} />
                    <OpenTabs folder={bookmark} handleClose={handleClose} />
                    <OpenAllInNewWindow folder={bookmark} handleClose={handleClose} />
                    <OpenAllInIncognitoWindow folder={bookmark} handleClose={handleClose} />
                    <Divider />
                    <CheckBookmarks folder={bookmark} handleClose={handleClose} setIsOpen={setIsOpen} />
                    <Divider />
                    <AddFolder folder={bookmark} handleClose={handleClose} />
                    <EditFolder folder={bookmark} handleClose={handleClose} />
                    <DeleteFolder folder={bookmark} handleClose={handleClose} />
                </Box>
                }
            </Menu>
        </React.Fragment>
    );
};

export default memo(BookmarkFolder);
