import React, { memo } from "react";
import { Typography } from "@mui/material";
import userStore from "../../../core/stores/userStore";
import { decodedUrl } from "../../../utils/helpers";
import HighlightedText from "../HighlightedText";


export default memo(function LinkUrl({ url, searchText }) {

  const { theme, fontWeight, fontColor } = userStore((state) => ({
    theme: state.settings.theme,
    fontWeight: state.settings.fontWeight,
    fontColor: state.settings.fontColor
  }))



  return (
    <Typography variant="h5" color="text.secondary" sx={{
      whiteSpace: "pre-wrap",
      wordWrap: "break-word",
      wordBreak: "break-all",
      textWrap: "break-word",
      overflow: "hidden",
      textOverflow: "ellipsis",
      display: "-webkit-box",
      WebkitLineClamp: "2",
      WebkitBoxOrient: "vertical",
      // marginTop: "2px",
      fontWeight: (fontWeight - 200 <= 0 ? 100 : fontWeight - 200),
      color: theme === "dark" ? "text.secondary" : fontColor || "text.secondary",
      opacity: 0.75
    }}>

      <HighlightedText text={decodedUrl(url)} searchText={searchText} />

    </Typography>

  );
})
