import React, { memo } from "react";
import { Card, CardContent, Typography } from "@mui/material";
import { colord } from "colord";
import TabTimeAgo from "../tab/TabTimeAgo";
import { decodedUrl, getHostnameFromUrl } from "../../../../utils/helpers";
import { useSettingsContext } from "../../../Providers/SettingsProvider";

const TabHistoryPreview = ({ title, url, lastAccessed }) => {


    const { theme, fontColor } = useSettingsContext();


    const hostname = getHostnameFromUrl(url);
    const settings = useSettingsContext();
    const bgColor = colord(fontColor).isDark() ? "#fff" : "#666";


    return (
        <Card sx={{ padding: 0, minWidth: "300px" }}>

            <CardContent sx={{ padding: "8px", background: bgColor }}>
                <Typography
                    gutterBottom
                    variant="h3"
                    color="text.primary"
                    component="div"
                    sx={{
                        overflow: "hidden",
                        textOverflow: "ellipsis",
                        display: "-webkit-box",
                        WebkitLineClamp: "2",
                        fontWeight: 600,
                        WebkitBoxOrient: "vertical",
                        margin: "8px 0",
                        color: theme === "dark" ? "text.primary" : fontColor || "text.primary",
                    }}
                >
                    {title}
                </Typography>
                <Typography
                    variant="h4"
                    color="text.secondary"
                    sx={{
                        whiteSpace: "pre-wrap",
                        wordWrap: "break-word",
                        wordBreak: "break-all",
                        textWrap: "break-word",
                        overflow: "hidden",
                        textOverflow: "ellipsis",
                        display: "-webkit-box",
                        WebkitLineClamp: "3",
                        WebkitBoxOrient: "vertical",
                        color: theme === "dark" ? "text.primary" : fontColor || "text.primary",
                        opacity: 0.75
                    }}
                >
                    {decodedUrl(url)}
                </Typography>
                <TabTimeAgo hostname={hostname} lastAccessed={lastAccessed} />
            </CardContent>
        </Card>
    );
};

export default memo(TabHistoryPreview);