import React from "react";
import { Box, Divider } from "@mui/material";
import { tabStore } from "../../../core/stores/tabStore";
import { userStore } from "../../../core/stores/userStore";
import Sortable from "./Sortable";
import useFilteredTabs from "../../Hooks/useFilteredTabs";
import TabAsIcon from "./TabAsIcon";

const OpenedTabListIcon = () => {

    const { onlyCurrentWindowTabs, PinnedTabsAsIcons } = userStore((state) => ({
        onlyCurrentWindowTabs: state.settings.onlyCurrentWindowTabs,
        PinnedTabsAsIcons: state.settings.PinnedTabsAsIcons,
    }));

    const tabs = tabStore(state => state.tabs
        .filter(tab => onlyCurrentWindowTabs ? tab.windowId === state.currentWindow?.id : true)
        .filter(tab => PinnedTabsAsIcons ? tab.pinned === false : true)
        .map(tab => ({
            id: tab.id,
            title: tab.title,
            url: tab.url,
            hostname: tab.hostname,
            created_time: tab.created_time,
            lastAccessed: tab.lastAccessed,
            index: tab.index,
            windowId: tab.windowId,
            groupId: tab.groupId,
            pinned: tab.pinned
        }))
    ) || [];

    const filteredTabs = useFilteredTabs(tabs);

    if (filteredTabs.length === 0) {
        return null;
    }

    return (
        <Box>
            <Box
                sx={{
                    padding: "5px",
                    display: 'grid',
                    gridTemplateColumns: 'repeat(auto-fill, minmax(26px, 1fr))',
                    gap: 1,
                    width: '100%',
                }}
            >
                {filteredTabs.map(tab => (
                    <Sortable type="tab" id={tab.id} key={tab.id}>
                        <TabAsIcon key={`tab-avatar-${tab.id}`} tabId={tab.id} />
                    </Sortable>
                ))}
            </Box>
            <Divider />
        </Box>
    );
}

export default OpenedTabListIcon;
