import React, { memo, useEffect } from "react";
import { List, Box } from "@mui/material";
import { tabStore } from "../../../core/stores/tabStore";
import PageLoading from "../../component/pageLoading";
import { userStore } from "../../../core/stores/userStore";
import TabAdd from "../../component/TabAdd";
import TabGroupDomainList from "./TabGroupDomainList";
import OpenedTabList from "./OpenedTabList";
import OpenedTabListMixed from "./OpenedTabListMixed";
import TabGroupChromeList from "./TabGroupChromeList";
import OpenedTabListIcon from "./OpenedTabListIcon";
import TabWindowsList from "./TabWindowsList";
import HistorySearchresults from "../histories/HistorySearchresults";
import { DndProvider } from "../../Providers/DndEnableProvider";
import DndBox from "./DndBox";
import ClearTabs from "./ClearTabs";

const OpenedTabs = memo(() => {
  const startTime = Date.now();
  console.log("OpenedTabs render start");

  const { theme, groupBy, showBottomNav, showToolbar, searchText } = userStore((state) => ({
    theme: state.settings.theme,
    groupBy: state.settings.groupBy,
    showBottomNav: state.settings.showBottomNav,
    showToolbar: state.settings.showToolbar,
    searchText: state.searchText
  }));

  const { tabStoreInitialized, initTabs } = tabStore((state) => ({
    tabStoreInitialized: state.tabStoreInitialized,
    initTabs: state.initTabs,
  }));

  useEffect(() => {
    initTabs();
  }, [initTabs]);

  const renderContent = () => {
    const componentStartTime = Date.now();
    let component;
    switch (groupBy) {
      case 'domain':
        component = <TabGroupDomainList />;
        break;
      case 'chromeGroup':
        component = <TabGroupChromeList />;
        break;
      case 'mixed':
        component = <OpenedTabListMixed />;
        break;
      case 'icon':
        component = <OpenedTabListIcon />;
        break;
      case 'tabWindow':
        component = <TabWindowsList />;
        break;
      case 'list':
      default:
        component = <OpenedTabList />;
        break;
    }
    const componentEndTime = Date.now();
    console.log(`Component ${groupBy} render time: ${componentEndTime - componentStartTime}ms`);
    return component;
  };

  const endTime = Date.now();
  console.log(`OpenedTabs total render time: ${endTime - startTime}ms`);

  return (
    <DndProvider>
      <Box>
        <Box><ClearTabs /></Box>
        {tabStoreInitialized ? (
          <List sx={{ width: "100%", padding: 0 }}>
            <DndBox>
              {renderContent()}
            </DndBox>
            {searchText && <HistorySearchresults />}
            {!showBottomNav && !showToolbar && (
              <Box sx={{ position: 'sticky', bottom: 0, zIndex: 1, backgroundColor: theme === 'dark' ? 'background.paper' : 'background.default' }}>
                <TabAdd shortcut={true} />
              </Box>
            )}
          </List>
        ) : (
          <PageLoading />
        )}
      </Box>
    </DndProvider>
  );
});

export default OpenedTabs;