import React, { useRef, useEffect, memo } from "react";
import { ListItem, ListItemButton, Box, Tooltip, Stack } from "@mui/material";
import { getTabItemStyle } from "../../Hooks/useTabItemStyle";
import LinkAvatar from "../../component/links/LinkAvatar";
import LinkTitle from "../../component/links/LinkTitle";
import LinkPreview from "../../component/links/LinkPreview";
import { faviconURL } from "../../../utils/helpers";
import userStore from "../../../core/stores/userStore";
import { useTranslationContext } from "../../Providers/TranslationProvider";

const BrowserHistoryItem = ({ item, onSelect, selected, searchText }) => {
    const { t } = useTranslationContext();
    const { view, buttonPosition, tabPreviewEnable, tabPreviewWaitSeconds } = userStore((state) => ({
        view: state.settings.view,
        buttonPosition: state.settings.buttonPosition,
        tabPreviewEnable: state.settings.tabPreviewEnable,
        tabPreviewWaitSeconds: state.settings.tabPreviewWaitSeconds,
    }));

    const listItemRef = useRef(null);

    useEffect(() => {
        if (selected && listItemRef.current) {
            listItemRef.current.scrollIntoView({ behavior: 'smooth', block: 'center' });
        }
    }, [selected]);

    const handleOpenLink = () => {
        chrome.tabs.create({ url: item.url });
        if (typeof onSelect === 'function') {
            onSelect();
        }
    };

    return (
        <Box sx={{ position: "relative" }} ref={listItemRef}>
            <ListItem disablePadding onClick={handleOpenLink}>
                <Tooltip
                    followCursor
                    enterDelay={parseFloat(tabPreviewWaitSeconds) * 1000}
                    enterNextDelay={200}
                    componentsProps={{
                        tooltip: {
                            sx: {
                                padding: "15px",
                                background: "transparent",
                                zIndex: 99
                            }
                        }
                    }}
                    title={tabPreviewEnable ? <LinkPreview title={item.title} url={item.url} /> : ""}
                >
                    <ListItemButton
                        selected={selected}
                        component="div"
                        dense={view === "list"}
                        disableGutters
                        sx={getTabItemStyle(item.url)}
                    >
                        <Stack
                            sx={{
                                width: "100%",
                                paddingRight: buttonPosition === "row" ? "8px" : "0",
                                paddingLeft: buttonPosition === "row" ? "0" : "8px"
                            }}
                            direction={buttonPosition}
                            alignItems={"center"}
                            justifyContent={"space-between"}
                            spacing={1}
                        >
                            <LinkAvatar avatarUrl={faviconURL(item.url)} />
                            <LinkTitle title={item.title} url={item.url} lastAccessed={item.lastVisitTime} searchText={searchText} />
                        </Stack>
                    </ListItemButton>
                </Tooltip>
            </ListItem>
        </Box>
    );
};

export default memo(BrowserHistoryItem);
