import React from "react";
import { MenuItem, ListItemText, Typography, ListItemIcon, SvgIcon } from "@mui/material";
import { useTranslationContext } from "../../../Providers/TranslationProvider";
import tabStore from "../../../../core/stores/tabStore";

export default function PinTab({ tabId, onSelect }) {
    const { t } = useTranslationContext();

    const pinned = tabStore((state) => state.tabs.find(tab => tab.id === tabId)?.pinned);

    const handleClick = async () => {
        chrome.tabs.update(tabId, { pinned: !pinned });
        if (typeof onSelect === 'function') {
            onSelect();
        }
    };

    const PinIcon = () => (
        <SvgIcon>
            <svg xmlns="http://www.w3.org/2000/svg" height="18px" viewBox="0 -960 960 960" width="18px" fill="currentColor">
                <path d={pinned ? "M680-840v80h-40v327l-80-80v-247H400v87l-87-87-33-33v-47h400ZM480-40l-40-40v-240H240v-80l80-80v-46L56-792l56-56 736 736-58 56-264-264h-6v240l-40 40ZM354-400h92l-44-44-2-2-46 46Zm126-193Zm-78 149Z" : "m640-480 80 80v80H520v240l-40 40-40-40v-240H240v-80l80-80v-280h-40v-80h400v80h-40v280Zm-286 80h252l-46-46v-314H400v314l-46 46Zm126 0Z"} />
            </svg>
        </SvgIcon>
    );

    return (
        <MenuItem onClick={handleClick}>
            <ListItemIcon>
                <PinIcon />
            </ListItemIcon>
            <ListItemText>
                <Typography variant="body1">{t(pinned ? "tabMenu.UnpinTab" : "tabMenu.PinTab")}</Typography>
            </ListItemText>
        </MenuItem>
    );
}