import React from "react";
import { createRoot } from "react-dom/client";
import { HashRouter, Routes, Route } from "react-router-dom";
import App from "../App";

import Popup from "./Popup";
import "./index.css";
import { OverlayScrollbars } from "overlayscrollbars";
import "overlayscrollbars/overlayscrollbars.css";
import ClickExtensionIconAction from "./ClickExtensionIconAction";
import { initSentry, SentryErrorBoundary } from "../../services/sentry-config";
import { TranslationProvider } from "../Providers/TranslationProvider";
import { SettingsProvider } from "../Providers/SettingsProvider";

initSentry();
const container = document.getElementById("root");
const root = createRoot(container);
OverlayScrollbars(document.body, { scrollbars: { autoHide: "scroll" } });
root.render(

  <SentryErrorBoundary>
    <SettingsProvider>
      <TranslationProvider>
        <App>
          <HashRouter>
            <Routes>
              <Route exact path="/" element={<Popup />} />
              <Route exact path="/clickIcon" element={<ClickExtensionIconAction />} />
            </Routes>
          </HashRouter>
        </App>
      </TranslationProvider>
    </SettingsProvider>
  </SentryErrorBoundary>

);
