import React, { memo } from "react";
import { Box, Typography } from "@mui/material";
import { progressBarStore } from "../../../core/stores/progressBarStore";
import { formatDuration } from "../../../utils/helpers";
import { useSettingsContext } from "../../Providers/SettingsProvider";

const TotalDuration = ({ tabId }) => {

    const { fontColor } = useSettingsContext();
    const duration = progressBarStore(state => {
        const playData = state.playingProgress.find(t => t.tabId === tabId);
        return playData ? playData.duration : null;
    });

    return (
        <Box component='div'>
            <Typography noWrap variant="body2" sx={{ color: fontColor || 'text.primary' }}>{`${duration === null ? '∞' : formatDuration(duration)}`}</Typography>
        </Box>
    );
};

export default memo(TotalDuration);