import React from 'react';
import { ListItemIcon, ListItemText, MenuItem, Typography } from '@mui/material';
import { useTranslationContext } from '../../../../Providers/TranslationProvider';
import { Close } from '@mui/icons-material';
import { ClearAll } from '@mui/icons-material';
import tabStore, { setTabMetaCache } from '../../../../../core/stores/tabStore';

const DeleteTabHistories = ({ item, handleClose }) => {
    const { t } = useTranslationContext();

    const { setTabConfig } = tabStore((state) => ({
        setTabConfig: state.setTabConfig
    }))

    const handleDelete = async () => {
        const { tabId } = item;
        setTabConfig(tabId, { urlHistories: [] });
        await setTabMetaCache(tabId, 'urlHistories', []);
    };


    return (
        <MenuItem dense onClick={handleDelete}>
            <ListItemIcon>
                <ClearAll />
            </ListItemIcon>
            <ListItemText>
                <Typography variant="body1">{t('deleteTabHistories')}</Typography>
            </ListItemText>
        </MenuItem>
    );
};

export default DeleteTabHistories;
