import React, { memo } from 'react';
import userStore from '../../../../core/stores/userStore';
import Action from './Action';
import { UnfoldLess, UnfoldMore } from '@mui/icons-material';

const ActionCollapseAll = () => {
    const { showLabel, groupExpanded, updateSetting } = userStore(state => ({
        showLabel: state.settings.bottomNavOptions.showLabel && state.settings.bottomNavOptions.navs.find(nav => nav.name === 'collapseAll')?.showLabel,
        updateSetting: state.updateSetting,
        groupExpanded: state.settings.groupExpanded
    }));

    const handleClick = () => {
        updateSetting('groupExpanded', !groupExpanded);
        chrome.tabGroups?.query({}, groups => {
            for (const group of groups) {
                if (group.collapsed === groupExpanded) {
                    chrome.tabGroups?.update(group.id, { collapsed: !groupExpanded });
                }
            }

        })
    };

    return (
        <React.Fragment>
            <Action icon={groupExpanded ? <UnfoldMore size="small" /> : <UnfoldLess size="small" />} labelKey={groupExpanded ? 'CollapseAll' : 'ExpandAll'} showLabel={showLabel} handleClick={handleClick} />
        </React.Fragment>
    );
};

export default memo(ActionCollapseAll);