import { Avatar, ListItemAvatar, Box, AvatarGroup } from "@mui/material";
import React, { memo } from "react";
import { Public } from "@mui/icons-material";
import { useSettingsContext } from "../../../Providers/SettingsProvider";

const QuickAvatar = ({ favIconUrl, favIconUrls }) => {

    console.log('QuickAvatar', { favIconUrl, favIconUrls })

    const { view, fontScale, PinnedTabsAsIcons } = useSettingsContext();
    const avatarSize = Math.floor(20 * fontScale);

    if (favIconUrls && favIconUrls.length > 1) {
        let uniqueFavIconUrls = [...new Set(favIconUrls)];
        return (
            <ListItemAvatar sx={{ display: "flex", justifyContent: "space-between", width: "100%", minWidth: avatarSize, ...(uniqueFavIconUrls.length > 1 ? { border: "1px solid", borderColor: "secondary.main", borderRadius: "4px" } : {}) }}>
                <AvatarGroup
                    max={4}
                    total={favIconUrls.length}
                    slotProps={{
                        additionalAvatar: {
                            sx: {
                                width: avatarSize / 1.2,
                                height: avatarSize / 1.2,
                                background: "rgba(0,0,0,0.15)",
                                color: "error.main",
                                fontSize: "9px",
                                marginLeft: "-3px !important"
                            }
                        }
                    }}
                    renderSurplus={(surplus) => <span>{favIconUrls.length}</span>}
                >
                    {uniqueFavIconUrls.map((url, index) => (
                        <Avatar
                            key={index}
                            variant="circular"
                            sx={{
                                width: avatarSize / 1.2,
                                height: avatarSize / 1.2,
                                background: "rgba(0,0,0,0.15)",
                            }}
                            src={url}
                        />
                    ))}
                </AvatarGroup>
            </ListItemAvatar>
        );
    }

    return (
        <ListItemAvatar sx={{ display: "flex", justifyContent: "center", width: "100%", minWidth: avatarSize }} >
            <Avatar
                variant="square"
                sx={{
                    background: "transparent",
                    width: avatarSize,
                    height: avatarSize,
                    alignContent: "center",
                }}
                aria-label="recipe"
                src={favIconUrl}>
                <Public color="primary" />
            </Avatar>
        </ListItemAvatar>
    )
}

export default memo(QuickAvatar);