import React, { memo } from 'react';
import userStore from '../../../../core/stores/userStore';
import Action from './Action';
import { PictureInPictureAlt } from '@mui/icons-material';
import { getActiveTab } from '../../../../core/browers/tabs';
import { sendTabMessage } from '../../../../core/browers/runtime';

const ActionSwitchPip = () => {
    const showLabel = userStore(state => state.settings.bottomNavOptions.showLabel && state.settings.bottomNavOptions.navs.find(nav => nav.name === 'switchPIP')?.showLabel);
    const updateSetting = userStore(state => state.updateSetting);
    const activeTabIndex = userStore(state => state.activeTabIndex);

    const handleClick = async (event) => {
        event.preventDefault();
        event.stopPropagation();
        const activeTab = await getActiveTab();
        sendTabMessage(activeTab?.id, 'enterPictureInPicture');
    };

    return (
        <React.Fragment>
            <Action icon={<PictureInPictureAlt />} labelKey="switchPIP" showLabel={showLabel} handleClick={handleClick} />
        </React.Fragment>
    );
};

export default memo(ActionSwitchPip);