import React, { useState, useRef, memo, useEffect, Suspense, lazy } from "react";
import { ListItem, ListItemButton, Menu, Box, Stack } from "@mui/material";
import { playingVideoSwitchPip, removeTabById, setTabActive } from "../../../core/browers/tabs";
import { tabIntersectionObserver, tabStore } from "../../../core/stores/tabStore";
import { getTabItemStyle } from "../../Hooks/useTabItemStyle";
import { selectStore } from "../../../core/stores/selectStore";
import { siteStore } from "../../../core/stores/siteStore";
import { debounce } from "lodash-es";
import { useSettingsContext } from "../../Providers/SettingsProvider";
import TabFocus from "./tab/TabFocus";
import TabClose from "./tab/TabClose";
import TabTitle from "./tab/TabTitle";
import TabAvatar from "./tab/TabAvatar";
import TabTooltip from "./tab/TabTooltip";
import ReadingProgressBar from "../../component/ReadingProgressBar";
import TabUnPin from "./tab/TabUnPin";
import TabPip from "./tab/TabPip";
import PlayingProgressBar from "../../component/PlayingProgressBar";
import TabCheckbox from "./tab/TabCheckbox";
import TabVolume from "./tab/TabVolume";
import TabSelected from "./tab/TabSelected";
import TabsMenu from "./TabsMenu";
import TabMenu from "./tabMenu";
import TabHistories from "./tabHistory/TabHistories";
import TabHisotryExpand from "./tabHistory/TabHisotryExpand";
import TabIndex from "./tab/TabIndex";
import { colord } from "colord";
import { useDndEnableContext } from "../../Providers/DndEnableProvider";

export default memo(function TabItem({ tabId, ...props }) {
  console.log("TabItem render", tabId);
  const {
    view,
    enableProgressBar,
    readingProgressBar,
    playingProgressBar,
    buttonPosition,
    tabSwitchPip,
    tabHistoryOnlyOnActive,
    tabHistoryShowExpandBtn,
    showTabIndex,
  } = useSettingsContext();

  const { isTabIdSelected } = selectStore((state) => ({
    isTabIdSelected: state.selectedTabIds.includes(tabId),
  }));

  const { setEnableDnd } = useDndEnableContext();

  const tabRef = useRef(null);
  const [contextMenu, setContextMenu] = useState(null);
  const [contextMenuAnchorEl, setContextMenuAnchorEl] = useState(null);

  const {
    hasTab,
    haskeyboardSelected,
    keyboardSelected,
    isTabActive,
    tabWindowId,
    title,
    thumbnail,
    url,
    lastAccessed,
    status,
    favIconUrl,
    pinned,
    discarded,
    pipOn,
    audible,
    mutedInfo,
    hostname,
    index,
    active,
    windowId,
    urlHistoriesLength,
    currentWindowId,
    loadTabs,
  } = tabStore((state) => {
    const tab = state.tabs.find((t) => t.id === tabId) || {};
    return {
      hasTab: Boolean(tab),
      haskeyboardSelected: Boolean(state.keyboardSelected?.key),
      keyboardSelected: state.keyboardSelected?.key === tabId,
      index: tab?.index,
      isTabActive: tab?.active,
      tabWindowId: tab?.windowId,
      title: tab.title,
      thumbnail: tab.thumbnail,
      url: tab.url,
      lastAccessed: tab.lastAccessed,
      status: tab.status,
      favIconUrl: tab.favIconUrl,
      pinned: tab.pinned,
      discarded: tab.discarded,
      pipOn: tab.pipOn,
      audible: tab.audible,
      mutedInfo: tab.mutedInfo,
      hostname: tab.hostname,
      active: tab.active,
      windowId: tab.windowId,
      urlHistoriesLength: tab.urlHistories?.length,
      currentWindowId: state.currentWindow?.id,
      loadTabs: state.loadTabs,
    };
  });

  const isActiveTab = isTabActive && currentWindowId === tabWindowId;

  useEffect(() => {
    const handleContextMenuClose = () => {
      setContextMenu(null);
      setContextMenuAnchorEl(null);
    };

    window.addEventListener("blur", handleContextMenuClose);

    const debouncedScrollIntoView = debounce(() => {
      if (tabRef.current) {
        if (haskeyboardSelected && keyboardSelected) {
          tabRef.current.scrollIntoView({ behavior: "smooth", block: "nearest", inline: "nearest" });
        } else if (isActiveTab) {
          // tabRef.current.scrollIntoView({ behavior: "instant", block: "nearest", inline: "nearest" });
        }

        if (isActiveTab) {
          tabIntersectionObserver().observe(tabRef.current);
        } else {
          tabIntersectionObserver().unobserve(tabRef.current);
        }
      }
    }, 100);

    debouncedScrollIntoView();

    return () => {
      debouncedScrollIntoView.cancel();
      window.removeEventListener("blur", handleContextMenuClose);
    };
  }, [isActiveTab, keyboardSelected, haskeyboardSelected]);

  const handleMiddleMouseDown = (event) => {
    if (event.button === 1) {
      removeTabById(tabId);
    }
  };

  const handleContextMenu = (event) => {
    event.stopPropagation();
    event.preventDefault();
    setContextMenu({
      mouseX: event.clientX + 2,
      mouseY: event.clientY - 6,
    });
    setContextMenuAnchorEl(event.currentTarget);
    setEnableDnd(false);
  };

  const tabSiteConfig = siteStore(state => (state.sites.find((d) => d.hostname === hostname)))

  const handleHighlightTab = async (event) => {
    event.stopPropagation();
    try {
      const currentActiveTabId = tabStore.getState().activeTabId;
      const currentActiveTab = tabStore.getState().tabs.find((t) => t.id === currentActiveTabId);
      const currentActiveTabSiteConfig = siteStore.getState().sites.find((d) => d.hostname === currentActiveTab?.hostname);

      if (tabSwitchPip && currentActiveTab?.pipSupport && currentActiveTabSiteConfig?.tabSwitchPip !== false && currentActiveTabId !== tabId) {
        try {
          await playingVideoSwitchPip(currentActiveTabId, 'tab', 'playing_video_enterPictureInPicture')
        } catch (error) {
          console.error("tab Highlight Prev Tab enterPictureInPicture error", error);
        }
      }

      await setTabActive(tabId);
    } catch (error) {
      console.error("handleHighlightTab", error);
      loadTabs();
    }
  };

  const handleMouseEnter = () => {
    if (keyboardSelected) {
      tabStore.setState({ keyboardSelected: null });
    }
  };

  return (
    <Box
      className={(isActiveTab ? "tab-item active" : "tab-item") + (keyboardSelected ? " keybord-selected" : "")}
      sx={{ position: "relative" }}
    >
      <ListItem
        ref={tabRef}
        disablePadding
        onContextMenu={handleContextMenu}
        onClick={handleHighlightTab}
        onMouseEnter={handleMouseEnter}
        onMouseDown={handleMiddleMouseDown}
      >
        <ListItemButton
          component="div"
          dense={view === "list"}
          disableGutters
          selected={isActiveTab || keyboardSelected}
          sx={{
            ...getTabItemStyle(tabId, discarded), ...(tabSiteConfig?.backgroundColor ? {
              background: tabSiteConfig.backgroundColor,
              color: tabSiteConfig?.fontColor,
              "&:hover": {
                background: colord(tabSiteConfig.backgroundColor).alpha(0.5).toRgbString(),
                color: tabSiteConfig?.fontColor,
                '& .close-button': {
                  display: "flex",
                },
              },
              "&.Mui-selected": {
                background: colord(tabSiteConfig.backgroundColor).alpha(0.7).toRgbString(),
                color: tabSiteConfig?.fontColor,
                zIndex: 1,
                "&:hover": {
                  background: colord(tabSiteConfig.backgroundColor).alpha(0.5).toRgbString(),
                  color: tabSiteConfig?.fontColor,
                },
              }
            } : {})
          }}
        >

          <Stack
            {...props.listeners}
            sx={{
              width: "100%",
              paddingX: "8px",
              // overflow: "hidden" // Ensure the right Stack does not overflow the parent container
            }}
            direction={buttonPosition}
            alignItems={"center"}
            justifyContent={"space-between"}
            spacing={0.5}
          >          <TabTooltip
            title={title || ""}
            thumbnail={thumbnail}
            url={url}
            lastAccessed={lastAccessed}
          >
              <Stack direction="row" alignItems="center" spacing={1} sx={{ flexShrink: 1, minWidth: 0 }}>
                <Suspense fallback={null}><TabCheckbox tabId={tabId} /></Suspense>
                <TabAvatar status={status} favIconUrl={favIconUrl} url={url} pinned={pinned} />
                {showTabIndex && !isActiveTab && <TabIndex tabId={tabId} />}
                <TabTitle title={title} url={url} hostname={hostname} lastAccessed={lastAccessed} />
              </Stack>
            </TabTooltip>
            <Stack direction="row" alignItems="center" spacing={0.1} sx={{ flexShrink: 0 }}>
              {pipOn && <Suspense fallback={null}><TabPip tabId={tabId} /></Suspense>}
              {audible && <Suspense fallback={null}><TabVolume tabId={tabId} muted={mutedInfo.muted} /></Suspense>}
              {pinned && <Suspense fallback={null}><TabUnPin tabId={tabId} /></Suspense>}
              {tabHistoryShowExpandBtn && (((tabHistoryOnlyOnActive && isActiveTab) || !tabHistoryOnlyOnActive)) && <TabHisotryExpand tabId={tabId} />}
              {isActiveTab && <TabFocus />}
              {keyboardSelected ? <Suspense fallback={null}><TabSelected /></Suspense> : <TabClose tabId={tabId} />}
            </Stack>
          </Stack>

        </ListItemButton>
      </ListItem>
      {enableProgressBar && readingProgressBar && <Suspense fallback={null}><ReadingProgressBar tabId={tabId} /></Suspense>}
      {enableProgressBar && playingProgressBar && <Suspense fallback={null}><PlayingProgressBar tabId={tabId} /></Suspense>}
      <Menu
        dense="true"
        open={contextMenu !== null}
        onClose={() => {
          setContextMenu(null);
          setContextMenuAnchorEl(null);
          setEnableDnd(true);
        }}
        anchorEl={contextMenuAnchorEl}
      >
        {contextMenu !== null && (
          <Box>
            {isTabIdSelected ? <TabsMenu handleMenuClose={() => setContextMenu(null)} /> : <TabMenu tabId={tabId} handleMenuClose={() => setContextMenu(null)} />}
          </Box>
        )}
      </Menu>
      {((tabHistoryOnlyOnActive && isActiveTab) || !tabHistoryOnlyOnActive) && <TabHistories tabId={tabId} />}
    </Box>
  );
});