export const isSupportReadingList = () => {
    return "readingList" in chrome;
}



export const AddtoReadinglist = async (tab) => {

    if (!isSupportReadingList()) {
        return false
    }
    try {
        await chrome.readingList?.addEntry({
            hasBeenRead: false,
            title: tab.title,
            url: tab.url
        })
        return true;
    } catch (e) {
        console.error('AddtoReadinglist error', e);
    }

}

export const getReadingList = async () => {
    if (!isSupportReadingList()) {
        return [];
    }
    try {
        const entries = await chrome.readingList?.query({});
        return entries.sort((a, b) => b.creationTime - a.creationTime);
    } catch (e) {
        console.error("Failed to get reading list entries", e);
        return [];
    }
}


export const deleteReadingListItem = async (url) => {
    if (!isSupportReadingList()) {
        return false;
    }
    try {
        await chrome.readingList?.removeEntry({ url });
        return true;
    } catch (e) {
        console.error("Failed to delete reading list entry", e);
        return false;
    }
}

export const updateReadingListItem = async (url, updates) => {
    if (!isSupportReadingList()) {
        return false;
    }
    try {
        await chrome.readingList?.updateEntry({ url, ...updates });
        return true;
    } catch (e) {
        console.error("Failed to update reading list entry", e);
        return false;
    }
}
