import React from 'react';
import { LibraryBooks } from '@mui/icons-material';
import userStore from '../../../../core/stores/userStore';
import ReadingList from '../../readingList';
import DrawerAction from './DrawerAction';

const ActionReadingList = () => {
    const showLabel = userStore(state => state.settings.bottomNavOptions.showLabel && state.settings.bottomNavOptions.navs.find(nav => nav.name === 'readingList')?.showLabel);

    return (
        <React.Fragment>
            <DrawerAction
                icon={<LibraryBooks />}
                labelKey='readingList'
                ChildComponent={(props) => <ReadingList {...props} addBackGround={true} />}
                showLabel={showLabel}
            />
        </React.Fragment>
    );
};

export default ActionReadingList;