import React, { useEffect, useState } from "react";
import { Box, Stack, Typography } from "@mui/material";
import { useTranslationContext } from "../../../Providers/TranslationProvider";
import userStore from "../../../../core/stores/userStore";
import WithPermission from "../../../component/WithPermission";
import { bookmarkStore } from "../../../../core/stores/bookmarkStore";
import ColorTypography from "../../../component/ColorTypography";


const Bookmarks = () => {
    const { t } = useTranslationContext();

    const { bookmarkStoreInitialized, bookmarksCount, loadBookmarks } = bookmarkStore((state) => ({
        bookmarkStoreInitialized: state.bookmarkStoreInitialized,
        bookmarksCount: state.bookmarksCount,
        loadBookmarks: state.loadBookmarks,
    }));
    const { navSetting } = userStore((state) => ({
        navSetting: state.settings.tabNavOptions.navs.find(nav => nav.name === 'bookmarks')
    }));

    useEffect(() => {
        if (!bookmarkStoreInitialized) {
            loadBookmarks();
        }
    }, [])


    return (
        <>
            {navSetting?.showCount ? <ColorTypography>〈{bookmarksCount}〉</ColorTypography> : null}
        </>
    );
};

export default Bookmarks;
