import { IconButton, Tooltip, Typography } from "@mui/material";
import React, { memo } from "react";
import { useTranslationContext } from "../Providers/TranslationProvider";
import { FilterCenterFocus } from "@mui/icons-material";
import userStore from "../../core/stores/userStore";

const TabFocus = ({ label = "CurrentActiveTab" }) => {
  const { t } = useTranslationContext();

  const { updateSetting, activeTabIndex, fontColor } = userStore((state) => ({
    updateSetting: state.updateSetting,
    activeTabIndex: state.settings.activeTabIndex,
    fontColor: state.settings.fontColor,
  }));

  const handleClick = () => {
    if (activeTabIndex > 1) {
      updateSetting('activeTabIndex', 1);
    }
    const activeTabElement = document.querySelector('.tab-item.active');
    if (activeTabElement) {
      activeTabElement.scrollIntoView({ behavior: "smooth", block: "center", inline: "nearest" });
    }
  };

  return (
    <Tooltip arrow title={<Typography>{t(label)}</Typography>}>
      <IconButton onClick={handleClick} sx={{ padding: "4px", color: fontColor || "text.primary" }}>
        <FilterCenterFocus size="small" />
      </IconButton>
    </Tooltip>
  );
};

export default memo(TabFocus);
