import React, { memo, useState, useEffect } from 'react';
import userStore from '../../../../core/stores/userStore';
import Changelog from '../favoriteMenu/Changelog';
import Feedback from '../favoriteMenu/Feedback';
import Coffee from '../favoriteMenu/Coffee';
import RateExtension from '../favoriteMenu/RateExtension';
import { Badge, Menu, MenuList } from '@mui/material';
import { FavoriteBorder } from '@mui/icons-material';
import { getUserConfig, setUserConfig } from '../../../../core/user';
import Action from './Action';

const ActionVersion = () => {
    const showLabel = userStore(state => state.settings.bottomNavOptions.showLabel && state.settings.bottomNavOptions.navs.find(nav => nav.name === 'version')?.showLabel);

    const [menuEl, setMenuEl] = useState(null);
    const [newVersionBadge, setNewVersionBadge] = useState(false);

    const checkBadges = async () => {
        const newVersionBadge = await getUserConfig("new_version_badge");
        setNewVersionBadge(newVersionBadge);
    };

    const clearBadges = async () => {
        setNewVersionBadge(false);
        await setUserConfig("new_version_badge", false);
    };

    const handleClick = (event) => {
        setMenuEl(event.currentTarget);
        if (newVersionBadge) {
            clearBadges();
        }
    };

    const closeFavoriteMenu = () => {
        setMenuEl(null);
    };

    useEffect(() => {
        checkBadges();
    }, []);

    useEffect(() => {
        const handleWindowBlur = () => {
            closeFavoriteMenu();
        };
        window.addEventListener('blur', handleWindowBlur);
        return () => {
            window.removeEventListener('blur', handleWindowBlur);
        };
    }, []);

    return (
        <React.Fragment>
            <Action
                icon={newVersionBadge ? <Badge color="error" variant="dot"><FavoriteBorder /></Badge> : <FavoriteBorder />}
                labelKey={`v${chrome.runtime.getManifest().version}`}
                showLabel={showLabel}
                handleClick={handleClick}
            />
            <Menu
                id="nav-bottom-favorite-menu"
                anchorEl={menuEl}
                open={Boolean(menuEl)}
                onClose={closeFavoriteMenu}
                MenuListProps={{ "aria-labelledby": "nav-bottom-favorite-button" }}
            >
                <MenuList dense>
                    <Changelog onSelect={closeFavoriteMenu} />
                    <Feedback onSelect={closeFavoriteMenu} />
                    <Coffee onSelect={closeFavoriteMenu} />
                    <RateExtension onSelect={closeFavoriteMenu} />
                </MenuList>
            </Menu>
        </React.Fragment>
    );
};

export default memo(ActionVersion);