import React, { useState, useEffect } from "react";
import tabStore, { setTabMetaCache } from "../../../../core/stores/tabStore";
import TabHistoryItem from "./TabHistoryItem";
import userStore from "../../../../core/stores/userStore";
import UrlHistoryIcon from "./UrlHistoryIcon";
import { Box, IconButton, Tooltip } from "@mui/material";
import { UnfoldLess, UnfoldMore, Delete } from "@mui/icons-material";
import { useTranslationContext } from "../../../Providers/TranslationProvider";

export default function TabHistories({ tabId }) {
    const { t } = useTranslationContext();

    const { theme, fontColor, buttonPosition, tabHistoryEnable, tabHistoryExpand, tabHistoryShowIconWhenCollapsed, tabHistoryKeepCount, tabHistorySortBy } = userStore((state) => ({
        theme: state.settings.theme,
        fontColor: state.settings.fontColor,
        buttonPosition: state.settings.buttonPosition,
        tabHistoryEnable: state.settings.tabHistoryEnable,
        tabHistoryExpand: state.settings.tabHistoryExpand,
        tabHistoryShowIconWhenCollapsed: state.settings.tabHistoryShowIconWhenCollapsed,
        tabHistoryKeepCount: state.settings.tabHistoryKeepCount,
        tabHistorySortBy: state.settings.tabHistorySortBy,
    }));


    const { setTabConfig, urlHistories, expandUrlHistory } = tabStore((state) => ({
        setTabConfig: state.setTabConfig,
        urlHistories: state.tabs.find((t) => t.id === tabId)?.urlHistories,
        expandUrlHistory: state.tabs.find((t) => t.id === tabId)?.expandUrlHistory,
    }));

    const realExpand = tabHistoryEnable && (expandUrlHistory !== undefined ? expandUrlHistory : tabHistoryExpand);

    const handleDeleteHistory = async () => {
        setTabConfig(tabId, { urlHistories: [] });
        await setTabMetaCache(tabId, 'urlHistories', []);
    };

    const DeleteButton = (
        <Tooltip title={t('deleteTabHistories')}>
            <IconButton sx={{ padding: "4px", color: "inherit" }} onClick={handleDeleteHistory}>
                <Delete fontSize="small" />
            </IconButton>
        </Tooltip>
    );

    if (!tabHistoryEnable || !urlHistories || urlHistories.length < 2) {
        return null;
    }

    // Sort urlHistories by lastAccessed in descending order and limit to tabHistoryKeepCount
    const sortedUrlHistories = [...urlHistories]
        .sort((a, b) => a[tabHistorySortBy] - b[tabHistorySortBy])
        .slice(-Math.min(urlHistories.length, tabHistoryKeepCount || 10))

    return (
        <Box sx={{ position: "relative" }}>
            {realExpand ? (
                <Box sx={{ padding: '3px 0' }}>
                    {sortedUrlHistories.map((urlHistory, index) => (
                        <TabHistoryItem key={index} urlHistory={urlHistory} />
                    ))}
                </Box>
            ) : (
                tabHistoryShowIconWhenCollapsed && (
                    <Box sx={{
                        position: "relative",
                        padding: "3px 0",
                    }}>
                        <Box

                            display="grid"
                            gridTemplateColumns="repeat(auto-fill, minmax(20px, 1fr))"
                            gap={0.5}
                            alignItems="center"
                            sx={{
                                position: "relative",
                                paddingInlineStart: '30px',
                                '&:before': {
                                    borderLeft: `1px solid ${theme === "dark" ? "#fff" : (fontColor === 'inherit' || !fontColor) ? "#ccc" : fontColor}`,
                                    content: '""',
                                    height: '100%',
                                    left: buttonPosition === 'row' ? '18px' : 'auto',
                                    right: buttonPosition !== 'row' ? '18px' : 'auto',
                                    position: 'absolute',
                                    top: '-10px',
                                    zIndex: 999
                                },
                                '&:after': {
                                    borderTop: `1px solid ${theme === "dark" ? "#fff" : (fontColor === 'inherit' || !fontColor) ? "#ccc" : fontColor}`,
                                    content: '""',
                                    position: 'absolute',
                                    top: `11px`,
                                    left: buttonPosition === 'row' ? '18px' : 'auto',
                                    right: buttonPosition !== 'row' ? '18px' : 'auto',
                                    width: '12px',
                                    zIndex: 999
                                },
                            }}
                        >

                            {sortedUrlHistories.map((urlHistory, index) => (
                                <UrlHistoryIcon key={index} urlHistory={urlHistory} />
                            ))}

                        </Box>
                    </Box>
                )
            )}
        </Box>
    );
}