import { Box, Tooltip, Typography } from "@mui/material";
import React, { memo } from "react";
import { useTranslationContext } from "../../../Providers/TranslationProvider";
import tabStore from "../../../../core/stores/tabStore";


const TabIndex = ({ tabId }) => {

  const { t } = useTranslationContext();

  const { hotTabs } = tabStore((state) => ({
    hotTabs: state.hotTabs
  }))

  const index = hotTabs.find(h => h.tabId === tabId)?.tabIndex;

  if (index === undefined) {
    return null;
  }

  return (
    <Tooltip arrow placement="top" title={<Typography>{t('tabIndexTips', { index: index })}</Typography>} componentsProps={{ tooltip: { sx: { zIndex: 999 } } }}>
      <Box sx={{ borderRadius: '50%', border: '1px solid #eee', width: '10px', height: '10px', display: 'flex', alignItems: 'center', justifyContent: 'center', padding: '6px' }}>
        <Typography variant="caption">{index}</Typography>
      </Box>
    </Tooltip>
  );
};

export default memo(TabIndex);
