import React, { useState } from "react";
import {
    MenuItem,
    FormControl,
    InputAdornment,
    IconButton,
    Box,
    Typography,
    Autocomplete,
    TextField
} from "@mui/material";
import { Check, RadioButtonChecked, Circle } from "@mui/icons-material";
import { chromeGroupAvailableColors } from "../../../../../utils/colors";
import { sendRuntimeMessage } from "../../../../../core/browers/runtime";
import { useTranslationContext } from "../../../../Providers/TranslationProvider";
import { tabStore } from "../../../../../core/stores/tabStore";
import userStore from "../../../../../core/stores/userStore";

export default function UpdateGroup({ group, onClose }) {
    const { t } = useTranslationContext();
    const { setGroup } = tabStore((state) => ({
        setGroup: state.setGroup
    }));

    const { customGroupNames } = userStore((state) => ({
        customGroupNames: [{ name: group?.title }, ...state.settings.customGroupNames]
    }));

    const [groupName, setGroupName] = useState(group?.title);
    const [colorSelected, setColorSelected] = useState(group?.color || "grey");

    const handleSetGroup = (title, color) => {
        setGroup(group.id, { title, color });
        chrome.tabGroups?.update(group.id, { title, color });
        if (typeof onClose === 'function') {
            onClose();
        }
    };

    const handleGroupNameChange = (event, newValue) => {
        setGroupName(newValue?.name || event.target.value);
    };

    const handleColorChange = (color) => {
        setColorSelected(color);
        chrome.tabGroups?.update(group.id, { title: groupName, color });
    };

    const handleKeyDown = (event) => {
        event.stopPropagation();
        if (event.key === 'Enter') {
            handleSetGroup(groupName, colorSelected);
        }
    };

    return (
        <Box>
            <MenuItem dense sx={{ pt: 2 }} onKeyDown={handleKeyDown}>
                <FormControl fullWidth variant="outlined">
                    <Autocomplete
                        id="rename-group-name-input"
                        options={customGroupNames}
                        getOptionLabel={(option) => option.name}
                        value={customGroupNames.find(option => option.name === groupName) || { name: groupName }}
                        onChange={(event, newValue) => handleGroupNameChange(event, newValue)}
                        onInputChange={(event, newInputValue) => setGroupName(newInputValue)}
                        freeSolo
                        renderInput={(params) => (
                            <TextField
                                {...params}
                                label={<Typography variant="h4">{t("groupMenu.RenameGroup")}</Typography>}
                                InputProps={{
                                    ...params.InputProps,
                                    size: "small",
                                    endAdornment: (
                                        <InputAdornment position="end">
                                            <IconButton
                                                edge="end"
                                                onClick={() => handleSetGroup(groupName, colorSelected)}
                                                aria-hidden="false"
                                            >
                                                <Check />
                                            </IconButton>
                                        </InputAdornment>
                                    )
                                }}
                            />
                        )}
                        isOptionEqualToValue={(option, value) => option.name === value.name}
                    />
                </FormControl>
            </MenuItem>
            <MenuItem dense>
                {chromeGroupAvailableColors.map(colorInfo => (
                    <IconButton
                        fontSize="16px"
                        key={colorInfo.color}
                        onClick={() => handleColorChange(colorInfo.color)}
                        aria-hidden="false"
                    >
                        {colorSelected === colorInfo.color ? (
                            <RadioButtonChecked sx={{ color: colorInfo.hex, fontSize: "14px" }} />
                        ) : (
                            <Circle sx={{ color: colorInfo.hex, fontSize: "14px" }} />
                        )}
                    </IconButton>
                ))}
            </MenuItem>
        </Box>
    );
}
