import { ListItemIcon, MenuItem, SvgIcon, Typography } from "@mui/material";
import React from "react";
import { useTranslationContext } from "../../../Providers/TranslationProvider";


export default function RateExtension({ onSelect }) {

    const { t } = useTranslationContext();
    const handleClick = event => {
        const url = navigator.userAgent.includes("Edg") ? `https://microsoftedge.microsoft.com/addons/detail/${chrome.runtime.id}` : `https://chromewebstore.google.com/detail/${chrome.runtime.id}`;
        chrome.tabs.create({ url: url, active: true })
        if (typeof onSelect === 'function') {
            onSelect();
        }
    };

    return (
        <MenuItem onClick={handleClick}>
            <ListItemIcon>
                <SvgIcon>
                    <svg xmlns="http://www.w3.org/2000/svg" height="24px" viewBox="0 -960 960 960" width="24px" fill="currentColor"><path d="m384-334 96-74 96 74-36-122 90-64H518l-38-124-38 124H330l90 64-36 122ZM233-120l93-304L80-600h304l96-320 96 320h304L634-424l93 304-247-188-247 188Zm247-369Z" /></svg>
                </SvgIcon>
            </ListItemIcon>
            <Typography noWrap variant="body1">
                {t("RateExtension")}
            </Typography>
        </MenuItem>
    )
}