import React, { useState, useEffect, useCallback } from 'react';
import { Box, IconButton, Tooltip, Typography } from '@mui/material';
import { toast } from 'react-toastify';
import { useTranslationContext } from '../Providers/TranslationProvider';
import { Security } from '@mui/icons-material';

const WithPermission = ({ permissionName, children }) => {
    const [permission, setPermission] = useState(false);
    const { t } = useTranslationContext("translation");

    useEffect(() => {
        if (permissionName) {
            const checkPermission = async () => {
                const result = await chrome.permissions.contains({ permissions: [permissionName] });
                setPermission(result);
            };

            checkPermission();

            const handlePermissionAdded = (permissions) => {
                if (permissions.permissions.includes(permissionName)) {
                    setPermission(true);
                }
            };

            chrome.permissions.onAdded.addListener(handlePermissionAdded);

            return () => {
                chrome.permissions.onAdded.removeListener(handlePermissionAdded);
            };
        }
    }, [permissionName]);

    const handleClick = useCallback(async () => {
        const granted = await chrome.permissions.contains({ permissions: [permissionName] });
        if (!granted) {
            const requestGranted = await chrome.permissions.request({ permissions: [permissionName] });
            if (!requestGranted) {
                toast.error(t("deniedMessage"));
            } else {
                setPermission(true);
            }
        } else {
            setPermission(true);
        }
    }, [permissionName, t]);

    if (permissionName && !permission) {
        return (
            <Box position="relative">
                <Box>
                    {children}
                </Box>
                <Tooltip title={<Typography>{t("featureRequiresPermission", { permission: permissionName })}</Typography>}>
                    <Box
                        sx={{
                            width: "100%",
                            height: "100%",
                            position: 'absolute',
                            top: 0,
                            left: 0,
                            backgroundColor: 'rgba(0, 0, 0, 0.02)',
                            display: 'flex',
                            alignItems: 'center',
                            justifyContent: 'flex-end',
                            '&:hover .icon-button': {
                                display: 'flex'
                            }
                        }}
                    >
                        <IconButton
                            className="icon-button"
                            variant="contained"
                            color="error"
                            onClick={handleClick}
                            sx={{ display: 'none' }}
                        >
                            <Security />
                        </IconButton>
                    </Box>
                </Tooltip>
            </Box>
        );
    }

    return children;
};

export default WithPermission;