import React, { memo } from 'react';
import { Add } from '@mui/icons-material';
import userStore from '../../../../core/stores/userStore';
import Action from './Action';

const ActionNewTab = () => {
    const showLabel = userStore(state => state.settings.bottomNavOptions.showLabel && state.settings.bottomNavOptions.navs.find(nav => nav.name === 'newTab')?.showLabel);

    const handleClick = () => {
        chrome.tabs.create({ active: true });
    };

    return (
        <React.Fragment>
            <Action icon={<Add />} labelKey="NewTab" showLabel={showLabel} handleClick={handleClick} />
        </React.Fragment>
    );
};

export default memo(ActionNewTab);