import React, { forwardRef } from "react";
import { MenuList, Box, Divider } from "@mui/material";
import * as Sentry from "@sentry/react";

import CloseTab from "./CloseTab";
import CloseOtherTabs from "./CloseOtherTabs";
import CloseSameSiteTabs from "./CloseSameSiteTabs";
import CloseSameSiteOtherTabs from "./CloseSameSiteOtherTabs";
import Reload from "./Reload";
import DiscardTab from "./DiscardTab";
import ReplicateTab from "./ReplicateTab";
import PinTab from "./PinTab";
import MuteTab from "./MuteTab";
import PipTab from "./PipTab";
import PipSiteConfig from "./PipSiteConfig";
import TabGroup from "./TabGroup";
import AddToReadingList from "./AddToReadingList";
import AddTabOnRight from "./AddTabOnRight";
import SiteSettings from "./SiteSettings";
import CleanBrowsingData from "./CleanBrowsingData";
import ClearCookies from "./ClearCookies";
import CopyTitle from "./CopyTitle";
import CopyLink from "./CopyLink";
import CopyAsMarkdown from "./CopyAsMarkdown";
import PinToQuickAccess from "./PinToQuickAccess";
import KeepTabActive from "./KeepTabActive";
import TabZoom from "./TabZoom";
import AddTabToGroup from "./AddTabToGroup";
import TabHistoryExpand from "../tabHistory/menuItems/TabHistoryExpand";
import RenameTab from "./RenameTab";
import MoveTabToNewWindow from "./MoveTabToNewWindow";
import RemoveFromGroup from "./RemoveFromGroup";
import MoveTabToAnotherWindow from "./MoveTabToAnotherWindow";

export default forwardRef(function TabMenu({ tabId, handleMenuClose }, ref) {
    const renderMenuItem = (Component, props) => (
        <Sentry.ErrorBoundary fallback={null}>
            <Component {...props} />
        </Sentry.ErrorBoundary>
    );

    const handleClick = (event) => {
        event.stopPropagation();
    };

    return (
        <Box ref={ref} onClick={handleClick}>
            <MenuList dense sx={{ minWidth: "200px" }}>



                {renderMenuItem(RenameTab, { tabId, onSelect: handleMenuClose })}
                {renderMenuItem(AddTabOnRight, { tabId, onSelect: handleMenuClose })}
                {renderMenuItem(MoveTabToNewWindow, { tabId, onSelect: handleMenuClose })}
                {renderMenuItem(AddTabToGroup, { tabId, onSelect: handleMenuClose })}
                {renderMenuItem(RemoveFromGroup, { tabId, onSelect: handleMenuClose })}
                {renderMenuItem(MoveTabToAnotherWindow, { tabId, onSelect: handleMenuClose })}
                <Divider sx={{ mt: 0, mb: 0 }} />
                {renderMenuItem(Reload, { tabId, onSelect: handleMenuClose })}
                {renderMenuItem(ReplicateTab, { tabId, onSelect: handleMenuClose })}
                {renderMenuItem(PinTab, { tabId, onSelect: handleMenuClose })}
                {renderMenuItem(MuteTab, { tabId, onSelect: handleMenuClose })}
                {renderMenuItem(PipTab, { tabId, onSelect: handleMenuClose })}
                {renderMenuItem(PipSiteConfig, { tabId, onSelect: handleMenuClose })}
                <Divider sx={{ mt: 0, mb: 0 }} />
                {renderMenuItem(DiscardTab, { tabId, onSelect: handleMenuClose })}
                {renderMenuItem(KeepTabActive, { tabId, onSelect: handleMenuClose })}

                {renderMenuItem(TabZoom, { tabId, onSelect: handleMenuClose })}

                <Divider sx={{ mt: 0, mb: 0 }} />
                {renderMenuItem(PinToQuickAccess, { tabId, onSelect: handleMenuClose })}
                {renderMenuItem(AddToReadingList, { tabId, onSelect: handleMenuClose })}
                <Divider sx={{ mt: 0, mb: 0 }} />


                {/* {renderMenuItem(TabGroup, { tabId, onSelect: handleMenuClose })} */}


                {renderMenuItem(TabHistoryExpand, { tabId, handleClose: handleMenuClose })}

                {renderMenuItem(SiteSettings, { tabId, onSelect: handleMenuClose })}
                {renderMenuItem(CleanBrowsingData, { tabId, onSelect: handleMenuClose })}
                {renderMenuItem(ClearCookies, { tabId, onSelect: handleMenuClose, props: { divide: true } })}
                {renderMenuItem(CopyTitle, { tabId, onSelect: handleMenuClose })}
                {renderMenuItem(CopyLink, { tabId, onSelect: handleMenuClose })}
                {renderMenuItem(CopyAsMarkdown, { tabId, onSelect: handleMenuClose })}
                <Divider />
                {renderMenuItem(CloseTab, { tabId, onSelect: handleMenuClose })}
                {renderMenuItem(CloseOtherTabs, { tabId, onSelect: handleMenuClose })}
                {renderMenuItem(CloseSameSiteTabs, { tabId, onSelect: handleMenuClose })}
                {renderMenuItem(CloseSameSiteOtherTabs, { tabId, onSelect: handleMenuClose })}
            </MenuList>
        </Box>
    );
})