import { Grid, List } from "@mui/material";
import React, { memo, useRef, useMemo } from "react";
import SortableTabItem from "./SortableTabItem";
import { tabStore } from "../../../core/stores/tabStore";
import { userStore } from "../../../core/stores/userStore";
import SortableTabGroup from "./SortableTabGroup";
import useFilteredTabs from "../../Hooks/useFilteredTabs";
import { SortableContext, rectSortingStrategy } from "@dnd-kit/sortable";

const OpenedTabListMixed = () => {
  const { onlyCurrentWindowTabs, PinnedTabsAsIcons } = userStore((state) => ({
    onlyCurrentWindowTabs: state.settings.onlyCurrentWindowTabs,
    PinnedTabsAsIcons: state.settings.PinnedTabsAsIcons,
  }));

  const tabs = tabStore((state) =>
    state.tabs
      .filter((tab) => (onlyCurrentWindowTabs ? tab.windowId === state.currentWindow?.id : true))
      .filter((tab) => (PinnedTabsAsIcons ? !tab.pinned : true))
      .map((tab) => ({
        id: tab.id,
        title: tab.title,
        url: tab.url,
        hostname: tab.hostname,
        created_time: tab.created_time,
        lastAccessed: tab.lastAccessed,
        index: tab.index,
        windowId: tab.windowId,
        groupId: tab.groupId,
        pinned: tab.pinned,
      }))
  ) || [];

  const filteredTabs = useFilteredTabs(tabs);
  const tabContainerRef = useRef(null);

  const mixedTabs = useMemo(() => {
    const mixedTabs = [];
    const groupIds = new Set();

    for (const tab of filteredTabs) {
      if (tab.groupId === -1) {
        mixedTabs.push({ type: "tab", id: tab.id });
      } else if (!groupIds.has(tab.groupId)) {
        mixedTabs.push({ type: "group", id: tab.groupId });
        groupIds.add(tab.groupId);
      }
    }

    return mixedTabs;
  }, [filteredTabs]);

  const sortableItems = useMemo(() => mixedTabs.map(mix => mix.id), [mixedTabs]);

  return (
    <Grid container ref={tabContainerRef}>
      <SortableContext items={sortableItems} strategy={rectSortingStrategy}>
        <List sx={{ width: "100%", padding: 0, position: "relative" }}>
          {mixedTabs.map((mix) => (
            mix.type === "group" ? (
              <SortableTabGroup key={`group-${mix.id}`} groupId={mix.id} />
            ) : (
              <SortableTabItem key={`tab-${mix.id}`} tabId={mix.id} />
            )
          ))}
        </List>
      </SortableContext>
    </Grid>
  );
};

export default memo(OpenedTabListMixed);
