import React from "react";
import { MenuItem, ListItemText, Typography, Button, Dialog, AppBar, Toolbar, IconButton, Slide, Box, TextField, Stack, CircularProgress, Backdrop } from "@mui/material";
import { Close, Add } from "@mui/icons-material";
import tabStore from "../../../../../core/stores/tabStore";
import { faviconURL, fetchWebsiteIcon } from "../../../../../utils/helpers";
import * as Sentry from "@sentry/react";
import { useTranslationContext } from "../../../../Providers/TranslationProvider";
import { chromeGroupAvailableColors } from "../../../../../utils/colors";
import { Circle, RadioButtonChecked } from "@mui/icons-material";

const Transition = React.forwardRef(function Transition(props, ref) {
    return <Slide direction="up" ref={ref} {...props} />;
});

export default function Edit({ uuid, onSelect }) {

    const { t } = useTranslationContext();

    const { shortcut, setQuickAccess } = tabStore(state => ({
        shortcut: state.quickAccess.find(shortcut => shortcut.uuid === uuid),
        setQuickAccess: state.setQuickAccess
    }))

    const [open, setOpen] = React.useState(false);
    const [urls, setUrls] = React.useState(shortcut.urls?.map(u => ({ url: u.url, title: u.title })) || [{ url: shortcut.url, title: shortcut.title }]);
    const [loading, setLoading] = React.useState(false);
    const [title, setTitle] = React.useState(shortcut.title);
    const [color, setColor] = React.useState(shortcut.color || "");
    const [shortName, setShortName] = React.useState(shortcut.short_name || "");

    const handleClickOpen = () => {
        setOpen(true);
    };

    const handleClose = () => {
        setOpen(false);
        if (typeof onSelect === 'function') {
            onSelect();
        }
    };

    const handleFormSubmit = async (event) => {
        event.preventDefault();
        setLoading(true);
        const formData = new FormData(event.currentTarget);
        let formJson = Object.fromEntries(formData.entries());
        formJson.urls = await Promise.all(urls.filter(u => u.url.trim() !== "").map(async (u) => {
            let data = { url: u.url, title: u.title }
            try {
                data.favIconUrl = await fetchWebsiteIcon(u.url);
            } catch (error) {
                data.favIconUrl = faviconURL(u.url);
            }
            return data;
        }));

        try {
            setQuickAccess(uuid, { ...shortcut, ...formJson, title, color, short_name: shortName });
            console.log(formJson);
            handleClose();
            if (typeof onSelect === 'function') {
                onSelect();
            }
        } catch (error) {
            Sentry.captureException(error);
        } finally {
            setLoading(false);
        }
    };

    const handleDragStart = (event) => {
        event.stopPropagation();
    };

    const handleUrlChange = (index, event) => {
        const newUrls = [...urls];
        newUrls[index].url = event.target.value;
        setUrls(newUrls);
    };

    const handleTitleChange = (index, event) => {
        const newUrls = [...urls];
        newUrls[index].title = event.target.value;
        setUrls(newUrls);
    };

    const handleAddUrl = () => {
        setUrls([...urls, { url: "", title: "" }]);
    };

    const handleRemoveUrl = (index) => {
        const newUrls = urls.filter((_, i) => i !== index);
        setUrls(newUrls);
    };

    return (
        <React.Fragment>
            <MenuItem onClick={handleClickOpen}>
                <ListItemText>
                    <Typography variant="body1">{t("Edit")}</Typography>
                </ListItemText>
            </MenuItem>
            <Dialog
                fullScreen
                open={open}
                onClose={handleClose}
                TransitionComponent={Transition}
                PaperProps={{
                    component: 'form',
                    onSubmit: handleFormSubmit,
                }}
            >
                <AppBar sx={{ position: 'relative' }}>
                    <Toolbar>
                        <IconButton
                            edge="start"
                            color="inherit"
                            onClick={handleClose}
                            aria-label="close"
                        >
                            <Close />
                        </IconButton>
                        <Typography sx={{ ml: 2, flex: 1 }} variant="h6" component="div">
                            {t('cancel')}
                        </Typography>
                        <Button type="submit" color="inherit" disabled={loading}>
                            {t('Save')}
                        </Button>
                    </Toolbar>
                </AppBar>
                <Box sx={{ p: 2 }}>
                    <Stack spacing={2} direction="column">
                        <TextField
                            fullWidth
                            name="title"
                            label={t("Name")}
                            multiline
                            defaultValue={shortcut.title}
                            onMouseDown={handleDragStart}
                            onChange={(e) => setTitle(e.target.value)}
                        />
                        <TextField
                            fullWidth
                            name="short_name"
                            label={t("shortName")}
                            inputProps={{ maxLength: 6 }}
                            value={shortName}
                            onChange={(e) => setShortName(e.target.value)}
                        />
                        {shortcut.type === "group" && (
                            <Stack direction="row">
                                {chromeGroupAvailableColors.map(colorInfo => (
                                    <IconButton
                                        fontSize="16px"
                                        key={colorInfo.color}
                                        onClick={() => setColor(colorInfo.color)}
                                    >
                                        {color === colorInfo.color ? (
                                            <RadioButtonChecked sx={{ color: colorInfo.hex, fontSize: "14px" }} />
                                        ) : (
                                            <Circle sx={{ color: colorInfo.hex, fontSize: "14px" }} />
                                        )}
                                    </IconButton>
                                ))}
                            </Stack>
                        )}
                        {urls.map((url, index) => (
                            <Stack key={index} direction="row" alignItems="center" justifyContent="space-between">
                                <Box sx={{ border: '1px solid #ccc', p: 2, mb: 2 }}>
                                    <TextField
                                        fullWidth
                                        size="small"
                                        name={`title-${index}`}
                                        label={t("title")}
                                        multiline
                                        value={url.title}
                                        onChange={(event) => handleTitleChange(index, event)}
                                        onMouseDown={handleDragStart}
                                        sx={{ mb: 2 }}
                                    />
                                    <TextField
                                        fullWidth
                                        size="small"
                                        name={`url-${index}`}
                                        label={t("URL")}
                                        multiline
                                        value={url.url}
                                        onChange={(event) => handleUrlChange(index, event)}
                                        onMouseDown={handleDragStart}
                                        sx={{ mb: 1 }}
                                    />

                                </Box>
                                <IconButton onClick={() => handleRemoveUrl(index)}>
                                    <Close />
                                </IconButton>

                            </Stack>
                        ))}
                        <Button onClick={handleAddUrl} startIcon={<Add />}>
                            {t("Add URL")}
                        </Button>
                    </Stack>
                </Box>
                <Backdrop
                    sx={{ color: '#fff', zIndex: (theme) => theme.zIndex.drawer + 1 }}
                    open={loading}
                >
                    <CircularProgress color="inherit" />
                </Backdrop>
            </Dialog>
        </React.Fragment >
    );
}