import React, { memo } from 'react';
import userStore from '../../../../core/stores/userStore';
import Action from './Action';
import { AddAPhoto } from '@mui/icons-material';
import snapshotStore from '../../../../core/stores/snapshotStore';
import { toast } from 'react-toastify';
import { useTranslation } from 'react-i18next';

const ActionCreateSnapshot = () => {
    const { t } = useTranslation();

    const showLabel = userStore(state => state.settings.bottomNavOptions.showLabel && state.settings.bottomNavOptions.navs.find(nav => nav.name === 'createSnapshot')?.showLabel);

    const handleClick = async () => {
        try {
            await snapshotStore.getState().addSnapshot();
            toast.success(t('createSnapshotSuccess'));
        } catch (error) {
            toast.error(t('createSnapshotFailed'));
        }
    };

    return (
        <React.Fragment>
            <Action icon={<AddAPhoto />} labelKey="createSnapshot" showLabel={showLabel} handleClick={handleClick} />
        </React.Fragment>
    );
};

export default memo(ActionCreateSnapshot);