import React, { useState } from "react";
import { DndContext, useSensor, useSensors, MouseSensor, DragOverlay, PointerSensor, TouchSensor, closestCenter } from "@dnd-kit/core";
import { tabStore } from "../../../core/stores/tabStore";
import TabItem from "./TabItem";
import TabGroupChrome from "./TabGroupChrome";
import TabAsIcon from "./TabAsIcon";
import { toast } from "react-toastify";
import { useTranslationContext } from "../../Providers/TranslationProvider";

export default React.memo(function DndBox({ children }) {


    const { t } = useTranslationContext("error");

    const [activeId, setActiveId] = useState(null);
    const [activeType, setActiveType] = useState(null);

    const sensors = useSensors(
        useSensor(PointerSensor, { activationConstraint: { distance: 10 } }),
        useSensor(TouchSensor, { activationConstraint: { distance: 10 } }),
        useSensor(MouseSensor, { activationConstraint: { distance: 10 } })
    );

    const handleDragStart = (event) => {
        console.log("handleDragStart", event);
        setActiveId(event.active.id);
        setActiveType(event.active.data.current.type);
    };

    const handleOnDragMove = (event) => {

    }
    const handleOnDragOver = (event) => {

    }
    const handleOnDragCancel = (event) => {

    }

    const handleDragEnd = async (event) => {

        console.log("handleDragEnd", event);


        try {
            const { active, over } = event;
            console.log('handleDragEnd', event);
            setActiveId(null);
            setActiveType(null);

            if (!active?.id || !over?.id || active.id === over.id) return;

            const activeType = active.data.current.type;
            const overType = over.data.current.type;

            if ((activeType === 'tab' || activeType === 'tab-icon') && (overType === 'tab' || overType === 'tab-icon')) {
                await handleTabToTabMove(active.id, over.id);
            } else if ((activeType === 'tab' || activeType === 'tab-icon') && overType === 'group') {
                await handleTabToGroupMove(active.id, over.id);
            } else if (activeType === 'group' && (overType === 'tab' || overType === 'tab-icon')) {
                await handleGroupToTabMove(active.id, over.id);
            } else if (activeType === 'group' && overType === 'group') {
                await handleGroupToGroupMove(active.id, over.id);
            }

            await tabStore.getState().loadGroups();
            await tabStore.getState().loadTabs();

        } catch (error) {
            console.error("handleDragEnd error", error);
            if (/saved groups are not editable/i.test(error.message)) {
                toast.error(t('Saved groups are not editable.'));
            }
        }

    };

    const handleTabWithoutOver = async (activeId) => {
        const activeTab = tabStore.getState().tabs.find(tab => tab.id === activeId);
        if (activeTab.groupId > 0) {

        }

    }

    const handleTabToTabMove = async (activeId, overId) => {
        const activeTab = tabStore.getState().tabs.find(tab => tab.id === activeId);
        const overTab = tabStore.getState().tabs.find(tab => tab.id === overId);

        if (overTab.groupId > -1) {
            await chrome.tabs.group({ tabIds: [activeId], groupId: overTab.groupId });
        } else {
            await chrome.tabs.ungroup(activeId);
        }

        await chrome.tabs.move(activeId, { index: overTab.index, windowId: overTab.windowId });
    };

    const handleTabToGroupMove = async (activeId, overId) => {
        const activeTab = tabStore.getState().tabs.find(tab => tab.id === activeId);
        const overGroupTabs = tabStore.getState().tabs.filter(tab => tab.groupId === overId);
        const firstTabInOverGroup = overGroupTabs[0]
        const lastTabInOverGroup = overGroupTabs[overGroupTabs.length - 1];

        const isAbove = activeTab?.index > lastTabInOverGroup?.index;

        if (isAbove) {
            await chrome.tabs.move(activeId, { index: firstTabInOverGroup.index, windowId: firstTabInOverGroup.windowId });
        } else {
            await chrome.tabs.move(activeId, { index: lastTabInOverGroup.index, windowId: lastTabInOverGroup.windowId });
        }
    };

    const handleGroupToTabMove = async (activeId, overId) => {
        const overTab = tabStore.getState().tabs.find(tab => tab.id === overId);
        await chrome.tabGroups?.move(activeId, { index: overTab.index });
    };

    const handleGroupToGroupMove = async (activeId, overId) => {
        const activeGroupTabs = tabStore.getState().tabs.filter(tab => tab.groupId === activeId);
        const overGroupTabs = tabStore.getState().tabs.filter(tab => tab.groupId === overId);

        const firstActiveTabIndex = Math.min(...activeGroupTabs.map(tab => tab.index));
        const firstOverTabIndex = Math.min(...overGroupTabs.map(tab => tab.index));
        const lastOverTabIndex = Math.max(...overGroupTabs.map(tab => tab.index));
        const overWindowId = overGroupTabs[0].windowId;

        try {
            if (firstActiveTabIndex < firstOverTabIndex) {
                await chrome.tabGroups?.move(activeId, { index: lastOverTabIndex });
            } else {
                await chrome.tabGroups?.move(activeId, { index: firstOverTabIndex });
            }
        } catch (error) {
            console.error("Cannot move the group to an index that is in the middle of another group.", error);
        }
    };

    return (
        <DndContext
            sensors={sensors}
            // collisionDetection={closestCenter}
            onDragStart={handleDragStart}
            onDragEnd={handleDragEnd}
        >
            {children}
            <DragOverlay>
                {activeId ? (
                    activeType === 'tab' ? <TabItem tabId={activeId} /> :
                        activeType === 'group' ? <TabGroupChrome groupId={activeId} hiddenTabs={true} /> :
                            activeType === 'tab-icon' ? <TabAsIcon tabId={activeId} /> :
                                null
                ) : null}
            </DragOverlay>
        </DndContext>
    );
});