import React from 'react';
import { ListItemIcon, ListItemText, MenuItem, Typography } from '@mui/material';
import { useTranslationContext } from '../../../../Providers/TranslationProvider';
import { Close } from '@mui/icons-material';
import tabStore, { setTabMetaCache } from '../../../../../core/stores/tabStore';

const DeleteTabHistory = ({ item, handleClose }) => {
    const { t } = useTranslationContext();

    const handleDelete = async () => {
        const { tabId } = item;
        const tabConfig = await tabStore.getState().getTabConfig(tabId);
        const updatedUrlHistories = tabConfig.urlHistories.filter(history => history.url !== item.url);
        await tabStore.getState().setTabConfig(tabId, { urlHistories: updatedUrlHistories });
        await setTabMetaCache(tabId, 'urlHistories', updatedUrlHistories);
        if (typeof handleClose === 'function') {
            handleClose();
        }
    };

    return (
        <MenuItem dense onClick={handleDelete}>
            <ListItemIcon>
                <Close />
            </ListItemIcon>
            <ListItemText>
                <Typography variant="body1">{t('deleteTabHistory')}</Typography>
            </ListItemText>
        </MenuItem>
    );
};

export default DeleteTabHistory;
