import { Button, Stack, Typography } from "@mui/material";
import React, { memo } from "react";
import { toast } from "react-toastify";
import { useTranslationContext } from "../../../Providers/TranslationProvider";
import { useState } from "react";
import { useEffect } from "react";

const PermissionRequired = ({ permission, updatePermission }) => {
    const { t } = useTranslationContext();

    const [hasPermission, setHasPermission] = useState(true);


    useEffect(() => {
        if (permission) {
            try {
                chrome.permissions.contains({ permissions: [permission] }).then(result => {
                    setHasPermission(result);
                });
            } catch (error) {
                console.log("check permissions error", error);
            }


            const handlePermissionAdded = (permissions) => {
                if (permissions.permissions.includes(permission)) {
                    setHasPermission(true);
                }
            };
            chrome.permissions.onAdded.addListener(handlePermissionAdded);
            return () => {
                chrome.permissions.onAdded.removeListener(handlePermissionAdded);
            };
        }
    }, [permission]);

    const handleClick = () => {
        chrome.permissions.contains({ permissions: [permission] }).then(granted => {
            if (!granted) {
                chrome.permissions.request({ permissions: [permission] }).then(granted => {
                    if (!granted) {
                        toast.error(t("deniedMessage"));
                    } else {
                        setHasPermission(granted);
                        if (typeof updatePermission === 'function') {
                            updatePermission(granted);

                        }
                    }
                });
            } else {
                setHasPermission(granted);
                if (typeof updatePermission === 'function') {
                    updatePermission(granted);
                }
            }
        });
    };
    if (hasPermission) {
        return null;
    }

    return (
        <Stack direction="column" alignItems="center" spacing={2} sx={{ p: 2 }}>
            <Typography>{t("featureRequiresPermission", { permission })}</Typography>
            <Button variant="contained" color="error" onClick={handleClick}>{t("grantPermission")}</Button>
        </Stack>
    );
};

export default memo(PermissionRequired);