import React, { memo, useState } from "react";
import { MenuItem, ListItemText, Typography, ListItemIcon, Tooltip, Box, Stack, SvgIcon } from "@mui/material";
import { useTranslationContext } from "../../../Providers/TranslationProvider";
import tabStore from "../../../../core/stores/tabStore";
import { selectStore } from "../../../../core/stores/selectStore";

const RemoveTabsFromGroup = ({ onSelect }) => {
    const { t } = useTranslationContext();

    const { selectedTabIds } = selectStore((state) => ({
        selectedTabIds: state.selectedTabIds,
    }));

    const hasGroups = tabStore(state => state.tabs.filter(tab => (selectedTabIds.includes(tab.id) && tab.groupId > 0))).length > 0
    const handleClick = async () => {
        chrome.tabs.ungroup(selectedTabIds);
        if (typeof onSelect === 'function') {
            onSelect();
        }
    };

    if (!hasGroups) {
        return null;
    }

    return (
        <MenuItem onClick={handleClick}>
            <ListItemIcon>
                <SvgIcon>
                    <svg xmlns="http://www.w3.org/2000/svg" height="18px" viewBox="0 -960 960 960" width="18px" fill="currentColor"><path d="M200-440v-80h560v80H200Z" /></svg>
                </SvgIcon>

            </ListItemIcon>
            <ListItemText>
                <Typography variant="body1">{t("removeFromGroup")}</Typography>
            </ListItemText>
        </MenuItem>
    );
};

export default memo(RemoveTabsFromGroup);