import React from "react";
import { useContext } from "react";

import { Button, Divider, Grid, ToggleButton, ToggleButtonGroup, Typography } from "@mui/material";
import { useTranslation } from "react-i18next";
import App from "../App";
import { userStore } from "../../core/stores/userStore";
import { sendRuntimeMessage } from "../../core/browers/runtime";
import { useTranslationContext } from "../Providers/TranslationProvider";


export default function ClickExtensionIconAction() {

    const { t } = useTranslationContext("options", { keyPrefix: "settings.appearance.extAction" });

    const { updateSetting, clickExtensionIconAction } = userStore((state) => ({
        updateSetting: state.updateSetting,
        clickExtensionIconAction: state.settings.clickExtensionIconAction
    }));


    const handleClickExtensionIconActionChange = async (e, v) => {
        if (v) {
            await updateSetting("clickExtensionIconAction", v)
            if (v === "openSidePanel") {
                if ('sidePanel' in chrome) {
                    await chrome.sidePanel.setPanelBehavior({ openPanelOnActionClick: true }).catch((error) => console.error(error));
                    await sendRuntimeMessage('open_sidepanel');
                }
            } else {
                if ('sidePanel' in chrome) {
                    await chrome.sidePanel.setPanelBehavior({ openPanelOnActionClick: false }).catch((error) => console.error(error));

                }
                if (v === 'openPopup') {
                    await chrome.action.setPopup({ popup: "popup.html" });
                }
                await sendRuntimeMessage('open_popup');
            }
            setTimeout(() => {
                window.close();
            }, 500);
        }
    }

    return (
        <App>
            <Grid container justifyContent="space-around" alignItems="center" sx={{ width: "280px", height: "180px" }}  >
                <Grid item xs={12} p={3}>
                    <Typography variant="h2" textAlign="center" >{t("label")}</Typography>
                </Grid>
                <Divider />
                <Grid item  >
                    <Button variant="contained" onClick={(e) => handleClickExtensionIconActionChange(e, "openSidePanel")}>
                        <Typography variant="body1">{t("openSidePanel")}</Typography>
                    </Button>

                </Grid>
                <Grid item justifyContent="center" >
                    <Button variant="contained" onClick={(e) => handleClickExtensionIconActionChange(e, "openPopup")}>
                        <Typography variant="body1">{t("openPopup")}</Typography>
                    </Button>

                </Grid>
            </Grid>
        </App>
    )

}