import React from "react";
import { ListItemIcon, ListItemText, MenuItem, SvgIcon, Typography } from "@mui/material";
import { sendRuntimeMessage } from "../../../../../core/browers/runtime";
import { useTranslationContext } from "../../../../Providers/TranslationProvider";

export default function DeleteGroup({ group, onClose }) {
    const { t } = useTranslationContext();

    const handleDeleteGroup = async (event) => {
        event.stopPropagation();
        if (group && group.id > 0) {
            const response = await sendRuntimeMessage("chrome_delete_group", { groupId: group.id });
            if (response.success === false) {
                toast.error(t(response.error, { ns: "error" }) || "Failed to delete group");
            }
        }
        if (typeof onClose === 'function') {
            onClose();
        }
    };

    return (
        <MenuItem dense onClick={handleDeleteGroup}>
            <ListItemIcon>
                <SvgIcon>
                    <svg xmlns="http://www.w3.org/2000/svg" height="20px" viewBox="0 -960 960 960" width="20px" fill="currentColor">
                        <path d="m459-408 93-93 93 93 51-51-93-93 93-93-51-51-93 93-93-93-51 51 93 93-93 93 51 51ZM312-240q-29.7 0-50.85-21.15Q240-282.3 240-312v-480q0-29.7 21.15-50.85Q282.3-864 312-864h480q29.7 0 50.85 21.15Q864-821.7 864-792v480q0 29.7-21.15 50.85Q821.7-240 792-240H312Zm0-72h480v-480H312v480ZM168-96q-29.7 0-50.85-21.15Q96-138.3 96-168v-552h72v552h552v72H168Zm144-696v480-480Z" />
                    </svg>
                </SvgIcon>
            </ListItemIcon>
            <ListItemText>
                <Typography variant="body1">{t("groupMenu.DeleteGroup")}</Typography>
            </ListItemText>
        </MenuItem>
    );
}
