import React, { memo } from "react";
import { MenuItem, ListItemText, Typography, ListItemIcon, SvgIcon } from "@mui/material";
import tabStore from "../../../../core/stores/tabStore";
import * as Sentry from "@sentry/react";
import { useTranslationContext } from "../../../Providers/TranslationProvider";

const AddTabOnRight = ({ tabId, onSelect }) => {
    const { t } = useTranslationContext();

    const { index, windowId } = tabStore((state) => {
        const tab = state.tabs.find(tab => tab.id === tabId);
        return {
            index: tab?.index,
            windowId: tab?.windowId
        };
    });

    const handleClick = async () => {
        try {
            chrome.tabs.create({ index: index + 1, windowId: windowId });
            if (typeof onSelect === 'function') {
                onSelect();
            }
        } catch (error) {
            Sentry.captureException(error);
        }
    };

    return (
        <MenuItem onClick={handleClick}>
            <ListItemIcon>
                <SvgIcon>
                    <svg xmlns="http://www.w3.org/2000/svg" height="20px" viewBox="0 -960 960 960" width="20px" fill="currentColor">
                        <path d="M216-216v-528 528Zm0 72q-29.7 0-50.85-21.15Q144-186.3 144-216v-528q0-29.7 21.15-50.85Q186.3-816 216-816h528q29.7 0 50.85 21.15Q816-773.7 816-744v317q-18-4-36-4.5t-36 1.5v-314H216v528h314q-2 18-1.5 36t4.5 36H216Zm555 96-51-51 56-57H624v-72h152l-56-57 51-51 141 144L771-48ZM444-288h72v-156h156v-72H516v-156h-72v156H288v72h156v156Z" />
                    </svg>
                </SvgIcon>
            </ListItemIcon>
            <ListItemText>
                <Typography variant="body1">{t("tabMenu.AddTabOnRight")}</Typography>
            </ListItemText>
        </MenuItem>
    );
}

export default memo(AddTabOnRight);