import React, { memo } from "react";
import { Checkbox } from "@mui/material";
import { selectStore } from "../../../../core/stores/selectStore";


const TabCheckbox = memo(({ tabId }) => {
    const { waitForSelectTab, addSelectedTabId, deleteSelectedTabId, isTabIdSelected } = selectStore((state) => ({
        waitForSelectTab: state.waitForSelectTab,
        addSelectedTabId: state.addSelectedTabId,
        deleteSelectedTabId: state.deleteSelectedTabId,
        isTabIdSelected: state.selectedTabIds.includes(tabId)
    }));

    const handleCheckboxChange = (event) => {
        event.stopPropagation();
        if (event.target.checked) {
            addSelectedTabId(tabId);
        } else {
            deleteSelectedTabId(tabId);
        }
    };

    if (!waitForSelectTab) {
        return null;
    }
    return <Checkbox sx={{ padding: "3px 0px 3px 3px" }} onChange={handleCheckboxChange} onClick={(e) => e.stopPropagation()} checked={isTabIdSelected} />;
});

export default TabCheckbox;
