import React, { useRef } from 'react';
import { ListItemIcon, ListItemText, MenuItem, Typography } from '@mui/material';
import { useTranslationContext } from '../../../Providers/TranslationProvider';
import { FactCheck } from '@mui/icons-material';
import { bookmarkStore } from '../../../../core/stores/bookmarkStore';
import { memo } from 'react';
import PremiumTipsIcon from '../../../component/PremiumTipsIcon';

const CheckBookmarks = ({ folder, handleClose, setIsOpen }) => {
    const { t } = useTranslationContext();
    const { setBookmarksStatus, bookmarks } = bookmarkStore((state) => ({
        setBookmarksStatus: state.setBookmarksStatus,
        bookmarks: state.bookmarks,
    }));

    const checkStatusRef = useRef(new Map());

    const checkBookmark = async (bookmark, retries = 3) => {
        if (bookmark.url) {
            const urlPattern = /^https?:\/\//i;
            if (urlPattern.test(bookmark.url)) {
                console.log("check bookmark", retries, bookmark.title);
                if (!checkStatusRef.current.has(bookmark.id)) {
                    checkStatusRef.current.set(bookmark.id, { status: 'loading' });
                    setBookmarksStatus(bookmark.id, { status: 'loading' });
                }
                if (checkStatusRef.current.get(bookmark.id)?.status !== 'accessible') {
                    try {
                        const controller = new AbortController();
                        const timeoutId = setTimeout(() => controller.abort(), 3000);
                        const response = await fetch(bookmark.url, {
                            signal: controller.signal,
                            headers: {
                                'User-Agent': navigator.userAgent
                            },
                            redirect: 'follow'
                        });
                        clearTimeout(timeoutId);
                        console.log("checkBookmark response.status", bookmark.title, response.status);
                        if (!response.ok && response.status !== 401 && response.status !== 403) {
                            setBookmarksStatus(bookmark.id, { statusCode: response.status, statusText: response.statusText });
                            throw new Error(`Bookmark ${bookmark.title} is not accessible.`);
                        }
                        if (response.redirected && response.url !== bookmark.url) {
                            const finalUrl = response.url;
                            console.log(`Final URL after redirects: ${finalUrl}`);
                            checkStatusRef.current.set(bookmark.id, { status: 'accessible', finalUrl, statusCode: response.status });
                            setBookmarksStatus(bookmark.id, { status: 'accessible', finalUrl, statusCode: response.status });
                            await checkBookmark({ ...bookmark, url: finalUrl }, retries);
                        } else {
                            checkStatusRef.current.set(bookmark.id, { status: 'accessible', statusCode: response.status });
                            setBookmarksStatus(bookmark.id, { status: 'accessible', statusCode: response.status });
                        }
                    } catch (error) {
                        if (retries > 0) {
                            console.error(`Retrying bookmark ${bookmark.title}, attempts left: ${retries}`);
                            setTimeout(() => checkBookmark(bookmark, retries - 1), 1000);
                        } else {
                            console.error(`Error checking bookmark ${bookmark.title}:`, error);
                            checkStatusRef.current.set(bookmark.id, { status: 'inaccessible' });
                            setBookmarksStatus(bookmark.id, { status: 'inaccessible' });
                        }
                    }
                }
            } else {
                checkStatusRef.current.set(bookmark.id, { status: 'accessible' });
                setBookmarksStatus(bookmark.id, { status: 'accessible' });
            }

            const isDuplicate = bookmarks.filter(b => b.url === bookmark.url).length > 1;
            if (isDuplicate) {
                const duplicateBookmarks = bookmarks.filter(b => b.url === bookmark.url);
                const dupBookmarks = duplicateBookmarks.map(bookmark => ({ bookmarkId: bookmark.id, bookmarkPaths: getBookmarkPath(bookmark.id) }));
                setBookmarksStatus(bookmark.id, { duplicate: true, bookmarks: duplicateBookmarks, dupBookmarks });
            }
        }
    };

    const getBookmarkPath = (bookmarkId) => {
        const path = [];
        const findPath = (id) => {
            const bookmark = bookmarks.find(b => b.id === id);
            if (bookmark) {
                path.unshift(bookmark.title);
                if (bookmark.parentId) {
                    findPath(bookmark.parentId);
                }
            }
        };
        findPath(bookmarkId);
        return path;
    };

    const getAllBookmarks = (bookmark) => {
        let bookmarks = [];
        if (bookmark.url) {
            bookmarks.push(bookmark);
        } else if (bookmark.children) {
            for (const child of bookmark.children) {
                bookmarks = bookmarks.concat(getAllBookmarks(child));
            }
        }
        return bookmarks;
    };

    const checkBookmarksInBatches = async (bookmarks, batchSize = 5) => {
        for (let i = 0; i < bookmarks.length; i += batchSize) {
            const batch = bookmarks.slice(i, i + batchSize);
            await Promise.all(batch.map(bookmark => checkBookmark(bookmark)));
        }
    };

    const handleClick = async () => {
        handleClose();
        setIsOpen(true);
        if (folder && folder.children) {
            const allBookmarks = getAllBookmarks(folder);
            checkBookmarksInBatches(allBookmarks);
        }
    };

    return (
        <MenuItem dense onClick={handleClick}>
            <ListItemIcon>
                <FactCheck />
            </ListItemIcon>
            <ListItemText>
                <Typography variant="body1">{t('checkBookmarks')}</Typography>
            </ListItemText>
            <PremiumTipsIcon />
        </MenuItem>
    );
};

export default memo(CheckBookmarks);
