import React, { useEffect, useState, useRef } from 'react';
import {
    Box,
    TextField,
    List,
    Typography,
    Divider,
    Stack
} from '@mui/material';
import { useTranslation } from 'react-i18next';
import { OverlayScrollbarsComponent } from 'overlayscrollbars-react';
import { useHotkeys } from "react-hotkeys-hook";
import { selectStore } from "../../../core/stores/selectStore";
import BrowserHistoryItem from './BrowserHistoryItem';
import PageLoading from '../../component/pageLoading';
import { debounce } from "lodash-es"; import userStore from '../../../core/stores/userStore';
import BgOverlay from '../BgOverlay';
import BrowserHistoryHeader from './BrowserHistoryHeader';
;

const BrowserHistory = ({ onSelect }) => {

    const { t } = useTranslation();
    const { fontColor, searchQuery, setSearchQuery } = userStore((state) => ({
        fontColor: state.settings.fontColor,
        searchQuery: state.searchQuery,
        setSearchQuery: state.setSearchQuery
    }));

    const [state, setState] = useState({
        historyItems: [],
        isLoading: true,
        activeIndex: -1,
        tempHistoryItems: []
    });

    const searchInputRef = useRef(null);
    const listRef = useRef(null);

    const fetchHistory = (query = '') => {
        const oneMonthAgo = new Date();
        oneMonthAgo.setMonth(oneMonthAgo.getMonth() - 1);
        return new Promise((resolve) => {
            chrome.history?.search({ text: query, maxResults: 100, startTime: oneMonthAgo.getTime(), endTime: Date.now() }, (historyItems) => {
                resolve(historyItems);
            });
        });
    };

    useEffect(() => {
        const loadHistory = async () => {
            const historyList = await fetchHistory();
            setState(prevState => ({
                ...prevState,
                historyItems: historyList,
                tempHistoryItems: historyList,
                isLoading: false
            }));
        };
        loadHistory();
    }, []);

    useEffect(() => {
        selectStore.setState({ popupPanelActived: true });
        if (searchInputRef.current) {
            searchInputRef.current.focus();
        }
        return () => {
            selectStore.setState({ popupPanelActived: false });
        };
    }, []);

    const debouncedSearchHistory = debounce(async (query) => {
        setState(prevState => ({ ...prevState, isLoading: true }));
        const keywords = query.trim().split(/\s+/); // Trim and split by any whitespace
        const historyLists = await Promise.all(keywords.map(keyword => fetchHistory(keyword)));
        const mergedHistoryList = [].concat(...historyLists);
        const keywordCount = keywords.length;
        const filteredHistoryList = mergedHistoryList.filter(item => {
            const itemText = `${item.title} ${item.url}`.toLowerCase();
            return keywords.every(keyword => itemText.includes(keyword.toLowerCase()));
        });
        const uniqueHistoryList = Array.from(new Set(filteredHistoryList.map(item => item.id)))
            .map(id => filteredHistoryList.find(item => item.id === id));
        setState(prevState => ({
            ...prevState,
            tempHistoryItems: uniqueHistoryList,
            isLoading: false
        }));
    }, 300);

    useEffect(() => {
        debouncedSearchHistory(searchQuery);
    }, [searchQuery]);

    useEffect(() => {
        if (!state.isLoading) {
            setState(prevState => ({
                ...prevState,
                historyItems: state.tempHistoryItems
            }));
        }
    }, [state.isLoading, state.tempHistoryItems]);

    useHotkeys('ArrowDown', (event) => {
        event.preventDefault();
        setState(prevState => ({
            ...prevState,
            activeIndex: (prevState.activeIndex + 1) % prevState.historyItems.length
        }));
    }, { enableOnFormTags: ["TEXTAREA", "INPUT"] });

    useHotkeys('ArrowUp', (event) => {
        event.preventDefault();
        setState(prevState => ({
            ...prevState,
            activeIndex: (prevState.activeIndex - 1 + prevState.historyItems.length) % prevState.historyItems.length
        }));
    }, { enableOnFormTags: ["TEXTAREA", "INPUT"] });

    useHotkeys('Enter', (event) => {
        event.preventDefault();
        if (state.activeIndex >= 0 && state.activeIndex < state.historyItems.length) {
            const selectedItem = state.historyItems[state.activeIndex];
            if (selectedItem) {
                chrome.tabs.create({ url: selectedItem.url });
                if (typeof onSelect === 'function') {
                    onSelect();
                }
            }
        }
    }, { enableOnFormTags: ["TEXTAREA", "INPUT"] });

    if (state.isLoading && state.historyItems.length === 0) {
        return <PageLoading />;
    }

    return (
        <OverlayScrollbarsComponent defer>
            <BgOverlay />
            <Box sx={{ padding: 0, overflowX: "hidden", color: fontColor || "text.primary" }}>
                <BrowserHistoryHeader />

                {state.historyItems.length === 0 ? (
                    <Typography variant="body1" sx={{ padding: "30px 8px", textAlign: "center", color: "text.secondary" }}>
                        {t('emptyHere')}
                    </Typography>
                ) : (

                    <List sx={{ width: "100%", padding: 0 }} ref={listRef}>
                        {
                            state.historyItems.map((historyItem, index) => (
                                <BrowserHistoryItem key={historyItem.id} item={historyItem} onSelect={onSelect} selected={index === state.activeIndex} data-index={index} searchText={searchQuery} />
                            ))
                        }
                    </List>

                )}

            </Box>
        </OverlayScrollbarsComponent>
    );
};

export default BrowserHistory;