import React from "react";
import { MenuItem, ListItemText, Typography, ListItemIcon, SvgIcon } from "@mui/material";
import { useTranslationContext } from "../../../Providers/TranslationProvider";
import { sendRuntimeMessage } from "../../../../core/browers/runtime";
import { selectStore } from "../../../../core/stores/selectStore";

export default function MoveTabsToNewWindow({ onSelect }) {
    const { t } = useTranslationContext();

    const { selectedTabIds } = selectStore((state) => ({
        selectedTabIds: state.selectedTabIds,
    }));

    const handleClick = async () => {
        await sendRuntimeMessage('move_tabs_to_new_window', { tabIds: selectedTabIds })
        if (typeof onSelect === 'function') {
            onSelect();
        }
    };

    return (
        <MenuItem onClick={handleClick}>
            <ListItemIcon>
                <SvgIcon>
                    <svg xmlns="http://www.w3.org/2000/svg" height="18px" viewBox="0 -960 960 960" width="18px" fill="currentColor"><path d="M200-120q-33 0-56.5-23.5T120-200v-120h80v120h560v-480H200v120h-80v-200q0-33 23.5-56.5T200-840h560q33 0 56.5 23.5T840-760v560q0 33-23.5 56.5T760-120H200Zm260-140-56-56 83-84H120v-80h367l-83-84 56-56 180 180-180 180Z" /></svg>
                </SvgIcon >
            </ListItemIcon>
            <ListItemText>
                <Typography variant="body1">{t("moveTabToNewWindow")}</Typography>
            </ListItemText>
        </MenuItem>
    );
}