import React, { useState } from 'react';
import { IconButton, Popper, Slider, Typography } from "@mui/material";
import { sendTabMessage } from '../../../core/browers/runtime';
import userStore from '../../../core/stores/userStore';
import { progressBarStore } from '../../../core/stores/progressBarStore';
import { Box } from '@mui/material';


const RateControl = ({ tabId }) => {
    const { theme, fontColor } = userStore((state) => ({
        theme: state.settings.theme,
        fontColor: state.settings.fontColor
    }));

    const playData = progressBarStore(state => state.playingProgress.find(t => t.tabId === tabId));
    const setPlayingProgress = progressBarStore(state => state.setPlayingProgress);

    const [rateAnchorEl, setRateAnchorEl] = useState(null);

    const rateOpen = Boolean(rateAnchorEl);

    const handleRateOpen = (event) => {
        setRateAnchorEl(event.currentTarget);
    };

    const handleRateClose = () => {
        setRateAnchorEl(null);
    };

    const handlePlaybackRateChangeCommitted = (event, value) => {
        event.preventDefault();
        event.stopPropagation();
        if (playData) {
            sendTabMessage(tabId, 'playback_rate_change', { value: value });
        }
    };

    const sliderStyles = {
        color: theme === 'dark' ? '#fff' : 'rgba(0,0,0,0.87)',
        '& .MuiSlider-thumb': {
            width: 6,
            height: 6,
            transition: '0.3s cubic-bezier(.47,1.64,.41,.8)',
            '&::before': {
                boxShadow: '0 2px 12px 0 rgba(0,0,0,0.4)',
            },
            '&:hover, &.Mui-focusVisible': {
                boxShadow: `0px 0px 0px 8px ${theme === 'dark' ? 'rgb(255 255 255 / 16%)' : 'rgb(0 0 0 / 16%)'}`,
            },
            '&.Mui-active': {
                width: 8,
                height: 8,
            },
        },
        '& .MuiSlider-rail': {
            opacity: 0.28,
        },
    };

    return (
        <Box onMouseLeave={handleRateClose}>
            <IconButton
                sx={{ padding: "5px", color: fontColor || 'text.primary' }}
                aria-owns={rateOpen ? `mouse-over-rate-${tabId}` : undefined}
                onMouseEnter={(event) => { handleRateOpen(event); }}
            >
                <Typography variant="body2"> {playData?.playbackRate ? `${playData.playbackRate}x` : '1x'}</Typography>
            </IconButton>
            <Popper
                id={`mouse-over-rate-${tabId}`}
                open={rateOpen}
                anchorEl={rateAnchorEl}
                sx={{ padding: "20px 20px 10px", background: theme === 'dark' ? 'rgba(0,0,0,0.87)' : '#fff', borderRadius: '4px' }}
                anchororigin={{
                    vertical: 'center',
                    horizontal: 'center',
                }}
                transformorigin={{
                    vertical: 'center',
                    horizontal: 'center',
                }}
                onClose={handleRateClose}

                onMouseEnter={(event) => { event.stopPropagation(); }}
            >
                <Slider
                    value={playData ? playData.playbackRate : 1}
                    min={0.5}
                    max={2}
                    step={0.25}
                    marks
                    size="small"
                    orientation="vertical"
                    valueLabelDisplay="on"
                    onChangeCommitted={handlePlaybackRateChangeCommitted}
                    onClick={(event) => { event.stopPropagation(); }}
                    onChange={(event) => { event.stopPropagation(); }}
                    valueLabelFormat={(value) => `${playData ? playData.playbackRate : 1}X`}
                    sx={{
                        padding: 0,
                        width: "1px",
                        height: "120px",
                        ...sliderStyles
                    }}
                />
            </Popper>
        </Box>
    );
};

export default RateControl;