import React, { memo } from "react";
import { Box, ListItemText, Typography } from "@mui/material";
import userStore from "../../core/stores/userStore";
import HistoryUrl from "./HistoryUrl";
import HistoryTimeAgo from "./HistoryTimeAgo";
import HighlightedText from "./HighlightedText";

const HistoryTitle = ({ history }) => {
    const { view, theme, fontWeight, fontColor, showFullUrl, searchText } = userStore((state) => ({
        view: state.settings.view,
        theme: state.settings.theme,
        fontWeight: state.settings.fontWeight,
        fontColor: state.settings.fontColor,
        showFullUrl: state.settings.showFullUrl,
        searchText: state.searchText,
    }));

    return (
        <Box sx={{ minWidth: 0, flex: 1 }}>
            <Typography
                variant="h4"
                noWrap
                sx={{
                    paddingTop: "4px",
                    paddingBottom: "4px",
                    fontWeight: fontWeight,
                    color: theme === "dark" ? "text.primary" : fontColor || "text.primary",
                }}
            >
                <HighlightedText text={history.title} searchText={searchText} />
            </Typography>
            {view === "list" && searchText && <HistoryUrl url={history.url} />}

            {view === "visual" && (showFullUrl ? <HistoryUrl url={history.url} /> : <HistoryTimeAgo url={history.url} lastAccessed={history.lastAccessed} />)}
        </Box>
    );
};

export default memo(HistoryTitle);