import { IconButton, Tooltip, Typography } from "@mui/material";
import React from "react";
import tabStore, { setTabMetaCache } from "../../../../core/stores/tabStore";
import { useSettingsContext } from "../../../Providers/SettingsProvider";
import { UnfoldMore, UnfoldLess } from "@mui/icons-material";
import { useTranslationContext } from "../../../Providers/TranslationProvider";
import userStore from "../../../../core/stores/userStore";
import { setTabActive } from "../../../../core/browers/tabs";

export default function TabHisotryExpand({ tabId }) {
    const { t } = useTranslationContext();
    const { tabHistoryEnable, tabHistoryExpand } = userStore((state) => ({
        tabHistoryEnable: state.settings.tabHistoryEnable,
        tabHistoryExpand: state.settings.tabHistoryExpand,
    }));

    const { urlHistoriesLength, expandUrlHistory, setTabConfig, active } = tabStore((state) => {
        const tab = state.tabs.find((t) => t.id === tabId) || {};
        return {
            urlHistoriesLength: tab.urlHistories?.length,
            expandUrlHistory: tab.expandUrlHistory,
            setTabConfig: state.setTabConfig,
            active: tab.active
        };
    });

    const realExpand = tabHistoryEnable && (expandUrlHistory === undefined ? tabHistoryExpand : expandUrlHistory);

    const handleChange = async (event) => {
        event.stopPropagation();
        let newStatus = !realExpand;

        if (!active) {
            await setTabActive(tabId);
            newStatus = true;
        }
        await setTabConfig(tabId, { expandUrlHistory: newStatus });
        await setTabMetaCache(tabId, 'expandUrlHistory', newStatus);


    };

    if (!tabHistoryEnable || !urlHistoriesLength || urlHistoriesLength < 2) {
        return null;
    }

    return (
        <Tooltip title={<Typography>{realExpand ? t('collapseTabHistories') : t('expandTabHistories')}</Typography>}>
            <IconButton sx={{ padding: "3px", color: "inherit" }} onClick={handleChange}>
                {realExpand ? <UnfoldLess fontSize="small" /> : <UnfoldMore fontSize="small" />}
            </IconButton>
        </Tooltip>
    );
}