import React, { forwardRef } from "react";
import { MenuList, Box, Divider } from "@mui/material";
import Delete from "./Delete";
import OpenInNewTab from "./OpenInNewTab";
import OpenInNewWindow from "./OpenInNewWindow";
import OpenInIncognitoWindow from "./OpenInIncognitoWindow";
import CopyLink from "./CopyLink";
import CopyTitle from "./CopyTitle";
import CopyAsMarkdown from "./CopyAsMarkdown";
import Edit from "./Edit";
import * as Sentry from "@sentry/react";

export default forwardRef(function QuickAccessMenuList({ uuid, handleMenuClose }, ref) {

    const menuItems = [
        <Sentry.ErrorBoundary key="open-in-new-tab" fallback={<div>Error loading OpenInNewTab</div>}>
            <OpenInNewTab uuid={uuid} onSelect={handleMenuClose} />
        </Sentry.ErrorBoundary>,
        <Sentry.ErrorBoundary key="open-in-new-window" fallback={<div>Error loading OpenInNewWindow</div>}>
            <OpenInNewWindow uuid={uuid} onSelect={handleMenuClose} />
        </Sentry.ErrorBoundary>,
        <Sentry.ErrorBoundary key="open-in-incognito-window" fallback={<div>Error loading OpenInIncognitoWindow</div>}>
            <OpenInIncognitoWindow uuid={uuid} onSelect={handleMenuClose} />
        </Sentry.ErrorBoundary>,
        <Divider key="divider-1" />,
        <Sentry.ErrorBoundary key="delete" fallback={<div>Error loading Delete</div>}>
            <Delete uuid={uuid} onSelect={handleMenuClose} />
        </Sentry.ErrorBoundary>,
        <Divider key="divider-2" />,
        <Sentry.ErrorBoundary key="edit" fallback={<div>Error loading Edit</div>}>
            <Edit uuid={uuid} onSelect={handleMenuClose} />
        </Sentry.ErrorBoundary>,
        <Divider key="divider-3" />,
        <Sentry.ErrorBoundary key="copy-link" fallback={<div>Error loading CopyLink</div>}>
            <CopyLink uuid={uuid} onSelect={handleMenuClose} />
        </Sentry.ErrorBoundary>,
        <Sentry.ErrorBoundary key="copy-title" fallback={<div>Error loading CopyTitle</div>}>
            <CopyTitle uuid={uuid} onSelect={handleMenuClose} />
        </Sentry.ErrorBoundary>,
        <Sentry.ErrorBoundary key="copy-as-markdown" fallback={<div>Error loading CopyAsMarkdown</div>}>
            <CopyAsMarkdown uuid={uuid} onSelect={handleMenuClose} />
        </Sentry.ErrorBoundary>
    ];

    return (
        <Box ref={ref}>
            <MenuList dense sx={{ minWidth: "200px" }}>
                {menuItems}
            </MenuList>
        </Box>
    );
})