import React, { useState, useEffect, useRef } from 'react';
import { Box, Divider, Stack, Typography, IconButton, TextField } from '@mui/material';
import { useQuickAccessContext } from '../../Providers/QuickAccessProvider';
import { useTranslationContext } from '../../Providers/TranslationProvider';
import { Search, Close as CloseIcon } from '@mui/icons-material';
import userStore from '../../../core/stores/userStore';
import tabStore from '../../../core/stores/tabStore';
import ColorTypography from '../../component/ColorTypography';

const QuickListHeader = () => {
    const { t } = useTranslationContext();

    const { fontColor, searchQuery, setSearchQuery } = userStore((state) => ({
        fontColor: state.settings.fontColor,
        searchQuery: state.searchQuery,
        setSearchQuery: state.setSearchQuery
    }));

    const quickAccessCount = tabStore(state => state.quickAccess.length);

    const [isSearching, setIsSearching] = useState(false);
    const searchInputRef = useRef(null);

    useEffect(() => {
        if (isSearching) {
            searchInputRef.current?.focus();
        }
    }, [isSearching]);

    const handleSearchClick = () => setIsSearching(true);
    const handleCloseSearch = () => {
        setIsSearching(false);
        setSearchQuery('');
    };

    return (
        <Box sx={{ marginTop: "10px" }}>
            <Stack direction="row" justifyContent="space-between" alignItems="center" spacing={1} sx={{ paddingX: 2 }}>
                {isSearching || searchQuery ? (
                    <Stack direction="row" alignItems="center" spacing={1} sx={{ width: '100%' }}>
                        <TextField
                            size='small'
                            fullWidth
                            variant="outlined"
                            placeholder={t('searchByKeyword')}
                            value={searchQuery}
                            onChange={(e) => setSearchQuery(e.target.value)}
                            inputRef={searchInputRef}
                            InputProps={{
                                startAdornment: <Search sx={{ mr: 1, color: fontColor || 'text.primary' }} />,
                                endAdornment: <IconButton sx={{ color: fontColor || 'text.primary' }} onClick={handleCloseSearch}><CloseIcon /></IconButton>,
                                sx: {
                                    borderColor: fontColor || 'text.primary',
                                    color: fontColor || 'text.primary',
                                    '&::placeholder': {
                                        color: fontColor || 'text.primary',
                                        opacity: 1
                                    },
                                },
                            }}
                        />
                    </Stack>
                ) : (
                    <>
                        <ColorTypography variant='h4'>{t("quickAccess")} 〈{quickAccessCount}〉</ColorTypography>
                        <Stack direction="row" spacing={1}>
                            <IconButton sx={{ color: fontColor || 'text.primary' }} onClick={handleSearchClick}>
                                <Search />
                            </IconButton>
                        </Stack>
                    </>
                )}
            </Stack>
            <Divider sx={{ marginY: 1 }} />
        </Box>
    );
};

export default QuickListHeader;
