import React, { useState, useEffect, forwardRef } from "react";
import { Box, Stack, Tab, Tabs, Tooltip, Typography } from "@mui/material";
import * as Sentry from "@sentry/react";
import OpenTabs from "./Navs/OpenTabs";
import RecentlyTabs from "./Navs/RecentlyTabs";
import BrowserHistory from "./Navs/BrowserHistory";
import Bookmarks from "./Navs/Bookmarks";
import ReadingList from "./Navs/ReadingList";
import userStore from "../../../core/stores/userStore";
import QuickAccess from "./Navs/QuickAccess";
import { Tab as TabIcon } from "@mui/icons-material";
import { History } from "@mui/icons-material";
import { LibraryBooks } from "@mui/icons-material";
import { Bookmark } from "@mui/icons-material";
import { SwitchAccessShortcut } from "@mui/icons-material";
import { Delete } from "@mui/icons-material";
import { useTranslationContext } from "../../Providers/TranslationProvider";
import ColorTypography from "../../component/ColorTypography";
import { VerticalSplit } from "@mui/icons-material";
import { DynamicFeed } from "@mui/icons-material";



const TabNavs = forwardRef((props, ref) => {

    const { t } = useTranslationContext();

    const { updateSetting, activeTabIndex, navs, theme, showTabNavs, fontColor } = userStore(state => ({
        updateSetting: state.updateSetting,
        activeTabIndex: state.settings.activeTabIndex,
        navs: state.settings.tabNavOptions.navs.filter(nav => nav.enable === true),
        theme: state.settings.theme,
        showTabNavs: state.settings.showTabNavs,
        fontColor: state.settings.fontColor
    }));

    const actions = {
        OpenTabs: <OpenTabs key={"tab-nav-tabs"} />,
        RecentlyTabs: <RecentlyTabs key={"tab-nav-recentlyTabs"} />,
        history: <BrowserHistory key={"tab-nav-history"} />,
        readingList: <ReadingList key={"tab-nav-readingList"} />,
        bookmarks: <Bookmarks key={"tab-nav-bookmarks"} />,
        quickAccess: <QuickAccess key={"tab-nav-quickAccess"} />,

    };
    const icons = {
        OpenTabs: <DynamicFeed sx={{ color: fontColor || 'text.primary' }} key={'icon-tabs'} />,
        RecentlyTabs: <Delete sx={{ color: fontColor || 'text.primary' }} key={'icon-recently-tabs'} />,
        history: <History sx={{ color: fontColor || 'text.primary' }} key={"icon-history"} />,
        readingList: <LibraryBooks sx={{ color: fontColor || 'text.primary' }} key={"icon-readingList"} />,
        bookmarks: <Bookmark sx={{ color: fontColor || 'text.primary' }} key={"icon-bookmarks"} />,
        quickAccess: <SwitchAccessShortcut sx={{ color: fontColor || 'text.primary' }} key={"icon-quickAccess"} />,

    };

    console.log('TabNavs', navs);

    const handleChange = async (event, newValue) => {
        console.log('handleSelectTabChange', newValue);
        await updateSetting('activeTabIndex', newValue);
    };

    const renderNavs = navs.map((nav, index) => (

        <Tab
            component="div"
            key={nav.id}
            icon={<Tooltip title={<ColorTypography>{t(nav['name'])}</ColorTypography>}>{icons[nav.name]}</Tooltip>}
            iconPosition={navs.length <= 3 ? "start" : "top"}
            label={actions[nav.name]}
            value={nav.id}
            sx={{ minWidth: 0, minHeight: "0", padding: "10px" }}
            wrapped
        />

    ));

    if (!showTabNavs) {
        return null;
    }

    return (
        <Box ref={ref} sx={{ position: "relative", maxWidth: "100%" }}>
            {showTabNavs &&
                <>
                    <Tabs
                        component="div"
                        value={activeTabIndex}
                        onChange={handleChange}
                        variant="fullWidth"
                        scrollButtons="auto"
                        allowScrollButtonsMobile
                        sx={{ minHeight: "0" }}
                    >
                        {renderNavs}
                    </Tabs>
                    <Box sx={{ width: "100%", opacity: 0.5, position: "absolute", bottom: 0, height: '1px', background: "#ccc" }} />
                </>
            }
        </Box>
    )
});

export default TabNavs;
