import { useState, useEffect } from 'react';

const useBookmarkFolders = () => {
    const [folders, setFolders] = useState([]);

    useEffect(() => {
        const fetchFolders = () => {
            chrome.bookmarks?.getTree((bookmarkTreeNodes) => {
                const extractFolders = (nodes, level = 0, parentId = null) => {
                    let folders = [];
                    nodes.forEach((node) => {
                        if (node.children && !node.url && node.id) {
                            const childFolderCount = node.children.filter(child => child.children && !child.url).length;
                            folders.push({ id: node.id, title: node.title, level, childFolderCount, parentId });
                            folders = folders.concat(extractFolders(node.children, level + 1, node.id));
                        }
                    });
                    return folders;
                };
                setFolders(extractFolders(bookmarkTreeNodes));
            });
        };

        fetchFolders();
    }, []);

    return folders;
};

export default useBookmarkFolders;
