import React, { memo } from "react";
import Tooltip from "@mui/material/Tooltip";
import { IconButton, Typography } from "@mui/material";
import { DarkMode, LightMode } from "@mui/icons-material";
import { useTranslationContext } from "../Providers/TranslationProvider";
import { userStore } from "../../core/stores/userStore";
import { sendRuntimeMessage } from "../../core/browers/runtime";

const ThemeSwitch = () => {
  const { t } = useTranslationContext('translation');
  const { theme, fontColor, updateSetting } = userStore((state) => ({
    theme: state.settings.theme,
    fontColor: state.settings.fontColor,
    updateSetting: state.updateSetting,
  }));

  const modeConfig = (() => {
    switch (theme) {
      case "dark":
        return { title: t("DarkMode"), icon: <DarkMode /> };
      case "light":
      default:
        return { title: t("LightMode"), icon: <LightMode /> };
    }
  })();

  const handleChange = () => {
    const newTheme = theme === "dark" ? "light" : "dark";
    updateSetting("theme", newTheme);
    sendRuntimeMessage("update_setting", { key: "theme", value: newTheme });
  };

  return (
    <Tooltip arrow title={<Typography>{modeConfig.title}</Typography>}>
      <IconButton sx={{ color: fontColor || "text.primary" }} onClick={handleChange}>
        {modeConfig.icon}
      </IconButton>
    </Tooltip>
  );
};

export default memo(ThemeSwitch);
