import React, { memo } from "react";
import { MenuItem, ListItemText, Typography, ListItemIcon, SvgIcon } from "@mui/material";
import { useTranslationContext } from "../../../Providers/TranslationProvider";
import { copyTextToClipboard } from "../../../../utils/helpers";
import tabStore from "../../../../core/stores/tabStore";

const CopyTitle = ({ tabId, onSelect }) => {
    const { t } = useTranslationContext();

    const title = tabStore((state) => state.tabs.find(t => t.id === tabId)?.title);

    const handleClick = async () => {
        copyTextToClipboard(title);
        if (typeof onSelect === 'function') {
            onSelect();
        }
    };

    return (
        <MenuItem onClick={handleClick}>
            <ListItemIcon>
                <SvgIcon>
                    <svg xmlns="http://www.w3.org/2000/svg" height="20px" viewBox="0 -960 960 960" width="20px" fill="currentColor">
                        <path d="M360-240q-29.7 0-50.85-21.15Q288-282.3 288-312v-480q0-29.7 21.15-50.85Q330.3-864 360-864h384q29.7 0 50.85 21.15Q816-821.7 816-792v480q0 29.7-21.15 50.85Q773.7-240 744-240H360Zm0-72h384v-480H360v480ZM216-96q-29.7 0-50.85-21.15Q144-138.3 144-168v-552h72v552h456v72H216Zm144-216v-480 480Z" />
                    </svg>
                </SvgIcon>
            </ListItemIcon>
            <ListItemText>
                <Typography variant="body1">{t("tabMenu.CopyTitle")}</Typography>
            </ListItemText>
        </MenuItem>
    );
};

export default memo(CopyTitle);