import React, { memo } from 'react';
import userStore from '../../../../core/stores/userStore';
import { SwitchAccessShortcut } from '@mui/icons-material';
import DrawerAction from './DrawerAction';
import QuickAccess from '../../quickAccess';

const ActionQuickAccess = () => {
    const showLabel = userStore(state => state.settings.bottomNavOptions.showLabel && state.settings.bottomNavOptions.navs.find(nav => nav.name === 'quickAccess')?.showLabel);

    return (
        <React.Fragment>
            <DrawerAction
                icon={<SwitchAccessShortcut />}
                labelKey='quickAccess'
                ChildComponent={(props) => <QuickAccess {...props} addBackGround={true} />}
                showLabel={showLabel}
            />
        </React.Fragment>
    );
};

export default memo(ActionQuickAccess);