import React from "react";
import { Card, CardContent, Typography } from "@mui/material";
import userStore from "../../../core/stores/userStore";
import { decodedUrl } from "../../../utils/helpers";



export default function LinkPreview({ title, url }) {
    const { fontWeight } = userStore((state) => ({
        fontWeight: state.settings.fontWeight,
    }))

    return (
        <Card sx={{
            padding: 0, minWidth: "300px",
        }}>
            <CardContent sx={{ padding: "8px" }}>
                <Typography gutterBottom variant="h3" color="text.primary" component="div" sx={{
                    overflow: "hidden",
                    textOverflow: "ellipsis",
                    display: "-webkit-box",
                    WebkitLineClamp: "2",
                    fontWeight: fontWeight,
                    WebkitBoxOrient: "vertical", margin: " 8px 0"
                }}>
                    {title}
                </Typography>
                <Typography variant="h4" color="text.secondary" sx={{ whiteSpace: "pre-wrap", wordWrap: "break-word", wordBreak: "break-all", textWrap: "break-word", overflow: "hidden", textOverflow: "ellipsis", display: "-webkit-box", WebkitLineClamp: "3", WebkitBoxOrient: "vertical" }}>
                    {decodedUrl(url)}
                </Typography>

            </CardContent>

        </Card>
    )
}