import React from 'react';
import { ListItemIcon, ListItemText, MenuItem, SvgIcon, Typography } from '@mui/material';
import { countBookmarkUrls } from '../../../../utils/helpers';
import { useTranslationContext } from '../../../Providers/TranslationProvider';

const OpenAllInNewWindow = ({ folder, handleClose }) => {

    const { t } = useTranslationContext();

    const handleClick = () => {
        const urls = [];

        const collectUrls = (bookmark) => {
            if (bookmark.url) {
                urls.push(bookmark.url);
            } else if (bookmark.children) {
                bookmark.children.forEach(child => collectUrls(child));
            }
        };

        collectUrls(folder);

        if (urls.length > 0) {
            chrome.windows.create({ url: urls });
        }

        handleClose();
    };

    return (
        <MenuItem dense onClick={handleClick}>
            <ListItemIcon>
                <SvgIcon>
                    <svg xmlns="http://www.w3.org/2000/svg" height="24px" viewBox="0 -960 960 960" width="24px" fill="currentColor"><path d="M200-120q-33 0-56.5-23.5T120-200v-560q0-33 23.5-56.5T200-840h240v80H200v560h560v-240h80v240q0 33-23.5 56.5T760-120H200Zm440-400v-120H520v-80h120v-120h80v120h120v80H720v120h-80Z" /></svg>
                </SvgIcon>
            </ListItemIcon>
            <ListItemText>
                <Typography variant="body1">{t('openAllInNewWindow', { count: countBookmarkUrls(folder) })}</Typography>
            </ListItemText>
        </MenuItem>
    );
};

export default OpenAllInNewWindow;
