import React, { memo } from "react";
import { IconButton } from "@mui/material";
import { PlayArrow, Pause } from "@mui/icons-material";
import { progressBarStore } from "../../../core/stores/progressBarStore";
import { sendTabMessage } from "../../../core/browers/runtime";
import { useSettingsContext } from "../../Providers/SettingsProvider";

const PlayPauseButton = ({ tabId }) => {

    const { fontColor } = useSettingsContext();

    const paused = progressBarStore(state => {
        const playData = state.playingProgress.find(t => t.tabId === tabId);
        return playData?.paused;
    });

    const handlePlayVideo = (event) => {
        event.preventDefault();
        event.stopPropagation();
        sendTabMessage(tabId, 'play_video');
    };

    const handlePauseVideo = (event) => {
        event.preventDefault();
        event.stopPropagation();
        sendTabMessage(tabId, 'pause_video');
    };

    return (
        <IconButton
            sx={{ padding: "5px", color: fontColor || 'text.primary' }}
            onClick={paused ? handlePlayVideo : handlePauseVideo}
        >
            {paused ? <PlayArrow /> : <Pause />}
        </IconButton>
    );
};

export default memo(PlayPauseButton);