import React, { memo } from 'react';
import userStore from '../../../../core/stores/userStore';
import Action from './Action';
import { ViewQuilt, ViewList } from '@mui/icons-material';
import { sendRuntimeMessage } from '../../../../core/browers/runtime';

const ActionViewModes = () => {
    const { showLabel, updateSetting, view } = userStore(state => ({
        showLabel: state.settings.bottomNavOptions.showLabel && state.settings.bottomNavOptions.navs.find(nav => nav.name === 'viewModes')?.showLabel,
        updateSetting: state.updateSetting,
        view: state.settings.view,
    }));

    const handleClick = () => {
        const newView = view === 'visual' ? 'list' : 'visual';
        updateSetting("view", newView);
        sendRuntimeMessage("update_setting", { key: "view", value: newView });
    };

    return (
        <React.Fragment>
            <Action
                icon={view === 'visual' ? <ViewQuilt /> : <ViewList />}
                labelKey={view === 'visual' ? "view.Compact" : "view.Visual"}
                showLabel={showLabel}
                handleClick={handleClick}
            />
        </React.Fragment>
    );
};

export default memo(ActionViewModes);