import React, { useState, useRef, memo } from 'react';
import { IconButton, Popper, Slider, Tooltip, Typography, Box } from "@mui/material";
import { VolumeUp, VolumeOff } from "@mui/icons-material";
import userStore from '../../../core/stores/userStore';
import { progressBarStore } from '../../../core/stores/progressBarStore';
import { sendTabMessage } from '../../../core/browers/runtime';
import { memoryStore } from '../../../utils/memoryStore';

const VolumeControl = ({ tabId }) => {
    const { theme, fontColor } = userStore((state) => ({
        theme: state.settings.theme,
        fontColor: state.settings.fontColor
    }));
    const playData = progressBarStore(state => state.playingProgress.find(t => t.tabId === tabId));

    const setPlayingProgress = progressBarStore(state => state.setPlayingProgress);

    const [volumeAnchorEl, setVolumeAnchorEl] = useState(null);
    const [volumeTooltip, setVolumeTooltip] = useState("");

    const volumeOpen = Boolean(volumeAnchorEl);

    const handleVolumeOpen = (event) => {
        setVolumeAnchorEl(event.currentTarget);
    };

    const handleVolumeClose = () => {
        setVolumeAnchorEl(null);
    };

    const handleVolumeMouseMove = (event) => {
        event.preventDefault();
        event.stopPropagation();
        const rect = event.currentTarget.getBoundingClientRect();
        const mouseRelativeY = rect.bottom - event.clientY;
        const volume = ((mouseRelativeY / rect.height) * 100 - 1).toFixed(0);
        if (volume > 100 || volume < 0) {
            return;
        }
        setVolumeTooltip(volume + "%");
    };

    const handleVolumeChanged = (event, value) => {
        if (playData) {
            event.preventDefault();
            event.stopPropagation();
            memoryStore.set(`playing_progress_bar_locked_${tabId}`, playData.currentTime);
            setPlayingProgress(tabId, { ...playData, volume: value });
        }
    };

    const handleVolumeChangeCommitted = (event, value) => {
        if (playData) {
            event.preventDefault();
            event.stopPropagation();
            memoryStore.remove(`playing_progress_bar_locked_${tabId}`);
            sendTabMessage(tabId, 'volume_change', { value: value });
        }
    };

    const handleSwitchMuteVideo = (event) => {
        event.preventDefault();
        event.stopPropagation();
        if (playData) {
            sendTabMessage(tabId, 'video_switch_mute');
        }
    };

    const sliderStyles = {
        color: theme === 'dark' ? '#fff' : 'rgba(0,0,0,0.87)',
        '& .MuiSlider-thumb': {
            width: 6,
            height: 6,
            transition: '0.3s cubic-bezier(.47,1.64,.41,.8)',
            '&::before': {
                boxShadow: '0 2px 12px 0 rgba(0,0,0,0.4)',
            },
            '&:hover, &.Mui-focusVisible': {
                boxShadow: `0px 0px 0px 8px ${theme === 'dark' ? 'rgb(255 255 255 / 16%)' : 'rgb(0 0 0 / 16%)'}`,
            },
            '&.Mui-active': {
                width: 8,
                height: 8,
            },
        },
        '& .MuiSlider-rail': {
            opacity: 0.28,
        },
    };

    return (
        <Box onMouseLeave={handleVolumeClose}>
            <IconButton
                sx={{ padding: "5px", color: fontColor || 'text.primary' }}
                aria-owns={volumeOpen ? `mouse-over-volume-${tabId}` : undefined}
                onMouseEnter={handleVolumeOpen}
                onClick={handleSwitchMuteVideo}
            >
                {playData.volume === 0 || playData.muted === true ? <VolumeOff /> : <VolumeUp />}
            </IconButton>
            <Popper
                id={`mouse-over-volume-${tabId}`}
                open={volumeOpen}
                anchorEl={volumeAnchorEl}
                sx={{ padding: "20px 20px 10px", background: theme === 'dark' ? 'rgba(0,0,0,0.87)' : '#fff', borderRadius: '4px' }}
                anchororigin={{
                    vertical: 'center',
                    horizontal: 'center',
                }}
                transformorigin={{
                    vertical: 'center',
                    horizontal: 'center',
                }}
                onClose={handleVolumeClose}
                onMouseEnter={(event) => { event.stopPropagation(); }}
            >
                <Tooltip placement="right" title={<Typography>{volumeTooltip}</Typography>} followCursor>
                    <Slider
                        value={playData.volume}
                        min={0}
                        max={1}
                        step={0.01}
                        size="small"
                        orientation="vertical"
                        onMouseMove={handleVolumeMouseMove}
                        onChange={(event, value) => handleVolumeChanged(event, value)}
                        onChangeCommitted={(event, value) => handleVolumeChangeCommitted(event, value)}
                        onClick={(event) => { event.stopPropagation(); }}
                        valueLabelFormat={(value) => `${Math.floor(value * 100)}%`}
                        sx={{
                            padding: 0,
                            width: "1px",
                            height: "120px",
                            ...sliderStyles
                        }}
                    />
                </Tooltip>
            </Popper>
        </Box>
    );
};

export default memo(VolumeControl);