import React, { useCallback, useState } from 'react';
import { DndContext, useSensor, useSensors, PointerSensor, TouchSensor, MouseSensor, closestCenter, DragOverlay } from '@dnd-kit/core';
import { SortableContext, sortableKeyboardCoordinates, arrayMove, rectSortingStrategy, verticalListSortingStrategy } from '@dnd-kit/sortable';
import { restrictToVerticalAxis } from '@dnd-kit/modifiers';
import BookmarkFolder from './BookmarkFolder';
import BookmarkTreeNode from './BookmarkTreeNode';

const SortableContainer = ({ initialItems, children }) => {
    const [items, setItems] = useState(initialItems);
    const [activeId, setActiveId] = useState(null);
    const [activeType, setActiveType] = useState(null);

    const sensors = useSensors(
        useSensor(PointerSensor, { activationConstraint: { distance: 10 } }),
        useSensor(TouchSensor, { activationConstraint: { distance: 10 } }),
        useSensor(MouseSensor, { activationConstraint: { distance: 10 } })
    );

    console.log("SortableContainer", { initialItems });

    const handleDragStart = (event) => {
        console.log("handleDragStart", event);
        setActiveId(event.active.id);
        setActiveType(event.active.data.current.type);
    };

    const handleDragEnd = useCallback((event) => {
        console.log("handleDragEnd", activeType, event);

        const { active, over } = event;
        if (!active || !over || active.id === over.id) return;

        const activeItem = items.find(item => item.id === active.id);
        const overItem = items.find(item => item.id === over.id);
        console.log("drop info", { activeItem, overItem });

        // chrome.bookmarks.move(activeItem.id, { index: overItem.index, parentId: overItem.parentId });
        setActiveId(null);
    }, [items]);

    const handleDragCancel = () => {
        setActiveId(null);
    };

    return (
        <DndContext
            sensors={sensors}
            collisionDetection={closestCenter}
            modifiers={[restrictToVerticalAxis]}
            onDragStart={handleDragStart}
            onDragEnd={handleDragEnd}
            onDragCancel={handleDragCancel}
            onDragOver={e => console.log("onDragOver", e)}
        >
            {/* <SortableContext items={items} strategy={verticalListSortingStrategy}> */}
            {children}
            {/* </SortableContext> */}
            <DragOverlay>
                {activeId && activeType ? (
                    activeType === 'bookmark' ? <BookmarkTreeNode bookmarkId={activeId} /> : <BookmarkFolder bookmark={items.find(item => item.id === activeId)} />

                ) : null}
            </DragOverlay>
        </DndContext>
    );
};

export default SortableContainer;
