import React, { memo } from "react";
import { Box, ListItemText, Typography } from "@mui/material";
import LinkUrl from "./LinkUrl";
import TimeAgo from "./LinkTimeAgo";
import userStore from "../../../core/stores/userStore";
import HighlightedText from "../HighlightedText";


export default memo(function LinkTitle({ title, url, lastAccessed, searchText }) {

    const { view, theme, fontWeight, fontColor, showFullUrl } = userStore((state) => ({
        view: state.settings.view,
        theme: state.settings.theme,
        fontWeight: state.settings.fontWeight,
        fontColor: state.settings.fontColor,
        showFullUrl: state.settings.showFullUrl,
    }))

    return (

        <Box sx={{ minWidth: 0, flex: 1 }}>
            <Typography
                variant="h4"
                noWrap
                sx={{
                    paddingTop: "4px",
                    paddingBottom: "4px",
                    fontWeight: fontWeight,
                    color: theme === "dark" ? "text.primary" : fontColor || "text.primary",
                }}
            >
                <HighlightedText text={title} searchText={searchText} />

            </Typography>

            {view === "list" && searchText && <LinkUrl url={url} searchText={searchText} />}


            {view === "visual" && (showFullUrl ? <LinkUrl url={url} searchText={searchText} /> : <TimeAgo url={url} lastAccessed={lastAccessed} />)}
        </Box>


    )
})