import React, { useState, useEffect } from 'react';
import { BottomNavigationAction, Drawer, Tooltip, Typography } from '@mui/material';
import { useTranslation } from 'react-i18next';
import PermissionRequired from './PermissionRequired';
import userStore from '../../../../core/stores/userStore';
import Action from './Action';

const DrawerAction = ({ icon, labelKey, permissionName, ChildComponent, showLabel }) => {
    const { t } = useTranslation();
    const { theme, fontColor } = userStore(state => ({
        theme: state.settings.theme,
        fontColor: state.settings.fontColor
    }));

    const [open, setOpen] = useState(false);
    const [permission, setPermission] = useState(false);

    useEffect(() => {
        if (permissionName) {
            chrome.permissions.contains({ permissions: [permissionName] }).then(result => {
                setPermission(result);
            });

            const handlePermissionAdded = (permissions) => {
                if (permissions.permissions.includes(permissionName)) {
                    setPermission(true);
                }
            };

            chrome.permissions.onAdded.addListener(handlePermissionAdded);

            return () => {
                chrome.permissions.onAdded.removeListener(handlePermissionAdded);
            };
        }
    }, [permissionName]);

    const handleClick = () => {
        setOpen(true);
    };

    const handleClose = () => {
        setOpen(false);
    };

    const updatePermission = (granted) => {
        setPermission(granted);
    };

    return (
        <>
            <Action icon={icon} labelKey={labelKey} showLabel={showLabel} handleClick={handleClick} />
            <Drawer
                open={open}
                onClose={handleClose}
                anchor="bottom"
                PaperProps={{ sx: { maxHeight: "70vh", minHeight: "70vh" } }}
            >
                {permissionName && !permission ? (
                    <PermissionRequired permission={permissionName} updatePermission={updatePermission} />
                ) : (
                    ChildComponent && <ChildComponent onSelect={handleClose} />
                )}
            </Drawer>
        </>
    );
};

export default DrawerAction;