import React, { useState, useEffect } from 'react';
import { useTranslation } from 'react-i18next';
import userStore from '../../../../core/stores/userStore';
import { BottomNavigationAction, Menu, MenuList, Tooltip, Typography } from '@mui/material';
import { MoreHoriz } from '@mui/icons-material';
import TabAdd from '../../../component/TabAdd';
import SortTabsByDomain from '../moreMenu/SortTabsByDomain';
import DiscardInactiveTabs from '../moreMenu/DiscardInactiveTabs';
import CloseAllDiscardedTabs from '../moreMenu/CloseAllDiscardedTabs';
import GroupTabsByDomain from '../moreMenu/GroupTabsByDomain';
import UngroupAll from '../moreMenu/UngroupAll';
import RemoveDuplicateTabs from '../moreMenu/RemoveDuplicateTabs';
import Reload from '../moreMenu/Reload';
import Action from './Action';
import SortTabsByUrl from '../moreMenu/SortTabsByUrl';

const ActionMore = () => {
    const showLabel = userStore(state => state.settings.bottomNavOptions.showLabel && state.settings.bottomNavOptions.navs.find(nav => nav.name === 'more')?.showLabel);
    const [menuEl, setMenuEl] = useState(null);

    const handleClick = (event) => {
        setMenuEl(event.currentTarget);
    };

    const handleClose = () => {
        setMenuEl(null);
    };

    useEffect(() => {
        const handleWindowBlur = () => {
            handleClose();
        };

        window.addEventListener('blur', handleWindowBlur);
        return () => {
            window.removeEventListener('blur', handleWindowBlur);
        };
    }, []);

    return (
        <React.Fragment>
            <Action icon={<MoreHoriz />} labelKey="More" showLabel={showLabel} handleClick={handleClick} />
            <Menu
                id="nav-bottom-more-menu"
                anchorEl={menuEl}
                open={Boolean(menuEl)}
                onClose={handleClose}
                MenuListProps={{ "aria-labelledby": "nav-bottom-more-button" }}
            >
                <MenuList dense>
                    <TabAdd shortcut={true} onSelect={handleClose} />
                    <SortTabsByDomain onSelect={handleClose} />
                    <SortTabsByUrl onSelect={handleClose} />
                    <DiscardInactiveTabs onSelect={handleClose} />
                    <CloseAllDiscardedTabs onSelect={handleClose} />
                    <GroupTabsByDomain onSelect={handleClose} />
                    <UngroupAll onSelect={handleClose} />
                    <RemoveDuplicateTabs onSelect={handleClose} />
                    <Reload onSelect={handleClose} />
                </MenuList>
            </Menu>
        </React.Fragment>
    )
}

export default ActionMore;