import React, { memo, useState, useEffect } from 'react';
import userStore from '../../../../core/stores/userStore';
import tabStore from '../../../../core/stores/tabStore';
import Action from './Action';
import { VolumeOff, VolumeUp } from '@mui/icons-material';

const ActionMuteAllTabs = () => {
    const showLabel = userStore(state => state.settings.bottomNavOptions.showLabel && state.settings.bottomNavOptions.navs.find(nav => nav.name === 'muteAllTabs')?.showLabel);
    const soundTabs = tabStore(state => state.tabs.filter(tab => tab.audible));

    const [muted, setMuted] = useState(() => soundTabs.every(tab => tab.mutedInfo.muted));

    const handleClick = async () => {
        for (const tab of soundTabs) {
            await chrome.tabs.update(tab.id, { muted: !muted });
        }
        setMuted(prevMuted => !prevMuted);
    };

    return (
        <React.Fragment>
            <Action icon={muted ? <VolumeUp /> : <VolumeOff />} labelKey={muted ? "" : "MuteAllTabsMakingSounds"} showLabel={showLabel} handleClick={handleClick} />
        </React.Fragment>
    );
};

export default memo(ActionMuteAllTabs);