import React from "react";
import { filterTabs, scrollToActiveTab } from "../../utils/helpers";
import { isHotkeyPressed, useHotkeys } from "react-hotkeys-hook";
import { tabStore } from "../../core/stores/tabStore";
import { userStore } from "../../core/stores/userStore";
import { useEffect } from "react";
import { setTabActive } from "../../core/browers/tabs";
import { getHistories, historyStore } from "../../core/stores/historyStore";
import { sendRuntimeMessage } from "../../core/browers/runtime";
import { selectStore } from "../../core/stores/selectStore";
import { delay, get } from "lodash-es";
import { getCurrentWindow } from "../../core/browers/windows";
import { toast } from "react-toastify";

const currentWindow = await getCurrentWindow();

export default function Hotkeys() {



    const { activeTabIndex, PinnedTabsAsIcons, searchText, orderBy, sortBy, historyDataSource, tabSearchShowHistory, onlyCurrentWindowTabs, setSearchText } = userStore((state) => ({
        activeTabIndex: state.settings.activeTabIndex,
        PinnedTabsAsIcons: state.settings.PinnedTabsAsIcons,
        searchText: state.searchText,
        orderBy: state.settings.orderBy,
        sortBy: state.settings.sortBy,
        historyDataSource: state.settings.historyDataSource,
        tabSearchShowHistory: state.settings.tabSearchShowHistory,
        onlyCurrentWindowTabs: state.settings.onlyCurrentWindowTabs,
        setSearchText: state.setSearchText

    }));
    // const currentWindow = tabStore(state => state.currentWindow)

    const { tabCount, keyboardSelected, tabs, keyItems, activeTabId } = tabStore(state => ({
        tabCount: state.tabs.length,
        keyboardSelected: state.keyboardSelected,
        tabs: state.tabs
            .filter(tab => onlyCurrentWindowTabs ? tab.windowId === currentWindow?.id : true)
            .filter(tab => PinnedTabsAsIcons ? tab.pinned === false : true)
            .map(tab => ({ id: tab.id, url: tab.url })),

        keyItems: state.keyItems,
        activeTabId: state.activeTabId
    }));






    const { waitForSelectTab, setWaitForSelectTab, hasSelectedTabIds, popupPanelActived } = selectStore((state) => ({
        waitForSelectTab: state.waitForSelectTab,
        setWaitForSelectTab: state.setWaitForSelectTab,
        hasSelectedTabIds: state.selectedTabIds.length > 0,
        popupPanelActived: state.popupPanelActived
    }))


    const getHistoryItems = async (searchText) => {
        const loadHistories = await getHistories(historyDataSource);
        return filterTabs(loadHistories, searchText, 'desc', 'closed_time').map(cur => ({
            type: "history",
            key: cur.urlMD5,
            url: cur.url
        }));
    }


    const initList = async () => {
        let lists = [];
        if (activeTabIndex <= 1) {
            lists = filterTabs(tabs, searchText, orderBy, sortBy, currentWindow.id).reduce((acc, cur, idx) => {
                return [...acc, { type: "tab", key: cur.id, url: cur.url }];
            }, []);
            if (searchText && tabSearchShowHistory) {
                lists = [...lists, ... (await getHistoryItems(searchText)).slice(0, 10)]
            }
        }
        if (activeTabIndex === 3) {
            lists = await getHistoryItems(searchText);
        }

        return lists
    }

    useEffect(() => {
        initList().then((items) => {
            tabStore.setState({ keyItems: items })
        })
        console.log('keyItems', keyItems)
    }, [activeTabIndex, PinnedTabsAsIcons, searchText, orderBy, sortBy, historyDataSource, tabCount])


    useHotkeys("ArrowUp", (event) => {
        if (popupPanelActived) {
            return;
        }
        event.preventDefault();
        event.stopPropagation();
        const selectIndex = keyItems.findIndex(item => item.key === (keyboardSelected?.key || activeTabId)) || 0;
        const nextIndex = selectIndex - 1 < 0 ? keyItems.length - 1 : selectIndex - 1;
        tabStore.setState({ keyboardSelected: keyItems[nextIndex] })

    }, { enableOnFormTags: ["TEXTAREA", "INPUT"] })
    useHotkeys("ArrowDown", (event) => {
        if (popupPanelActived) {
            return;
        }
        event.preventDefault();
        event.stopPropagation();
        const selectIndex = keyItems.findIndex(item => item.key === (keyboardSelected?.key || activeTabId)) || 0;
        const nextIndex = selectIndex + 1 > keyItems.length - 1 ? 0 : selectIndex + 1;

        tabStore.setState({ keyboardSelected: keyItems[nextIndex] })
    }, { enableOnFormTags: ["TEXTAREA", "INPUT"] })

    useHotkeys("Enter", async (event) => {
        if (popupPanelActived) {
            return;
        }
        event.preventDefault();
        event.stopPropagation();
        if (keyItems.length === 1) {
            tabStore.setState({ keyboardSelected: keyItems[0] })
        }
        if (keyboardSelected) {
            tabStore.setState({ keyboardSelected: null })
            userStore.setState({ searchText: "" })
            if (keyboardSelected.type === "tab") {
                await setTabActive(keyboardSelected.key)
            }
            if (keyboardSelected.type === "history") {
                await chrome.tabs.create({ url: keyboardSelected.url, active: true });
                await historyStore.getState().deleteHistory(keyboardSelected.key);
                await userStore.getState().updateSetting('activeTabIndex', 0)
                await sendRuntimeMessage("update_setting", { key: "activeTabIndex", value: 0 })
            }

        } else if (searchText) {
            chrome.permissions.contains({ permissions: ["search"] }, async (granted) => {
                if (!granted) {
                    chrome.permissions.request({ permissions: ["search"] }, async (granted) => {
                        if (!granted) {
                            toast.error(t("settings.history.dataSource.deniedMessage"));
                        } else {
                            chrome.search.query({ disposition: "NEW_TAB", text: searchText })
                            setSearchText("");
                            delay(scrollToActiveTab, 500);

                        }
                    })
                } else {

                    chrome.search.query({ disposition: "NEW_TAB", text: searchText })
                    setSearchText("");
                    delay(scrollToActiveTab, 500);

                }
            })
        }
    }, { enableOnFormTags: ["TEXTAREA", "INPUT"] })

    useHotkeys("Shift", (event) => {
        if (popupPanelActived) {
            return;
        }
        if (event.type === "keydown" && isHotkeyPressed('shift') && !waitForSelectTab) {
            setWaitForSelectTab(true);
        }
        if (event.type === 'keyup' && !hasSelectedTabIds) {
            setWaitForSelectTab(false);
        }
    }, {
        enableOnFormTags: ["TEXTAREA", "INPUT"],
        keyup: true,
        keydown: true

    })

    useHotkeys("esc", (event) => {
        selectStore.setState({ popupPanelActived: false })
        if (keyboardSelected) {
            tabStore.setState({ keyboardSelected: null })
        }
        setWaitForSelectTab(false);
    }, { enableOnFormTags: ["TEXTAREA", "INPUT"] })

    return <div></div>
}
