import React, { memo, useState } from "react";
import { MenuItem, ListItemText, Typography, ListItemIcon, Tooltip, Stack } from "@mui/material";
import { useTranslationContext } from "../../../Providers/TranslationProvider";
import tabStore from "../../../../core/stores/tabStore";
import { MonitorHeart, Help } from "@mui/icons-material";
import { sendRuntimeMessage } from "../../../../core/browers/runtime";

const KeepTabActive = ({ tabId, onSelect }) => {
    const { t } = useTranslationContext();
    const [showTooltip, setShowTooltip] = useState(false);

    const autoDiscardable = tabStore((state) => state.tabs.find(tab => tab.id === tabId)?.autoDiscardable);

    const handleClick = async () => {
        chrome.tabs.update(tabId, { autoDiscardable: false });
        sendRuntimeMessage("page_keep_alive", { tabId: tabId });
        if (typeof onSelect === 'function') {
            onSelect();
        }
    };

    if (!autoDiscardable) {
        return null;
    }

    return (
        <MenuItem
            onClick={handleClick}
            onMouseEnter={() => setShowTooltip(true)}
            onMouseLeave={() => setShowTooltip(false)}
        >
            <ListItemIcon>
                <MonitorHeart />
            </ListItemIcon>
            <ListItemText>
                <Stack direction="row" alignItems="center" justifyContent="space-between">
                    <Typography variant="body1">{t("tabMenu.KeepTabActive")}</Typography>
                    {showTooltip && <Tooltip
                        arrow
                        placement="top"
                        title={<Typography>{t("tabMenu.KeepTabActiveDesc")}</Typography>}

                    >
                        <Help fontSize="small" color="secondary" sx={{ opacity: '0.5' }} />
                    </Tooltip>
                    }
                </Stack>
            </ListItemText>
        </MenuItem>
    );
};

export default memo(KeepTabActive);