import React from 'react';
import { ListItemIcon, ListItemText, MenuItem, SvgIcon, Typography } from '@mui/material';
import { FileCopy } from '@mui/icons-material'; // Import the FileCopy icon
import { useTranslationContext } from '../../../../Providers/TranslationProvider';

const CopyMarkdown = ({ item, handleClose }) => {

    const { t } = useTranslationContext();


    const handleClick = () => {
        const markdown = `[${item.title}](${item.url})`;
        navigator.clipboard.writeText(markdown);
        handleClose();
    };

    return (
        <MenuItem dense onClick={handleClick}>
            <ListItemIcon>
                <SvgIcon>
                    <svg xmlns="http://www.w3.org/2000/svg" height="20px" viewBox="0 -960 960 960" width="20px" fill="currentColor">
                        <path d="M360-240q-29.7 0-50.85-21.15Q288-282.3 288-312v-480q0-29.7 21.15-50.85Q330.3-864 360-864h384q29.7 0 50.85 21.15Q816-821.7 816-792v480q0 29.7-21.15 50.85Q773.7-240 744-240H360Zm0-72h384v-480H360v480ZM216-96q-29.7 0-50.85-21.15Q144-138.3 144-168v-552h72v552h456v72H216Zm216-336h48v-192h48v144h44v-144h52v192h48v-208q0-13.6-9-22.8-9-9.2-21-9.2H462q-12 0-21 9.2t-9 22.8v208Zm-72 120v-480 480Z" />
                    </svg>
                </SvgIcon>
            </ListItemIcon>
            <ListItemText>
                <Typography variant="body1">{t('tabMenu.CopyAsMarkdown')}</Typography>
            </ListItemText>
        </MenuItem>
    );
};

export default CopyMarkdown;
