import React, { useEffect } from "react";
import { Box, Stack, Typography } from "@mui/material";
import { useTranslationContext } from "../../../Providers/TranslationProvider";
import tabStore from "../../../../core/stores/tabStore";
import userStore from "../../../../core/stores/userStore";
import ColorTypography from "../../../component/ColorTypography";


const OpenTabs = () => {
    const { t } = useTranslationContext();

    const { navSetting } = userStore((state) => ({
        navSetting: state.settings.tabNavOptions.navs.find(nav => nav.name === 'OpenTabs')
    }));

    const tabCount = tabStore((state) => state.tabs.length);
    const initTabs = tabStore((state) => state.initTabs);

    useEffect(() => {
        if (tabCount === 0) {
            initTabs();
        }
    }, [tabCount, initTabs]);

    return (
        navSetting?.showCount ? <ColorTypography>〈{tabCount}〉</ColorTypography> : null
    );
};

export default OpenTabs;
