import { useEffect, useRef } from "react";
import { toast } from "react-toastify";
import snapshotStore, { getCachedWindows, removeCachedWindows } from "../../../core/stores/snapshotStore";
import { useTranslationContext } from "../../Providers/TranslationProvider";

const ReopenSnapshotToast = () => {
    const { t } = useTranslationContext();
    const hasCheckedRef = useRef(false);

    const checkReopenSnapshot = async () => {
        if (hasCheckedRef.current) return;
        hasCheckedRef.current = true;

        const cachedWindows = await getCachedWindows();
        if (cachedWindows) {
            console.log("cachedWindows", cachedWindows);
            // await snapshotStore.getState().addSnapshot(cachedWindows);
            await removeCachedWindows();
            const openTabs = await chrome.tabs.query({ windowType: "normal", pinned: false });
            const openWindows = new Set(openTabs.map(tab => tab.windowId)).size;
            if (openTabs.length < 5 && cachedWindows.tabCount >= 3 && openWindows === 1) {
                const toastId = toast.info(
                    t("reopenSnapshotMessage", { windowCount: cachedWindows.windowCount, tabCount: cachedWindows.tabCount }),
                    {
                        position: "bottom-center",
                        autoClose: false,
                        closeOnClick: true,
                        theme: "colored",
                        onClick: async () => {
                            const extensionUrl = chrome.runtime.getURL("/options.html#/snapshots");
                            await chrome.tabs.create({ url: extensionUrl, active: true });
                            toast.dismiss(toastId);
                        },
                    }
                );
            }
        }
    };

    useEffect(() => {
        checkReopenSnapshot();
    }, []);

    return null;
};

export default ReopenSnapshotToast;
