import React, { useState, useEffect, useRef } from "react";
import { List, Collapse, Stack, Button, Typography, Divider, ListItem, Menu, Box } from "@mui/material";
import { ArrowDropDown, ArrowRight, ArrowLeft } from "@mui/icons-material";
import TabItem from "./TabItem";
import TabGroupDomainMenu from "./TabGroupDomainMenu";
import TabAdd from "../../component/TabAdd";
import { filterTabs, getHostnameFromUrl } from "../../../utils/helpers";
import { getCustomColor } from "../../../utils/colors";
import { crc32 } from "../../../utils/crypto";
import { useWindowsEvents } from "../../eventHandlers/useWindowsEvents";
import { tabStore } from "../../../core/stores/tabStore";
import { userStore } from "../../../core/stores/userStore";
import { siteStore } from "../../../core/stores/siteStore";
import { useTranslationContext } from "../../Providers/TranslationProvider";
import SiteAvatars from "./SiteAvatars";
import GroupHeader from "./groups/GroupHeader";

const TabGroupDomain = React.memo(({ hostname, index }) => {
  const { t } = useTranslationContext();
  const { menuTarget, setMenuTarget } = useWindowsEvents();

  const group = { id: index, title: hostname };

  const {
    searchText,
    orderBy,
    sortBy,
    buttonPosition,
    colorfulBackground,
    GroupCollapsedShowIcon,
    PinnedTabsAsIcons,
    onlyCurrentWindowTabs,
    groupExpanded
  } = userStore((state) => ({
    searchText: state.searchText,
    orderBy: state.settings.orderBy,
    sortBy: state.settings.sortBy,
    buttonPosition: state.settings.buttonPosition,
    colorfulBackground: state.settings.colorfulBackground,
    GroupCollapsedShowIcon: state.settings.GroupCollapsedShowIcon,
    PinnedTabsAsIcons: state.settings.PinnedTabsAsIcons,
    onlyCurrentWindowTabs: state.settings.onlyCurrentWindowTabs,
    groupExpanded: state.settings.groupExpanded
  }));

  const currentWindow = tabStore((state) => state.currentWindow);

  const tabs = tabStore((state) => state.tabs
    .filter((tab) => (onlyCurrentWindowTabs ? tab.windowId === currentWindow.id : true))
    .filter((tab) => (PinnedTabsAsIcons ? !tab.pinned : true))
    .filter((tab) => getHostnameFromUrl(tab.url) === hostname)
  );

  const siteConfig = siteStore((state) => state.sites.find((s) => s.hostname === hostname));

  const filteredTabs = filterTabs(tabs, searchText, orderBy, sortBy, currentWindow.id);

  const tabIds = filteredTabs.map((tab) => tab.id);
  group.tabIds = tabIds;

  const [expanded, setExpanded] = useState(groupExpanded);
  const [contextMenu, setContextMenu] = useState(null);
  const [contextMenuAnchorEl, setContextMenuAnchorEl] = useState(null);

  const menuAnchorEl = useRef(null);
  const menuEl = useRef(null);

  const handleOpenMenu = (event) => {
    event.preventDefault();
    event.stopPropagation();
    setMenuTarget(menuAnchorEl.current); // Ensure menuTarget is set to menuAnchorEl
  };

  const handleContextMenu = (event) => {
    event.preventDefault();
    setContextMenu({
      mouseX: event.clientX + 2,
      mouseY: event.clientY - 6
    });
    setContextMenuAnchorEl(event.currentTarget);
  };

  const handleContextMenuClose = () => {
    setContextMenu(null);
    setContextMenuAnchorEl(null);
  };

  useEffect(() => {
    setExpanded(groupExpanded);
  }, [groupExpanded]);

  if (tabIds.length === 0) {
    return null;
  }

  const handleChange = (event) => {
    event.preventDefault();
    event.stopPropagation();
    setExpanded((exp) => !exp);
  };

  const colorSetting = getCustomColor(siteConfig?.color, crc32(hostname));
  group.color = colorSetting.color;

  const groupArrowIcon = expanded ? (
    <ArrowDropDown sx={{ color: colorSetting.textColor, fontSize: "24px" }} />
  ) : (
    buttonPosition === "row" ? (
      <ArrowRight sx={{ color: colorSetting.textColor, fontSize: "24px" }} />
    ) : (
      <ArrowLeft sx={{ color: colorSetting.textColor, fontSize: "24px" }} />
    )
  );

  const sideBorder = buttonPosition === "row"
    ? { margin: "5px 0", borderLeft: `2px solid ${colorSetting.hex}` }
    : { margin: "5px 0", borderRight: `2px solid ${colorSetting.hex}` };

  const TabItems = tabIds.map((tabId) => <TabItem key={tabId} tabId={tabId} />);

  return (
    <List key={group.id} disablePadding sx={sideBorder}>
      <ListItem
        disableGutters
        sx={{ padding: 0 }}
        onClick={handleChange}
        onContextMenu={handleContextMenu}
        ref={menuAnchorEl}
      >
        <Stack
          sx={{ width: "100%", paddingRight: buttonPosition === "row" ? "10px" : undefined, paddingLeft: buttonPosition !== "row" ? "10px" : undefined }}
          spacing={0.5}
          direction={buttonPosition}
          alignContent="center"
          justifyContent="space-between"
        >
          {/* <Button
            variant="text"
            sx={{
              flexDirection: buttonPosition,
              borderRadius: "0",
              backgroundColor: colorSetting.hex,
              color: colorSetting.textColor,
              "&:hover": {
                backgroundColor: colorSetting.hex,
              },
              padding: "3px 8px 3px 5px",
            }}
          >
            {groupArrowIcon}
            <Typography variant="h4" noWrap>
              {group.title || t("tabMenu.UntitledGroup")}
            </Typography>
          </Button> */}
          <Box flexShrink={0}>
            <GroupHeader group={group} />
          </Box>

          <Stack direction={buttonPosition} alignContent="center" justifyContent="flex-end" sx={{ flexGrow: 1, minWidth: 0 }}>
            {!expanded && GroupCollapsedShowIcon && <SiteAvatars tabIds={tabIds} max={6} />}
            <TabAdd showText={false} hostname={group.title} windowId={group.windowId} />
          </Stack>
        </Stack>
      </ListItem>
      {expanded && (
        <Collapse in={expanded} timeout="auto" sx={{ borderTop: "1px solid #eee" }}>
          <Divider />
          {expanded && <List disablePadding sx={{ width: "100%", padding: 0 }}>
            {TabItems}
          </List>
          }
          <Divider component="li" />
        </Collapse>
      )}
      <Menu
        key={"menu" + group.id}
        dense="true"
        open={contextMenu !== null}
        anchorReference="anchorPosition"
        anchorPosition={
          contextMenu !== null
            ? { top: contextMenu.mouseY, left: contextMenu.mouseX }
            : undefined
        }
        onClose={handleContextMenuClose}
      >
        <TabGroupDomainMenu key={"menu" + group.id} group={group} onSelect={handleContextMenuClose} />
      </Menu>
    </List>
  );
});

export default TabGroupDomain;