import React, { useEffect, useState } from "react";
import { Box, List } from "@mui/material";
import { filterTabs } from "../../../utils/helpers";
import ChromeHistoryItem from "./ChromeHistoryItem";
import { getHistories } from "../../../core/stores/historyStore";
import { toast } from "react-toastify";
import userStore from "../../../core/stores/userStore";
import { useTranslationContext } from "../../Providers/TranslationProvider";
import WithPermission from "../../component/WithPermission";
import PermissionRequired from "../bottomNav/actions/PermissionRequired";

const ChromeHistories = ({ limit, onSelect }) => {

    const { t } = useTranslationContext();

    const { searchText, updateSetting } = userStore((state) => ({
        searchText: state.searchText,
        updateSetting: state.updateSetting
    }));

    const [visitHistories, setVisitHistories] = useState([]);
    const [filteredHistories, setFilteredHistories] = useState([]);
    const fetchHistories = async () => {
        const histories = await getHistories('chrome');
        setVisitHistories(histories);
    };

    useEffect(() => {


        const checkPermissions = async () => {
            const granted = await chrome.permissions.contains({ permissions: ["history"] });
            if (!granted) {
                const requestGranted = await chrome.permissions.request({ permissions: ["history"] });
                if (!requestGranted) {
                    toast.error(t("deniedMessage"));
                    await updateSetting("historyDataSource", 'recentTabs');
                }
            }
        };

        fetchHistories();
        // checkPermissions();
    }, [updateSetting]);

    useEffect(() => {
        let filtered = filterTabs(visitHistories, searchText, 'desc', 'lastVisitTime');
        if (limit) {
            filtered = filtered.slice(0, limit);
        }
        setFilteredHistories(filtered);
    }, [visitHistories, searchText, limit]);

    return (
        <Box sx={{ padding: 0 }}>
            <PermissionRequired permission="history" updatePermission={fetchHistories} />
            <List sx={{ width: "100%", padding: 0 }}>
                {filteredHistories.map((history) => (
                    <ChromeHistoryItem key={history.urlMD5} history={history} onSelect={onSelect} />
                ))}
            </List>

        </Box>
    );
};

export default React.memo(ChromeHistories);