import React, { memo } from 'react';
import { BottomNavigationAction, Tooltip, Typography } from '@mui/material';
import { useTranslation } from 'react-i18next';
import userStore from '../../../../core/stores/userStore';

const Action = ({ icon, labelKey, showLabel, handleClick }) => {
    const { t } = useTranslation();
    const { theme, fontColor } = userStore(state => ({
        theme: state.settings.theme,
        fontColor: state.settings.fontColor
    }));

    const tooltipTitle = showLabel || !labelKey ? "" : <Typography>{t(labelKey)}</Typography>;

    return (
        <Tooltip arrow title={tooltipTitle} placement="top">
            <BottomNavigationAction
                showLabel={showLabel}
                label={t(labelKey)}
                sx={{
                    variant: "h4",
                    color: theme === 'dark' ? 'text.primary' : fontColor || 'text.primary',
                    paddingX: 0,
                    paddingTop: 0,
                    minWidth: 0,
                    '& .MuiBottomNavigationAction-label': {
                        display: showLabel ? 'block' : 'none',
                    },
                }}
                icon={icon}
                onClick={handleClick}
            />
        </Tooltip>
    );
};

export default memo(Action);