import React, { memo } from "react";
import { Box, IconButton, LinearProgress, Tooltip, Typography } from "@mui/material";
import { customColors } from "../../utils/colors";
import { progressBarStore } from "../../core/stores/progressBarStore";
import { FirstPage, LastPage } from "@mui/icons-material";
import { useTranslationContext } from "../Providers/TranslationProvider";
import { sendTabMessage } from "../../core/browers/runtime";
import userStore from "../../core/stores/userStore";
import { siteStore } from "../../core/stores/siteStore";
import tabStore from "../../core/stores/tabStore";
import { useSettingsContext } from "../Providers/SettingsProvider";

const ReadingProgressBar = ({ tabId }) => {
  const { t } = useTranslationContext();

  const { theme, fontColor } = userStore((state) => ({
    theme: state.settings.theme,
    fontColor: state.settings.fontColor
  }));
  const hostname = tabStore(state => state.tabs.find(tab => tab.id === tabId)?.hostname);

  const globalReadingProgressBar = userStore(state => state.settings.readingProgressBar && state.settings.enableProgressBar);

  const readingProgressBarEnabled = siteStore(state => {
    const site = state.sites.find(site => site.hostname === hostname);
    return site?.readingProgressBar !== false && globalReadingProgressBar;
  });

  const progress = progressBarStore(state => state.readingProgress.find(t => t.tabId === tabId)?.progress) || 0;
  const color = customColors[tabId % customColors.length];

  const handleScrollToTop = () => {
    sendTabMessage(tabId, 'scroll_to_top');
  };

  const handleScrollToBottom = () => {
    sendTabMessage(tabId, 'scroll_to_bottom');
  };

  if (!readingProgressBarEnabled || progress <= 0) {
    return null;
  }
  const sliderStyles = {
    // color: theme === 'dark' ? '#fff' : 'rgba(0,0,0,0.87)',
    color: fontColor || 'text.primary',
    '& .MuiSlider-thumb': {
      width: 6,
      height: 6,
      transition: '0.3s cubic-bezier(.47,1.64,.41,.8)',
      '&::before': {
        boxShadow: '0 2px 12px 0 rgba(0,0,0,0.4)',
      },
      '&:hover, &.Mui-focusVisible': {
        boxShadow: `0px 0px 0px 8px ${theme === 'dark' ? 'rgb(255 255 255 / 16%)' : 'rgb(0 0 0 / 16%)'}`,
      },
      '&.Mui-active': {
        width: 8,
        height: 8,
      },
    },
    '& .MuiSlider-rail': {
      opacity: 0.28,
    },
  };

  return (
    <Box sx={{ width: "100%", display: "block" }}>
      <Box sx={{ display: "flex", alignItems: "center" }}>
        <Tooltip arrow placement="top" title={<Typography>{t('ScrollToTop')}</Typography>}>
          <IconButton sx={{ marginLeft: "5px", padding: "5px", color: fontColor || 'text.primary' }} onClick={handleScrollToTop}>
            <FirstPage size="small" />
          </IconButton>
        </Tooltip>
        <Box sx={{ width: "100%", mx: 1 }}>
          <LinearProgress
            sx={{
              color: fontColor || 'text.primary',
              height: "1px",
              "& .MuiLinearProgress-bar": {
                background: color.hex
              }
            }}
            variant="determinate"
            value={progress}
          />
        </Box>
        <Tooltip arrow placement="top" title={<Typography>{t('ScrollToBottom')}</Typography>}>
          <IconButton sx={{ padding: "4px", color: fontColor || 'text.primary' }} onClick={handleScrollToBottom}>
            <LastPage size="small" />
          </IconButton>
        </Tooltip>
        <Box sx={{ minWidth: '40px' }}>
          <Typography variant="subtitle2" sx={{ color: fontColor || 'text.primary' }}>{`${progress}%`}</Typography>
        </Box>
      </Box>
    </Box>
  );
};

export default memo(ReadingProgressBar);
