import React, { useState, memo } from "react";
import { Box, Slider, Stack, Tooltip, Typography } from "@mui/material";
import { progressBarStore } from "../../../core/stores/progressBarStore";
import { memoryStore } from "../../../utils/memoryStore";
import { sendTabMessage } from "../../../core/browers/runtime";
import { formatDuration } from "../../../utils/helpers";
import userStore from "../../../core/stores/userStore";
import { useHotkeys } from "react-hotkeys-hook/dist";


const ProgressBarSlider = ({ tabId }) => {

    const { theme, fontColor } = userStore((state) => ({
        theme: state.settings.theme,
        fontColor: state.settings.fontColor
    }));
    const [markTooltip, setMarkTooltip] = useState("");
    const playData = progressBarStore(state => {
        const data = state.playingProgress.find(t => t.tabId === tabId);
        return data ? {
            currentTime: data.currentTime,
            duration: data.duration
        } : null;
    });
    const setPlayingProgress = progressBarStore(state => state.setPlayingProgress);

    const handleMouseMove = (event) => {
        if (!playData) return;
        const rect = event.currentTarget.getBoundingClientRect();
        const mouseRelativeX = event.clientX - Math.floor(rect.left);
        if (mouseRelativeX < 0) return;
        setMarkTooltip(formatDuration((mouseRelativeX / rect.width) * playData.duration, playData.duration));
    };

    const handleJumpToTimeChanged = (event, value) => {
        if (!playData) return;
        event.preventDefault();
        event.stopPropagation();
        memoryStore.set(`playing_progress_bar_locked_${tabId}`, value);
        setPlayingProgress(tabId, { ...playData, currentTime: value });
    };

    const handleJumpToTimeChangeCommitted = (event, value) => {
        if (!playData) return;
        event.preventDefault();
        event.stopPropagation();
        memoryStore.remove(`playing_progress_bar_locked_${tabId}`);
        sendTabMessage(tabId, 'video_jump_to_time', { value: value });
    };

    useHotkeys("ArrowLeft", (event) => {
        if (!playData) return;
        event.preventDefault();
        event.stopPropagation();
        if (playData.currentTime) {
            const time = playData.currentTime - 5 < 0 ? 0 : playData.currentTime - 5;
            sendTabMessage(tabId, 'video_jump_to_time', { value: time });
        }
    }, { enableOnFormTags: ["TEXTAREA", "INPUT"] });

    useHotkeys("ArrowRight", (event) => {
        if (!playData) return;
        event.preventDefault();
        event.stopPropagation();
        if (playData.currentTime) {
            const time = playData.currentTime + 5 > playData.duration ? playData.duration - 3 : playData.currentTime + 5;
            sendTabMessage(tabId, 'video_jump_to_time', { value: time });
        }
    }, { enableOnFormTags: ["TEXTAREA", "INPUT"] });


    const sliderStyles = {
        // color: theme === 'dark' ? '#fff' : 'rgba(0,0,0,0.87)',
        color: fontColor || 'text.primary',
        '& .MuiSlider-thumb': {
            width: 6,
            height: 6,
            transition: '0.3s cubic-bezier(.47,1.64,.41,.8)',
            '&::before': {
                boxShadow: '0 2px 12px 0 rgba(0,0,0,0.4)',
            },
            '&:hover, &.Mui-focusVisible': {
                boxShadow: `0px 0px 0px 8px ${theme === 'dark' ? 'rgb(255 255 255 / 16%)' : 'rgb(0 0 0 / 16%)'}`,
            },
            '&.Mui-active': {
                width: 8,
                height: 8,
            },
        },
        '& .MuiSlider-rail': {
            opacity: 0.28,
        },
    };

    return (

        <Tooltip placement="top" title={<Typography>{markTooltip}</Typography>} followCursor>

            <Slider
                value={playData ? playData.currentTime : 0}
                aria-label="time-indicator"
                size="small"
                min={0}
                step={1}
                max={playData ? playData.duration : 0}
                onMouseMove={handleMouseMove}
                onClick={(event) => { event.stopPropagation(); }}
                valueLabelFormat={(value) => `${formatDuration(value, playData ? playData.duration : 0)}`}
                onChange={handleJumpToTimeChanged}
                onChangeCommitted={handleJumpToTimeChangeCommitted}
                sx={{
                    height: "1px",

                    ...sliderStyles
                }}
            />

        </Tooltip>

    );
};

export default memo(ProgressBarSlider);