import { ArrowDownward } from "@mui/icons-material";
import { Button, Divider, Tooltip, Typography } from "@mui/material";
import React from "react";
import tabStore from "../../../core/stores/tabStore";
import userStore from "../../../core/stores/userStore";
import { sendRuntimeMessage } from "../../../core/browers/runtime";
import { useTranslationContext } from "../../Providers/TranslationProvider";
import snapshotStore from "../../../core/stores/snapshotStore";
import ColorTypography from "../../component/ColorTypography";


export default function ClearTabs() {

    const { t } = useTranslationContext();
    const onlyCurrentWindowTabs = userStore((state) => state.settings.onlyCurrentWindowTabs);
    const { theme, fontColor } = userStore((state) => ({
        theme: state.settings.theme,
        fontColor: state.settings.fontColor,
    }));
    const { tabCount, currentWindow } = tabStore((state) => ({
        tabCount: state.tabs.length,
        currentWindow: state.currentWindow
    }))

    const handleClick = async () => {
        await snapshotStore.getState().addSnapshot();
        const response = await sendRuntimeMessage("clear_tabs", { windowId: onlyCurrentWindowTabs ? currentWindow.id : 0 });
    }

    return (
        <Divider textAlign="right" sx={{
            // py: 1,
            color: theme === "dark" ? "text.primary" : fontColor || "text.primary",
            opacity: 0.5,
            '&:after': {
                width: "0%"
            }

        }}>
            {tabCount > 1 &&
                <Tooltip
                    placement="top"
                    enterDelay={1000}
                    enterNextDelay={500}
                    title={<Typography>{t('clearTabsTips')}</Typography>}
                    sx={{ zIndex: 999 }}
                >
                    <Button
                        onClick={handleClick}
                        size="9px"
                        variant="text"
                        startIcon={<ArrowDownward fontSize="small" sx={{ fontWeight: 100, fontSize: '9px', color: theme === "dark" ? "text.primary" : fontColor || "text.primary" }} />}
                    >
                        <ColorTypography variant="caption">{t('clear')}</ColorTypography>
                    </Button>
                </Tooltip>

            }
        </Divider>
    )
}