import { Box, Button, IconButton, Menu, Tooltip, Popover, List, ListItem, ListItemText, Stack, Typography, ListItemButton, ListItemAvatar, Avatar } from "@mui/material";
import React, { useState, useEffect, memo, useRef } from "react";
import QuickAvatar from "./quickAvatar";
import Preview from "./Preview";
import QuickAccessMenuList from "./menu/QuickAccessMenuList";
import { tabStore } from "../../../../core/stores/tabStore";
import userStore from "../../../../core/stores/userStore";

export default memo(function TabQuickAccess({ quickAccess }) {

    const { quickAccessGroupView, quickAccessGroupClickAction } = userStore((state) => ({
        quickAccessGroupView: state.settings.quickAccessGroupView,
        quickAccessGroupClickAction: state.settings.quickAccessGroupClickAction,
    }))

    const [contextMenu, setContextMenu] = useState(null);
    const [contextMenuAnchorEl, setContextMenuAnchorEl] = useState(null);
    const [popoverOpen, setPopoverOpen] = useState(false);
    const [popoverAnchorEl, setPopoverAnchorEl] = useState(null);
    const tabRef = useRef(null);
    useEffect(() => {
        window.addEventListener("blur", handleContextMenuClose);

        return () => {
            window.removeEventListener("blur", handleContextMenuClose);
        };
    }, [])
    const handleContextMenu = event => {
        event.preventDefault();
        event.stopPropagation();
        setContextMenu({
            mouseX: event.clientX + 2,
            mouseY: event.clientY - 6
        });
        setContextMenuAnchorEl(event.currentTarget);
    }

    const handleContextMenuClose = () => {
        setContextMenu(null);
        setContextMenuAnchorEl(null);
    }

    const handleOpenUrl = async (event) => {

        try {
            if (quickAccess.urls && quickAccess.urls.length > 0) {
                if (quickAccessGroupClickAction === 'openAllUrls') {
                    for (const urlConfig of quickAccess.urls) {
                        await chrome.tabs.create({ url: urlConfig.url });
                        await new Promise(resolve => setTimeout(resolve, 500)); // Delay 500ms
                    }
                } else {
                    setPopoverAnchorEl(event.currentTarget);
                    setPopoverOpen(true);
                }

            } else {
                await chrome.tabs.create({ url: quickAccess.url });
            }
        } catch (error) {
            console.error(error);
        }
    }

    const handlePopoverClose = () => {
        setPopoverOpen(false);
        setPopoverAnchorEl(null);
    }

    const handleListItemClick = async (url) => {
        try {
            await chrome.tabs.create({ url });
        } catch (error) {
            console.error(error);
        }
    }

    return (
        <Box
            sx={{ flex: 1, minWidth: "1.75rem", }}
            ref={tabRef}
        >
            <Tooltip
                followCursor
                enterDelay={quickAccess.urls?.length > 1 ? 2000 : 100}
                enterNextDelay={quickAccess.urls?.length > 1 ? 1000 : 100}

                componentsProps={{
                    tooltip: {
                        sx: {
                            padding: "15px",
                            background: "transparent",
                        }
                    }
                }}
                title={
                    <Preview quickAccess={quickAccess} />
                }>
                {quickAccess.urls?.length > 1 ? (<Box

                    size="small"
                    sx={{
                        border: "1px groove transparent",
                        padding: "2px",
                    }}
                    onContextMenu={handleContextMenu}
                    onClick={handleOpenUrl}

                >
                    {quickAccessGroupView === 'short_name' ? (
                        <Button size="small" color="primary" fullWidth variant="outlined" sx={{ minWidth: '42px', padding: "2px 0" }}>
                            <Typography color="primary.main" variant="caption" noWrap>{quickAccess.short_name || quickAccess.title.substring(0, 6)}</Typography>
                        </Button>
                    ) : (
                        <QuickAvatar favIconUrl={quickAccess.favIconUrl} favIconUrls={quickAccess.urls?.map(u => (u.favIconUrl))} />)
                    }

                </Box>) : (<IconButton
                    variant="square"
                    color="inherit"
                    size="small"
                    sx={{
                        border: "1px groove transparent",
                        padding: "5px",
                    }}
                    onContextMenu={handleContextMenu}
                    onClick={handleOpenUrl}
                >
                    <QuickAvatar favIconUrl={quickAccess.favIconUrl} favIconUrls={quickAccess.urls?.map(u => (u.favIconUrl))} />
                </IconButton>)}

            </Tooltip >
            <Menu
                dense="true"
                open={contextMenu !== null}
                anchorReference="anchorPosition"
                anchorPosition={contextMenu !== null ? { top: contextMenu.mouseY, left: contextMenu.mouseX } : undefined}
                onClose={handleContextMenuClose}
                anchorEl={contextMenuAnchorEl}>
                <QuickAccessMenuList uuid={quickAccess.uuid} handleMenuClose={handleContextMenuClose} />
            </Menu>
            <Popover
                open={popoverOpen}
                anchorEl={popoverAnchorEl}
                onClose={handlePopoverClose}
                anchorOrigin={{
                    vertical: 'bottom',
                    horizontal: 'center',
                }}
                transformOrigin={{
                    vertical: 'top',
                    horizontal: 'center',
                }}
            >

                {quickAccessGroupClickAction === 'expandIcons' && (
                    <Box sx={{ width: "100%" }}>
                        <Box sx={{
                            padding: "5px",
                            display: 'flex',
                            flexWrap: 'wrap',
                            justifyContent: 'flex-start',
                            gap: 1,
                            width: '100%',
                        }}>
                            {quickAccess.urls?.map((urlConfig, index) => (
                                <TabQuickAccess key={index} quickAccess={urlConfig} />
                            ))}
                        </Box>
                    </Box>
                )}
                {quickAccessGroupClickAction === 'expandList' && <List>
                    {quickAccess.urls?.map((urlConfig, index) => (
                        <ListItem dense disablePadding key={index}>
                            <ListItemButton dense sx={{ pl: 1, pr: 0, pt: 0, pb: 0 }} onClick={() => handleListItemClick(urlConfig.url)}>
                                <ListItemAvatar sx={{ minWidth: 0, padding: '5px 10px' }}>
                                    <Avatar
                                        sx={{ width: "18px", height: "18px" }}
                                        src={urlConfig.favIconUrl}>
                                    </Avatar>
                                </ListItemAvatar>
                                <ListItemText
                                    sx={{ px: "5px" }}
                                    primary={urlConfig.title} secondary={urlConfig.url} secondaryTypographyProps={{
                                        sx: {
                                            whiteSpace: "pre-wrap", overflow: "hidden", textOverflow: "ellipsis", display: "-webkit-box", WebkitLineClamp: "2", WebkitBoxOrient: "vertical"
                                        }
                                    }} />
                            </ListItemButton>
                        </ListItem>

                    ))}
                </List>
                }

            </Popover>
        </Box >
    )
})