import React, { memo } from "react";
import { Box, Typography } from "@mui/material";
import { progressBarStore } from "../../../core/stores/progressBarStore";
import { formatDuration } from "../../../utils/helpers";
import { useSettingsContext } from "../../Providers/SettingsProvider";


const CurrentTime = ({ tabId }) => {

    const { fontColor } = useSettingsContext();
    const { currentTime, duration } = progressBarStore(state => {
        const playData = state.playingProgress.find(t => t.tabId === tabId);
        return playData ? { currentTime: playData.currentTime, duration: playData.duration } : { currentTime: 0, duration: 0 };
    });

    return (
        <Box component='div'>
            <Typography noWrap variant="body2" sx={{ color: fontColor || 'text.primary' }}>{`${formatDuration(currentTime, duration)}`}</Typography>
        </Box>
    );
};

export default memo(CurrentTime);