import React, { memo, useState, useEffect } from "react";
import { MenuItem, ListItemText, Typography, ListItemIcon, Tooltip, IconButton, Stack, Box, InputAdornment, Input } from "@mui/material";
import { useTranslationContext } from "../../../Providers/TranslationProvider";
import { ZoomIn, ZoomOut, Fullscreen, ZoomInMap, Add, Remove } from "@mui/icons-material";
import tabStore from "../../../../core/stores/tabStore";
import { Check } from "@mui/icons-material";

const TabZoom = ({ tabId, onSelect }) => {
    const { t } = useTranslationContext();
    const [zoomLevel, setZoomLevel] = useState(1);
    const [fullscreenMode, setFullscreenMode] = useState(false);
    const [isEditing, setIsEditing] = useState(false);
    const [inputValue, setInputValue] = useState(Math.round(zoomLevel * 100));
    const { active, url } = tabStore(state => ({
        active: state.tabs.find(tab => tab.id === tabId)?.active,
        url: state.tabs.find(tab => tab.id === tabId)?.url
    }));

    useEffect(() => {
        const fetchZoomLevel = async () => {
            const currentZoomLevel = await chrome.tabs.getZoom(tabId);
            setZoomLevel(currentZoomLevel);
            setInputValue(Math.round(currentZoomLevel * 100));
        };
        fetchZoomLevel();

        const checkFullscreenMode = async () => {
            const currentWindow = await chrome.windows.getCurrent();
            setFullscreenMode(currentWindow.state === "fullscreen");
        };
        checkFullscreenMode();
    }, [tabId]);

    const handleZoomIn = async () => {
        const newZoomLevel = Math.min(zoomLevel + 0.01, 5);
        setZoomLevel(newZoomLevel);
        setInputValue(Math.round(newZoomLevel * 100));
        await chrome.tabs.setZoom(tabId, newZoomLevel);
    };

    const handleZoomOut = async () => {
        const newZoomLevel = Math.max(zoomLevel - 0.01, 0.25);
        setZoomLevel(newZoomLevel);
        setInputValue(Math.round(newZoomLevel * 100));
        await chrome.tabs.setZoom(tabId, newZoomLevel);
    };

    const handleToggleFullscreenMode = async () => {
        const currentWindow = await chrome.windows.getCurrent();
        if (currentWindow.state === "fullscreen") {
            await chrome.windows.update(currentWindow.id, { state: "normal" });
        } else {
            await chrome.windows.update(currentWindow.id, { state: "fullscreen" });
        }
        setFullscreenMode(!fullscreenMode);
        if (typeof onSelect === 'function') {
            onSelect();
        }
    };

    const handleZoomLevelChange = async () => {
        let newZoomLevel = parseFloat(inputValue);
        if (!isNaN(newZoomLevel) && newZoomLevel >= 1 && newZoomLevel <= 1000) {
            await chrome.tabs.setZoom(tabId, newZoomLevel / 100);
            setZoomLevel(newZoomLevel / 100);
        } else {
            setInputValue(Math.round(zoomLevel * 100)); // Reset to current zoom level if invalid
        }
        setIsEditing(false);
        if (typeof onSelect === 'function') {
            onSelect();
        }
    };

    const handleKeyDown = (event) => {
        if (event.key === 'Enter') {
            handleZoomLevelChange();
        }
    };

    const handleInputChange = (event) => {
        const value = event.target.value;
        if (/^\d*$/.test(value)) { // Only allow numeric input
            setInputValue(value);
        }
    };

    if (!active || url.startsWith('chrome://')) {
        return null;
    }

    return (
        <MenuItem>
            <ListItemIcon>
                <ZoomIn />
            </ListItemIcon>
            <ListItemText>
                <Box display="flex" justifyContent="center" width="100%">
                    <Stack spacing={2} direction="row" alignItems="center" justifyContent="flex-start">
                        <Typography>
                            {t('zoom')}
                        </Typography>
                        <IconButton onClick={handleZoomOut}>
                            <Remove />
                        </IconButton>
                        {isEditing ? (
                            <Input
                                value={inputValue}
                                onChange={handleInputChange}
                                onKeyDown={handleKeyDown}
                                inputProps={{ type: 'text', style: { height: '20px' } }}
                                endAdornment={
                                    <InputAdornment position="end">
                                        <IconButton onClick={handleZoomLevelChange}>
                                            <Check />
                                        </IconButton>
                                    </InputAdornment>
                                }
                            />
                        ) : (
                            <Typography onClick={() => setIsEditing(true)}>
                                {`${Math.round(zoomLevel * 100)}%`}
                            </Typography>
                        )}
                        <IconButton onClick={handleZoomIn}>
                            <Add />
                        </IconButton>
                        <Tooltip arrow placement="top" title={<Typography>{fullscreenMode ? t("exitFullscreen") : t("fullscreen")}</Typography>}>
                            {fullscreenMode ? <ZoomInMap onClick={handleToggleFullscreenMode} /> : <Fullscreen onClick={handleToggleFullscreenMode} />}
                        </Tooltip>
                    </Stack>
                </Box>
            </ListItemText>
        </MenuItem>
    );
};

export default memo(TabZoom);