import React, { memo, forwardRef } from "react";
import { useTranslationContext } from "../../Providers/TranslationProvider";
import userStore from "../../../core/stores/userStore";
import tabStore from "../../../core/stores/tabStore";
import { Stack } from "@mui/material";
import TabAdd from "../../component/TabAdd";
import TabsMute from "../../component/TabsMute";
import TabFocus from "../../component/TabFocus";
import ExpandedSwitch from "../../component/ExpandedSwitch";
import TabOrderBy from "../../component/TabOrderBy";
import TabGroupBy from "../../component/TabGroupBy";
import ViewSwitch from "../../component/ViewSwitch";
import ThemeSwitch from "../../component/ThemeSwitch";
import BookmarksHeader from "../bookmarks/BookmarksHeader";

const Toolbar = forwardRef((props, ref) => {
    const { t } = useTranslationContext();

    const {
        showToolbar,
        activeTabIndex,
        toolbar_hide_label,
        toolbar_newtab,
        toolbar_showby,
        toolbar_orderby,
        toolbar_view,
        toolbar_colorMode,
        toolbar_muteAllTabs,
        toolbar_scrollToActiveTab
    } = userStore((state) => state.settings);

    const { soundTabsCount, isActiveTabIntersecting } = tabStore((state) => ({
        soundTabsCount: state.tabs.reduce((count, tab) => tab.audible && !tab.mutedInfo.muted ? count + 1 : count, 0),
        isActiveTabIntersecting: state.isActiveTabIntersecting,
    }));


    if (!showToolbar) {
        return null;
    }



    return (
        <Stack ref={ref} direction="row" justifyContent={toolbar_newtab ? "space-between" : "flex-end"} alignItems="center">
            {toolbar_newtab && <TabAdd showText={!toolbar_hide_label} />}
            {activeTabIndex === 2 ? (<BookmarksHeader showBack={false} />) :
                (<Stack direction="row" justifyContent="center" alignItems="center" spacing={0}>
                    {soundTabsCount > 0 && toolbar_muteAllTabs && <TabsMute />}
                    {activeTabIndex <= 1 && !isActiveTabIntersecting && toolbar_scrollToActiveTab && <TabFocus label={t("ScrollToActiveTab")} />}
                    <ExpandedSwitch sx={{ flexShrink: 0 }} />
                    {activeTabIndex <= 1 && toolbar_orderby && <TabOrderBy showText={!toolbar_hide_label} />}
                    {activeTabIndex <= 1 && toolbar_showby && <TabGroupBy showText={!toolbar_hide_label} />}
                    {activeTabIndex <= 1 && toolbar_view && <ViewSwitch />}
                    {toolbar_colorMode && <ThemeSwitch />}
                </Stack>)
            }
        </Stack>
    );
});

export default memo(Toolbar);