import { immer } from "zustand/middleware/immer";
import { userPublicLocalStorage } from "../user";
import { shallow } from 'zustand/shallow';
import { createWithEqualityFn } from 'zustand/traditional';
import { subscribeWithSelector } from 'zustand/middleware';
import { v4 as uuidv4 } from 'uuid';
import throttle from 'lodash-es/throttle';

export const snapshotCacheInstance = async () => {
    return await userPublicLocalStorage('snapshots')
};

export const snapshotStore = createWithEqualityFn(subscribeWithSelector(immer((set, get) => ({
    summaries: [],
    snapshotDetails: {},
    selectdSnapshotId: null,
    selectedTabs: [],
    fetchSummaries: async () => {
        const cacheInstance = await snapshotCacheInstance();
        const keys = await cacheInstance.keys();
        const summaries = await Promise.all(keys
            .filter(key => key.startsWith('snapshot_summary_'))
            .map(key => cacheInstance.get(key))
        );
        set({ summaries });
    },
    fetchSnapshot: async (id) => {
        const cacheInstance = await snapshotCacheInstance();
        return await cacheInstance.get(`snapshot_detail_${id}`);
    },
    addSnapshot: async (cached = null) => {
        const id = uuidv4();
        let windows = cached?.windows || await chrome.windows.getAll({ populate: true });
        const tabGroups = await chrome.tabGroups?.query({});

        windows = windows.map(window => ({
            ...window,
            tabs: window.tabs.filter(tab => tab.url && !tab.pinned),
            tabGroups: window.tabGroups ? window.tabGroups.filter(group => group.windowId === window.id) : tabGroups.filter(group => group.windowId === window.id)
        }));

        const timestamp = cached?.createdAt || Date.now();
        const summary = {
            id,
            createdAt: timestamp,
            windowCount: cached?.windowCount || windows.length,
            tabCount: cached?.tabCount || windows.reduce((acc, window) => acc + window.tabs.length, 0),
        };
        const snapshot = {
            ...summary,
            windows
        };
        set({ summaries: [...get().summaries, summary] });

        const cacheInstance = await snapshotCacheInstance();
        await cacheInstance.set(`snapshot_summary_${id}`, summary);
        await cacheInstance.set(`snapshot_detail_${id}`, snapshot);
    },
    removeSnapshot: async (id) => {
        const cacheInstance = await snapshotCacheInstance();
        await cacheInstance.remove(`snapshot_summary_${id}`);
        await cacheInstance.remove(`snapshot_detail_${id}`);
        set({ summaries: get().summaries.filter(s => s.id !== id) });
    },
    addImportedSnapshot: async (importedSnapshot) => {
        if (!importedSnapshot || !importedSnapshot.id || !importedSnapshot.createdAt || !importedSnapshot.windowCount || !importedSnapshot.tabCount || !importedSnapshot.windows) {
            throw new Error("Invalid snapshot data");
        }

        const { id, createdAt, windowCount, tabCount, windows } = importedSnapshot;
        const summary = { id, createdAt, windowCount, tabCount };
        const snapshot = { ...summary, windows };

        const existingSnapshot = get().summaries.find(snapshot => snapshot.id === id);
        if (existingSnapshot) {
            throw new Error("Snapshot with the same ID already exists");
        }

        set({ summaries: [...get().summaries, summary] });

        const cacheInstance = await snapshotCacheInstance();
        await cacheInstance.set(`snapshot_summary_${id}`, summary);
        await cacheInstance.set(`snapshot_detail_${id}`, snapshot);
    },
    addSelectdTabId: (tabId, windowId) => {
        const newSelectedTabs = { ...get().selectedTabs };
        if (!newSelectedTabs[windowId]) {
            newSelectedTabs[windowId] = [];
        }
        newSelectedTabs[windowId].push(tabId);
        set({ selectedTabs: newSelectedTabs });
    },
    removeSelectTabId: (tabId, windowId) => {
        const newSelectedTabs = { ...get().selectedTabs };
        newSelectedTabs[windowId] = newSelectedTabs[windowId].filter(id => id !== tabId);
        set({ selectedTabs: newSelectedTabs });

    }

})), shallow));

const throttledAddCachedWindows = throttle(async () => {
    const windows = await chrome.windows.getAll({ populate: true });
    const tabGroups = await chrome.tabGroups?.query({});
    const timestamp = Date.now();

    const filteredWindows = windows
        .map(window => ({
            ...window,
            tabs: window.tabs.filter(tab => tab.url && !tab.pinned),
            tabGroups: tabGroups.filter(tabGroup => tabGroup.windowId === window.id)
        }))
        .filter(window => window.tabs.length > 0);

    const tabCount = filteredWindows.reduce((acc, window) => acc + window.tabs.length, 0);

    if (tabCount <= 3) {
        return;
    }

    const summary = {
        createdAt: timestamp,
        windowCount: filteredWindows.length,
        tabCount,
        tabGroupCount: tabGroups.length
    };

    const cacheInstance = await snapshotCacheInstance();
    await cacheInstance.set('cachedWindows', { ...summary, windows: filteredWindows });
}, 30000);

export const addCachedWindows = throttledAddCachedWindows;

export const getCachedWindows = async () => {
    const cacheInstance = await snapshotCacheInstance();
    return await cacheInstance.get('cachedWindows');
};

export const removeCachedWindows = async () => {
    const cacheInstance = await snapshotCacheInstance();
    await cacheInstance.remove('cachedWindows');
};



export default snapshotStore;