import React from "react";
import { MenuItem, ListItemText, Typography, ListItemIcon, SvgIcon } from "@mui/material";
import { useTranslationContext } from "../../../Providers/TranslationProvider";
import userStore from "../../../../core/stores/userStore";
import { selectStore } from "../../../../core/stores/selectStore";
import tabStore from "../../../../core/stores/tabStore";


export default function AddTabsToQuickAccess({ onSelect }) {
    const { t } = useTranslationContext();

    const pinToQuickAccess = userStore(state => state.settings.pinToQuickAccess);

    const { selectedTabIds } = selectStore((state) => ({
        selectedTabIds: state.selectedTabIds,
    }));
    const { setQuickAccess, tabUrls } = tabStore((state) => ({
        setQuickAccess: state.setQuickAccess,
        tabUrls: state.tabs.filter(tab => (selectedTabIds.includes(tab.id))).map(tab => {
            return { title: tab.title, url: tab.url, favIconUrl: tab.favIconUrl }
        })
    }));


    const handleClick = async () => {
        const data = {
            title: t('containsCountURLs', { count: tabUrls.length }),
            favIconUrl: tabUrls[0]?.favIconUrl || "",
            urls: tabUrls
        };
        setQuickAccess(crypto.randomUUID(), data);
        if (typeof onSelect === 'function') {
            onSelect();
        }
    };

    if (!pinToQuickAccess) return null;

    return (
        <MenuItem dense onClick={handleClick}>
            <ListItemIcon>
                <SvgIcon>
                    <svg xmlns="http://www.w3.org/2000/svg" height="24px" viewBox="0 -960 960 960" width="24px" fill="currentColor">
                        <path d="M720-320v-80h-80v-80h80v-80h80v80h80v80h-80v80h-80ZM600-80q-127-48-203.5-158T320-484q0-91 36-172.5T458-800H320v-80h280v280h-80v-148q-57 51-88.5 119.5T400-484q0 102 54 187.5T600-167v87Z" />
                    </svg>
                </SvgIcon>
            </ListItemIcon>
            <ListItemText>
                <Typography variant="body1">{t("tabMenu.PinToQuickAccess")}</Typography>
            </ListItemText>
        </MenuItem>
    );
}