import React, { memo } from "react";
import { Typography } from "@mui/material";
import userStore from "../../core/stores/userStore";
import { decodedUrl } from "../../utils/helpers";
import HighlightedText from "./HighlightedText";

const HistoryUrl = ({ url }) => {
  const { theme, fontWeight, fontColor, searchText } = userStore((state) => ({
    theme: state.settings.theme,
    fontWeight: state.settings.fontWeight,
    fontColor: state.settings.fontColor,
    searchText: state.searchText
  }));



  return (
    <Typography
      variant="h5"
      color="text.secondary"
      sx={{
        whiteSpace: "pre-wrap",
        wordWrap: "break-word",
        wordBreak: "break-all",
        textWrap: "break-word",
        overflow: "hidden",
        textOverflow: "ellipsis",
        display: "-webkit-box",
        WebkitLineClamp: "2",
        WebkitBoxOrient: "vertical",
        fontWeight: Math.max(100, fontWeight - 200),
        color: theme === "dark" ? "text.secondary" : fontColor || "text.secondary",
        opacity: 0.75
      }}
    >
      <HighlightedText text={decodedUrl(url)} searchText={searchText} />
    </Typography>
  );
};

export default memo(HistoryUrl);
