import React, { memo } from "react";
import { Box, ListItemText, Typography } from "@mui/material";
import userStore from "../../../core/stores/userStore";
import ColorTypography from "../../component/ColorTypography";
import { decodedUrl } from "../../../utils/helpers";
import HighlightedText from "../../component/HighlightedText";
import { bookmarkStore } from "../../../core/stores/bookmarkStore";


export default memo(function BookmarkTitle({ title, url }) {

    const { view, theme, fontWeight, fontColor, bookmarkShowUrl, searchQuery } = userStore((state) => ({
        view: state.settings.view,
        theme: state.settings.theme,
        fontWeight: state.settings.fontWeight,
        fontColor: state.settings.fontColor,
        bookmarkShowUrl: state.settings.bookmarkShowUrl,
        searchQuery: state.searchQuery
    }))



    return (

        <Box sx={{ minWidth: 0, flex: 1 }}>
            <ColorTypography
                variant="h4"
                sx={{
                    paddingTop: "4px",
                    paddingBottom: "4px",
                }}
            >
                <HighlightedText text={title} searchText={searchQuery} />

            </ColorTypography>

            {(bookmarkShowUrl || searchQuery) && <Typography variant="h5" color="text.secondary" sx={{
                whiteSpace: "pre-wrap",
                wordWrap: "break-word",
                wordBreak: "break-all",
                textWrap: "break-word",
                overflow: "hidden",
                textOverflow: "ellipsis",
                display: "-webkit-box",
                WebkitLineClamp: "1",
                WebkitBoxOrient: "vertical",
                fontWeight: (fontWeight - 200 <= 0 ? 100 : fontWeight - 200),
                color: theme === "dark" ? "text.secondary" : fontColor || "text.secondary",
                opacity: 0.3
            }}>
                <HighlightedText text={decodedUrl(url)} searchText={searchQuery} />
            </Typography>
            }

        </Box>


    )
})