import { immer } from "zustand/middleware/immer";
import { shallow } from 'zustand/shallow';
import { createWithEqualityFn } from 'zustand/traditional';
import { subscribeWithSelector } from 'zustand/middleware';

export const selectStore = createWithEqualityFn(subscribeWithSelector(immer((set) => ({
    waitForSelectTab: false,
    selectedTabIds: [],
    popupPanelActived: false,
    setWaitForSelectTab: (value) => {
        set((state) => {
            state.waitForSelectTab = value;
            if (!value) {
                state.selectedTabIds = [];
            }
        });
    },
    setSelectedTabIds: (value) => set((state) => { state.selectedTabIds = value; }),
    addSelectedTabId: (tabId) => {
        set((state) => {
            state.selectedTabIds.push(tabId);
        });
    },
    deleteSelectedTabId: (tabId) => {
        set((state) => {
            state.selectedTabIds = state.selectedTabIds.filter(id => id !== tabId);
            if (state.selectedTabIds.length === 0) {
                state.waitForSelectTab = false;
            }
        });
    },
})), shallow));

