import React, { memo, useState } from "react";
import { MenuItem, ListItemText, Typography, ListItemIcon, Tooltip, Box, Stack } from "@mui/material";
import { useTranslationContext } from "../../../Providers/TranslationProvider";
import { Pause, Help } from "@mui/icons-material";
import tabStore from "../../../../core/stores/tabStore";

const DiscardTab = ({ tabId, onSelect }) => {
    const { t } = useTranslationContext();
    const [showTooltip, setShowTooltip] = useState(false);

    const discarded = tabStore((state) => state.tabs.find(tab => tab.id === tabId)?.discarded);

    const handleClick = async () => {
        chrome.tabs.discard(tabId);
        if (typeof onSelect === 'function') {
            onSelect();
        }
    };

    if (discarded) {
        return null;
    }

    return (
        <MenuItem
            onClick={handleClick}
            onMouseEnter={() => setShowTooltip(true)}
            onMouseLeave={() => setShowTooltip(false)}
        >
            <ListItemIcon>
                <Pause />
            </ListItemIcon>
            <ListItemText>
                <Stack direction="row" alignItems="center" justifyContent="space-between">
                    <Typography variant="body1">{t("tabMenu.DiscardTab")}</Typography>
                    {showTooltip && <Tooltip arrow placement="top" title={<Typography>{t("tabMenu.DiscardTabDesc")}</Typography>} >
                        <Help fontSize="small" color="secondary" sx={{ opacity: '0.5' }} />
                    </Tooltip>}
                </Stack>
            </ListItemText>
        </MenuItem>
    );
};

export default memo(DiscardTab);