import React, { useState, useEffect, forwardRef } from "react";
import { Box, Divider, BottomNavigation } from "@mui/material";
import userStore from "../../../core/stores/userStore";
import ActionSettings from "./actions/ActionSettings";
import ActionVersion from "./actions/ActionVersion";
import ActionMore from "./actions/ActionMore";
import ActionNewTab from "./actions/ActionNewTab";
import ActionRecentlyTabs from "./actions/ActionRecentlyTabs";
import ActionHistory from "./actions/ActionHistory";
import ActionReadingList from "./actions/ActionReadingList";
import ActionBookmarks from "./actions/ActionBookmarks";
import ActionQuickAccess from "./actions/ActionQuickAccess";
import ActionSearch from "./actions/ActionSearch";
import ActionMuteAllTabs from "./actions/ActionMuteAllTabs";
import ActionScrollToActiveTab from "./actions/ActionScrollToActiveTab";
import ActionCollapseAll from "./actions/ActionCollapseAll";
import ActionThemeModes from "./actions/ActionThemeModes";
import ActionViewModes from "./actions/ActionViewModes";
import ActionCreateSnapshot from "./actions/ActionCreateSnapshot";
import { getUserConfig } from "../../../core/user";
import * as Sentry from "@sentry/react";
import ActionSwitchPip from "./actions/ActionSwitchPip";

const actions = {
    settings: <ActionSettings key={"settings"} />,
    version: <ActionVersion key={"version"} />,
    more: <ActionMore key={"more"} />,
    newTab: <ActionNewTab key={"newTab"} />,
    recentlyTabs: <ActionRecentlyTabs key={"recentlyTabs"} />,
    history: <ActionHistory key={"history"} />,
    readingList: <ActionReadingList key={"readingList"} />,
    bookmarks: <ActionBookmarks key={"bookmarks"} />,
    quickAccess: <ActionQuickAccess key={"quickAccess"} />,
    search: <ActionSearch key={"search"} />,
    muteAllTabs: <ActionMuteAllTabs key={"muteAllTabs"} />,
    scrollToActiveTab: <ActionScrollToActiveTab key={"scrollToActiveTab"} />,
    collapseAll: <ActionCollapseAll key={"collapseAll"} />,
    themeModes: <ActionThemeModes key={"themeModes"} />,
    viewModes: <ActionViewModes key={"viewModes"} />,
    createSnapshot: <ActionCreateSnapshot key={"createSnapshot"} />,
    switchPIP: <ActionSwitchPip key={"createSnapshot"} />,
};

const BottomNav = forwardRef((props, ref) => {
    const { navs, theme, showBottomNav, fontColor } = userStore(state => ({
        navs: state.settings.bottomNavOptions.navs.filter(nav => nav.enable === true),
        theme: state.settings.theme,
        showBottomNav: state.settings.showBottomNav,
        fontColor: state.settings.fontColor
    }));

    const [showVersionBadge, setShowVersionBadge] = useState(false);

    useEffect(() => {
        let isMounted = true;
        getUserConfig("new_changelog_badge").then(newVersionBadge => {
            if (isMounted && newVersionBadge && !navs.find(nav => nav.name === "version")) {
                setShowVersionBadge(true);
            }
        }).catch(error => {
            Sentry.captureException(error);
        });
        return () => {
            isMounted = false;
        };
    }, [navs]);

    const renderNavs = navs.map(nav => (
        <Sentry.ErrorBoundary key={nav.name} fallback={null}>
            {actions[nav.name]}
        </Sentry.ErrorBoundary>
    ));

    if (!showBottomNav) {
        return null;
    }

    return (
        <Box ref={ref} sx={{ position: "fixed", bottom: 0, left: 0, right: 0 }} >
            <Divider />
            <BottomNavigation
                sx={{
                    background: "transparent",
                    height: "auto",
                    minHeight: "32px",
                    padding: "5px"
                }}
            >
                {showVersionBadge && actions.version}
                {renderNavs}
            </BottomNavigation>
        </Box>
    );
});

export default BottomNav;
