import React from 'react';
import { Button, Typography, Box, Tooltip } from '@mui/material';
import GroupArrowIcon from './GroupArrowIcon';
import { useSettingsContext } from '../../../Providers/SettingsProvider';
import { useTranslationContext } from '../../../Providers/TranslationProvider';
import { colord } from 'colord';
import { getCustomColor } from '../../../../utils/colors';
import tabStore from '../../../../core/stores/tabStore';

const GroupHeader = ({ group }) => {
    const { buttonPosition, fontWeight, showGroupTabCount, groupTitleMulitLines } = useSettingsContext();
    const { t } = useTranslationContext();

    if (!group) {
        return null;
    }

    const colorSetting = getCustomColor(group.color);
    const isOpera = navigator.userAgent.includes("OPR");
    const tabs = tabStore.getState().tabs.filter(tab => tab.groupId === group.id);
    const tabCount = tabs.length;
    const title = isOpera ? tabs[0]?.workspaceName || t("tabMenu.UntitledGroup") : group.title || t("tabMenu.UntitledGroup");
    const firstTabTitle = tabs[0]?.title || '';

    const typographyStyles = {
        whiteSpace: "pre-wrap",
        wordWrap: "break-word",
        wordBreak: "break-all",
        textWrap: "break-word",
        overflow: "hidden",
        textOverflow: "ellipsis",
        display: "-webkit-box",
        WebkitLineClamp: Math.max(groupTitleMulitLines, 1),
        WebkitBoxOrient: "vertical",
        paddingTop: "2px",
        paddingBottom: "2px",
        fontWeight: fontWeight,
        opacity: 1,
        textAlign: "left"

    };

    return (
        <Tooltip
            enterDelay={500}
            enterNextDelay={300}
            title={<Typography>{tabCount > 1 ? t("groupNameTooltips", { groupName: group.title, firstTabTitle, count: tabCount - 1 }) : t("groupNameTooltip", { groupName: group.title, firstTabTitle })}</Typography>}
        >
            <Button
                variant="text"
                sx={{
                    flexDirection: buttonPosition,
                    borderRadius: "0",
                    backgroundColor: colorSetting.hex,
                    color: colorSetting.textColor,
                    "&:hover": {
                        backgroundColor: colord(colorSetting.hex).alpha(0.5).toRgbString(),
                    },
                    padding: "3px 8px 3px 5px",
                    display: 'flex',
                    alignItems: 'center',
                    justifyContent: 'flex-start',
                    whiteSpace: 'nowrap',
                    borderRadius: "4px"
                }}
            >
                <GroupArrowIcon collapsed={group.collapsed} textColor={colorSetting.textColor} />
                <Typography variant="h4" sx={typographyStyles}>
                    {title}
                </Typography>
                {showGroupTabCount && tabCount > 0 && (
                    <Typography component="span" sx={{ marginLeft: 1 }}>
                        〈{tabCount}〉
                    </Typography>
                )}
            </Button>
        </Tooltip>
    );
};

export default GroupHeader;