import React from "react";
import { Box, Stack, Typography } from "@mui/material";
import { useTranslationContext } from "../../../Providers/TranslationProvider";
import historyStore from "../../../../core/stores/historyStore";
import userStore from "../../../../core/stores/userStore";
import { useEffect } from "react";
import ColorTypography from "../../../component/ColorTypography";


const RecentlyTabs = () => {
    const { t } = useTranslationContext();


    const { navSetting } = userStore((state) => ({
        navSetting: state.settings.tabNavOptions.navs.find(nav => nav.name === 'RecentlyTabs')
    }));

    useEffect(() => {
        historyStore.getState().loadHistories();
    }, [])

    const historyCount = historyStore((state) => state.histories.filter(h => h.closed_time > (new Date()).getTime() - 1000 * 86400 * (navSetting?.days || 2)).length);

    return (
        navSetting?.showCount ? <ColorTypography>〈{historyCount}〉</ColorTypography> : null
    );
};

export default RecentlyTabs;
