import React, { useRef, useEffect } from "react";
import {
  ListItem,
  ListItemButton,
  Box,
  IconButton,
  Stack,
  Tooltip,
} from "@mui/material";
import { memo } from "react";
import { Close, ArrowForward } from "@mui/icons-material";
import { userStore } from "../../../core/stores/userStore";
import { tabStore } from "../../../core/stores/tabStore";
import { getTabItemStyle } from "../../Hooks/useTabItemStyle";
import { isHotkeyPressed } from 'react-hotkeys-hook';
import { faviconURL } from "../../../utils/helpers";
import HistoryTitle from "../../component/HistoryTitle";
import HistoryAvatar from "../../component/HistoryAvatar";
import HistoryPreview from "../../component/HistoryPreview";
import { useTranslationContext } from "../../Providers/TranslationProvider";

const ChromeHistoryItem = ({ history, onSelect }) => {
  const { t } = useTranslationContext();
  const {
    updateSetting,
    view,
    buttonPosition,
    tabPreviewEnable,
    tabPreviewWaitSeconds,
    setSearchText,
    activeTabIndex,
    showFullUrl,
    AlwaysShowCloseButton,
  } = userStore((state) => ({
    updateSetting: state.updateSetting,
    view: state.settings.view,
    buttonPosition: state.settings.buttonPosition,
    tabPreviewEnable: state.settings.tabPreviewEnable,
    tabPreviewWaitSeconds: state.settings.tabPreviewWaitSeconds,
    setSearchText: state.setSearchText,
    activeTabIndex: state.settings.activeTabIndex,
    showFullUrl: state.settings.showFullUrl,
    AlwaysShowCloseButton: state.settings.AlwaysShowCloseButton,
  }));

  const { keyboardSelected } = tabStore((state) => ({
    keyboardSelected: state.keyboardSelected?.key === history.urlMD5,
  }));

  const tabRef = useRef(null);

  useEffect(() => {
    if (keyboardSelected && tabRef.current) {
      tabRef.current.scrollIntoView({ behavior: "smooth", block: "nearest", inline: "nearest" });
    }
  }, [keyboardSelected]);

  const handleOpenHistory = async () => {
    const isCtrlPressed = isHotkeyPressed('ctrl');
    await chrome.tabs.create({ url: history.url, active: !isCtrlPressed });
    if (!isCtrlPressed) {
      setSearchText("");
      if (activeTabIndex > 1) {
        updateSetting('activeTabIndex', 1);
      }
    }
    if (typeof onSelect === 'function') {
      onSelect();
    }
  };

  const handleDeleteHistory = async (event) => {
    event.preventDefault();
    event.stopPropagation();
    chrome.history?.deleteUrl({ url: history.url });
    tabRef.current.style.display = "none";
  };

  return (
    <Box ref={tabRef} sx={{ position: "relative" }}>
      <ListItem disablePadding onClick={handleOpenHistory}>
        <Tooltip
          followCursor
          enterDelay={parseFloat(tabPreviewWaitSeconds) * 1000}
          enterNextDelay={200}
          componentsProps={{
            tooltip: {
              sx: {
                padding: "15px",
                background: "transparent",
                zIndex: 99,
              },
            },
          }}
          title={tabPreviewEnable ? <HistoryPreview history={history} /> : ""}
        >
          <ListItemButton
            component="div"
            dense={view === "list"}
            disableGutters
            selected={keyboardSelected}
            sx={getTabItemStyle(history.id)}
          >
            <Stack
              sx={{
                width: "100%",
                ...buttonPosition === "row" ? { paddingRight: "8px" } : { paddingLeft: "8px" },
              }}
              direction={buttonPosition}
              alignItems={"center"}
              justifyContent={"space-between"}
              spacing={1}
            >
              <HistoryAvatar avatarUrl={faviconURL(history.url)} />
              <HistoryTitle history={history} />
              {keyboardSelected ? (
                <Tooltip arrow open={true} title={t('OpenLink')}>
                  <IconButton sx={{ padding: "4px", color: "inherit" }}>
                    <ArrowForward size="small" />
                  </IconButton>
                </Tooltip>
              ) : (
                <IconButton
                  className="close-button"
                  sx={{ padding: "4px", color: "inherit", display: AlwaysShowCloseButton ? "flex" : "none" }}
                  onClick={handleDeleteHistory}
                >
                  <Close size="small" />
                </IconButton>
              )}
            </Stack>
          </ListItemButton>
        </Tooltip>
      </ListItem>
    </Box>
  );
};

export default memo(ChromeHistoryItem);
