import React from 'react';
import { ListItemIcon, ListItemText, MenuItem, Typography } from '@mui/material';
import { countBookmarkUrls } from '../../../../utils/helpers';
import { useTranslationContext } from '../../../Providers/TranslationProvider';
import { AddModerator } from '@mui/icons-material';

const OpenAllInIncognitoWindow = ({ folder, handleClose }) => {

    const { t } = useTranslationContext();

    const handleClick = () => {
        const urls = [];

        const collectUrls = (bookmark) => {
            if (bookmark.url) {
                urls.push(bookmark.url);
            } else if (bookmark.children) {
                bookmark.children.forEach(child => collectUrls(child));
            }
        };

        collectUrls(folder);

        if (urls.length > 0) {
            chrome.windows.create({ incognito: true, url: urls });
        }

        handleClose();
    };

    return (
        <MenuItem dense onClick={handleClick}>
            <ListItemIcon>
                <AddModerator />
            </ListItemIcon>
            <ListItemText>
                <Typography variant='body1'>
                    {t('openAllInIncognitoWindow', { count: countBookmarkUrls(folder) })}
                </Typography>
            </ListItemText>
        </MenuItem>
    );
};

export default OpenAllInIncognitoWindow;
