import React, { useState, useEffect, memo } from "react";
import ArrowDropDownIcon from "@mui/icons-material/ArrowDropDown";
import CheckIcon from "@mui/icons-material/Check";
import {
  MenuList,
  MenuItem,
  ListItemText,
  Menu,
  Button,
  ListItemIcon,
  Typography,
  Tooltip,
  IconButton,
  Box,
} from "@mui/material";
import { useTranslationContext } from "../Providers/TranslationProvider";
import { userStore } from "../../core/stores/userStore";
import { sendRuntimeMessage } from "../../core/browers/runtime";
import { LineStyle } from "@mui/icons-material";

const TabGroupBy = ({ showText = true }) => {
  const { t } = useTranslationContext('translation');
  const [anchorEl, setAnchorEl] = useState(null);
  const open = Boolean(anchorEl);

  const { groupBy, updateSetting, theme, fontColor } = userStore((state) => ({
    groupBy: state.settings.groupBy,
    updateSetting: state.updateSetting,
    theme: state.settings.theme,
    fontColor: state.settings.fontColor,
  }));

  useEffect(() => {
    const handleMenuClose = () => setAnchorEl(null);
    window.addEventListener("blur", handleMenuClose);
    return () => {
      window.removeEventListener("blur", handleMenuClose);
    };
  }, []);

  const groupings = [
    { type: "list", name: t("ShowBy.List") },
    { type: "mixed", name: t("ShowBy.Mixed") },
    { type: "domain", name: t("ShowBy.Website") },
    { type: "chromeGroup", name: t("ShowBy.Group") },
    { type: "icon", name: t("ShowBy.icon") },
    { type: "tabWindow", name: t("ShowBy.tabWindow") },
  ];

  const groupingName = groupings.find((g) => g.type === groupBy)?.name || "";

  const handleClick = (event) => {
    event.stopPropagation();
    setAnchorEl(event.currentTarget);
  };

  const handleGroupingChanged = async (grouping) => {
    updateSetting("groupBy", grouping.type);
    sendRuntimeMessage("update_setting", { key: "groupBy", value: grouping.type });
    setAnchorEl(null);
  };

  return (
    <Box>
      <Tooltip arrow title={<Typography>{t("ShowBy.ShowBy")}</Typography>}>
        {showText ? (
          <Button
            id="session-grouping-button"
            size="small"
            sx={{ fontSize: "12px", color: fontColor || "text.primary" }}
            aria-label="settings"
            aria-controls={open ? "session-grouping-menu" : undefined}
            aria-haspopup="true"
            aria-expanded={open ? "true" : undefined}
            onClick={handleClick}
          >
            <Typography noWrap variant="body1" sx={{ color: theme === "dark" ? "text.primary" : fontColor || "text.primary" }}>
              {groupingName}
            </Typography>
            <ArrowDropDownIcon size="small" />
          </Button>
        ) : (
          <IconButton onClick={handleClick} sx={{ color: fontColor || "text.primary" }}>
            <LineStyle size="small" />
          </IconButton>
        )}
      </Tooltip>
      <Menu
        dense={"true"}
        open={open}
        onClose={() => setAnchorEl(null)}
        anchorEl={anchorEl}
        id={"session-grouping-menu"}
        MenuListProps={{
          "aria-labelledby": "session-grouping-button",
        }}
      >
        <MenuList dense sx={{ minWidth: "200px" }}>
          {groupings.map((grouping) => (
            <MenuItem onClick={() => handleGroupingChanged(grouping)} key={grouping.type}>
              {grouping.type === groupBy ? (
                <>
                  <ListItemIcon>
                    <CheckIcon />
                  </ListItemIcon>
                  <ListItemText>
                    <Typography variant="body1">
                      {t("ShowBy.ShowBy")} : {grouping.name}
                    </Typography>
                  </ListItemText>
                </>
              ) : (
                <ListItemText inset>
                  <Typography variant="body1">
                    {t("ShowBy.ShowBy")} : {grouping.name}
                  </Typography>
                </ListItemText>
              )}
            </MenuItem>
          ))}
        </MenuList>
      </Menu>
    </Box>
  );
};

export default memo(TabGroupBy);
