import React, { useRef, useEffect, useState, useCallback, useMemo } from "react";
import { ListItem, ListItemButton, Box, Tooltip, Stack, CircularProgress, IconButton, Menu, Avatar, Typography, Button } from "@mui/material";
import { faviconURL } from "../../../utils/helpers";
import userStore from "../../../core/stores/userStore";
import { getTabItemStyle } from "../../Hooks/useTabItemStyle";
import LinkTitle from "../../component/links/LinkTitle";
import LinkPreview from "../../component/links/LinkPreview";
import { bookmarkStore } from "../../../core/stores/bookmarkStore";
import { Check, Close, Public } from "@mui/icons-material";
import { useTranslationContext } from "../../Providers/TranslationProvider";
import OpenInNewTab from "./menuItems/OpenInNewTab";
import OpenInNewWindow from "./menuItems/OpenInNewWindow";
import OpenInIncognitoWindow from "./menuItems/OpenInIncognitoWindow";
import EditBookmark from "./menuItems/EditBookmark";
import DeleteBookmark from "./menuItems/DeleteBookmark";
import CopyUrl from "./menuItems/CopyUrl";
import CopyTitle from "./menuItems/CopyTitle";
import CopyMarkdown from "./menuItems/CopyMarkdown";
import { memo } from "react";
import BookmarkStatus from "./BookmarkStatus";
import BookmarkTitle from "./BookmarkTitle";

const BookmarkTreeNode = ({ bookmarkId }) => {
    const { bookmark, isSearching } = bookmarkStore((state) => ({
        bookmark: state.bookmarks.find(b => b.id === bookmarkId),
        isSearching: Boolean(state.searchQuery)
    }));
    console.log("BookmarkTreeNode render", bookmark.title)


    const { t } = useTranslationContext();
    const { theme, fontScale, fontColor, view, buttonPosition, tabPreviewEnable, tabPreviewWaitSeconds, bookmarkTreeConnectorLines, avatorSize, bookmarkViewMode } = userStore((state) => ({
        theme: state.settings.theme,
        fontScale: state.settings.fontScale,
        fontColor: state.settings.fontColor,
        view: state.settings.view,
        buttonPosition: state.settings.buttonPosition,
        tabPreviewEnable: state.settings.tabPreviewEnable,
        tabPreviewWaitSeconds: state.settings.tabPreviewWaitSeconds,
        bookmarkTreeConnectorLines: state.settings.bookmarkTreeConnectorLines,
        avatorSize: Math.floor(20 * state.settings.fontScale),
        bookmarkViewMode: state.settings.bookmarkViewMode,
    }));



    const listItemRef = useRef(null);
    const [contextMenu, setContextMenu] = useState(null);
    const [isDeleting, setIsDeleting] = useState(false);
    const [countdown, setCountdown] = useState(10);
    const [timer, setTimer] = useState(null);

    // useEffect(() => {
    //     if (selected && listItemRef.current) {
    //         listItemRef.current.scrollIntoView({ behavior: 'smooth', block: 'center' });
    //     }
    // }, [selected]);

    useEffect(() => {
        const handleWindowBlur = () => {
            setContextMenu(null);
        };

        window.addEventListener("blur", handleWindowBlur);
        return () => {
            window.removeEventListener("blur", handleWindowBlur);
        };
    }, []);

    const handleOpenLink = async () => {
        try {
            chrome.tabs.create({ url: bookmark.url });
            if (typeof onSelect === 'function') {
                // onSelect();
            }
        } catch (error) {
            console.error("Failed to open link:", error);
        }
    };

    const handleContextMenu = useCallback((event) => {
        event.preventDefault();
        setContextMenu(contextMenu === null ? listItemRef.current : null);
    }, [contextMenu]);

    const handleClose = useCallback(() => {
        setContextMenu(null);
    }, []);

    const startCountdown = useCallback(() => {
        setIsDeleting(true);
        setTimer(setInterval(() => {
            setCountdown(prev => prev - 1);
        }, 1000));
    }, []);

    const cancelDelete = useCallback(() => {
        clearInterval(timer);
        setIsDeleting(false);
        setCountdown(10);
    }, [timer]);

    useEffect(() => {
        if (countdown === 0) {
            handleDelete();
        }
    }, [countdown]);

    const handleDelete = useCallback(async () => {
        clearInterval(timer);
        try {
            await chrome.bookmarks?.remove(bookmark.id);
        } catch (error) {
            console.error("Failed to delete bookmark:", error);
        }
        setIsDeleting(false);
        setCountdown(10);
    }, [bookmark.id, timer]);

    return (
        <Box
            sx={bookmarkTreeConnectorLines && bookmarkViewMode === 'tree' ? {
                position: "relative",
                paddingInlineStart: '20px',
                '&:before': {
                    borderLeft: `1px solid ${theme === "dark" ? "#fff" : (fontColor === 'inherit' || !fontColor) ? "#ccc" : fontColor}`,
                    content: '""',
                    height: '100%',
                    // left: `${bookmark.level * 1}rem`,
                    position: 'absolute',
                    top: 0,
                },
                '&:after': {
                    borderTop: `1px solid ${theme === "dark" ? "#fff" : (fontColor === 'inherit' || !fontColor) ? "#ccc" : fontColor}`,
                    content: '""',
                    position: 'absolute',
                    top: `${20 * fontScale}px`,
                    width: '12px',
                },
            } : {}}
            ref={listItemRef}
            onContextMenu={handleContextMenu}
        >
            <ListItem disablePadding sx={{
                pl: isSearching ? 0 : bookmark.level * 1,
                pr: "10px"
            }} onClick={handleOpenLink}>

                <ListItemButton
                    // selected={selected}
                    component="div"
                    dense={view === "list"}
                    disableGutters
                    sx={{
                        ...getTabItemStyle(bookmark.url),
                    }}
                >
                    <Stack
                        sx={{
                            width: "100%",
                            paddingRight: buttonPosition === "row" ? "8px" : !isSearching && bookmarkTreeConnectorLines ? 0 : "8px",
                            paddingLeft: buttonPosition === "row" ? !isSearching && bookmarkTreeConnectorLines ? 0 : "8px" : "8px"
                        }}
                        direction={buttonPosition}
                        alignItems={"center"}
                        justifyContent={"space-between"}
                        spacing={1}
                    >
                        <Avatar
                            variant="square"
                            sx={{
                                background: "transparent",
                                width: avatorSize,
                                height: avatorSize,
                                alignContent: "center"
                            }}
                            aria-label="recipe"
                            src={faviconURL(bookmark.url)}
                        >
                            <Public sx={{ fontSize: avatorSize }} color="primary" />
                        </Avatar>
                        <Tooltip
                            followCursor
                            enterDelay={parseFloat(tabPreviewWaitSeconds) * 1000}
                            enterNextDelay={1000}
                            componentsProps={{
                                tooltip: {
                                    sx: {
                                        padding: "15px",
                                        background: "transparent",
                                        zIndex: 99
                                    }
                                }
                            }}
                            title={tabPreviewEnable ? <LinkPreview title={bookmark.title} url={bookmark.url} /> : ""}
                        >
                            <>
                                <BookmarkTitle title={bookmark.title} url={bookmark.url} lastAccessed={bookmark.dateAdded} />
                            </>
                        </Tooltip>
                        <BookmarkStatus bookmarkId={bookmarkId} startCountdown={startCountdown} isDeleting={isDeleting} />
                        {isDeleting && (
                            <Button
                                startIcon={
                                    <Box sx={{ position: 'relative', display: 'inline-flex' }}>
                                        <CircularProgress color="error" variant="determinate" value={(countdown / 10) * 100} size={20} />
                                        <Box
                                            sx={{
                                                top: 0,
                                                left: 0,
                                                bottom: 0,
                                                right: 0,
                                                position: 'absolute',
                                                display: 'flex',
                                                alignItems: 'center',
                                                justifyContent: 'center',
                                            }}
                                        >
                                            <Typography variant="caption" component="div" sx={{ color: 'text.error' }}>
                                                {`${countdown}`}
                                            </Typography>
                                        </Box>
                                    </Box>
                                }
                                onClick={(e) => { e.stopPropagation(); cancelDelete(); }}
                                color="error"
                                size="small"
                                variant="text"
                            >
                                {t('undo')}
                            </Button>
                        )}
                    </Stack>
                </ListItemButton>

            </ListItem>
            <Menu anchorEl={contextMenu} open={Boolean(contextMenu)} onClose={handleClose}>
                {Boolean(contextMenu) && <Box>
                    <OpenInNewTab bookmark={bookmark} handleClose={handleClose} />
                    <OpenInNewWindow bookmark={bookmark} handleClose={handleClose} />
                    <OpenInIncognitoWindow bookmark={bookmark} handleClose={handleClose} />
                    <EditBookmark bookmark={bookmark} handleClose={handleClose} />
                    <DeleteBookmark bookmark={bookmark} handleClose={handleClose} startCountdown={startCountdown} />
                    <CopyUrl bookmark={bookmark} handleClose={handleClose} />
                    <CopyTitle bookmark={bookmark} handleClose={handleClose} />
                    <CopyMarkdown bookmark={bookmark} handleClose={handleClose} />
                </Box>}
            </Menu>
        </Box>
    );
};

export default memo(BookmarkTreeNode);
