import { delay } from "lodash-es";;
import { formatTabData, setTabActive } from "./tabs";
import chromeStorage from "../../utils/chromeStorage";
import { getUserConfig } from "../user";
import { getDomainFromUrl } from "../../utils/helpers";
import { chromeGroupAvailableColors } from "../../utils/colors";


export const openGroupAndCollapseOthers = async (groupId, windowId) => {
    try {

        if (groupId > 0) {
            const group = await chrome.tabGroups?.get(groupId);
            if (!group) return;
            if (group.collapsed) {
                await chrome.tabGroups?.update(group.id, { collapsed: false });
            }
            if (!windowId) {
                windowId = group.windowId;
            }
        }

        let params = { collapsed: false }
        if (windowId) {
            params.windowId = windowId;
        }

        const openedTabGroups = await chrome.tabGroups?.query(params);
        for (const openedTabGroup of openedTabGroups) {
            if (openedTabGroup.id !== groupId) {
                await chrome.tabGroups?.update(openedTabGroup.id, { collapsed: true });
            }
        }
    } catch (error) {
        console.error("Error in openGroupAndCollapseOthers:", error);
    }
};

export const disableGroupCollapse = async (groupId) => {
    try {
        delay(async () => {
            await chrome.tabGroups?.update(groupId, { collapsed: false });
        }, 50);

        const old = await chromeStorage('session').get("ignoreEventsTabGroupIds") || [];
        await chromeStorage('session').set("ignoreEventsTabGroupIds", [...old, groupId]);
        await activeGroupFirstTab(groupId);
    } catch (error) {
        console.error("Error in disableGroupCollapse:", error);
    }
};

