export const groupTabsByDomain = async (windowId) => {
    const errors = [];
    try {
        const settings = await getUserConfig('user_settings');
        const tabQuery = {
            windowType: 'normal',
            pinned: settings.groupFilterOutPinnedTabs ? false : undefined,
        };

        const windows = windowId ? [{ id: windowId }] : await chrome.windows.getAll({ windowTypes: ['normal'] });

        for (const window of windows) {
            tabQuery.windowId = window.id;
            const tabs = await chrome.tabs.query(tabQuery);
            if (!tabs || tabs.length === 0) {
                continue;
            }

            const domainTabs = tabs.reduce((acc, tab) => {
                const domain = parseUrlGroupName(tab.url);
                if (!acc[domain]) {
                    acc[domain] = [];
                }
                acc[domain].push(tab);
                return acc;
            }, {});

            let colorIndex = 0;
            for (const [domain, domainTabList] of Object.entries(domainTabs)) {
                if (domainTabList.length > 0) {
                    try {
                        const color = chromeGroupAvailableColors[colorIndex % chromeGroupAvailableColors.length].color;
                        const groupId = await chrome.tabs.group({ tabIds: domainTabList.map(tab => tab.id), createProperties: { windowId: domainTabList[0].windowId } });
                        await chrome.tabGroups?.update(groupId, { title: domain, color: color, collapsed: true });
                        colorIndex++;
                        await new Promise(resolve => setTimeout(resolve, 100)); // 添加延迟
                    } catch (error) {
                        console.error(`Error grouping tabs for domain ${domain}:`, error);
                        errors.push(`${error.message}`);
                    }
                }
            }
        }
    } catch (error) {
        console.error("Error grouping tabs by domain:", error);
        errors.push(`${error.message}`);
    }

    if (errors.length > 0) {
        throw new Error(errors.join('\n'));
    }
}

export const sortTabsByDomain = async (windowId) => {
    try {
        const settings = await getUserConfig('user_settings');
        const tabQuery = {
            windowType: 'normal',
            currentWindow: settings.onlyCurrentWindowTabs || false,
            pinned: settings.groupFilterOutPinnedTabs ? false : undefined,
            windowId: windowId || undefined
        };

        const tabs = await chrome.tabs.query(tabQuery);
        if (!tabs || tabs.length === 0) {
            return;
        }

        const domainTabs = tabs.reduce((acc, tab) => {
            if (tab.groupId !== -1) {
                return acc; // Skip tabs that are already in a group
            }
            const domain = getDomainFromUrl(tab.url);
            if (!acc[domain]) {
                acc[domain] = [];
            }
            acc[domain].push(tab);
            return acc;
        }, {});

        let newIndex = 0;
        const sortedDomains = Object.keys(domainTabs).sort();
        for (const domain of sortedDomains) {
            const domainTabList = domainTabs[domain];
            for (const tab of domainTabList) {
                await chrome.tabs.move(tab.id, { index: newIndex });
                newIndex++;
            }
        }
    } catch (error) {
        console.error("Error sorting tabs by domain:", error);
    }
}
export const sortTabsByURL = async (windowId) => {
    try {
        const settings = await getUserConfig('user_settings');
        const tabQuery = {
            windowType: 'normal',
            currentWindow: settings.onlyCurrentWindowTabs || false,
            pinned: settings.groupFilterOutPinnedTabs ? false : undefined,
            windowId: windowId || undefined
        };

        const tabs = await chrome.tabs.query(tabQuery);
        if (!tabs || tabs.length === 0) {
            return;
        }

        const urlTabs = tabs.filter(tab => tab.groupId === -1).sort((a, b) => a.url.localeCompare(b.url));

        for (let newIndex = 0; newIndex < urlTabs.length; newIndex++) {
            await chrome.tabs.move(urlTabs[newIndex].id, { index: newIndex });
        }
    } catch (error) {
        console.error("Error sorting tabs by URL:", error);
    }
}

export const parseUrlGroupName = (url) => {
    try {
        const parsedUrl = new URL(url);
        switch (parsedUrl.protocol) {
            case 'http:':
            case 'https:':
                return parsedUrl.hostname.replace("www.", '');
            case 'chrome:':
                return parsedUrl.origin;
            case 'chrome-extension:':
                return 'extension';
            case 'view-source:':
                return parsedUrl.protocol;
            default:
                return url.split('/').slice(0, 3).join('/');
        }
    } catch (error) {
        console.error("Error parseUrlGroupName:", error);
        return url.split('/').slice(0, 3).join('/');

    }
}

export const tabJoinDomainGroup = async (tabId) => {
    try {
        const tab = await chrome.tabs.get(tabId);
        if (!tab.url || tab.url === 'about:blank' || tab.url === 'chrome://newtab/') {
            console.warn('tabJoinDomainGroup: Invalid tab URL', tab);
            return false;
        }

        const settings = await getUserConfig('user_settings');
        if (settings?.groupFilterOutPinnedTabs && tab.pinned) {
            console.warn('tabJoinDomainGroup: Tab is pinned', tab);
            return false;
        }

        const groupName = parseUrlGroupName(tab.url);
        const existGroup = await chrome.tabGroups?.query({ title: groupName, windowId: tab.windowId });
        console.info('tabJoinDomainGroup: Checking existing group', { groupName, existGroup });

        if (!existGroup.length) {
            const groupId = await chrome.tabs.group({ tabIds: [tabId], createProperties: { windowId: tab.windowId } });
            await chrome.tabGroups?.update(groupId, { title: groupName, color: chromeGroupAvailableColors[groupId % chromeGroupAvailableColors.length].color, collapsed: true });
            console.info('tabJoinDomainGroup: Added new group', { groupName, groupId });
        } else {
            const existGroupId = existGroup[0].id;
            if (tab.groupId === -1 || tab.groupId !== existGroupId) {
                await chrome.tabs.group({ tabIds: [tabId], groupId: existGroupId });
                await setTabActive(tabId);
            }
        }
    } catch (error) {
        console.error('tabJoinDomainGroup: Error joining tab to group', error);
        return false;
    }
}

