import React from "react";
import { MenuItem, ListItemText, Typography, ListItemIcon } from "@mui/material";
import tabStore from "../../../../../core/stores/tabStore";
import * as Sentry from "@sentry/react";
import { useTranslationContext } from "../../../../Providers/TranslationProvider";

export default function OpenInNewTab({ uuid, onSelect }) {

    const { t } = useTranslationContext();

    const shortcut = tabStore(state => state.quickAccess.find(shortcut => shortcut.uuid === uuid));

    const handleClick = async () => {
        try {
            if (shortcut.urls && shortcut.urls.length > 0) {
                const tabIds = [];
                for (const urlConfig of shortcut.urls) {
                    const tab = await chrome.tabs.create({ url: urlConfig.url });
                    tabIds.push(tab.id);
                }
                if (shortcut.type === 'group') {
                    const groupId = await chrome.tabs.group({ tabIds });
                    await chrome.tabGroups.update(groupId, {
                        title: shortcut.title,
                        color: shortcut.color || 'grey'
                    });
                }
            } else {
                await chrome.tabs.create({ url: shortcut.url });
            }
            if (typeof onSelect === 'function') {
                onSelect();
            }
        } catch (error) {
            console.error(error);
            Sentry.captureException(error);
        }
    };

    return (
        <MenuItem onClick={handleClick}>
            <ListItemText>
                <Typography variant="body1">
                    {shortcut.urls && shortcut.urls.length > 0 ? t("openAll", { count: shortcut.urls.length }) : t("OpenInNewTab")}
                </Typography>
            </ListItemText>
        </MenuItem>
    );
}