import React, { useState, memo, useEffect } from "react";
import { MenuItem, ListItemText, Typography, ListItemIcon, SvgIcon, Dialog, DialogTitle, DialogContent, DialogActions, TextareaAutosize, Checkbox, FormControlLabel, Button, RadioGroup, Radio } from "@mui/material";
import { useTranslationContext } from "../../../Providers/TranslationProvider";
import { setTabMetaCache, tabStore } from "../../../../core/stores/tabStore";
import { siteStore } from "../../../../core/stores/siteStore";
import { sendRuntimeMessage, sendTabMessage } from "../../../../core/browers/runtime";

const RenameTab = ({ tabId, onSelect }) => {
    const { t } = useTranslationContext();
    const [open, setOpen] = useState(false);
    const [newTitle, setNewTitle] = useState("");
    const [rememberTitle, setRememberTitle] = useState(true);
    const [updatePageTitle, setUpdatePageTitle] = useState(true);
    const [matchPattern, setMatchPattern] = useState("exact");
    const [originalTitle, setOriginalTitle] = useState("");

    const { currentTitle, url, hostname, titleOptions, setTabConfig } = tabStore(state => {
        const tab = state.tabs.find(tab => tab.id === tabId);
        return {
            currentTitle: tab?.title,
            url: tab?.url,
            hostname: tab?.hostname || new URL(tab?.url).hostname,
            titleOptions: tab?.titleOptions || [],
            setTabConfig: state.setTabConfig
        };
    });

    const { siteTitleOptions, setSiteConfig } = siteStore(state => ({
        siteTitleOptions: state.sites.find(site => site.hostname === hostname)?.titleOptions || [],
        setSiteConfig: state.setSiteConfig
    }));

    useEffect(() => {
        const siteTitleMatchedOption = siteTitleOptions.find(option => {
            if (option.matchPattern === 'exact') {
                return option.url === url;
            } else if (option.matchPattern === 'contains') {
                return url.includes(option.url) && url.includes('?');
            }
            return false;
        });

        if (siteTitleMatchedOption) {
            setNewTitle(siteTitleMatchedOption.newTitle);
            setRememberTitle(siteTitleMatchedOption.rememberTitle);
            setUpdatePageTitle(siteTitleMatchedOption.updatePageTitle);
            setMatchPattern(siteTitleMatchedOption.matchPattern);
            setOriginalTitle(siteTitleMatchedOption.originalTitle);
        } else {
            setNewTitle(currentTitle);
            setOriginalTitle(currentTitle);
        }
    }, []);

    const handleDialogOpen = (event) => {
        event.stopPropagation();
        setOpen(true);
    };

    const handleDialogClose = (event) => {
        event.stopPropagation();
        setOpen(false);
    };

    const handleSave = async (event) => {
        event.stopPropagation();

        setTabConfig(tabId, { titleOptions: [...titleOptions, { newTitle, matchPattern, url, originalTitle }] });
        setTabMetaCache(tabId, "titleOptions", [...titleOptions, { newTitle, matchPattern, url, originalTitle }]);
        sendRuntimeMessage('update_tab_title_options', { tabId, titleOptions: [...titleOptions, { newTitle, matchPattern, url, originalTitle }] });

        if (updatePageTitle) {
            await sendTabMessage(tabId, 'update_tab_title', { title: newTitle });
        }

        if (rememberTitle) {
            const newConfig = {
                id: crypto.randomUUID(),
                url,
                newTitle,
                originalTitle,
                rememberTitle,
                updatePageTitle,
                matchPattern
            };

            const updatedSiteTitleOptions = [...siteTitleOptions];
            const existingIndex = updatedSiteTitleOptions.findIndex(option =>
                option.url === newConfig.url && option.matchPattern === newConfig.matchPattern
            );

            if (existingIndex !== -1) {
                updatedSiteTitleOptions[existingIndex] = { ...updatedSiteTitleOptions[existingIndex], ...newConfig };
            } else {
                updatedSiteTitleOptions.push(newConfig);
            }

            await setSiteConfig(hostname, { titleOptions: updatedSiteTitleOptions });
            await sendRuntimeMessage('update_site_config', { hostname, config: { titleOptions: updatedSiteTitleOptions } });
        }

        setOpen(false);
        if (typeof onSelect === 'function') {
            onSelect();
        }
    }

    const handleReset = async () => {
        setNewTitle(originalTitle);
        setTabConfig(tabId, { titleOptions: titleOptions.filter(o => o.url != url) });
        setTabMetaCache(tabId, "titleOptions", titleOptions.filter(o => o.url != url));
        sendRuntimeMessage('update_tab_title', { tabId, newTitle: originalTitle });

        if (updatePageTitle) {
            await sendTabMessage(tabId, 'update_tab_title', { title: originalTitle });
        }

        const updatedTitleOptions = titleOptions.filter(option =>
            !(option.url === url && option.matchPattern === matchPattern)
        );

        await setSiteConfig(hostname, { titleOptions: updatedTitleOptions });
        await sendRuntimeMessage('update_site_config', { hostname, config: { titleOptions: updatedTitleOptions } });
    }

    const hasQueryParams = url.includes('?');

    return (
        <>
            <MenuItem onClick={handleDialogOpen}>
                <ListItemIcon>
                    <SvgIcon>
                        <svg xmlns="http://www.w3.org/2000/svg" height="20px" viewBox="0 -960 960 960" width="20px" fill="currentColor"><path d="M200-200h57l391-391-57-57-391 391v57Zm-80 80v-170l528-527q12-11 26.5-17t30.5-6q16 0 31 6t26 18l55 56q12 11 17.5 26t5.5 30q0 16-5.5 30.5T817-647L290-120H120Zm640-584-56-56 56 56Zm-141 85-28-29 57 57-29-28Z" /></svg>
                    </SvgIcon>
                </ListItemIcon>
                <ListItemText>
                    <Typography variant="body1">{t("RenameTab")}</Typography>
                </ListItemText>
            </MenuItem>
            <Dialog open={open} onClose={handleDialogClose} onClick={(e) => e.stopPropagation()}>
                <DialogTitle>{t("RenameTitle")}</DialogTitle>
                <DialogContent>
                    <TextareaAutosize
                        autoFocus
                        minRows={3}
                        style={{ width: '100%', marginTop: '10px', marginBottom: '10px', p: 1 }}
                        placeholder={t("NewTitle")}
                        value={newTitle}
                        onChange={(e) => setNewTitle(e.target.value)}
                    />
                    <FormControlLabel
                        control={
                            <Checkbox
                                checked={rememberTitle}
                                onChange={(e) => setRememberTitle(e.target.checked)}
                            />
                        }
                        label={t("RememberTitleForSite")}
                    />
                    {rememberTitle && (
                        <RadioGroup
                            row
                            value={matchPattern}
                            onChange={(e) => setMatchPattern(e.target.value)}
                        >
                            <FormControlLabel value="exact" control={<Radio />} label={t("ExactMatch")} />
                            <FormControlLabel
                                value="contains"
                                control={<Radio />}
                                label={t("ContainsMatch")}
                                disabled={!hasQueryParams}
                            />
                        </RadioGroup>
                    )}
                    <FormControlLabel
                        control={
                            <Checkbox
                                checked={updatePageTitle}
                                onChange={(e) => setUpdatePageTitle(e.target.checked)}
                            />
                        }
                        label={t("UpdatePageTitle")}
                    />
                </DialogContent>
                <DialogActions>
                    <Button onClick={handleDialogClose}>{t("Cancel")}</Button>
                    <Button onClick={handleReset}>{t("restore")}</Button>
                    <Button onClick={handleSave}>{t("Save")}</Button>
                </DialogActions>
            </Dialog>
        </>
    );
};

export default memo(RenameTab);
