import React, { createContext, useContext, useState } from "react";
import userStore from "../../core/stores/userStore";
import { useEffect } from "react";

const DndEnableContext = createContext();

export const DndProvider = ({ children }) => {

    const { orderBy, sortBy } = userStore((state) => ({
        orderBy: state.settings.orderBy,
        sortBy: state.settings.sortBy,
    }));


    const [enableDnd, setEnableDnd] = useState(sortBy === 'index' && orderBy === 'asc');

    useEffect(() => {
        setEnableDnd(sortBy === 'index' && orderBy === 'asc');
    }, [orderBy, sortBy]);


    return (
        <DndEnableContext.Provider value={{ enableDnd, setEnableDnd }}>
            {children}
        </DndEnableContext.Provider>
    );
};

export const useDndEnableContext = () => {
    const context = useContext(DndEnableContext);
    if (!context) {
        throw new Error('useDndEnableContext must be used within a DndProvider');
    }
    return context;
};
