import React, { memo } from 'react';
import userStore from '../../../../core/stores/userStore';
import { FilterCenterFocus } from '@mui/icons-material';
import Action from './Action';

const ActionScrollToActiveTab = () => {
    const showLabel = userStore(state => state.settings.bottomNavOptions.showLabel && state.settings.bottomNavOptions.navs.find(nav => nav.name === 'scrollToActiveTab')?.showLabel);
    const updateSetting = userStore(state => state.updateSetting);
    const activeTabIndex = userStore(state => state.activeTabIndex);

    const handleClick = async () => {
        if (activeTabIndex > 1) {
            updateSetting('activeTabIndex', 1);
        }
        const activeTabElement = document.querySelector('.tab-item.active');
        if (activeTabElement) {
            activeTabElement.scrollIntoView({ behavior: "smooth", block: "center", inline: "nearest" });
        }
    };

    return (
        <React.Fragment>
            <Action icon={<FilterCenterFocus />} labelKey="ScrollToActiveTab" showLabel={showLabel} handleClick={handleClick} />
        </React.Fragment>
    );
};

export default memo(ActionScrollToActiveTab);