import React, { memo } from "react";
import { Box, Typography } from "@mui/material";

import { useSettingsContext } from "../../../Providers/SettingsProvider";
import { getHostnameFromUrl } from "../../../../utils/helpers";
import TabTimeAgo from "./TabTimeAgo";
import TabUrl from "./TabUrl";
import HighlightedText from "../../../component/HighlightedText";
import userStore from "../../../../core/stores/userStore";
import { siteStore } from "../../../../core/stores/siteStore";


const TabTitle = ({ title, url, hostname, lastAccessed }) => {

    console.log("TabTitle render", title);

    const { view, theme, fontWeight, fontColor, showFullUrl, searchText, tabTitleMulitLines } = userStore((state) => ({
        view: state.settings.view,
        theme: state.settings.theme,
        fontWeight: state.settings.fontWeight,
        fontColor: state.settings.fontColor,
        showFullUrl: state.settings.showFullUrl,
        searchText: state.searchText,
        tabTitleMulitLines: state.settings.tabTitleMulitLines,
    }));

    hostname = hostname || getHostnameFromUrl(url);

    const tabSiteConfig = siteStore(state => (state.sites.find((d) => d.hostname === hostname)))
    const typographyStyles = {
        whiteSpace: "pre-wrap",
        wordWrap: "break-word",
        wordBreak: "break-all",
        textWrap: "break-word",
        overflow: "hidden",
        textOverflow: "ellipsis",
        display: "-webkit-box",
        WebkitLineClamp: Math.max(tabTitleMulitLines, 1),
        WebkitBoxOrient: "vertical",
        paddingTop: "2px",
        paddingBottom: "2px",
        fontWeight: fontWeight,
        color: tabSiteConfig?.fontColor ?? (theme === "dark" ? "text.primary" : fontColor || "text.primary"),
        opacity: 1,
    };

    return (
        <Box sx={{ minWidth: 0, flex: 1 }}>
            <Typography
                variant="h4"
                sx={typographyStyles}
            >
                <HighlightedText text={title} searchText={searchText} />
            </Typography>
            {view === "list" && searchText && <TabUrl url={url} searchText={searchText} WebkitLineClamp={1} />}
            {view === "visual" && (
                <Box>
                    {showFullUrl ? (
                        <TabUrl url={url} />
                    ) : (
                        <TabTimeAgo hostname={hostname} lastAccessed={lastAccessed} />
                    )}
                </Box>
            )}
        </Box>
    );
};

export default memo(TabTitle);