import React, { memo } from "react";
import { MenuItem, ListItemText, Typography } from "@mui/material";
import { tabStore } from "../../../../core/stores/tabStore";
import { sendRuntimeMessage } from "../../../../core/browers/runtime";
import userStore from "../../../../core/stores/userStore";
import { useTranslationContext } from "../../../Providers/TranslationProvider";

const CloseSameSiteOtherTabs = ({ tabId, onSelect }) => {
    const { t } = useTranslationContext();


    const { origin, windowId } = tabStore(state => {
        const tab = state.tabs.find(t => t.id === tabId);
        return {
            origin: tab?.origin,
            windowId: tab?.windowId,
        };
    });

    const handleClick = async () => {
        await sendRuntimeMessage('close_same_domain_other_tabs', { tabId, origin, windowId: windowId });
        if (typeof onSelect === 'function') {
            onSelect();
        }
    };

    return (
        <MenuItem onClick={handleClick}>
            <ListItemText inset>
                <Typography variant="body1">{t("tabMenu.CloseSameSiteOtherTabs")}</Typography>
            </ListItemText>
        </MenuItem>
    );
};

export default memo(CloseSameSiteOtherTabs);