import React, { useState, useEffect } from "react";
import { ListItemIcon, MenuItem, SvgIcon, Typography } from "@mui/material";
import { toast } from "react-toastify";
import userStore from "../../../../core/stores/userStore";
import { useTranslationContext } from "../../../Providers/TranslationProvider";

export default function RemoveDuplicateTabs({ onSelect }) {
    const { t } = useTranslationContext();
    const [duplicateTabs, setDuplicateTabs] = useState([]);
    const theme = userStore(state => state.settings.theme);

    useEffect(() => {
        const fetchDuplicateTabs = async () => {
            const tabs = await chrome.tabs.query({ currentWindow: true });
            let uniqueUrls = new Set();
            let duplicates = [];
            tabs.forEach((tab) => {
                if (uniqueUrls.has(tab.url)) {
                    duplicates.push(tab.id);
                } else {
                    uniqueUrls.add(tab.url);
                }
            });
            setDuplicateTabs(duplicates);
        };

        fetchDuplicateTabs();
    }, []);

    const handleClick = async () => {
        if (duplicateTabs.length > 0) {
            await chrome.tabs.remove(duplicateTabs);
            toast.success(`${duplicateTabs.length} ${t("DuplicateTabsRemoved")}`, {
                position: "bottom-center",
                theme: theme,
                hideProgressBar: true,
                autoClose: 1500
            });
            if (typeof onSelect === 'function') {
                onSelect();
            }
        }
    };

    return (
        <MenuItem disabled={duplicateTabs.length === 0} onClick={handleClick}>
            <ListItemIcon>
                <SvgIcon>
                    <svg xmlns="http://www.w3.org/2000/svg" height="24px" viewBox="0 -960 960 960" width="24px" fill="currentColor"><path d="m476-420 84-84 84 84 56-56-84-84 84-84-56-56-84 84-84-84-56 56 84 84-84 84 56 56ZM320-240q-33 0-56.5-23.5T240-320v-480q0-33 23.5-56.5T320-880h480q33 0 56.5 23.5T880-800v480q0 33-23.5 56.5T800-240H320Zm0-80h480v-480H320v480ZM160-80q-33 0-56.5-23.5T80-160v-560h80v560h560v80H160Zm160-720v480-480Z" /></svg>
                </SvgIcon>
            </ListItemIcon>
            <Typography noWrap variant="body1">
                {duplicateTabs.length > 0 ? t("RemoveDuplicateTabs", { count: duplicateTabs.length }) : t("NoDuplicateTabs")}
            </Typography>
        </MenuItem>
    );
}