import React from "react";
import { Tooltip } from "@mui/material";
import { useSettingsContext } from "../../../Providers/SettingsProvider";
import TabPreview from "./TabPreview";


const TabTooltip = ({ title, url, thumbnail, lastAccessed, children }) => {

    const { tabPreviewEnable, tabPreviewWaitSeconds } = useSettingsContext();

    return (

        <Tooltip

            followCursor

            enterDelay={parseFloat(tabPreviewWaitSeconds) * 1000}

            enterNextDelay={1000}

            componentsProps={{

                tooltip: {

                    sx: {

                        padding: "15px",

                        background: "transparent",

                        zIndex: 99,

                    },

                },

            }}

            title={tabPreviewEnable ? (
                <TabPreview

                    title={title}
                    thumbnail={thumbnail}

                    url={url}

                    lastAccessed={lastAccessed}

                />

            ) : ""}

        >
            {children}

        </Tooltip>

    );

};


export default TabTooltip;
