import { ListItemIcon, MenuItem, SvgIcon, Typography } from "@mui/material";
import React from "react";
import { sendRuntimeMessage } from "../../../../core/browers/runtime";
import userStore from "../../../../core/stores/userStore";
import tabStore from "../../../../core/stores/tabStore";
import { useTranslationContext } from "../../../Providers/TranslationProvider";

export default function CloseAllDiscardedTabs({ onSelect }) {
    const { t } = useTranslationContext();

    const onlyCurrentWindowTabs = userStore((state) => state.settings.onlyCurrentWindowTabs);
    const currentWindow = tabStore((state) => state.currentWindow);

    const handleClick = () => {
        sendRuntimeMessage("close_all_discarded_tabs", { windowId: onlyCurrentWindowTabs ? currentWindow.id : 0 });
        if (typeof onSelect === 'function') {
            onSelect();
        }
    };

    return (
        <MenuItem onClick={handleClick}>
            <ListItemIcon>
                <SvgIcon>
                    <svg xmlns="http://www.w3.org/2000/svg" height="24px" viewBox="0 -960 960 960" width="24px" fill="currentColor"><path d="m576-80-56-56 104-104-104-104 56-56 104 104 104-104 56 56-104 104 104 104-56 56-104-104L576-80ZM120-320v-80h280v80H120Zm0-160v-80h440v80H120Zm0-160v-80h440v80H120Z" /></svg>
                </SvgIcon>
            </ListItemIcon>
            <Typography noWrap variant="body1">
                {t("CloseAllDiscardedTabs")}
            </Typography>
        </MenuItem>
    );
}