import React, { useEffect } from 'react';
import { Box, Divider } from '@mui/material';
import { selectStore } from "../../../core/stores/selectStore";
import BgOverlay from '../BgOverlay';
import userStore from '../../../core/stores/userStore';
import BookmarksHeader from './BookmarksHeader';
import BookmarksList from './BookmarksList';
import { OverlayScrollbarsComponent } from 'overlayscrollbars-react';
import { bookmarkStore } from '../../../core/stores/bookmarkStore';
import PageLoading from '../../component/pageLoading';

const BrowserBookmarksTree = ({ onSelect, addBackGround }) => {
    const fontColor = userStore(state => state.settings.fontColor);

    const { loadBookmarks, bookmarkStoreInitialized } = bookmarkStore((state) => ({
        loadBookmarks: state.loadBookmarks,
        bookmarkStoreInitialized: state.bookmarkStoreInitialized,
    }));

    useEffect(() => {
        loadBookmarks();
        selectStore.setState({ popupPanelActived: true });
        return () => {
            selectStore.setState({ popupPanelActived: false });
        };
    }, [bookmarkStoreInitialized]);


    if (!bookmarkStoreInitialized) {
        return <PageLoading />
    }

    return (
        <React.Fragment>
            {addBackGround && <BgOverlay />}
            <Box sx={{
                padding: 0,
                overflowX: "hidden",
                color: fontColor || "text.primary",
                height: 'calc(100vh - 64px)', // Adjust this value based on your header height
                display: 'flex',
                flexDirection: 'column'
            }}>
                <Box sx={{ marginTop: "10px" }}>
                    <BookmarksHeader showBack={true} />
                </Box>
                <Divider sx={{ marginY: 1 }} />
                <OverlayScrollbarsComponent options={{ scrollbars: { autoHide: 'scroll' } }} style={{ flexGrow: 1 }}>
                    <BookmarksList onSelect={onSelect} />
                </OverlayScrollbarsComponent>
            </Box>
        </React.Fragment>
    );
};

export default BrowserBookmarksTree;