import React from 'react';
import userStore from '../../../../core/stores/userStore';
import Action from './Action';
import { DarkMode } from '@mui/icons-material';
import { LightMode } from '@mui/icons-material';
import { sendRuntimeMessage } from '../../../../core/browers/runtime';

const ActionThemeModes = () => {


    const { showLabel, updateSetting, theme } = userStore(state => ({
        showLabel: state.settings.bottomNavOptions.showLabel && state.settings.bottomNavOptions.navs.find(nav => nav.name === 'themeModes')?.showLabel,
        updateSetting: state.updateSetting,
        theme: state.settings.theme,

    }));


    const handleClick = async () => {
        updateSetting("theme", theme === 'dark' ? 'light' : 'dark');
        sendRuntimeMessage("update_setting", { key: "theme", value: theme === 'dark' ? 'light' : 'dark' })
    }

    return (
        <React.Fragment>
            <Action icon={theme === 'dark' ? <DarkMode /> : <LightMode />} labelKey={theme === 'dark' ? 'DarkMode' : 'LightMode'} showLabel={showLabel} handleClick={handleClick} />
        </React.Fragment>
    );
};

export default ActionThemeModes;