import React, { forwardRef } from "react";
import { MenuList, MenuItem, ListItemText, Typography, ListItemIcon, SvgIcon, Tooltip, Box } from "@mui/material";
import { Close as CloseIcon, Pause, PostAdd } from "@mui/icons-material";
import { toast } from "react-toastify";
import { sampleGroupColor } from "../../../utils/colors";
import { AddtoReadinglist, isSupportReadingList } from "../../../core/browers/readingList";
import { userStore } from "../../../core/stores/userStore";
import { tabStore } from "../../../core/stores/tabStore";
import { sendRuntimeMessage } from "../../../core/browers/runtime";
import { useTranslationContext } from "../../Providers/TranslationProvider";
import WithPermission from "../../component/WithPermission";
import AddTabsToGroup from "./tabsMenu/AddTabsToGroup";
import { selectStore } from "../../../core/stores/selectStore";
import AddTabsToQuickAccess from "./tabsMenu/AddTabsToQuickAccess";
import RemoveTabsFromGroup from "./tabsMenu/RemoveTabsFromGroup";
import MoveTabsToNewWindow from "./tabsMenu/MoveTabsToNewWindow";
import MoveTabsToAnotherWindow from "./tabsMenu/MoveTabsToAnotherWindow";

const TabsMenu = forwardRef(function TabsMenu({ handleMenuClose }, ref) {
  const { t } = useTranslationContext();

  const { setWaitForSelectTab, selectedTabIds, setSelectedTabIds } = selectStore((state) => ({
    setWaitForSelectTab: state.setWaitForSelectTab,
    selectedTabIds: state.selectedTabIds,
    setSelectedTabIds: state.setSelectedTabIds
  }));

  const { theme, onlyCurrentWindowTabs } = userStore(state => ({
    theme: state.settings.theme,
    onlyCurrentWindowTabs: state.settings.onlyCurrentWindowTabs
  }));

  const currentWindow = tabStore(state => state.currentWindow);

  const cleanUp = () => {
    setSelectedTabIds([]);
    setWaitForSelectTab(false);
    handleMenuClose();
  };

  const handleCloseSelectedTabs = async () => {
    await sendRuntimeMessage('bulk_close_tabs', { tabIds: selectedTabIds });
    cleanUp();
  };

  const handleCloseOtherTabs = async () => {
    await sendRuntimeMessage('close_other_tabs', { tabIds: selectedTabIds, windowId: onlyCurrentWindowTabs ? currentWindow.id : 0 });
    cleanUp();
  };

  const handleReloadTabs = async () => {
    for (const tabId of selectedTabIds) {
      await chrome.tabs.reload(tabId);
    }
    cleanUp();
  };

  const handleRemoveTabsFromGroup = async () => {
    chrome.tabs.ungroup(selectedTabIds);
    cleanUp();
  };

  const handleAddTabsToGroup = async () => {
    chrome.tabs.group({ tabIds: selectedTabIds }, groupId => {
      const groupName = t('tabMenu.UntitledGroup');
      const groupColor = sampleGroupColor().tabGroupColor;
      chrome.tabGroups?.update(groupId, { title: groupName, color: groupColor });
    });
    cleanUp();
  };

  const handleAddTabsToReadingList = async () => {
    const tabs = tabStore.getState().tabs.filter(t => selectedTabIds.includes(t.id));
    for (const tab of tabs) {
      await AddtoReadinglist(tab);
    }
    toast.success(t("tabMenu.AddedToReadingListSuccessfully"), {
      position: "bottom-center",
      hideProgressBar: true,
      theme: theme,
      autoClose: 1500
    });
    cleanUp();
  };

  const handleDiscardTabs = async () => {
    for (const tabId of selectedTabIds) {
      await chrome.tabs.discard(tabId);
    }
    cleanUp();
  };

  const selectedTabs = tabStore.getState().tabs.filter(t => selectedTabIds.includes(t.id));
  const allTabsUngrouped = selectedTabs.every(tab => tab.groupId === -1);

  return (
    <Box ref={ref}>
      <MenuList dense sx={{ minWidth: "200px" }}>
        <MenuItem onClick={handleCloseSelectedTabs}>
          <ListItemIcon>
            <CloseIcon />
          </ListItemIcon>
          <ListItemText>
            <Typography variant="body1">{t("tabMenu.closeTab")}</Typography>
          </ListItemText>
        </MenuItem>
        <MenuItem onClick={handleCloseOtherTabs}>
          <ListItemText inset>
            <Typography variant="body1">{t("tabMenu.CloseOtherTabs")}</Typography>
          </ListItemText>
        </MenuItem>
        <MenuItem onClick={handleReloadTabs}>
          <ListItemIcon>
            <SvgIcon>
              <svg xmlns="http://www.w3.org/2000/svg" height="20px" viewBox="0 -960 960 960" width="20px" fill="currentColor">
                <path d="M227-346q-16-30-25.5-63.5T192-480q0-121 85-206t209-82l-57-57 51-51 144 144-144 144-51-51 57-57q-94-2-158 62t-64 154q0 22 4 42t12 39l-53 53ZM480-84 336-228l144-144 51 51-57 57q94 2 158-62t64-154q0-22-4-42t-12-39l53-53q16 30 25.5 63.5T768-480q0 120-85 205.5T474-192l57 57-51 51Z" />
              </svg>
            </SvgIcon>
          </ListItemIcon>
          <ListItemText>
            <Typography variant="body1">{t("tabMenu.Reload")}</Typography>
          </ListItemText>
        </MenuItem>
        <MenuItem onClick={handleDiscardTabs}>
          <ListItemIcon>
            <Pause />
          </ListItemIcon>
          <ListItemText>
            <Tooltip arrow placement="top" title={<Typography>{t("tabMenu.DiscardTabDesc")}</Typography>}>
              <Typography variant="body1">{t("tabMenu.DiscardTab", { count: selectedTabIds.length })}</Typography>
            </Tooltip>
          </ListItemText>
        </MenuItem>
        <WithPermission permissionName={"readingList"}>
          <MenuItem onClick={handleAddTabsToReadingList}>
            <ListItemIcon>
              <PostAdd />
            </ListItemIcon>
            <ListItemText>
              <Typography variant="body1">{t("tabMenu.AddToReadingList")}</Typography>
            </ListItemText>
          </MenuItem>
        </WithPermission>
        <MoveTabsToNewWindow onSelect={cleanUp} />
        <MoveTabsToAnotherWindow onSelect={cleanUp} />
        <AddTabsToGroup onSelect={cleanUp} />
        <RemoveTabsFromGroup onSelect={cleanUp} />
        <AddTabsToQuickAccess onSelect={cleanUp} />


        {!allTabsUngrouped && (
          <MenuItem onClick={handleRemoveTabsFromGroup}>
            <ListItemIcon>
              <SvgIcon>
                <svg xmlns="http://www.w3.org/2000/svg" height="20px" viewBox="0 -960 960 960" width="20px" fill="currentColor">
                  <path d="m758-240-72-72h106v-408H278l-38-38v-34q2-29 22.5-50.5T312-864h480q29.7 0 50.85 21.15Q864-821.7 864-792v480q0 30-21 51t-51 21h-34Zm12 216L555-240H312q-29.7 0-50.85-21.15Q240-282.3 240-312v-243L24-771l51-51L821-75l-51 51ZM312-312h171L312-483v171ZM168-96q-29.7 0-50.85-21.15Q96-138.3 96-168v-552h72v552h540v72H168Zm257-280Zm104-99Z" />
                </svg>
              </SvgIcon>
            </ListItemIcon>
            <ListItemText>
              <Typography variant="body1">{t("tabMenu.RemoveFromGroup")}</Typography>
            </ListItemText>
          </MenuItem>
        )}
      </MenuList>
    </Box>
  );
});

export default TabsMenu;