import React, { useState } from 'react';
import { MenuItem, Dialog, DialogTitle, DialogContent, DialogActions, Button, TextField, Box, Typography, ListItemIcon, ListItemText } from '@mui/material';
import { useTranslationContext } from '../../../Providers/TranslationProvider';
import SelectFolder from '../SelectFolder';
import { CreateNewFolder } from '@mui/icons-material';

const AddFolder = ({ folder, handleClose }) => {
    const { t } = useTranslationContext();
    const [dialogOpen, setDialogOpen] = useState(false);
    const [folderName, setFolderName] = useState('');
    const [parentFolder, setParentFolder] = useState(folder.id || '');

    const handleMenuItemClick = () => {
        setDialogOpen(true);
    };

    const handleDialogClose = () => {
        setDialogOpen(false);
        handleClose();
    };

    const handleSave = () => {
        chrome.bookmarks?.create({
            parentId: parentFolder,
            title: folderName
        }, () => {
            setDialogOpen(false);
            handleClose();
        });
    };

    const handleFolderChange = (newValue) => {
        if (newValue && newValue.id) {
            setParentFolder(newValue.id);
        } else {
            setParentFolder('');
        }
    };

    const isSaveDisabled = !folderName || !parentFolder;

    const handleInputChange = (event) => {
        event.stopPropagation();
        setFolderName(event.target.value);
    };

    const handleKeyDown = (event) => {
        event.stopPropagation();
    };

    return (
        <Box>
            <MenuItem dense onClick={handleMenuItemClick}>
                <ListItemIcon>
                    <CreateNewFolder />
                </ListItemIcon>
                <ListItemText>
                    <Typography variant="body1">{t('addFolder')}</Typography>
                </ListItemText>
            </MenuItem>
            <Dialog fullWidth open={dialogOpen} onClose={handleDialogClose}>
                <DialogTitle>{t('addFolder')}</DialogTitle>
                <DialogContent>
                    <Box mb={2}>
                        <TextField
                            label={t('folderName')}
                            value={folderName}
                            onChange={handleInputChange}
                            onKeyDown={handleKeyDown}
                            fullWidth
                            margin="normal"
                        />
                    </Box>
                    <Box mb={2}>
                        <SelectFolder
                            selectedFolder={parentFolder}
                            onChange={handleFolderChange}
                        />
                    </Box>
                </DialogContent>
                <DialogActions>
                    <Button onClick={handleDialogClose} color="secondary" variant="contained">
                        {t('Cancel')}
                    </Button>
                    <Button onClick={handleSave} color="primary" variant="contained" style={{ marginRight: 8 }} disabled={isSaveDisabled}>
                        {t('Save')}
                    </Button>
                </DialogActions>
            </Dialog>
        </Box>
    );
};

export default AddFolder;
