import React, { memo } from 'react';
import userStore from '../../../../core/stores/userStore';
import DrawerAction from './DrawerAction';
import SearchBox from '../../../component/SearchBox';
import { Search } from '@mui/icons-material';

const ActionSearch = () => {
    const showLabel = userStore(state => state.settings.bottomNavOptions.showLabel && state.settings.bottomNavOptions.navs.find(nav => nav.name === 'search')?.showLabel);

    return (
        <React.Fragment>
            <DrawerAction
                icon={<Search />}
                labelKey='search'
                ChildComponent={SearchBox}
                showLabel={showLabel}
            />
        </React.Fragment>
    );
};

export default memo(ActionSearch);