import { ListItemIcon, MenuItem, SvgIcon, Typography } from "@mui/material";
import React from "react";
import tabStore from "../../../../core/stores/tabStore";
import { useTranslationContext } from "../../../Providers/TranslationProvider";

export default function Reload({ onSelect }) {
    const { t } = useTranslationContext();

    const loadTabsAndGroups = tabStore((state) => ({
        loadTabs: state.loadTabs,
        loadGroups: state.loadGroups
    }));

    const handleClick = async () => {
        const { loadTabs, loadGroups } = loadTabsAndGroups;
        await loadGroups();
        await loadTabs();
        if (typeof onSelect === 'function') {
            onSelect();
        }
    };

    return (
        <MenuItem onClick={handleClick}>
            <ListItemIcon>
                <SvgIcon>
                    <svg xmlns="http://www.w3.org/2000/svg" height="24px" viewBox="0 -960 960 960" width="24px" fill="currentColor">
                        <path d="M160-160v-80h110l-16-14q-52-46-73-105t-21-119q0-111 66.5-197.5T400-790v84q-72 26-116 88.5T240-478q0 45 17 87.5t53 78.5l10 10v-98h80v240H160Zm400-10v-84q72-26 116-88.5T720-482q0-45-17-87.5T650-648l-10-10v98h-80v-240h240v80H690l16 14q49 49 71.5 106.5T800-482q0 111-66.5 197.5T560-170Z" />
                    </svg>
                </SvgIcon>
            </ListItemIcon>
            <Typography noWrap variant="body1">
                {t("Reload")}
            </Typography>
        </MenuItem>
    );
}