import React, { memo, useRef, useEffect, useState } from "react";
import { Box } from "@mui/material";
import TabGroupChrome from "./TabGroupChrome";
import { tabStore } from "../../../core/stores/tabStore";
import { userStore } from "../../../core/stores/userStore";

const TAB_GROUP_ID_NONE = -1;

const TabGroupChromeList = () => {
  const { onlyCurrentWindowTabs } = userStore((state) => ({
    onlyCurrentWindowTabs: state.settings.onlyCurrentWindowTabs,
  }));

  const { loadGroups, groupIds } = tabStore((state) => ({
    loadGroups: state.loadGroups,
    groupIds: state.groups.filter((g => onlyCurrentWindowTabs ? g.windowId === state.currentWindow.id : true)).map(g => g.id),
  }));

  // const [groupIds, setGroupIds] = useState([]);

  useEffect(() => {
    // loadGroups();
  }, [onlyCurrentWindowTabs]);


  const allGroupIdsWithNewPlaceholder = [...groupIds, TAB_GROUP_ID_NONE];

  const tabContainerRef = useRef(null);

  return (
    <Box ref={tabContainerRef}>
      {allGroupIdsWithNewPlaceholder.map(groupId => (
        <TabGroupChrome key={groupId} groupId={groupId} />
      ))}
    </Box>
  );
};

export default memo(TabGroupChromeList);