import React, { useState, useEffect, useRef, memo } from "react";
import { Divider, Box } from "@mui/material";
import { OverlayScrollbars } from "overlayscrollbars";
import SearchBox from "../component/SearchBox";
import OpenedTabPined from "./tabs/OpenedTabPined";
import Hotkeys from "../component/Hotkeys";
import { userStore } from "../../core/stores/userStore";
import tabStore from "../../core/stores/tabStore";
import BgOverlay from "./BgOverlay";
import TabContent from "./TabContent";
import BottomNav from "./bottomNav";
import ReopenSnapshotToast from "./toasts/ReopenSnapshotToast";
import { useTranslationContext } from "../Providers/TranslationProvider";
import { scrollToActiveTab } from "../../utils/helpers";
import { siteStore } from "../../core/stores/siteStore";
import Toolbar from "./toolbar";
import { tabChangedEvents } from "../eventHandlers/tabChangedEvents";
import { initRuntimeMessageEvents } from "../eventHandlers/runtimeMessageEvents";
import TabNavs from "./tabNavs/TabNavs";
import { bookmarkStore } from "../../core/stores/bookmarkStore";
import ClearTabs from "./tabs/ClearTabs";

const fontScaleMap = new Map([
  [0.5625, 0.8875],
  [0.625, 0.89],
  [0.6875, 0.915],
  [0.75, 1],
  [0.8125, 0.935],
  [0.875, 0.945],
  [0.9375, 0.955],
  [1, 1.0125],
  [1.0625, 1.025],
  [1.125, 1.15],
  [1.1875, 1.185],
  [1.25, 1.205],
]);

const SidePanel = () => {

  const {
    backgroundImageUrl,
    backgroundImageBlur,
    backgroundImageBrightness,
    backgroundImageFade,
    searchBoxPosition,
    showToolbar,
    showBottomNav,
    activeTabIndex,
    fontScale,
  } = userStore((state) => state.settings);

  const { tabCount, quickAccessCount } = tabStore((state) => ({
    tabCount: state.tabs.length,
    quickAccessCount: state.quickAccess.length,
  }));

  const [usedHeight, setUsedHeight] = useState('75vh');

  const refs = {
    searchBoxRef: useRef(),
    toolbarRef: useRef(),
    tabNavsRef: useRef(),
    pinedRef: useRef(),
    bottomNavRef: useRef(),
  };

  const realHeight = () => {
    let h = 0;
    for (const ref of Object.values(refs)) {
      if (ref.current) {
        h += ref.current.offsetHeight;
      }
    }
    setUsedHeight(`calc(100vh - ${h}px)`);
  }

  useEffect(() => {
    tabChangedEvents();
    initRuntimeMessageEvents();
  }, [])

  const { loadBookmarks, bookmarkStoreInitialized } = bookmarkStore((state) => ({
    loadBookmarks: state.loadBookmarks,
    bookmarkStoreInitialized: state.bookmarkStoreInitialized,
  }));

  useEffect(() => {
    if (activeTabIndex === 2 && !bookmarkStoreInitialized) {
      loadBookmarks();
    }
  }, [bookmarkStoreInitialized]);

  useEffect(() => {

    realHeight();
    window.addEventListener('resize', realHeight);

    const tabContentWrapper = document.querySelector('#tab-content-wrapper');
    if (tabContentWrapper) {
      OverlayScrollbars(tabContentWrapper, {
        overflow: { x: 'hidden' },
        scrollbars: { autoHide: 'scroll' }
      });
    }
    scrollToActiveTab();
    return () => {
      window.removeEventListener('resize', realHeight);
    };
  }, [activeTabIndex, searchBoxPosition, showToolbar, showBottomNav, fontScale, tabCount, quickAccessCount]);

  return (
    <React.Fragment>
      <Hotkeys />
      <BgOverlay />
      <Box sx={{
        position: "relative", height: "100vh", overflow: "hidden",
        backdropFilter: backgroundImageUrl ? "none" : `blur(${backgroundImageBlur}px) brightness(${backgroundImageBrightness}) opacity(${backgroundImageFade})`
      }}>
        {searchBoxPosition === 'top' && <Box ref={refs.searchBoxRef}><SearchBox /></Box>}
        <Divider />
        {showToolbar && <Toolbar ref={refs.toolbarRef} />}
        <Divider />
        <OpenedTabPined ref={refs.pinedRef} />
        <Box sx={{ width: "100%" }}>
          <Box ref={refs.tabNavsRef}><TabNavs /></Box>
          <Box
            id="tab-content-wrapper"
            component='div'
            sx={{ height: usedHeight, overflowX: "hidden", overflowY: "auto" }}
          >
            <TabContent />
          </Box>
          {searchBoxPosition === 'bottom' && (
            <Box ref={refs.searchBoxRef} sx={{ borderTop: "1px solid", borderColor: 'divider' }}>
              <SearchBox />
            </Box>
          )}
        </Box>
        {showBottomNav && <BottomNav ref={refs.bottomNavRef} />}
        <ReopenSnapshotToast key="reopenSnapshotToast" />
      </Box>
    </React.Fragment>
  );
}

export default memo(SidePanel);