import React, { memo } from "react";
import { MenuItem, ListItemText, Typography, ListItemIcon, SvgIcon } from "@mui/material";
import { useTranslationContext } from "../../../Providers/TranslationProvider";
import tabStore from "../../../../core/stores/tabStore";
import userStore from "../../../../core/stores/userStore";
import { siteStore } from "../../../../core/stores/siteStore";
import { sendRuntimeMessage } from "../../../../core/browers/runtime";

const PipSiteConfig = ({ tabId, onSelect }) => {
    const { t } = useTranslationContext();

    const enableSwitchPip = userStore(state => state.settings.tabSwitchPip);

    const { pipSupport, hostname } = tabStore(state => {
        const tab = state.tabs.find(tab => tab.id === tabId);
        return {
            pipSupport: tab?.pipSupport,
            hostname: tab?.hostname
        };
    });

    const { siteConfig, setSiteConfig } = siteStore(state => ({
        siteConfig: state.sites.find(d => d.hostname === hostname),
        setSiteConfig: state.setSiteConfig
    }));

    const handleClick = async () => {
        const newConfig = { tabSwitchPip: (typeof siteConfig?.tabSwitchPip === 'boolean') ? !siteConfig?.tabSwitchPip : false };
        await setSiteConfig(hostname, newConfig);
        await sendRuntimeMessage('update_site_config', { hostname, config: newConfig });
        if (typeof onSelect === 'function') {
            onSelect();
        }
    };

    if (!pipSupport || !enableSwitchPip) {
        return null;
    }

    return (
        <MenuItem onClick={handleClick}>
            <ListItemIcon>
                <SvgIcon>
                    <svg xmlns="http://www.w3.org/2000/svg" height="24px" viewBox="0 -960 960 960" width="24px" fill="currentColor">
                        <path d={siteConfig?.tabSwitchPip === false
                            ? "M160-160q-33 0-56.5-23.5T80-240v-480q0-33 23.5-56.5T160-800h640q33 0 56.5 23.5T880-720v480q0 33-23.5 56.5T800-160H160Zm0-80h640v-480H160v480Zm0 0v-480 480Zm280-40h320v-240H440v240Zm80-80v-80h160v80H520Z"
                            : "m554-520 80 80h66q25 0 42.5-17.5T760-500v-120q0-25-17.5-42.5T700-680H500q-22 0-38 14t-21 34l33 32h206v80H554Zm317 317-71-71v-446H354l-80-80h526q33 0 56.5 23.5T880-720v480q0 10-2 19.5t-7 17.5ZM383-463Zm194-34ZM818-28 686-160H160q-33 0-56.5-23.5T80-240v-480q0-33 23.5-56.5T160-800l80 80h-80v480h446L26-820l57-57L875-85l-57 57Z"} />
                    </svg>
                </SvgIcon>
            </ListItemIcon>
            <ListItemText>
                <Typography variant="body1">{t(siteConfig?.tabSwitchPip === false ? "tabMenu.EnableSiteTabSwitchPip" : "tabMenu.DisableSiteTabSwitchPip")}</Typography>
            </ListItemText>
        </MenuItem>
    );
};

export default memo(PipSiteConfig);