import React, { useState, useEffect, useCallback } from "react";
import { CircularProgress, IconButton, Tooltip, Button, Box, Typography, SvgIcon, Dialog, DialogTitle, DialogContent, DialogActions, List, ListItem, ListItemText, Stack, Link, Chip } from "@mui/material";
import { Check, Close, Delete } from "@mui/icons-material";
import { useTranslationContext } from "../../Providers/TranslationProvider";
import { bookmarkStore } from "../../../core/stores/bookmarkStore";

const BookmarkStatus = ({ bookmarkId, startCountdown, isDeleting }) => {
    const { t } = useTranslationContext();
    const { bookmarkStatus, bookmarks, setBookmarksStatus } = bookmarkStore((state) => ({
        bookmarkStatus: state.bookmarksStatus.find(status => status.id === bookmarkId),
        bookmarks: state.bookmarks,
        setBookmarksStatus: state.setBookmarksStatus,
    }));

    const [openDialog, setOpenDialog] = useState(false);
    const [openUrlDialog, setOpenUrlDialog] = useState(false);


    const handleDelete = (event) => {
        event.stopPropagation();
        if (typeof startCountdown === 'function') {
            startCountdown();
        }
    };

    const handleDeleteDupBookmarks = async (event, bookmarkId) => {
        event.stopPropagation();
        try {
            await chrome.bookmarks.remove(bookmarkId);
            setBookmarksStatus(bookmarkId, { duplicate: false });
        } catch (error) {
            console.error("Failed to delete duplicate bookmark:", error);
        }

    };

    const handleDuplicateClick = (event) => {
        event.stopPropagation();
        setOpenDialog(true);
    };

    const handleCloseDialog = (event) => {
        event.stopPropagation();
        setOpenDialog(false);
    };

    const handleUrlDialogOpen = (event) => {
        event.stopPropagation();
        setOpenUrlDialog(true);
    };

    const handleUrlDialogClose = (event) => {
        event.stopPropagation();
        setOpenUrlDialog(false);
    };

    const handleUpdateUrl = () => {
        chrome.bookmarks.update(bookmarkId, { url: bookmarkStatus.finalUrl }, () => {
            setBookmarksStatus(bookmarkId, { finalUrl: '' });
            setOpenUrlDialog(false);
        });
    };

    const bookmark = bookmarks.find(b => b.id === bookmarkId);

    const handleUrlClick = (url) => {
        chrome.tabs.create({ url, active: true });
    };

    const highlightDifference = (oldUrl, newUrl) => {
        if (!oldUrl || !newUrl) {
            return {
                oldUrl: <span>{oldUrl}</span>,
                newUrl: <span>{newUrl}</span>
            };
        }

        let i = 0;
        while (i < oldUrl.length && i < newUrl.length && oldUrl[i] === newUrl[i]) {
            i++;
        }

        let j = oldUrl.length - 1;
        let k = newUrl.length - 1;
        while (j >= i && k >= i && oldUrl[j] === newUrl[k]) {
            j--;
            k--;
        }

        return {
            oldUrl: (
                <>
                    <span>{oldUrl.slice(0, i)}</span>
                    <span style={{ backgroundColor: 'yellow' }}>{oldUrl.slice(i, j + 1)}</span>
                    <span>{oldUrl.slice(j + 1)}</span>
                </>
            ),
            newUrl: (
                <>
                    <span>{newUrl.slice(0, i)}</span>
                    <span style={{ backgroundColor: 'yellow' }}>{newUrl.slice(i, k + 1)}</span>
                    <span>{newUrl.slice(k + 1)}</span>
                </>
            )
        };
    };

    if (isDeleting) {
        return null;
    }

    const { oldUrl: highlightedOldUrl, newUrl: highlightedNewUrl } = highlightDifference(bookmark?.url, bookmarkStatus?.finalUrl);

    return (
        <Box>
            {bookmarkStatus?.status === 'loading' && (
                <Tooltip title={t('loading')} placement="top" PopperProps={{ style: { zIndex: 1500 } }}>
                    <IconButton size="small" onClick={(e) => e.stopPropagation()}>
                        <CircularProgress size={12} />
                    </IconButton>
                </Tooltip>
            )}
            {bookmarkStatus?.duplicate && (
                <>
                    <Tooltip title={t('duplicateBookmarks')} placement="top" PopperProps={{ style: { zIndex: 1500 } }}>
                        <IconButton size="small" onClick={handleDuplicateClick}>
                            <SvgIcon>
                                <svg xmlns="http://www.w3.org/2000/svg" height="24px" viewBox="0 -960 960 960" width="24px" fill="currentColor"><path d="M160-120q-33 0-56.5-23.5T80-200v-280h80v280h360v80H160Zm160-160q-33 0-56.5-23.5T240-360v-280h80v280h360v80H320Zm160-160q-33 0-56.5-23.5T400-520v-240q0-33 23.5-56.5T480-840h320q33 0 56.5 23.5T880-760v240q0 33-23.5 56.5T800-440H480Zm0-80h320v-160H480v160Z" /></svg>
                            </SvgIcon>
                        </IconButton>
                    </Tooltip>
                    <Dialog fullWidth open={openDialog} onClose={handleCloseDialog} onContextMenu={e => e.stopPropagation()} onClick={(e) => { e.stopPropagation(); }}>
                        <DialogTitle>{t('duplicateBookmarks')}</DialogTitle>
                        <DialogContent sx={{ padding: 0 }}>
                            <List>
                                {bookmarkStatus.dupBookmarks?.map((dupBookmark, index) => (
                                    <ListItem dense key={index} sx={{ display: 'flex', alignItems: 'center' }}>
                                        <IconButton size="small" onClick={(e) => { handleDeleteDupBookmarks(e, dupBookmark.bookmarkId) }}>
                                            <Delete color="error" />
                                        </IconButton>
                                        <Typography direction="row" spacing={0}>
                                            {dupBookmark.bookmarkPaths?.slice(1).join('/')}
                                        </Typography>
                                    </ListItem>
                                ))}
                            </List>
                        </DialogContent>
                        <DialogActions>
                            <Button onClick={handleCloseDialog}>{t('cancel')}</Button>
                        </DialogActions>
                    </Dialog>
                </>
            )
            }
            {
                bookmarkStatus?.finalUrl && (
                    <>
                        <Tooltip title={t('updateBookmarkUrl')} placement="top" PopperProps={{ style: { zIndex: 1500 } }}>
                            <IconButton color="warning" size="small" onClick={handleUrlDialogOpen}>
                                <SvgIcon>
                                    <svg xmlns="http://www.w3.org/2000/svg" height="24px" viewBox="0 -960 960 960" width="24px" fill="currentColor"><path d="M120-160v-80h110l-16-14q-52-46-73-105t-21-119q0-111 66.5-197.5T360-790v84q-72 26-116 88.5T200-478q0 45 17 87.5t53 78.5l10 10v-98h80v240H120Zm360-120q-17 0-28.5-11.5T440-320q0-17 11.5-28.5T480-360q17 0 28.5 11.5T520-320q0 17-11.5 28.5T480-280Zm-40-160v-240h80v240h-80Zm160 270v-84q72-26 116-88.5T760-482q0-45-17-87.5T690-648l-10-10v98h-80v-240h240v80H730l16 14q49 49 71.5 106.5T840-482q0 111-66.5 197.5T600-170Z" /></svg>
                                </SvgIcon>
                            </IconButton>
                        </Tooltip>
                        <Dialog fullWidth open={openUrlDialog} onClose={handleUrlDialogClose} onClick={(e) => { e.stopPropagation(); }}>
                            <DialogTitle variant="h3">{t('updateBookmarkUrl')}</DialogTitle>
                            <DialogContent>
                                <Typography sx={{ marginY: 2, color: 'warning.main' }}>{t('foundUrlRedirect')}</Typography>
                                <Typography sx={{ marginBottom: 2, wordBreak: 'break-word' }}>
                                    {t('oldUrl')}: <Link href="#" onClick={() => handleUrlClick(bookmark?.url)}>{highlightedOldUrl}</Link>
                                </Typography>
                                <Typography sx={{ wordBreak: 'break-word' }}>
                                    {t('newUrl')}: <Link href="#" onClick={() => handleUrlClick(bookmarkStatus.finalUrl)}>{highlightedNewUrl}</Link>
                                </Typography>
                            </DialogContent>
                            <DialogActions>
                                <Button onClick={handleUrlDialogClose}>{t('cancel')}</Button>
                                <Button onClick={handleUpdateUrl} color="primary">{t('Save')}</Button>
                            </DialogActions>
                        </Dialog>
                    </>
                )
            }

            {
                bookmarkStatus?.status === 'accessible' && (
                    <Tooltip title={t('accessible')} placement="top" PopperProps={{ style: { zIndex: 1500 } }}>
                        <>
                            {bookmarkStatus.statusCode && bookmarkStatus.statusCode !== 200 && <Chip label={bookmarkStatus.statusCode} size="small" />}
                            <IconButton size="small" onClick={(e) => e.stopPropagation()}>
                                <Check color="success" />
                            </IconButton>
                        </>
                    </Tooltip>
                )
            }
            {
                bookmarkStatus?.status === 'inaccessible' && (
                    <>
                        {bookmarkStatus.statusCode &&
                            <Tooltip title={bookmarkStatus.statusText || ""} placement="top" PopperProps={{ style: { zIndex: 1500 } }}>
                                <Chip label={bookmarkStatus.statusCode} size="small" />
                            </Tooltip>}
                        <Tooltip title={t('deleteBookmark')} placement="top" PopperProps={{ style: { zIndex: 1500 } }}>
                            <IconButton size="small" onClick={handleDelete}>
                                <Close color="error" />
                            </IconButton>
                        </Tooltip>
                    </>
                )
            }

        </Box >
    );
};

export default BookmarkStatus;
