import React, { memo } from "react";
import { useSortable } from "@dnd-kit/sortable";
import { CSS } from '@dnd-kit/utilities';
import TabAsIcon from "./TabAsIcon";

const SortableTabIcon = ({ tabId }) => {
    const {
        attributes,
        listeners,
        setNodeRef,
        transform,
        transition,
        isDragging,
    } = useSortable({ id: tabId, data: { type: "tab-icon" } });

    const style = {
        transform: CSS.Transform.toString(transform),
        transition,
        opacity: isDragging ? 0.5 : 1,
    };

    return (
        <div
            ref={setNodeRef}
            style={style}
            {...listeners}
            {...attributes}
        >
            <TabAsIcon tabId={tabId} />
        </div>
    );
};

export default memo(SortableTabIcon);
