import React, { useEffect, useState } from 'react';
import { Typography, Box } from '@mui/material';
import { useTranslationContext } from '../../Providers/TranslationProvider';
import userStore from '../../../core/stores/userStore';
import SortableContainer from './SortableContainer';
import BookmarkTreeNode from './BookmarkTreeNode';
import { bookmarkStore, setupBookmarkListeners } from '../../../core/stores/bookmarkStore';
import { memo } from 'react';
import BookmarkFolder from './BookmarkFolder';
import PermissionRequired from '../bottomNav/actions/PermissionRequired';

const BookmarksList = ({ onSelect }) => {

    console.log("BookmarksList render")

    const { t } = useTranslationContext();

    const { theme, fontColor, fontScale, bookmarkViewMode, bookmarkHideEmptyFolders, bookmarkSortOrder, bookmarkTreeConnectorLines, searchQuery } = userStore((state) => ({
        theme: state.settings.theme,
        fontColor: state.settings.fontColor,
        fontScale: state.settings.fontScale,
        bookmarkViewMode: state.settings.bookmarkViewMode,
        bookmarkHideEmptyFolders: state.settings.bookmarkHideEmptyFolders,
        bookmarkSortOrder: state.settings.bookmarkSortOrder,
        bookmarkTreeConnectorLines: state.settings.bookmarkTreeConnectorLines,
        searchQuery: state.searchQuery
    }));

    const { bookmarkTree, loadBookmarks } = bookmarkStore((state) => ({
        bookmarkTree: state.bookmarkTree,
        loadBookmarks: state.loadBookmarks
    }));

    const [selectedIndex, setSelectedIndex] = useState(-1);

    useEffect(() => {
        const cleanupListeners = setupBookmarkListeners();
        return () => {
            cleanupListeners();
        };
    }, []);

    const sortBookmarks = (bookmarks) => {
        switch (bookmarkSortOrder) {
            case 'title':
                return [...bookmarks].sort((a, b) => (a.title?.toLowerCase() || "").localeCompare(b.title?.toLowerCase() || ""));
            case 'url':
                return [...bookmarks].sort((a, b) => (a.url?.toLowerCase() || "").localeCompare(b.url?.toLowerCase() || ""));
            case 'mostUsed':
                // Implement most used sorting logic
                return bookmarks;
            case 'recentlyUsed':
                return [...bookmarks].sort((a, b) => new Date(b.dateLastUsed) - new Date(a.dateLastUsed));
            case 'recentlyAdded':
                return [...bookmarks].sort((a, b) => new Date(b.dateAdded) - new Date(a.dateAdded));
            default:
                return bookmarks;
        }
    };

    const filterBookmarks = (bookmarks, query) => {
        if (!query) return bookmarks;
        const keywords = query.replace(/\s+/g, ' ').trim().toLowerCase().split(' ');
        return bookmarks.filter(bookmark =>
            keywords.every(keyword =>
                (bookmark.title && bookmark.title.toLowerCase().includes(keyword)) ||
                (bookmark.url && bookmark.url.toLowerCase().includes(keyword))
            )
        );
    };

    const renderBookmarksTree = (bookmarks, level = 0) => {
        const sortedBookmarks = sortBookmarks(bookmarks);
        const filteredBookmarks = filterBookmarks(sortedBookmarks, searchQuery);
        const elements = filteredBookmarks.map(bookmark => {
            if (bookmark.url) {
                return (
                    <BookmarkTreeNode
                        key={bookmark.id}
                        bookmarkId={bookmark.id}
                        // selected={bookmark.id === bookmarkTree[selectedIndex]?.id}
                        onSelect={onSelect}
                        level={level}
                    />
                );
            } else if (bookmark.children) {
                return (
                    <BookmarkFolder
                        key={bookmark.id}
                        bookmarkId={bookmark.id}
                        // selected={bookmark.id === bookmarkTree[selectedIndex]?.id}
                        level={level}
                    />
                );
            }
            return null;
        });

        return (
            <Box id="tree" sx={{ position: 'relative' }}>
                {bookmarkTreeConnectorLines ? (
                    elements.map((element, index) => (
                        <Box key={index} sx={{ px: 1 }}>
                            {element}
                        </Box>
                    ))
                ) : (
                    elements.map((element, index) => (
                        <Box
                            key={index}
                            role="treeitem"
                        >
                            {element}
                        </Box>
                    ))
                )}
            </Box>
        );
    };

    const renderBookmarksList = (bookmarks) => {
        const flattenBookmarks = (items) => {
            return items.reduce((acc, item) => {
                if (item.url) {
                    acc.push(item);
                } else if (item.children) {
                    acc.push(...flattenBookmarks(item.children));
                }
                return acc;
            }, []);
        };

        const flatBookmarks = flattenBookmarks(bookmarks);
        const sortedFlatBookmarks = sortBookmarks(flatBookmarks);
        const filteredFlatBookmarks = filterBookmarks(sortedFlatBookmarks, searchQuery);

        return filteredFlatBookmarks.map((bookmark) => (
            <BookmarkTreeNode
                key={bookmark.id}
                bookmarkId={bookmark.id}
                // selected={bookmark.id === bookmarkTree[selectedIndex]?.id}
                onSelect={onSelect}
            />
        ));
    };

    const renderBookmarks = () => {
        if (bookmarkViewMode === 'tree' && !searchQuery) {
            // return renderBookmarksTree(bookmarkTree);
            if (bookmarkSortOrder === 'default') {
                return (
                    <SortableContainer initialItems={bookmarkTree}>
                        {renderBookmarksTree(bookmarkTree)}
                    </SortableContainer>
                );
            } else {
                return renderBookmarksTree(bookmarkTree);
            }
        } else {
            return renderBookmarksList(bookmarkTree);
        }
    };

    return (
        <Box>
            <PermissionRequired permission="bookmarks" updatePermission={loadBookmarks} />
            {bookmarkTree.length === 0 ? (
                <Typography variant="body1" sx={{ padding: "30px 8px", textAlign: "center", color: "text.secondary" }}>
                    {t('emptyHere')}

                </Typography>
            ) : (
                renderBookmarks()
            )}
        </Box>
    );
};

export default memo(BookmarksList);
