import React from 'react';
import userStore from '../../../../core/stores/userStore';
import { Bookmark } from '@mui/icons-material';
import DrawerAction from './DrawerAction';
import BrowserBookmarksTree from '../../bookmarks/BrowserBookmarksTree';

const ActionBookmarks = () => {
    const showLabel = userStore(state => state.settings.bottomNavOptions.showLabel && state.settings.bottomNavOptions.navs.find(nav => nav.name === 'bookmarks')?.showLabel);

    return (
        <DrawerAction
            icon={<Bookmark />}
            labelKey='bookmarks'
            permissionName="bookmarks"
            ChildComponent={(props) => <BrowserBookmarksTree {...props} addBackGround={true} />}
            showLabel={showLabel}
        />
    );
};

export default React.memo(ActionBookmarks);