import React, { memo } from "react";
import { MenuItem, ListItemText, Typography } from "@mui/material";
import tabStore from "../../../../../core/stores/tabStore";
import * as Sentry from "@sentry/react";
import { useTranslationContext } from "../../../../Providers/TranslationProvider";

const Delete = ({ uuid, onSelect }) => {
    const { t } = useTranslationContext();

    const removeQuickAccess = tabStore(state => state.removeQuickAccess);

    const handleClick = async () => {
        try {
            await removeQuickAccess(uuid);
            if (typeof onSelect === 'function') {
                onSelect();
            }
        } catch (error) {
            Sentry.captureException(error);
        }
    };

    return (
        <MenuItem onClick={handleClick}>
            <ListItemText>
                <Typography variant="body1">{t("Delete")}</Typography>
            </ListItemText>
        </MenuItem>
    );
}

export default memo(Delete);