import React from "react";
import { MenuItem, ListItemText, Typography, ListItemIcon, Popover, Box, IconButton, Tooltip } from "@mui/material";
import { useTranslationContext } from "../../../Providers/TranslationProvider";
import { selectStore } from "../../../../core/stores/selectStore";
import { sendRuntimeMessage } from "../../../../core/browers/runtime";
import { ArrowRight, ArrowDropDown, Add } from "@mui/icons-material";
import tabStore from "../../../../core/stores/tabStore";

export default function MoveTabsToAnotherWindow({ onSelect }) {
    const { t } = useTranslationContext();
    const [anchorEl, setAnchorEl] = React.useState(null);
    const [openPopover, setOpenPopover] = React.useState(false);
    const [existingWindows, setExistingWindows] = React.useState([]);

    const { selectedTabIds } = selectStore((state) => ({
        selectedTabIds: state.selectedTabIds,
    }));

    const handleMenuOpen = async (event) => {
        setAnchorEl(event.currentTarget);
        setOpenPopover(true);

        // Query existing windows excluding the current tab's window
        const windows = await chrome.windows.getAll({ populate: true, windowTypes: ['normal'] });
        setExistingWindows(windows.filter(window => !window.tabs.some(tab => selectedTabIds.includes(tab.id))));
    };

    const handleMenuClose = () => {
        setAnchorEl(null);
        setOpenPopover(false);
    };

    const handleMoveToWindow = async (windowId) => {
        await sendRuntimeMessage('move_tabs_to_window', { tabIds: selectedTabIds, windowId });
        tabStore.getState().loadGroups();
        tabStore.getState().loadTabs();
        handleMenuClose();
        if (typeof onSelect === 'function') {
            onSelect();
        }
    };

    const handleMoveToNewWindow = async () => {
        await sendRuntimeMessage('move_tabs_to_new_window', { tabIds: selectedTabIds });
        handleMenuClose();
        if (typeof onSelect === 'function') {
            onSelect();
        }
    };

    return (
        <React.Fragment>
            <MenuItem onClick={handleMenuOpen}>
                <ListItemIcon />
                <ListItemText>
                    <Typography variant="body1">{t("moveTabToAnotherWindow")}</Typography>
                </ListItemText>
                <IconButton>{openPopover ? <ArrowDropDown /> : <ArrowRight />}</IconButton>
            </MenuItem>

            <Popover
                open={openPopover}
                anchorEl={anchorEl}
                onClose={handleMenuClose}
                anchorOrigin={{
                    vertical: 'bottom',
                    horizontal: 'left',
                }}
                transformOrigin={{
                    vertical: 'top',
                    horizontal: 'left',
                }}
            >
                <Box sx={{ width: 250, padding: 1 }}>
                    <MenuItem onClick={handleMoveToNewWindow}>
                        <ListItemIcon>
                            <Add />
                        </ListItemIcon>
                        <ListItemText>
                            <Typography variant="body1">{t("newWindow")}</Typography>
                        </ListItemText>
                    </MenuItem>
                    {existingWindows.length > 0 && (
                        existingWindows.map((window) => (
                            <MenuItem key={window.id} onClick={() => handleMoveToWindow(window.id)}>
                                <ListItemText>
                                    <Tooltip title={window.tabs.length > 1 ? t('windowNameTooltips', { activeTabTitle: window.tabs.find(tab => tab.active).title, count: window.tabs.length - 1 }) : t('windowNameTooltip', { activeTabTitle: window.tabs.find(tab => tab.active).title })}>
                                        <Typography variant="body1">{window.tabs.find(tab => tab.active).title}</Typography>
                                    </Tooltip>
                                </ListItemText>
                            </MenuItem>
                        ))
                    )}
                </Box>
            </Popover>
        </React.Fragment>
    );
}